import React, { useState, useEffect } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { DollarSign, Lock, Eye, EyeOff } from 'lucide-react';
import {
    validateAccountNumber,
    validateBankName,
    validateAccountName,
    NIGERIAN_BANKS,
    formatAccountNumber,
} from '@/utils/bankingUtils';
import { Id } from '../../../convex/_generated/dataModel';

interface BankingCredentialsProps {
    userId?: Id<'users'>;
    isAdminView?: boolean;
}

export function BankingCredentials({ userId, isAdminView = false }: BankingCredentialsProps) {
    const { user } = useAuth();
    const targetUserId = userId || user?._id;

    const bankingInfo = useQuery(
        api.bankingCredentials.getBankingCredentials,
        targetUserId && user?._id ? { userId: targetUserId, viewerId: user._id } : 'skip'
    );

    const updateBanking = useMutation(api.bankingCredentials.updateBankingCredentials);

    const [isEditing, setIsEditing] = useState(false);
    const [showAccountNumber, setShowAccountNumber] = useState(false);
    const [formData, setFormData] = useState({
        bankName: '',
        accountNumber: '',
        accountName: '',
    });
    const [errors, setErrors] = useState<Record<string, string>>({});
    const [isSaving, setIsSaving] = useState(false);

    useEffect(() => {
        if (bankingInfo) {
            setFormData({
                bankName: bankingInfo.bankName || '',
                accountNumber: bankingInfo.accountNumber || '',
                accountName: bankingInfo.accountName || '',
            });
        }
    }, [bankingInfo]);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        // Validate
        const newErrors: Record<string, string> = {};

        if (!validateBankName(formData.bankName)) {
            newErrors.bankName = 'Please enter a valid bank name';
        }

        if (!validateAccountNumber(formData.accountNumber)) {
            newErrors.accountNumber = 'Account number must be 10 digits';
        }

        if (!validateAccountName(formData.accountName)) {
            newErrors.accountName = 'Please enter a valid account name';
        }

        setErrors(newErrors);

        if (Object.keys(newErrors).length > 0) {
            return;
        }

        if (!targetUserId) return;

        setIsSaving(true);

        try {
            await updateBanking({
                userId: targetUserId,
                bankName: formData.bankName.trim(),
                accountNumber: formData.accountNumber.replace(/[\s-]/g, ''),
                accountName: formData.accountName.trim(),
            });

            setIsEditing(false);
            setShowAccountNumber(false);
        } catch (error) {
            console.error('Failed to update banking credentials:', error);
            setErrors({ submit: 'Failed to save banking credentials. Please try again.' });
        } finally {
            setIsSaving(false);
        }
    };

    const hasCredentials = bankingInfo?.bankName && bankingInfo?.accountNumber && bankingInfo?.accountName;
    const canEdit = !isAdminView && user?._id === targetUserId;

    return (
        <Card glass>
            <CardHeader>
                <CardTitle className="flex items-center gap-2">
                    <DollarSign className="w-5 h-5 text-accent-500" />
                    Banking Information
                </CardTitle>
            </CardHeader>
            <CardContent>
                {!isAdminView && (
                    <div className="mb-4 p-3 rounded-lg bg-light-bg dark:bg-dark-bg flex items-start gap-2">
                        <Lock className="w-4 h-4 text-light-muted dark:text-dark-muted mt-0.5 flex-shrink-0" />
                        <p className="text-sm text-light-muted dark:text-dark-muted">
                            Your banking information is secure and only visible to you and administrators.
                            This is required to receive rewards for participating in teaching sessions.
                        </p>
                    </div>
                )}

                {!isEditing ? (
                    <div className="space-y-4">
                        {hasCredentials ? (
                            <>
                                <div>
                                    <label className="block text-sm font-medium text-light-muted dark:text-dark-muted mb-1">
                                        Bank Name
                                    </label>
                                    <p className="text-light-text dark:text-dark-text">
                                        {bankingInfo.bankName}
                                    </p>
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-light-muted dark:text-dark-muted mb-1">
                                        Account Number
                                    </label>
                                    <div className="flex items-center gap-2">
                                        <p className="text-light-text dark:text-dark-text font-mono">
                                            {showAccountNumber
                                                ? bankingInfo.accountNumber
                                                : formatAccountNumber(bankingInfo.accountNumber || '')}
                                        </p>
                                        <button
                                            onClick={() => setShowAccountNumber(!showAccountNumber)}
                                            className="text-light-muted dark:text-dark-muted hover:text-primary-500 transition-colors"
                                        >
                                            {showAccountNumber ? (
                                                <EyeOff className="w-4 h-4" />
                                            ) : (
                                                <Eye className="w-4 h-4" />
                                            )}
                                        </button>
                                    </div>
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-light-muted dark:text-dark-muted mb-1">
                                        Account Name
                                    </label>
                                    <p className="text-light-text dark:text-dark-text">
                                        {bankingInfo.accountName}
                                    </p>
                                </div>
                                {canEdit && (
                                    <Button
                                        onClick={() => setIsEditing(true)}
                                        variant="secondary"
                                        className="w-full"
                                    >
                                        Update Banking Information
                                    </Button>
                                )}
                            </>
                        ) : (
                            <div className="text-center py-6">
                                <p className="text-light-muted dark:text-dark-muted mb-4">
                                    {isAdminView
                                        ? 'This user has not added banking information yet.'
                                        : 'Add your banking information to receive rewards.'}
                                </p>
                                {canEdit && (
                                    <Button onClick={() => setIsEditing(true)} variant="primary">
                                        Add Banking Information
                                    </Button>
                                )}
                            </div>
                        )}
                    </div>
                ) : (
                    <form onSubmit={handleSubmit} className="space-y-4">
                        <div>
                            <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-1">
                                Bank Name <span className="text-red-500">*</span>
                            </label>
                            <select
                                value={formData.bankName}
                                onChange={(e) =>
                                    setFormData({ ...formData, bankName: e.target.value })
                                }
                                className="w-full px-4 py-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-light-text dark:text-dark-text focus:outline-none focus:ring-2 focus:ring-primary-500"
                            >
                                <option value="">Select your bank</option>
                                {NIGERIAN_BANKS.map((bank) => (
                                    <option key={bank} value={bank}>
                                        {bank}
                                    </option>
                                ))}
                            </select>
                            {errors.bankName && (
                                <p className="text-red-500 text-sm mt-1">{errors.bankName}</p>
                            )}
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-1">
                                Account Number <span className="text-red-500">*</span>
                            </label>
                            <input
                                type="text"
                                value={formData.accountNumber}
                                onChange={(e) =>
                                    setFormData({ ...formData, accountNumber: e.target.value })
                                }
                                placeholder="0123456789"
                                maxLength={10}
                                className="w-full px-4 py-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-light-text dark:text-dark-text focus:outline-none focus:ring-2 focus:ring-primary-500 font-mono"
                            />
                            {errors.accountNumber && (
                                <p className="text-red-500 text-sm mt-1">{errors.accountNumber}</p>
                            )}
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-1">
                                Account Name <span className="text-red-500">*</span>
                            </label>
                            <input
                                type="text"
                                value={formData.accountName}
                                onChange={(e) =>
                                    setFormData({ ...formData, accountName: e.target.value })
                                }
                                placeholder="John Doe"
                                className="w-full px-4 py-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-light-text dark:text-dark-text focus:outline-none focus:ring-2 focus:ring-primary-500"
                            />
                            {errors.accountName && (
                                <p className="text-red-500 text-sm mt-1">{errors.accountName}</p>
                            )}
                        </div>

                        {errors.submit && (
                            <p className="text-red-500 text-sm">{errors.submit}</p>
                        )}

                        <div className="flex gap-2">
                            <Button
                                type="submit"
                                variant="primary"
                                className="flex-1"
                                disabled={isSaving}
                            >
                                {isSaving ? 'Saving...' : 'Save'}
                            </Button>
                            <Button
                                type="button"
                                onClick={() => {
                                    setIsEditing(false);
                                    setErrors({});
                                    if (bankingInfo) {
                                        setFormData({
                                            bankName: bankingInfo.bankName || '',
                                            accountNumber: bankingInfo.accountNumber || '',
                                            accountName: bankingInfo.accountName || '',
                                        });
                                    }
                                }}
                                variant="secondary"
                                className="flex-1"
                                disabled={isSaving}
                            >
                                Cancel
                            </Button>
                        </div>
                    </form>
                )}
            </CardContent>
        </Card>
    );
}
