// Detect Nigerian phone network based on prefix
export function detectPhoneNetwork(phone: string): "MTN" | "Glo" | "Airtel" | "9Mobile" | "Unknown" {
    // Remove all non-digit characters
    const cleanPhone = phone.replace(/\D/g, '');

    // Get first 4 digits
    const prefix = cleanPhone.substring(0, 4);

    // MTN prefixes
    const mtnPrefixes = [
        '0703', '0706', '0803', '0806', '0810', '0813', '0814', '0816',
        '0903', '0906', '07025', '07026', '0704'
    ];

    // Glo prefixes
    const gloPrefixes = [
        '0705', '0805', '0807', '0811', '0815', '0905'
    ];

    // Airtel prefixes
    const airtelPrefixes = [
        '0701', '0708', '0802', '0808', '0812', '0901', '0902', '0904', '0907'
    ];

    // 9Mobile prefixes
    const nineMobilePrefixes = [
        '0809', '0817', '0818', '0909', '0908'
    ];

    if (mtnPrefixes.some(p => prefix.startsWith(p))) {
        return "MTN";
    }

    if (gloPrefixes.some(p => prefix.startsWith(p))) {
        return "Glo";
    }

    if (airtelPrefixes.some(p => prefix.startsWith(p))) {
        return "Airtel";
    }

    if (nineMobilePrefixes.some(p => prefix.startsWith(p))) {
        return "9Mobile";
    }

    return "Unknown";
}

// Validate Nigerian phone number format
export function isValidNigerianPhone(phone: string): boolean {
    const cleanPhone = phone.replace(/\D/g, '');

    // Should be 11 digits starting with 0, or 10 digits without 0
    if (cleanPhone.length === 11 && cleanPhone.startsWith('0')) {
        return true;
    }

    if (cleanPhone.length === 10 && !cleanPhone.startsWith('0')) {
        return true;
    }

    return false;
}

// Format phone number for display
export function formatPhoneNumber(phone: string): string {
    const cleanPhone = phone.replace(/\D/g, '');

    if (cleanPhone.length === 11) {
        return `${cleanPhone.substring(0, 4)} ${cleanPhone.substring(4, 7)} ${cleanPhone.substring(7)}`;
    }

    return phone;
}
