import { useState } from 'react';
import { Modal } from '../ui/Modal';
import { Badge } from '../ui/Badge';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { CheckCircle, Clock, XCircle, Users, Calendar, ChevronDown, ChevronUp } from 'lucide-react';
import { Card, CardContent } from '../ui/Card';
import { motion, AnimatePresence } from 'framer-motion';
import { Button } from '../ui/Button';

interface MeetingAttendanceModalProps {
    isOpen: boolean;
    onClose: () => void;
    meeting: any;
}

export function MeetingAttendanceModal({ isOpen, onClose, meeting }: MeetingAttendanceModalProps) {
    const [showStats, setShowStats] = useState(true);
    const attendance = useQuery(
        api.governance.getMeetingAttendance,
        meeting ? { meetingId: meeting._id } : 'skip'
    );

    const getStatusBadge = (status: string) => {
        switch (status) {
            case 'present':
                return <Badge className="bg-emerald-500 text-white">Present</Badge>;
            case 'late':
                return <Badge className="bg-amber-500 text-white">Late</Badge>;
            case 'absent':
                return <Badge className="bg-rose-500 text-white">Absent</Badge>;
            case 'excused':
                return <Badge className="bg-blue-500 text-white">Excused</Badge>;
            default:
                return <Badge variant="outline">Unknown</Badge>;
        }
    };

    const getStatusIcon = (status: string) => {
        switch (status) {
            case 'present':
                return <CheckCircle className="w-5 h-5 text-emerald-500" />;
            case 'late':
                return <Clock className="w-5 h-5 text-amber-500" />;
            case 'absent':
                return <XCircle className="w-5 h-5 text-rose-500" />;
            case 'excused':
                return <CheckCircle className="w-5 h-5 text-blue-500" />;
            default:
                return <XCircle className="w-5 h-5 text-light-muted" />;
        }
    };

    const stats = {
        present: attendance?.filter((a: any) => a.status === 'present').length || 0,
        late: attendance?.filter((a: any) => a.status === 'late').length || 0,
        absent: attendance?.filter((a: any) => a.status === 'absent').length || 0,
        excused: attendance?.filter((a: any) => a.status === 'excused').length || 0,
    };

    const total = attendance?.length || 0;
    const isLoading = attendance === undefined;

    return (
        <Modal
            isOpen={isOpen}
            onClose={onClose}
            title="Meeting Attendance"
            className="max-w-2xl"
        >
            {meeting && (
                <div className="space-y-4 md:space-y-6">
                    {/* Meeting Info */}
                    <div className="p-3 md:p-4 rounded-xl bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border">
                        <h3 className="font-bold text-base md:text-lg mb-2">{meeting.title}</h3>
                        <div className="flex flex-wrap items-center gap-2 md:gap-4 text-xs md:text-sm text-light-muted">
                            <div className="flex items-center gap-1">
                                <Calendar className="w-3 h-3 md:w-4 md:h-4" />
                                <span className="text-xs md:text-sm">{new Date(meeting.date).toLocaleDateString()}</span>
                            </div>
                            <div className="flex items-center gap-1">
                                <Clock className="w-3 h-3 md:w-4 md:h-4" />
                                <span className="text-xs md:text-sm">{meeting.startTime}</span>
                            </div>
                            <div className="flex items-center gap-1">
                                <Users className="w-3 h-3 md:w-4 md:h-4" />
                                <span className="text-xs md:text-sm">{total} Total</span>
                            </div>
                        </div>
                    </div>

                    {/* Stats Header with Toggle */}
                    <div className="flex items-center justify-between px-1">
                        <h4 className="text-xs font-black uppercase tracking-widest text-light-muted">Attendance Summary</h4>
                        <Button
                            variant="ghost"
                            size="sm"
                            className="h-7 text-[10px] font-bold gap-1"
                            onClick={() => setShowStats(!showStats)}
                        >
                            {showStats ? <><ChevronUp className="w-3 h-3" /> Hide Stats</> : <><ChevronDown className="w-3 h-3" /> Show Stats</>}
                        </Button>
                    </div>

                    {/* Collapsible Stats Grid */}
                    <AnimatePresence>
                        {showStats && (
                            <motion.div
                                initial={{ height: 0, opacity: 0 }}
                                animate={{ height: 'auto', opacity: 1 }}
                                exit={{ height: 0, opacity: 0 }}
                                className="overflow-hidden"
                            >
                                <div className="grid grid-cols-2 md:grid-cols-4 gap-2 md:gap-3">
                                    <Card className="border-emerald-500/20 bg-emerald-500/5">
                                        <CardContent className="p-3 md:p-4 text-center">
                                            <div className="text-xl md:text-2xl font-black text-emerald-500">{stats.present}</div>
                                            <div className="text-[9px] md:text-xs font-bold text-light-muted uppercase tracking-tighter">Present</div>
                                        </CardContent>
                                    </Card>
                                    <Card className="border-amber-500/20 bg-amber-500/5">
                                        <CardContent className="p-3 md:p-4 text-center">
                                            <div className="text-xl md:text-2xl font-black text-amber-500">{stats.late}</div>
                                            <div className="text-[9px] md:text-xs font-bold text-light-muted uppercase tracking-tighter">Late</div>
                                        </CardContent>
                                    </Card>
                                    <Card className="border-rose-500/20 bg-rose-500/5">
                                        <CardContent className="p-3 md:p-4 text-center">
                                            <div className="text-xl md:text-2xl font-black text-rose-500">{stats.absent}</div>
                                            <div className="text-[9px] md:text-xs font-bold text-light-muted uppercase tracking-tighter">Absent</div>
                                        </CardContent>
                                    </Card>
                                    <Card className="border-blue-500/20 bg-blue-500/5">
                                        <CardContent className="p-3 md:p-4 text-center">
                                            <div className="text-xl md:text-2xl font-black text-blue-500">{stats.excused}</div>
                                            <div className="text-[9px] md:text-xs font-bold text-light-muted uppercase tracking-tighter">Excused</div>
                                        </CardContent>
                                    </Card>
                                </div>
                            </motion.div>
                        )}
                    </AnimatePresence>

                    {/* Attendance List */}
                    <div className="border-t border-light-border dark:border-dark-border pt-4">
                        <h4 className="font-bold text-sm md:text-base mb-3 flex items-center gap-2">
                            <Users className="w-4 h-4 text-primary-500" />
                            Board Member Roster
                        </h4>

                        <div className="space-y-2 max-h-[40vh] md:max-h-96 overflow-y-auto pr-2 thin-scrollbar">
                            {isLoading ? (
                                // Skeleton for attendance list
                                [1, 2, 3, 4].map(i => (
                                    <div key={i} className="h-16 w-full bg-light-muted/10 animate-pulse rounded-xl border border-light-border/50 dark:border-dark-border/50" />
                                ))
                            ) : attendance && attendance.length > 0 ? (
                                attendance.map((record: any) => (
                                    <div
                                        key={record._id}
                                        className="flex items-center justify-between p-2 md:p-3 rounded-lg border border-light-border dark:border-dark-border hover:border-primary-500/30 transition-colors"
                                    >
                                        <div className="flex items-center gap-2 md:gap-3 flex-1 min-w-0">
                                            {getStatusIcon(record.status)}
                                            <div className="flex-1 min-w-0">
                                                <p className="font-bold text-sm md:text-base truncate">{record.userName}</p>
                                                {record.joinTime && (
                                                    <p className="text-[10px] md:text-xs text-light-muted">
                                                        Clocked in at {new Date(record.joinTime).toLocaleTimeString()}
                                                    </p>
                                                )}
                                            </div>
                                        </div>
                                        <div className="flex-shrink-0">
                                            {getStatusBadge(record.status)}
                                        </div>
                                    </div>
                                ))
                            ) : (
                                <div className="text-center py-8 text-light-muted">
                                    <Users className="w-12 h-12 mx-auto mb-2 opacity-20" />
                                    <p className="text-sm">No attendance records yet</p>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            )}
        </Modal>
    );
}
