import { useState } from 'react';
import { motion } from 'framer-motion';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useNavigate } from 'react-router-dom';
import { Card, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
// import { BackButton } from '@/components/ui/BackButton';
import { Input } from '@/components/ui/Input';
import { Textarea } from '../../components/ui/Textarea';
import { PremiumUserPicker } from '@/components/ui/PremiumUserPicker';
import { PremiumSelect } from '@/components/ui/PremiumSelect';
import { PageHeaderSkeleton, ListSkeleton } from '@/components/ui/Skeletons';

export function CreateBoardTask() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();
    const createTask = useMutation(api.boardTasks.createTask);

    const [taskTitle, setTaskTitle] = useState('');
    const [taskDescription, setTaskDescription] = useState('');
    const [taskType, setTaskType] = useState<string>('Ministry Assignment');
    const [taskPriority, setTaskPriority] = useState<string>('medium');
    const [assignedTo, setAssignedTo] = useState('');
    const [dueDate, setDueDate] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    const allUsers = useQuery(
        api.users.getAllUsers,
        user && (user.role === 'admin' || user.role === 'super_admin')
            ? { adminId: user._id }
            : 'skip'
    );

    const boardMembers = allUsers?.filter((u: any) =>
        u.role === 'board_member' || u.role === 'admin' || u.role === 'super_admin'
    ) || [];

    if (isLoading) {
        return (
            <div className="min-h-screen py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-3xl">
                    <PageHeaderSkeleton />
                    <ListSkeleton count={4} />
                </div>
            </div>
        );
    }
    if (!user) return <Navigate to="/login" replace />;
    if (user.role !== 'admin' && user.role !== 'super_admin') return <Navigate to="/board" replace />;

    const handleCreateTask = async () => {
        if (!taskTitle || !taskDescription || !assignedTo) return;
        setIsSubmitting(true);
        try {
            await createTask({
                adminId: user._id,
                title: taskTitle,
                description: taskDescription,
                type: taskType as any,
                priority: taskPriority as any,
                assignedTo: assignedTo as any,
                dueDate: dueDate ? new Date(dueDate).getTime() : undefined,
            });
            navigate('/board/admin');
        } catch (error) {
            console.error('Failed to create task:', error);
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="max-w-3xl mx-auto space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-10 text-center"
            >
                <div className="flex items-center justify-center gap-2 mb-2">
                    <div className="w-8 h-1 bg-primary-500 rounded-full" />
                    <span className="text-[10px] font-black uppercase tracking-[0.3em] text-primary-500">Operation Command</span>
                </div>
                <h1 className="text-4xl md:text-5xl font-black text-gradient font-display mb-2 tracking-tight">Issue Directive</h1>
                <p className="text-light-muted dark:text-dark-muted font-medium">Formulate and deploy a new mission objective.</p>
            </motion.div>

            <Card glass className="overflow-hidden border-primary-500/20">
                <div className="h-2 bg-gradient-to-r from-primary-500 via-secondary-500 to-primary-500" />
                <CardContent className="p-8 md:p-12 space-y-10">
                    <div className="space-y-8">
                        <Input
                            label="Directive Title"
                            value={taskTitle}
                            onChange={(e) => setTaskTitle(e.target.value)}
                            placeholder="State the core objective..."
                            className="text-lg font-bold h-14 rounded-2xl border-2 focus:border-primary-500 transition-all"
                        />

                        <Textarea
                            label="Operational Details"
                            value={taskDescription}
                            onChange={(e) => setTaskDescription(e.target.value)}
                            rows={6}
                            placeholder="Elucidate the mission requirements and expected outcomes..."
                            className="rounded-[24px] border-2 focus:border-primary-500 transition-all p-6 text-base italic"
                        />
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                        <PremiumSelect
                            label="Mission Classification"
                            value={taskType}
                            onChange={setTaskType}
                            options={[
                                { value: "Ministry Assignment", label: "Ministry Assignment" },
                                { value: "Administrative", label: "Administrative" },
                                { value: "Training", label: "Training" },
                                { value: "Personal Development", label: "Personal Development" },
                                { value: "Team Project", label: "Team Project" }
                            ]}
                        />

                        <PremiumSelect
                            label="Intensity Priority"
                            value={taskPriority}
                            onChange={setTaskPriority}
                            options={[
                                { value: "low", label: "Low Priority" },
                                { value: "medium", label: "Standard Priority" },
                                { value: "high", label: "High Priority" },
                                { value: "urgent", label: "Immediate Attention" }
                            ]}
                        />
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                        <div className="relative z-40">
                            <PremiumUserPicker
                                users={boardMembers}
                                selectedUserId={assignedTo}
                                onSelect={setAssignedTo}
                                label="Assigned Personnel"
                            />
                        </div>
                        <Input
                            type="date"
                            label="Deadline Alignment"
                            value={dueDate}
                            onChange={(e) => setDueDate(e.target.value)}
                            className="h-14 rounded-2xl border-2 focus:border-primary-500"
                        />
                    </div>

                    <div className="flex flex-col-reverse sm:flex-row justify-end gap-4 pt-8">
                        <Button
                            variant="ghost"
                            onClick={() => navigate(-1)}
                            className="h-14 px-8 rounded-2xl font-black text-[10px] uppercase tracking-widest border-2 hover:bg-light-muted/10"
                        >
                            Abort
                        </Button>
                        <Button
                            variant="primary"
                            onClick={handleCreateTask}
                            disabled={isSubmitting}
                            className="h-14 px-12 rounded-2xl font-black text-[10px] uppercase tracking-[0.2em] shadow-2xl shadow-primary-500/40"
                        >
                            {isSubmitting ? (
                                <div className="flex items-center gap-2">
                                    <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                                    <span>Deploying...</span>
                                </div>
                            ) : 'Deploy Directive'}
                        </Button>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}
