function FAQItem({ question, answer }: { question: string; answer: string }) {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <motion.div
            initial={false}
            className="glass rounded-lg overflow-hidden border border-light-border dark:border-dark-border"
        >
            <button
                onClick={() => setIsOpen(!isOpen)}
                className="w-full px-6 py-4 flex items-center justify-between text-left hover:bg-light-bg dark:hover:bg-dark-bg transition-colors"
            >
                <span className="font-semibold text-light-text dark:text-dark-text pr-4">
                    {question}
                </span>
                {isOpen ? (
                    <ChevronUp className="w-5 h-5 text-primary-500 flex-shrink-0" />
                ) : (
                    <ChevronDown className="w-5 h-5 text-light-muted dark:text-dark-muted flex-shrink-0" />
                )}
            </button>

            <motion.div
                initial={false}
                animate={{ height: isOpen ? 'auto' : 0 }}
                transition={{ duration: 0.3, ease: 'easeInOut' }}
                className="overflow-hidden"
            >
                <div className="px-6 pb-4 text-light-muted dark:text-dark-muted">
                    {answer}
                </div>
            </motion.div>
        </motion.div>
    );
}
