import { useState, useEffect, useRef } from 'react';
import { useQuery, usePaginatedQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { PostCard } from './PostCard';
import { PostCardSkeleton } from '@/components/ui/Skeletons';
import { AnimatePresence, motion } from 'framer-motion';
import { ArrowUp, Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/Button';

interface PostFeedProps {
    category?: 'Ancient Culture' | 'Wisdom of The Now';
    onCommentClick?: (post: any) => void;
    selectedPostId?: string;
    onlyOfficial?: boolean;
}

export function PostFeed(props: PostFeedProps) {
    const { category, onCommentClick } = props;
    const { user: _user } = useAuth();

    // Filter State: "All", "Prayers", "Questions"
    // "Ancient" and "Wisdom" tabs removed per request, but state structure maintained for compatibility
    const [filter, setFilter] = useState<{ type: 'postType' | 'all'; value?: string }>({
        type: 'all'
    });

    // Pagination & Feed Control
    // cutoffTime freezes the feed to showing posts created before load.
    const [cutoffTime, setCutoffTime] = useState(Date.now());

    // Infinite Scroll Query
    const { results, status, loadMore } = usePaginatedQuery(
        api.posts.getPaginatedFeed,
        {
            postType: filter.type === 'postType' ? filter.value as any : undefined,
            cutoffTime,
            userId: _user?._id,
        },
        { initialNumItems: 10 }
    );

    // New Posts Check (Live updates)
    const newPostCount = useQuery(api.posts.getNewPostCount, {
        postType: filter.type === 'postType' ? filter.value : undefined,
        cutoffTime,
    }) || 0;

    // Infinite Scroll Observer
    const loadMoreRef = useRef<HTMLDivElement>(null);
    useEffect(() => {
        const observer = new IntersectionObserver(
            ([entry]) => {
                if (entry.isIntersecting && status === "CanLoadMore") {
                    loadMore(10);
                }
            },
            { threshold: 0.1 }
        );

        if (loadMoreRef.current) observer.observe(loadMoreRef.current);
        return () => observer.disconnect();
    }, [status, loadMore]);

    // Scroll To Top Logic
    const [showScrollTop, setShowScrollTop] = useState(false);
    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 2000) setShowScrollTop(true);
            else setShowScrollTop(false);
        };
        window.addEventListener('scroll', handleScroll, { passive: true });
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    const handleRefresh = () => {
        setCutoffTime(Date.now());
        // Delay scroll slightly for smoother transition after data update
        setTimeout(() => {
            window.scrollTo({ top: 0, behavior: 'smooth' });
        }, 150);
    };

    if (status === "LoadingFirstPage" || results === undefined) {
        return (
            <div className="space-y-4">
                <PostCardSkeleton />
                <PostCardSkeleton />
                <PostCardSkeleton />
            </div>
        );
    }

    return (
        <div className="space-y-6 relative min-h-[50vh]">
            {/* New Posts Notification */}
            <AnimatePresence>
                {newPostCount > 0 && (
                    <motion.div
                        initial={{ opacity: 0, y: -20 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: -20 }}
                        className="flex justify-center mb-4 sticky top-24 z-30 pointer-events-none"
                    >
                        <Button
                            onClick={handleRefresh}
                            size="sm"
                            className="pointer-events-auto rounded-full shadow-xl bg-gray-900 hover:bg-gray-800 text-white gap-1.5 sm:gap-2 px-4 py-1.5 sm:px-6 sm:py-2 text-xs sm:text-sm border border-green-500/40 backdrop-blur-md"
                        >
                            <ArrowUp className="w-3.5 h-3.5 sm:w-4 sm:h-4" />
                            {newPostCount} New {newPostCount === 1 ? 'Post' : 'Posts'}
                        </Button>
                    </motion.div>
                )}
            </AnimatePresence>

            {/* Filters */}
            {!category && (
                <div className="flex gap-2 overflow-x-auto pb-2 scrollbar-hide -mx-4 px-4 sm:mx-0 sm:px-0 scroll-smooth">
                    <button
                        onClick={() => setFilter({ type: 'all' })}
                        className={`whitespace-nowrap px-3 py-2 sm:px-6 sm:py-2.5 rounded-full font-bold transition-all text-xs sm:text-sm border shadow-sm active:scale-95 ${filter.type === 'all'
                            ? 'bg-primary-500 text-white border-primary-500 shadow-primary-500/20'
                            : 'bg-white dark:bg-dark-card text-gray-600 dark:text-gray-300 border-gray-200 dark:border-gray-800 hover:bg-gray-50 dark:hover:bg-white/5'
                            }`}
                    >
                        All Posts
                    </button>
                    <button
                        onClick={() => setFilter({ type: 'postType', value: 'prayer' })}
                        className={`whitespace-nowrap px-3 py-2 sm:px-6 sm:py-2.5 rounded-full font-bold transition-all text-xs sm:text-sm border shadow-sm active:scale-95 ${filter.type === 'postType' && filter.value === 'prayer'
                            ? 'bg-purple-500 text-white border-purple-500 shadow-purple-500/20'
                            : 'bg-white dark:bg-dark-card text-gray-600 dark:text-gray-300 border-gray-200 dark:border-gray-800 hover:bg-gray-50 dark:hover:bg-white/5'
                            }`}
                    >
                        Prayers
                    </button>
                    <button
                        onClick={() => setFilter({ type: 'postType', value: 'question' })}
                        className={`whitespace-nowrap px-3 py-2 sm:px-6 sm:py-2.5 rounded-full font-bold transition-all text-xs sm:text-sm border shadow-sm active:scale-95 ${filter.type === 'postType' && filter.value === 'question'
                            ? 'bg-blue-500 text-white border-blue-500 shadow-blue-500/20'
                            : 'bg-white dark:bg-dark-card text-gray-600 dark:text-gray-300 border-gray-200 dark:border-gray-800 hover:bg-gray-50 dark:hover:bg-white/5'
                            }`}
                    >
                        Questions
                    </button>
                </div>
            )}

            {/* Posts List */}
            {results.length === 0 ? (
                <div className="text-center py-12 glass rounded-xl">
                    <p className="text-light-muted dark:text-dark-muted">
                        No posts found.
                    </p>
                </div>
            ) : (
                <div className="space-y-4">
                    {results.map((post: any) => (
                        <PostCard
                            key={post._id}
                            post={post as any}
                            onCommentClick={() => onCommentClick?.(post)}
                            hasUserLiked={(post as any).hasLiked}
                        />
                    ))}
                </div>
            )}

            {/* Infinite Scroll Loader */}
            <div ref={loadMoreRef} className="h-20 flex items-center justify-center py-4">
                {status === "LoadingMore" && (
                    <div className="flex items-center gap-2 text-light-muted dark:text-dark-muted">
                        <Loader2 className="w-5 h-5 animate-spin text-primary-500" />
                        <span className="text-sm font-medium">Loading more...</span>
                    </div>
                )}
            </div>

            {/* Scroll To Top Button */}
            <AnimatePresence>
                {showScrollTop && (
                    <motion.button
                        initial={{ opacity: 0, scale: 0.5 }}
                        animate={{ opacity: 1, scale: 1 }}
                        exit={{ opacity: 0, scale: 0.5 }}
                        onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}
                        className="fixed bottom-20 right-4 md:bottom-10 md:right-10 p-3 md:p-4 rounded-full bg-gray-900 text-white shadow-2xl z-40 hover:bg-gray-800 transition-all group border border-green-500/40"
                    >
                        <ArrowUp className="w-5 h-5 md:w-6 md:h-6 group-hover:-translate-y-1 transition-transform" />
                    </motion.button>
                )}
            </AnimatePresence>
        </div>
    );
}
