import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import {
    Heart, MessageCircle, Trash2, MoreVertical, Shield, Edit2, ChevronDown, ChevronUp,
    AlertCircle, Share2, UserPlus, UserMinus, HelpCircle, Download, BookOpen, HeartHandshake, Bookmark
} from 'lucide-react';
import { useMutation, useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Avatar } from '@/components/ui/Avatar';
import { Button } from '@/components/ui/Button';
import { Badge } from '@/components/ui/Badge';
import { formatDistanceToNow } from 'date-fns';
import { Id } from '../../../convex/_generated/dataModel';
import { TaskAssignModal } from '../tasks/TaskAssignModal';
import { Link, useNavigate } from 'react-router-dom';
import { ImageLightbox } from '../ui/ImageLightbox';
import { ConfirmationModal } from '../ui/ConfirmationModal';

interface PostCardProps {
    post: {
        _id: Id<"posts">;
        content: string;
        category: 'Ancient Culture' | 'Wisdom of The Now';
        isPinned: boolean;
        likeCount: number;
        commentCount: number;
        isResponseEnabled?: boolean;
        createdAt: number;
        imageUrl?: string;
        videoUrl?: string;
        postType?: 'general' | 'question' | 'prayer';
        questionContext?: 'personal_experience' | 'about_someone_else';
        questionCount?: number;
        scripture?: string;
        author: {
            _id: Id<"users">;
            name: string;
            role: 'admin' | 'member' | 'super_admin' | 'board_member';
            avatarUrl?: string;
        } | null;
        rewardPosition: number | null;
        hasLiked?: boolean;
    };
    onCommentClick?: () => void;
    hasUserLiked?: boolean;
}

export function PostCard({ post, onCommentClick, hasUserLiked = false }: PostCardProps) {
    const { user } = useAuth();
    const navigate = useNavigate();

    // API Hooks
    const likePost = useMutation(api.posts.likePost);

    // Use deletePostUser for deletion (handles both author and admin logic)
    const deletePostMutation = useMutation(api.editDelete.deletePostUser);

    // Use editPost from editDelete module
    const editPostMutation = useMutation(api.editDelete.editPost);

    // Use follows module for following
    const toggleFollow = useMutation(api.follows.toggleFollow);

    // Other mutations

    const togglePostDisabled = useMutation(api.posts.togglePostDisabled);

    // Settings
    const commentsEnabled = useQuery(api.settings.getCommentsEnabled);
    const questionsOnPostsEnabled = useQuery(api.settings.getQuestionsOnPostsEnabled);
    const isSaved = useQuery(api.posts.isPostSaved, user ? { userId: user._id, postId: post._id } : "skip");
    const toggleSave = useMutation(api.posts.toggleSavePost);

    // Local State
    const [isLiking, setIsLiking] = useState(false);
    const [localLiked, setLocalLiked] = useState(hasUserLiked);
    const [localLikeCount, setLocalLikeCount] = useState(post.likeCount);
    const [showMenu, setShowMenu] = useState(false);
    const [showAssignModal, setShowAssignModal] = useState(false);
    const [isEditing, setIsEditing] = useState(false);
    const [editContent, setEditContent] = useState(post.content);
    const [showLightbox, setShowLightbox] = useState(false);
    
    // Expansion State
    const [expansionLevel, setExpansionLevel] = useState(0); // 0: Collapsed, 1: Semi-expanded, 2: Full
    const isLongContent = post.content.length > 250;
    const isSuperLongContent = post.content.length > 1200;

    useEffect(() => {
        setExpansionLevel(0);
    }, [post._id]);

    const handleTextClick = (e: React.MouseEvent) => {
        if (!isLongContent) {
           if(!post.imageUrl && !post.videoUrl) navigate(`/post/${post._id}`);
           return;
        }
        e.stopPropagation();
        
        if (isSuperLongContent) {
            if (expansionLevel === 0) setExpansionLevel(1);
            else if (expansionLevel === 1) setExpansionLevel(2);
            // Optional: click to collapse from full
        } else {
            setExpansionLevel(expansionLevel === 0 ? 2 : 0);
        }
    };

    const handleReadMoreClick = (e: React.MouseEvent) => {
        e.stopPropagation();
        if (isSuperLongContent) {
            if (expansionLevel === 0) setExpansionLevel(1);
            else if (expansionLevel === 1) setExpansionLevel(2);
            else setExpansionLevel(0);
        } else {
            setExpansionLevel(expansionLevel === 0 ? 2 : 0);
        }
    };

    const [showDeleteModal, setShowDeleteModal] = useState(false);

    // Check follow status safely
    const isFollowing = useQuery(api.follows.isFollowing,
        user && post.author ? { followerId: user._id, followingId: post.author._id } : "skip"
    );

    const isAdmin = user?.role === 'admin' || user?.role === 'super_admin';
    const isOfficialPost = post.author?.role === 'admin' || post.author?.role === 'super_admin';


    // Derived State
    const isQuestion = post.postType === 'question';
    const isPrayer = post.postType === 'prayer';

    // Optimistic Like Updates
    useEffect(() => {
        setLocalLiked(hasUserLiked);
        setLocalLikeCount(post.likeCount);
    }, [hasUserLiked, post.likeCount]);

    // Handlers
    const handleLike = async () => {
        // One-way lock only on admin posts (to prevent Ruby farming)
        if (!user || isLiking) return;
        if (isOfficialPost && localLiked) return;

        const newLiked = !localLiked;
        setLocalLiked(newLiked);
        setLocalLikeCount(prev => newLiked ? prev + 1 : prev - 1);
        setIsLiking(true);
        try {
            await likePost({ userId: user._id, postId: post._id });
        } catch (error) {
            const msg = error instanceof Error ? error.message : '';
            if (!msg.includes('already liked')) {
                setLocalLiked(!newLiked);
                setLocalLikeCount(prev => !newLiked ? prev + 1 : prev - 1);
            }
            console.error('Failed to like post:', error);
        } finally {
            setIsLiking(false);
        }
    };

    const handleToggleFollow = async () => {
        if (!user || !post.author) return;
        try {
            await toggleFollow({ followerId: user._id, followingId: post.author._id });
            setShowMenu(false);
        } catch (error) {
            console.error('Failed to toggle follow:', error);
        }
    };



    const handleSave = async () => {
        if (!user) return;
        try {
            await toggleSave({ userId: user._id, postId: post._id });
            setShowMenu(false);
        } catch (error) {
            console.error('Failed to toggle save:', error);
        }
    };

    const handleDeleteClick = () => {
        if (!user) return;
        setShowDeleteModal(true);
        setShowMenu(false);
    };

    const confirmDelete = async () => {
        try {
            await deletePostMutation({ userId: user!._id, postId: post._id });
        } catch (error) {
            console.error('Failed to delete post:', error);
            // alert("Failed to delete post."); // Modal handles UX better
        }
    };

    const handleDisablePost = async () => {
        if (!isAdmin || !user) return;
        const reason = prompt("Enter reason for disabling this post:", "Violates community guidelines");
        if (reason === null) return; // Cancelled

        try {
            await togglePostDisabled({
                postId: post._id,
                adminId: user._id,
                disabled: true,
                reason: reason || "Disabled by admin"
            });
            setShowMenu(false);
        } catch (error) {
            console.error("Failed to disable post:", error);
            alert("Failed to disable post.");
        }
    };



    const handleEdit = () => {
        if (!isEditing) {
            setIsEditing(true);
            setShowMenu(false);
        } else {
            handleSaveEdit();
        }
    };

    const handleSaveEdit = async () => {
        if (!user) return;
        try {
            await editPostMutation({ userId: user._id, postId: post._id, content: editContent });
            setIsEditing(false);
        } catch (error) {
            console.error('Failed to edit post:', error);
            alert("Failed to update post.");
        }
    };

    const handleDownload = () => {
        if (post.imageUrl) {
            const link = document.createElement('a');
            link.href = post.imageUrl;
            link.download = `post-${post._id}.jpg`;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        } else {
            alert("Download feature coming soon!");
        }
    };

    const handleShare = () => {
        if (navigator.share) {
            navigator.share({
                title: `Post by ${post.author?.name}`,
                text: post.content,
                url: window.location.href
            }).catch(console.error);
        } else {
            navigator.clipboard.writeText(window.location.href);
            alert("Link copied to clipboard!");
        }
    };

    // Styling Logic
    const getCardStyle = () => {
        if (isQuestion) return "border-l-4 border-l-blue-500 bg-gradient-to-br from-blue-50/30 to-white dark:from-blue-900/20 dark:to-[#1A1A1A]";
        if (isPrayer) return "border-l-4 border-l-purple-500 bg-gradient-to-br from-purple-50/30 to-white dark:from-purple-900/20 dark:to-[#1A1A1A]";
        return "bg-white dark:bg-[#1A1A1A]";
    };

    return (
        <>
            <motion.div
                layout
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, scale: 0.95 }}
                className={`group relative rounded-3xl shadow-sm hover:shadow-md transition-all duration-300 border border-gray-100 dark:border-gray-800 overflow-hidden ${getCardStyle()}`}
            >
                {/* Header */}
                <div className="p-4 sm:p-5 pb-2">
                    <div className="flex items-start justify-between gap-4">
                        <div className="flex items-center gap-3">
                            <Link to={`/profile/${post.author?._id}`} className="relative block">
                                <Avatar
                                    src={post.author?.avatarUrl}
                                    alt={post.author?.name || 'User'}
                                    className={`w-12 h-12 ring-2 transition-transform hover:scale-105 ${isOfficialPost
                                        ? 'ring-primary-500 shadow-lg shadow-primary-500/20'
                                        : isQuestion ? 'ring-blue-400' : isPrayer ? 'ring-purple-400' : 'ring-transparent'
                                        }`}
                                />
                                {isOfficialPost && (
                                    <div className="absolute -bottom-1 -right-1 bg-primary-500 text-white p-0.5 rounded-full ring-2 ring-white dark:ring-[#1A1A1A]">
                                        <Shield className="w-3 h-3" />
                                    </div>
                                )}
                            </Link>
                            <div className="flex-1 min-w-0">
                                <div className="flex items-center gap-2 flex-wrap mb-0.5">
                                    <h3 className="font-bold text-gray-900 dark:text-gray-100 text-sm sm:text-base leading-tight group-hover:text-primary-500 transition-colors">
                                        {post.author?.name}
                                    </h3>
                                    {isOfficialPost ? (
                                        <Badge variant="primary" className="bg-primary-500/10 text-primary-600 dark:text-primary-400 border-0 text-[10px] py-0 px-1.5 h-4 flex-shrink-0">
                                            {post.author?.role === 'admin' ? 'Community Admin' : 'Super Admin'}
                                        </Badge>
                                    ) : (
                                        <Badge variant="outline" className={`text-[10px] py-0 px-1.5 h-4 flex-shrink-0 ${isQuestion ? 'text-blue-600 border-blue-200 dark:border-blue-800 bg-blue-50 dark:bg-blue-900/20' :
                                            isPrayer ? 'text-purple-600 border-purple-200 dark:border-purple-800 bg-purple-50 dark:bg-purple-900/20' :
                                                'text-gray-500 border-gray-200 dark:border-gray-700'
                                            }`}>
                                            {isQuestion ? 'Question' : isPrayer ? 'Prayer' : 'Member'}
                                        </Badge>
                                    )}
                                </div>
                                <div className="flex items-center gap-2 text-xs text-gray-500 dark:text-gray-400 flex-wrap">
                                    <span className="whitespace-nowrap">{formatDistanceToNow(post.createdAt, { addSuffix: true })}</span>
                                    <span></span>
                                    <span className="font-medium">
                                        {post.category}
                                    </span>
                                </div>
                            </div>
                        </div>

                        {/* Menu & Pins */}
                        <div className="relative z-10 flex items-start gap-2">


                            <button
                                onClick={() => setShowMenu(!showMenu)}
                                className="p-2 -mr-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-800 text-gray-400 transition-colors opacity-0 group-hover:opacity-100 focus:opacity-100"
                            >
                                <MoreVertical className="w-5 h-5" />
                            </button>
                            <AnimatePresence>
                                {showMenu && (
                                    <>
                                        <div className="fixed inset-0 z-10" onClick={() => setShowMenu(false)} />
                                        <motion.div
                                            initial={{ opacity: 0, scale: 0.95, y: 10 }}
                                            animate={{ opacity: 1, scale: 1, y: 0 }}
                                            exit={{ opacity: 0, scale: 0.95, y: 10 }}
                                            className="absolute right-0 top-full mt-2 w-48 bg-white dark:bg-[#252525] rounded-xl shadow-xl border border-gray-100 dark:border-gray-700 z-20 py-1 overflow-hidden"
                                        >
                                            {user && post.author && user._id !== post.author._id && (
                                                <button
                                                    onClick={handleToggleFollow}
                                                    className="w-full flex items-center gap-2 px-4 py-2.5 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-50 dark:hover:bg-white/5 transition-colors"
                                                >
                                                    {isFollowing ? (
                                                        <><UserMinus className="w-4 h-4" /> Unfollow</>
                                                    ) : (
                                                        <><UserPlus className="w-4 h-4" /> Follow</>
                                                    )}
                                                </button>
                                            )}

                                            {(user) && (
                                                <button onClick={handleSave} className="w-full flex items-center gap-2 px-4 py-2.5 text-sm hover:bg-gray-50 dark:hover:bg-white/5 text-gray-700 dark:text-gray-200">
                                                    <Bookmark className={`w-4 h-4 ${isSaved ? "fill-current" : ""}`} /> {isSaved ? 'Unsave Post' : 'Save Post'}
                                                </button>
                                            )}

                                            {isAdmin && (
                                                <>
                                                    {!isOfficialPost && (
                                                        <button
                                                            onClick={handleDisablePost}
                                                            className="w-full flex items-center gap-2 px-4 py-2.5 text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/10 transition-colors"
                                                        >
                                                            <AlertCircle className="w-4 h-4" /> Disable Post
                                                        </button>
                                                    )}
                                                </>
                                            )}

                                            {(isAdmin || user?._id === post.author?._id) && (
                                                <>
                                                    {(isAdmin || (user?._id === post.author?._id && (Date.now() - post.createdAt) < 30 * 60 * 1000)) && (
                                                        <button
                                                            onClick={handleEdit}
                                                            className="w-full flex items-center gap-2 px-4 py-2.5 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-50 dark:hover:bg-white/5 transition-colors"
                                                        >
                                                            <Edit2 className="w-4 h-4" /> Edit Post
                                                        </button>
                                                    )}
                                                    <button
                                                        onClick={handleDeleteClick}
                                                        className="w-full flex items-center gap-2 px-4 py-2.5 text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/10 transition-colors"
                                                    >
                                                        <Trash2 className="w-4 h-4" /> Delete Post
                                                    </button>
                                                </>
                                            )}
                                        </motion.div>
                                    </>
                                )}
                            </AnimatePresence>
                        </div>
                    </div>
                </div>

                {/* Content */}
                <div className="px-4 sm:px-6 py-2">
                    {isEditing ? (
                        <div className="space-y-3 mb-4">
                            <textarea
                                value={editContent}
                                onChange={(e) => setEditContent(e.target.value)}
                                className="w-full p-4 rounded-xl bg-gray-50 dark:bg-[#222] border-transparent focus:border-primary-500 focus:ring-0 text-base resize-none"
                                rows={4}
                            />
                            <div className="flex justify-end gap-2">
                                <Button variant="ghost" size="sm" onClick={() => setIsEditing(false)}>Cancel</Button>
                                <Button variant="primary" size="sm" onClick={handleSaveEdit}>Save</Button>
                            </div>
                        </div>
                    ) : (
                        <div onClick={() => !post.imageUrl && !post.videoUrl && navigate(`/post/${post._id}`)} className="cursor-pointer mb-4">
                            {/* Question Specific UI */}
                            {isQuestion && (
                                <div className="mb-2">
                                    <div className="flex items-center gap-2 mb-3">
                                        <div className="p-2 bg-blue-100 dark:bg-blue-900/30 text-blue-600 rounded-full">
                                            <HelpCircle className="w-5 h-5" />
                                        </div>
                                        <span className="text-sm font-semibold text-blue-600 dark:text-blue-400 uppercase tracking-wide">
                                            {post.questionContext === 'about_someone_else' ? 'Question for Others' : 'Personal Question'}
                                        </span>
                                    </div>
                                    <p 
                                        onClick={handleTextClick}
                                        className={`text-xl font-medium text-gray-900 dark:text-gray-100 leading-relaxed font-display break-words cursor-pointer transition-all ${
                                            expansionLevel === 0 ? 'line-clamp-4' : 
                                            expansionLevel === 1 ? 'line-clamp-[20]' : ''
                                        }`}>
                                        {post.content}
                                    </p>
                                    {isLongContent && (
                                        <button
                                            onClick={handleReadMoreClick}
                                            className="text-primary-500 text-base mt-1 font-sans hover:underline cursor-pointer flex items-center gap-1"
                                        >
                                            {expansionLevel === 2 ? (
                                                <>Show Less <ChevronUp className="w-4 h-4" /></>
                                            ) : expansionLevel === 1 ? (
                                                <>Read Full Message <ChevronDown className="w-4 h-4" /></>
                                            ) : (
                                                <>Read more <ChevronDown className="w-4 h-4" /></>
                                            )}
                                        </button>
                                    )}
                                </div>
                            )}

                            {/* Prayer Specific UI */}
                            {isPrayer && (
                                <div className="mb-2">
                                    <div className="flex items-center gap-2 mb-3 text-purple-600 dark:text-purple-400">
                                        <div className="p-1.5 bg-purple-100 dark:bg-purple-900/30 rounded-lg">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M19 14c1.49-1.28 3.6-1.28 5.09 0 1.49 1.28 1.49 3.36 0 4.63-1.49 1.28-3.6 1.28-5.09 0-1.49-1.28-1.49-3.36 0-4.63z" /><path d="M24 6h-6a2 2 0 0 0-2 2v10.17a3 3 0 0 1-.5 1.7l-2 2.83" /><path d="M13 14c1.49-1.28 3.6-1.28 5.09 0 1.49 1.28 1.49 3.36 0 4.63-1.49 1.28-3.6 1.28-5.09 0-1.49-1.28-1.49-3.36 0-4.63z" /><path d="M7 6h6a2 2 0 0 1 2 2v10.17a3 3 0 0 0 .5 1.7l2 2.83" /></svg>
                                        </div>
                                        <span className="text-sm font-semibold uppercase tracking-wide">Prayer Request</span>
                                    </div>

                                    {post.scripture && (
                                        <div className="flex items-center gap-2 mb-3 ml-1">
                                            <div className="bg-purple-100 dark:bg-purple-900/40 p-1 rounded text-purple-600 dark:text-purple-400">
                                                <BookOpen className="w-3.5 h-3.5" />
                                            </div>
                                            <span className="text-sm font-bold text-purple-700 dark:text-purple-300 font-display">
                                                {post.scripture}
                                            </span>
                                        </div>
                                    )}

                                    <p 
                                        onClick={handleTextClick}
                                        className={`text-lg italic text-gray-800 dark:text-gray-200 leading-relaxed border-l-4 border-purple-200 dark:border-purple-800 pl-4 py-2 bg-purple-50/50 dark:bg-purple-900/10 rounded-r-lg break-words cursor-pointer transition-all ${
                                            expansionLevel === 0 ? 'line-clamp-4' : 
                                            expansionLevel === 1 ? 'line-clamp-[20]' : ''
                                        }`}>
                                        "{post.content}"
                                    </p>
                                    {isLongContent && (
                                        <button 
                                            onClick={handleReadMoreClick}
                                            className="text-primary-500 text-base mt-1 ml-1 not-italic hover:underline flex items-center gap-1"
                                        >
                                            {expansionLevel === 2 ? (
                                                <>Show Less <ChevronUp className="w-4 h-4" /></>
                                            ) : expansionLevel === 1 ? (
                                                <>Read Full Message <ChevronDown className="w-4 h-4" /></>
                                            ) : (
                                                <>Read more <ChevronDown className="w-4 h-4" /></>
                                            )}
                                        </button>
                                    )}
                                </div>
                            )}

                            {/* Default UI */}
                            {!isQuestion && !isPrayer && (
                                <div className="mb-4">
                                    <p 
                                        onClick={handleTextClick}
                                        className={`whitespace-pre-wrap break-words text-gray-800 dark:text-gray-200 text-base leading-relaxed cursor-pointer transition-all ${
                                            expansionLevel === 0 ? 'line-clamp-[6]' : 
                                            expansionLevel === 1 ? 'line-clamp-[20]' : ''
                                        }`}>
                                        {post.content}
                                    </p>
                                    {isLongContent && (
                                        <button
                                            onClick={handleReadMoreClick}
                                            className="text-primary-500 font-medium mt-1 hover:underline flex items-center gap-1"
                                        >
                                            {expansionLevel === 2 ? (
                                                <>Show Less <ChevronUp className="w-4 h-4" /></>
                                            ) : expansionLevel === 1 ? (
                                                <>Read Full Message <ChevronDown className="w-4 h-4" /></>
                                            ) : (
                                                <>Read more <ChevronDown className="w-4 h-4" /></>
                                            )}
                                        </button>
                                    )}
                                </div>
                            )}
                        </div>
                    )}

                    {/* Media Attachments */}
                    {(post.imageUrl || post.videoUrl) && (
                        <div
                            className="relative mt-3 mb-2 rounded-2xl overflow-hidden bg-gray-100 dark:bg-gray-800 cursor-pointer group/image"
                            onClick={() => post.imageUrl && setShowLightbox(true)}
                        >
                            {post.imageUrl && (
                                <img
                                    src={post.imageUrl}
                                    alt="Post content"
                                    className="w-full h-auto max-h-[600px] object-cover hover:scale-[1.01] transition-transform duration-500"
                                />
                            )}
                            {post.videoUrl && (
                                <video src={post.videoUrl} controls className="w-full h-full object-cover" />
                            )}
                            {post.imageUrl && (
                                <div className="absolute inset-0 bg-black/0 group-hover/image:bg-black/10 transition-colors duration-300 pointer-events-none" />
                            )}
                        </div>
                    )}
                </div>

                {/* Footer Interactions */}
                <div className="px-4 sm:px-6 py-3 border-t border-gray-100 dark:border-gray-800/50 bg-gray-50/50 dark:bg-black/20">
                    <div className="flex items-center justify-between">
                        <div className="flex items-center gap-1 sm:gap-4">
                            {/* Like  one-way on admin posts only, toggleable on others */}
                            <Button
                                variant="ghost"
                                size="sm"
                                onClick={handleLike}
                                disabled={isOfficialPost && (localLiked || isLiking)}
                                title={isOfficialPost && localLiked ? "You've already liked this post" : "Like this post"}
                                className={`gap-2 hover:bg-pink-50 dark:hover:bg-pink-900/10 transition-colors ${
                                    localLiked
                                        ? `text-pink-500 ${isOfficialPost ? 'cursor-default opacity-90' : ''}`
                                        : 'text-gray-500 dark:text-gray-400'
                                }`}
                            >
                                <Heart className={`w-5 h-5 ${localLiked ? 'fill-current' : ''} ${isLiking ? 'scale-125' : ''} transition-transform`} />
                                <span className="font-medium">{localLikeCount}</span>
                                <span className="sr-only sm:not-sr-only text-xs sm:text-sm">Likes</span>
                            </Button>

                            {/* Comment - Hidden if disabled globally */}
                            {commentsEnabled && (
                                <Button
                                    variant="ghost"
                                    size="sm"
                                    onClick={onCommentClick}
                                    className={`gap-2 hover:bg-blue-50 dark:hover:bg-blue-900/10 ${isPrayer ? 'text-purple-500 hover:text-purple-600' : 'text-gray-500 dark:text-gray-400 hover:text-blue-500'}`}
                                >
                                    {isPrayer ? <HeartHandshake className="w-5 h-5" /> : <MessageCircle className="w-5 h-5" />}
                                    <span className="font-medium">{post.commentCount}</span>
                                    <span className="sr-only sm:not-sr-only text-xs sm:text-sm">{isPrayer ? 'Prayers' : 'Comments'}</span>
                                </Button>
                            )}

                            {/* Questions Button - Only for official posts & if enabled */}
                            {isOfficialPost && questionsOnPostsEnabled && (
                                <Button
                                    variant="ghost"
                                    size="sm"
                                    onClick={() => navigate(`/post/${post._id}?tab=questions`)}
                                    className="gap-2 hover:bg-purple-50 dark:hover:bg-purple-900/10 text-gray-500 dark:text-gray-400 hover:text-purple-500"
                                >
                                    <HelpCircle className="w-5 h-5" />
                                    <span className="font-medium">{post.questionCount || 0}</span>
                                    <span className="sr-only sm:not-sr-only text-xs sm:text-sm">Questions</span>
                                </Button>
                            )}
                        </div>

                        <div className="flex items-center gap-1 sm:gap-2">
                            <Button onClick={handleShare} variant="ghost" size="sm" className="p-2 text-gray-500 hover:text-primary-500 dark:text-gray-400">
                                <Share2 className="w-5 h-5" />
                            </Button>
                            {post.imageUrl && (
                                <Button onClick={handleDownload} variant="ghost" size="sm" className="p-2 text-gray-500 hover:text-primary-500 dark:text-gray-400">
                                    <Download className="w-5 h-5" />
                                </Button>
                            )}
                        </div>
                    </div>
                </div>
            </motion.div>

            {/* Modals */}
            <AnimatePresence>
                {showLightbox && post.imageUrl && (
                    <ImageLightbox
                        src={post.imageUrl}
                        alt="Post content"
                        onClose={() => setShowLightbox(false)}
                    />
                )}
            </AnimatePresence>

            <AnimatePresence>
                {showAssignModal && (
                    <TaskAssignModal
                        onClose={() => setShowAssignModal(false)}
                        isOpen={showAssignModal}
                        postId={post._id}
                    />
                )}
            </AnimatePresence>

            <ConfirmationModal
                isOpen={showDeleteModal}
                onClose={() => setShowDeleteModal(false)}
                onConfirm={confirmDelete}
                title="Delete Post"
                description="Are you sure you want to delete this post? This action cannot be undone."
                confirmText="Delete"
                variant="danger"
            />
        </>
    );
}
