import { useState } from 'react';
import { motion } from 'framer-motion';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { BookOpen, PlayCircle, FileText, CheckCircle2, Clock, Award } from 'lucide-react';
import { Card, CardContent } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
// import { BackButton } from '@/components/ui/BackButton';
import { Modal } from '@/components/ui/Modal';
import { PageHeaderSkeleton, StatsCardSkeleton } from '@/components/ui/Skeletons';

export function TrainingCenter() {
    const { user, isLoading } = useAuth();
    const [selectedModule, setSelectedModule] = useState<any>(null);
    const [selectedCategory, setSelectedCategory] = useState<string | undefined>(undefined);

    const modules = useQuery(
        api.training.getModules,
        { category: selectedCategory as any }
    );

    const myProgress = useQuery(
        api.training.getProgress,
        user ? { userId: user._id } : 'skip'
    );

    const trainingStats = useQuery(
        api.training.getTrainingStats,
        user ? { userId: user._id } : 'skip'
    );

    const startModule = useMutation(api.training.startModule);
    const completeModule = useMutation(api.training.completeModule);

    if (isLoading) {
        return (
            <div className="min-h-screen py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-7xl">
                    <PageHeaderSkeleton />
                    <div className="grid md:grid-cols-4 gap-4 mb-8">
                        <StatsCardSkeleton />
                        <StatsCardSkeleton />
                        <StatsCardSkeleton />
                        <StatsCardSkeleton />
                    </div>
                </div>
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    if (user.role !== 'board_member' && user.role !== 'admin' && user.role !== 'super_admin') {
        return <Navigate to="/feed" replace />;
    }

    const getModuleProgress = (moduleId: string) => {
        return myProgress?.find((p: any) => p.module?._id === moduleId);
    };

    const handleStartModule = async (moduleId: string) => {
        try {
            await startModule({ userId: user._id, moduleId: moduleId as any });
        } catch (error) {
            console.error('Failed to start module:', error);
        }
    };

    const handleCompleteModule = async (moduleId: string) => {
        try {
            await completeModule({ userId: user._id, moduleId: moduleId as any });
            setSelectedModule(null);
        } catch (error) {
            console.error('Failed to complete module:', error);
        }
    };

    const categories = ['Leadership', 'Ministry Skills', 'Spiritual Growth', 'Technical Skills', 'Team Building'];

    return (
        <div className="space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-10"
            >
                <div className="flex items-center gap-2 mb-2">
                    <div className="w-8 h-1 bg-secondary-500 rounded-full" />
                    <span className="text-[10px] font-black uppercase tracking-[0.3em] text-secondary-500">Wisdom Hub</span>
                </div>
                <h1 className="text-4xl md:text-5xl font-black text-gradient font-display mb-2 tracking-tight">Training Center</h1>
                <p className="text-light-muted dark:text-dark-muted font-medium">Elevating your capacity for leadership and service.</p>
            </motion.div>

            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 mb-12">
                <Card glass className="relative overflow-hidden group border-emerald-500/10 hover:border-emerald-500/30 transition-all duration-500">
                    <div className="absolute top-0 right-0 w-24 h-24 bg-emerald-500/5 rounded-bl-full -mr-8 -mt-8 group-hover:scale-110 transition-transform" />
                    <CardContent className="p-6">
                        <div className="flex items-center justify-between mb-4">
                            <div className="p-3 rounded-2xl bg-emerald-500/10 text-emerald-500">
                                <CheckCircle2 className="w-6 h-6" />
                            </div>
                            <div className="text-right text-3xl font-black font-display text-emerald-500">
                                {trainingStats?.completed || 0}
                            </div>
                        </div>
                        <h3 className="text-xs font-black uppercase tracking-widest text-light-muted mb-1">Fulfilled</h3>
                        <p className="text-[10px] font-bold text-emerald-500 uppercase tracking-tighter">Modules Mastered</p>
                    </CardContent>
                </Card>

                <Card glass className="relative overflow-hidden group border-amber-500/10 hover:border-amber-500/30 transition-all duration-500">
                    <div className="absolute top-0 right-0 w-24 h-24 bg-amber-500/5 rounded-bl-full -mr-8 -mt-8 group-hover:scale-110 transition-transform" />
                    <CardContent className="p-6">
                        <div className="flex items-center justify-between mb-4">
                            <div className="p-3 rounded-2xl bg-amber-500/10 text-amber-500">
                                <Clock className="w-6 h-6" />
                            </div>
                            <div className="text-right text-3xl font-black font-display text-amber-500">
                                {trainingStats?.inProgress || 0}
                            </div>
                        </div>
                        <h3 className="text-xs font-black uppercase tracking-widest text-light-muted mb-1">In Transit</h3>
                        <p className="text-[10px] font-bold text-amber-500 uppercase tracking-tighter">Currently Active</p>
                    </CardContent>
                </Card>

                <Card glass className="relative overflow-hidden group border-primary-500/10 hover:border-primary-500/30 transition-all duration-500">
                    <div className="absolute top-0 right-0 w-24 h-24 bg-primary-500/5 rounded-bl-full -mr-8 -mt-8 group-hover:scale-110 transition-transform" />
                    <CardContent className="p-6">
                        <div className="flex items-center justify-between mb-4">
                            <div className="p-3 rounded-2xl bg-primary-500/10 text-primary-500">
                                <BookOpen className="w-6 h-6" />
                            </div>
                            <div className="text-right text-3xl font-black font-display text-primary-500">
                                {trainingStats?.totalModules || 0}
                            </div>
                        </div>
                        <h3 className="text-xs font-black uppercase tracking-widest text-light-muted mb-1">Wisdom Repository</h3>
                        <p className="text-[10px] font-bold text-primary-500 uppercase tracking-tighter">Total Curated content</p>
                    </CardContent>
                </Card>

                <Card glass className="relative overflow-hidden group border-secondary-500/10 hover:border-secondary-500/30 transition-all duration-500">
                    <div className="absolute top-0 right-0 w-24 h-24 bg-secondary-500/5 rounded-bl-full -mr-8 -mt-8 group-hover:scale-110 transition-transform" />
                    <CardContent className="p-6">
                        <div className="flex items-center justify-between mb-4">
                            <div className="p-3 rounded-2xl bg-secondary-500/10 text-secondary-500">
                                <Award className="w-6 h-6" />
                            </div>
                            <div className="text-right text-3xl font-black font-display text-secondary-500">
                                {trainingStats?.completionPercentage || 0}%
                            </div>
                        </div>
                        <h3 className="text-xs font-black uppercase tracking-widest text-light-muted mb-1">Proficiency</h3>
                        <div className="h-1.5 w-full bg-light-muted/10 dark:bg-dark-muted/10 rounded-full overflow-hidden mt-2">
                            <div
                                className="h-full bg-secondary-500 transition-all duration-1000"
                                style={{ width: `${trainingStats?.completionPercentage || 0}%` }}
                            />
                        </div>
                    </CardContent>
                </Card>
            </div>

            <div className="flex flex-col md:flex-row md:items-center justify-between gap-6 mb-8">
                <div className="flex bg-light-card/50 dark:bg-dark-card/50 p-1.5 rounded-2xl border border-light-border dark:border-dark-border overflow-x-auto no-scrollbar max-w-full">
                    <button
                        onClick={() => setSelectedCategory(undefined)}
                        className={`px-6 py-2.5 rounded-xl font-black uppercase tracking-widest text-[10px] transition-all whitespace-nowrap ${selectedCategory === undefined
                            ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                            : 'text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text'
                            }`}
                    >
                        All Sectors
                    </button>
                    {categories.map((cat) => (
                        <button
                            key={cat}
                            onClick={() => setSelectedCategory(cat)}
                            className={`px-6 py-2.5 rounded-xl font-black uppercase tracking-widest text-[10px] transition-all whitespace-nowrap ${selectedCategory === cat
                                ? 'bg-secondary-500 text-white shadow-lg shadow-secondary-500/30'
                                : 'text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text'
                                }`}
                        >
                            {cat}
                        </button>
                    ))}
                </div>
            </div>

            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
                {modules?.map((module: any) => {
                    const progress = getModuleProgress(module._id);
                    return (
                        <motion.div
                            key={module._id}
                            initial={{ opacity: 0, scale: 0.95 }}
                            animate={{ opacity: 1, scale: 1 }}
                            whileHover={{ y: -8 }}
                            className="h-full"
                        >
                            <Card glass className={`h-full border-secondary-500/10 hover:border-secondary-500/40 transition-all duration-300 relative group overflow-hidden ${progress?.status === 'completed' ? 'opacity-80' : ''
                                }`}>
                                <CardContent className="p-8 flex flex-col h-full">
                                    <div className="flex items-center justify-between mb-6">
                                        <Badge variant="outline" className="text-[8px] font-black uppercase h-5 text-secondary-500 border-secondary-500/30">
                                            {module.category}
                                        </Badge>
                                        {progress && (
                                            <Badge className={`text-[8px] font-black uppercase h-5 ${progress.status === 'completed' ? 'bg-emerald-500' : 'bg-primary-500'
                                                }`}>
                                                {progress.status === 'completed' ? 'Fulfilled' : 'In Transit'}
                                            </Badge>
                                        )}
                                    </div>

                                    <h3 className="font-black text-xl text-light-text dark:text-dark-text mb-3 leading-tight tracking-tight group-hover:text-secondary-500 transition-colors">
                                        {module.title}
                                    </h3>
                                    <p className="text-sm text-light-muted dark:text-dark-muted mb-8 line-clamp-3 leading-relaxed flex-grow">
                                        {module.description}
                                    </p>

                                    <div className="flex items-center gap-6 mb-8 text-[10px] font-black uppercase tracking-widest text-light-muted/60">
                                        {module.videoUrl && (
                                            <div className="flex items-center gap-2">
                                                <PlayCircle className="w-3.5 h-3.5 text-secondary-500" />
                                                Video
                                            </div>
                                        )}
                                        {module.documentUrl && (
                                            <div className="flex items-center gap-2">
                                                <FileText className="w-3.5 h-3.5 text-secondary-500" />
                                                Text
                                            </div>
                                        )}
                                        {module.duration && (
                                            <div className="flex items-center gap-2">
                                                <Clock className="w-3.5 h-3.5 text-secondary-500" />
                                                {module.duration} MIN
                                            </div>
                                        )}
                                    </div>

                                    <Button
                                        variant={progress?.status === 'completed' ? 'ghost' : 'secondary'}
                                        className={`w-full rounded-2xl h-12 font-black text-[10px] uppercase tracking-[0.2em] transition-all duration-300 ${progress?.status === 'completed'
                                            ? 'border-2 border-secondary-500/20 hover:bg-secondary-500 hover:text-white'
                                            : 'shadow-xl shadow-secondary-500/20'
                                            }`}
                                        onClick={() => {
                                            setSelectedModule(module);
                                            if (!progress) {
                                                handleStartModule(module._id);
                                            }
                                        }}
                                    >
                                        {progress?.status === 'completed' ? 'Review Directive' : progress ? 'Resume Mission' : 'Commence Training'}
                                    </Button>
                                </CardContent>
                            </Card>
                        </motion.div>
                    );
                })}
            </div>

            {selectedModule && (
                <Modal
                    isOpen={!!selectedModule}
                    onClose={() => setSelectedModule(null)}
                    title={selectedModule.title}
                >
                    <div className="space-y-4">
                        <Badge variant="secondary">{selectedModule.category}</Badge>
                        <p className="text-light-muted dark:text-dark-muted">{selectedModule.description}</p>

                        {selectedModule.videoUrl && (
                            <div className="aspect-video rounded-lg overflow-hidden bg-black">
                                <iframe
                                    width="100%"
                                    height="100%"
                                    src={selectedModule.videoUrl}
                                    title={selectedModule.title}
                                    frameBorder="0"
                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                    allowFullScreen
                                ></iframe>
                            </div>
                        )}

                        {selectedModule.content && (
                            <div className="prose dark:prose-invert max-w-none">
                                <div dangerouslySetInnerHTML={{ __html: selectedModule.content }} />
                            </div>
                        )}

                        {selectedModule.documentUrl && (
                            <a
                                href={selectedModule.documentUrl}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="flex items-center gap-2 text-primary-500 hover:text-primary-600"
                            >
                                <FileText className="w-4 h-4" />
                                Download Course Material
                            </a>
                        )}

                        <div className="flex gap-3 pt-4">
                            <Button variant="ghost" onClick={() => setSelectedModule(null)}>
                                Close
                            </Button>
                            {getModuleProgress(selectedModule._id)?.status !== 'completed' && (
                                <Button
                                    variant="primary"
                                    onClick={() => handleCompleteModule(selectedModule._id)}
                                >
                                    Mark as Complete
                                </Button>
                            )}
                        </div>
                    </div>
                </Modal>
            )}
        </div>
    );
}
