import ImageKit from 'imagekit-javascript';

// Initialize ImageKit
const imagekit = new ImageKit({
    publicKey: import.meta.env.VITE_IMAGEKIT_PUBLIC_KEY,
    urlEndpoint: import.meta.env.VITE_IMAGEKIT_URL_ENDPOINT,
});

export { imagekit };

// Helper function to get authentication parameters from backend
export const getAuthenticationParameters = async () => {
    try {
        const response = await fetch('/api/imagekit-auth');
        if (!response.ok) {
            throw new Error('Failed to get authentication parameters');
        }
        return await response.json();
    } catch (error) {
        console.error('Error getting ImageKit auth:', error);
        throw error;
    }
};

// Upload file to ImageKit
export const uploadToImageKit = async (
    file: File,
    folder: 'avatars' | 'posts/images' | 'posts/videos' | 'announcements' | 'covers',
    onProgress?: (progress: number) => void
): Promise<string> => {
    return new Promise((resolve, reject) => {
        // Create FormData
        const formData = new FormData();
        formData.append('file', file);
        formData.append('fileName', file.name);
        formData.append('folder', `/baptizo/${folder}`);
        formData.append('publicKey', import.meta.env.VITE_IMAGEKIT_PUBLIC_KEY);

        // Upload using XMLHttpRequest for progress tracking
        const xhr = new XMLHttpRequest();

        xhr.upload.addEventListener('progress', (e) => {
            if (e.lengthComputable && onProgress) {
                const progress = Math.round((e.loaded / e.total) * 100);
                onProgress(progress);
            }
        });

        xhr.addEventListener('load', () => {
            if (xhr.status === 200) {
                try {
                    const response = JSON.parse(xhr.responseText);
                    resolve(response.url);
                } catch (error) {
                    reject(new Error('Failed to parse response'));
                }
            } else {
                reject(new Error(`Upload failed with status ${xhr.status}`));
            }
        });

        xhr.addEventListener('error', () => {
            reject(new Error('Upload failed'));
        });

        xhr.open('POST', 'https://upload.imagekit.io/api/v1/files/upload');

        // Add authentication
        const authString = btoa(`${import.meta.env.VITE_IMAGEKIT_PRIVATE_KEY}:`);
        xhr.setRequestHeader('Authorization', `Basic ${authString}`);

        xhr.send(formData);
    });
};

// Validate file type
export const validateImageFile = (file: File): boolean => {
    const validTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/webp'];
    return validTypes.includes(file.type);
};

export const validateVideoFile = (file: File): boolean => {
    const validTypes = ['video/mp4', 'video/webm', 'video/ogg'];
    return validTypes.includes(file.type);
};

// Get optimized image URL
export const getOptimizedImageUrl = (url: string, width?: number, height?: number): string => {
    if (!url) return '';

    const params = [];
    if (width) params.push(`w-${width}`);
    if (height) params.push(`h-${height}`);

    if (params.length === 0) return url;

    // Insert transformation parameters into URL
    const transformation = `tr:${params.join(',')}`;
    return url.replace('/baptizo/', `/baptizo/${transformation}/`);
};
