import { motion } from 'framer-motion';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Trophy, FileText, Calendar, Award, MessageSquare } from 'lucide-react';
import { BackButton } from '@/components/ui/BackButton';

export function MyResponses() {
    const { user, isLoading } = useAuth();

    // Get all user's responses
    const responses = useQuery(
        api.responses.getMyResponses,
        user?._id ? { userId: user._id } : 'skip'
    );

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <BackButton />
                <div className="mb-6 md:mb-8">
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient font-display mb-2">
                        My Responses
                    </h1>
                    <p className="text-sm md:text-base text-light-muted dark:text-dark-muted">
                        View all your responses and grades
                    </p>
                </div>

                {/* Stats */}
                {responses && responses.length > 0 && (
                    <div className="grid grid-cols-1 sm:grid-cols-3 gap-3 md:gap-4 mb-6 md:mb-8">
                        <Card glass>
                            <CardContent className="p-4">
                                <div className="flex items-center justify-between">
                                    <div>
                                        <p className="text-sm text-light-muted dark:text-dark-muted">Total</p>
                                        <p className="text-2xl font-bold text-light-text dark:text-dark-text">
                                            {responses.length}
                                        </p>
                                    </div>
                                    <MessageSquare className="w-8 h-8 text-primary-500 opacity-20" />
                                </div>
                            </CardContent>
                        </Card>

                        <Card glass>
                            <CardContent className="p-4">
                                <div className="flex items-center justify-between">
                                    <div>
                                        <p className="text-sm text-light-muted dark:text-dark-muted">Graded</p>
                                        <p className="text-2xl font-bold text-green-500">
                                            {responses.filter((r: any) => r.grade !== undefined).length}
                                        </p>
                                    </div>
                                    <Award className="w-8 h-8 text-green-500 opacity-20" />
                                </div>
                            </CardContent>
                        </Card>

                        <Card glass>
                            <CardContent className="p-4">
                                <div className="flex items-center justify-between">
                                    <div>
                                        <p className="text-sm text-light-muted dark:text-dark-muted">Avg Grade</p>
                                        <p className="text-2xl font-bold text-accent-500">
                                            {responses.filter((r: any) => r.grade !== undefined).length > 0
                                                ? Math.round(
                                                    responses
                                                        .filter((r: any) => r.grade !== undefined)
                                                        .reduce((sum: number, r: any) => sum + (r.grade || 0), 0) /
                                                    responses.filter((r: any) => r.grade !== undefined).length
                                                )
                                                : 0}
                                        </p>
                                    </div>
                                    <Trophy className="w-8 h-8 text-accent-500 opacity-20" />
                                </div>
                            </CardContent>
                        </Card>
                    </div>
                )}

                {/* Responses List */}
                <div className="space-y-4">
                    {responses && responses.length > 0 ? (
                        responses.map((response: any) => {
                            const gradeColor = response.grade
                                ? response.grade >= 80
                                    ? 'text-green-500'
                                    : response.grade >= 50
                                        ? 'text-yellow-500'
                                        : 'text-red-500'
                                : 'text-light-muted dark:text-dark-muted';

                            return (
                                <Card key={response._id} glass>
                                    <CardHeader>
                                        <div className="flex items-start justify-between">
                                            <div className="flex-1">
                                                <CardTitle className="text-lg mb-2">
                                                    {response.postTitle}
                                                </CardTitle>
                                                <div className="flex items-center gap-3 text-sm text-light-muted dark:text-dark-muted">
                                                    <div className="flex items-center gap-1">
                                                        <Calendar className="w-4 h-4" />
                                                        {new Date(response.createdAt).toLocaleDateString()}
                                                    </div>
                                                    <Badge variant="default">{response.category}</Badge>
                                                </div>
                                            </div>
                                            {response.grade !== undefined && (
                                                <div className="text-center">
                                                    <p className={`text-3xl font-bold ${gradeColor}`}>
                                                        {response.grade}
                                                    </p>
                                                    <p className="text-xs text-light-muted dark:text-dark-muted">
                                                        / 100
                                                    </p>
                                                </div>
                                            )}
                                        </div>
                                    </CardHeader>
                                    <CardContent>
                                        <div className="space-y-4">
                                            {/* Response Content */}
                                            <div>
                                                <h4 className="text-sm font-semibold text-light-text dark:text-dark-text mb-2">
                                                    Your Response
                                                </h4>
                                                <div className="bg-light-bg dark:bg-dark-bg p-4 rounded-lg text-sm text-light-text dark:text-dark-text">
                                                    {response.content}
                                                </div>
                                            </div>

                                            {/* Feedback */}
                                            {response.feedback && (
                                                <div className="border-t border-light-border dark:border-dark-border pt-4">
                                                    <h4 className="text-sm font-semibold text-light-text dark:text-dark-text mb-2 flex items-center gap-2">
                                                        <FileText className="w-4 h-4 text-accent-500" />
                                                        Admin Feedback
                                                    </h4>
                                                    <div className="bg-accent-50 dark:bg-accent-900/20 p-3 rounded-lg text-sm text-light-text dark:text-dark-text italic border-l-4 border-accent-500">
                                                        "{response.feedback}"
                                                    </div>
                                                </div>
                                            )}

                                            {/* Status */}
                                            {response.grade === undefined && (
                                                <div className="text-center p-3 rounded-lg bg-orange-50 dark:bg-orange-900/20 border border-orange-200 dark:border-orange-800">
                                                    <p className="text-sm text-orange-700 dark:text-orange-400">
                                                         Waiting for admin to grade
                                                    </p>
                                                </div>
                                            )}

                                            {/* Reward Status */}
                                            {response.rewardStatus === "paid" && (
                                                <motion.div
                                                    initial={{ scale: 0.95, opacity: 0 }}
                                                    animate={{ scale: 1, opacity: 1 }}
                                                    className="text-center p-4 rounded-xl bg-gradient-to-r from-green-500/10 via-green-500/20 to-green-500/10 border border-green-500/30 shadow-lg shadow-green-500/10"
                                                >
                                                    <div className="flex items-center justify-center gap-2 text-green-600 dark:text-green-400 font-bold uppercase tracking-tight">
                                                        <Trophy className="w-5 h-5 animate-bounce" />
                                                        <span>Official Reward Paid!</span>
                                                        <Trophy className="w-5 h-5 animate-bounce" />
                                                    </div>
                                                    <p className="text-xs text-green-600/70 dark:text-green-400/70 mt-1">
                                                        Check your bank account/wallet for the transfer.
                                                    </p>
                                                </motion.div>
                                            )}
                                        </div>
                                    </CardContent>
                                </Card>
                            );
                        })
                    ) : (
                        <Card glass>
                            <CardContent className="p-12 text-center text-light-muted">
                                <MessageSquare className="w-16 h-16 mx-auto mb-4 opacity-20" />
                                <p className="mb-2">You haven't submitted any responses yet</p>
                                <p className="text-sm">
                                    Look for admin posts with "Official Response" enabled and submit your answers!
                                </p>
                            </CardContent>
                        </Card>
                    )}
                </div>
            </div>
        </div>
    );
}

export default MyResponses;
