import { useState, useEffect } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useNavigate } from 'react-router-dom';
import { PostForm } from '@/components/posts/PostForm';
import { PostFeed } from '@/components/posts/PostFeed';
import { PostVisibilityToggle } from '@/components/posts/PostVisibilityToggle';
import { InfoIcon } from '@/components/ui/InfoIcon';
import { motion } from 'framer-motion';
import { Share2, BookOpen, Sparkles } from 'lucide-react';
import { cn } from '@/lib/cn';
import { PostCardSkeleton, PageHeaderSkeleton, TabsSkeleton } from '@/components/ui/Skeletons';

export function Teachings() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();
    const [activeTab, setActiveTab] = useState<'ancient' | 'wisdom'>(() => {
        const saved = localStorage.getItem('teachings_activeTab');
        return (saved === 'ancient' || saved === 'wisdom') ? saved : 'wisdom';
    });

    // Independent refresh triggers for each feed
    const [refreshTriggerAncient, setRefreshTriggerAncient] = useState(0);
    const [refreshTriggerWisdom, setRefreshTriggerWisdom] = useState(0);

    const [isAncientVisible, setIsAncientVisible] = useState(() => {
        const saved = localStorage.getItem('ancientCulture_postsVisible');
        return saved !== null ? JSON.parse(saved) : true;
    });

    const [isWisdomVisible, setIsWisdomVisible] = useState(() => {
        const saved = localStorage.getItem('wisdomNow_postsVisible');
        return saved !== null ? JSON.parse(saved) : true;
    });

    useEffect(() => {
        localStorage.setItem('teachings_activeTab', activeTab);
    }, [activeTab]);

    useEffect(() => {
        localStorage.setItem('ancientCulture_postsVisible', JSON.stringify(isAncientVisible));
    }, [isAncientVisible]);

    useEffect(() => {
        localStorage.setItem('wisdomNow_postsVisible', JSON.stringify(isWisdomVisible));
    }, [isWisdomVisible]);

    if (isLoading) {
        return (
            <div className="min-h-screen py-4 md:py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                    <PageHeaderSkeleton />
                    <TabsSkeleton />
                    <div className="space-y-4">
                        <PostCardSkeleton />
                        <PostCardSkeleton />
                    </div>
                </div>
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    const currentCategory = activeTab === 'ancient' ? 'Ancient Culture' : 'Wisdom of The Now';
    const isCurrentVisible = activeTab === 'ancient' ? isAncientVisible : isWisdomVisible;
    const setIsCurrentVisible = activeTab === 'ancient' ? setIsAncientVisible : setIsWisdomVisible;
    const currentRefreshTrigger = activeTab === 'ancient' ? refreshTriggerAncient : refreshTriggerWisdom;

    const handlePostSuccess = () => {
        if (activeTab === 'ancient') setRefreshTriggerAncient(prev => prev + 1);
        else setRefreshTriggerWisdom(prev => prev + 1);
    };

    return (
        <div className="min-h-screen py-4 md:py-8 pb-20 md:pb-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                {/* Header Section */}
                <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className={cn(
                        "mb-6 md:mb-8 p-6 md:p-8 rounded-2xl border transition-all duration-300",
                        activeTab === 'ancient'
                            ? "bg-gradient-to-br from-primary-500/10 via-primary-500/5 to-primary-600/10 border-primary-500/20"
                            : "bg-gradient-to-br from-secondary-500/10 via-secondary-500/5 to-secondary-600/10 border-secondary-500/20"
                    )}
                >
                    <div className="flex items-start gap-4 justify-between mb-4">
                        <div className="flex items-start gap-3 flex-1">
                            <motion.div
                                key={activeTab}
                                initial={{ scale: 0.8, opacity: 0 }}
                                animate={{ scale: 1, opacity: 1 }}
                                className={cn(
                                    "w-12 h-12 md:w-16 md:h-16 rounded-xl flex items-center justify-center shadow-lg flex-shrink-0 transition-colors duration-300",
                                    activeTab === 'ancient' ? "bg-primary-500 shadow-primary-500/30" : "bg-secondary-500 shadow-secondary-500/30"
                                )}
                            >
                                {activeTab === 'ancient' ? (
                                    <BookOpen className="w-6 h-6 md:w-8 md:h-8 text-white" />
                                ) : (
                                    <Sparkles className="w-6 h-6 md:w-8 md:h-8 text-white" />
                                )}
                            </motion.div>
                            <div className="flex-1 min-w-0">
                                <div className="flex items-center gap-2 mb-2">
                                    <h1 className={cn(
                                        "text-2xl md:text-4xl font-bold font-display transition-colors duration-300",
                                        activeTab === 'ancient' ? "text-primary-600 dark:text-primary-400" : "text-secondary-600 dark:text-secondary-400"
                                    )}>
                                        {activeTab === 'ancient' ? 'Ancient Culture' : 'Wisdom of The Now'}
                                    </h1>
                                    <InfoIcon
                                        title={activeTab === 'ancient' ? 'Ancient Culture' : 'Wisdom of The Now'}
                                        content={activeTab === 'ancient'
                                            ? "This section features teachings based on prophetic wisdom and systems from the Old Testament. Admin-created posts appear here for community learning and discussion."
                                            : "This section features teachings based on spiritual identity and insights from the New Testament. Engage with admin posts to deepen your understanding."
                                        }
                                    />
                                </div>
                                <p className="text-light-text dark:text-dark-text text-sm md:text-base leading-relaxed">
                                    {activeTab === 'ancient'
                                        ? "Prophetic wisdom and systems from the Old Testament"
                                        : "Spiritual identity and insights from the New Testament"
                                    }
                                </p>
                            </div>
                        </div>
                        <motion.button
                            whileTap={{ scale: 0.95 }}
                            onClick={async () => {
                                const url = `${window.location.origin}/teachings`;
                                if (navigator.share) {
                                    await navigator.share({ title: 'Teachings - Baptizo', url });
                                } else {
                                    await navigator.clipboard.writeText(url);
                                    alert('Link copied to clipboard!');
                                }
                            }}
                            className={cn(
                                "p-2 rounded-lg transition-all flex-shrink-0",
                                activeTab === 'ancient'
                                    ? "hover:bg-primary-100 dark:hover:bg-primary-900/20 text-primary-500"
                                    : "hover:bg-secondary-100 dark:hover:bg-secondary-900/20 text-secondary-500"
                            )}
                            title="Share this page"
                        >
                            <Share2 className="w-5 h-5" />
                        </motion.button>
                    </div>

                    {/* Tabs */}
                    <div className="flex p-1 bg-light-bg/50 dark:bg-dark-bg/50 backdrop-blur-sm rounded-xl border border-light-border dark:border-dark-border mt-6 relative">
                        {/* Animated background for tabs */}
                        <motion.div
                            layout
                            className={cn(
                                "absolute top-1 bottom-1 rounded-lg bg-white dark:bg-dark-card shadow-sm z-0",
                                activeTab === 'ancient' ? "left-1 right-1/2" : "left-1/2 right-1"
                            )}
                            initial={false}
                            transition={{ type: "spring", bounce: 0.2, duration: 0.6 }}
                        />

                        <button
                            onClick={() => setActiveTab('wisdom')}
                            className={cn(
                                "flex-1 py-2 text-sm md:text-base font-bold rounded-lg relative z-10 transition-colors duration-300",
                                activeTab === 'wisdom' ? "text-secondary-600 dark:text-secondary-400" : "text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text"
                            )}
                        >
                            Wisdom of The Now
                        </button>
                        <button
                            onClick={() => setActiveTab('ancient')}
                            className={cn(
                                "flex-1 py-2 text-sm md:text-base font-bold rounded-lg relative z-10 transition-colors duration-300",
                                activeTab === 'ancient' ? "text-primary-600 dark:text-primary-400" : "text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text"
                            )}
                        >
                            Ancient Culture
                        </button>
                    </div>
                </motion.div>

                <div className="space-y-8">
                    {/* Only show creation form here if we want inline creation. 
                        Since we are adding a dedicated 'Create Post' page, 
                        we might want to hide this or keep it for desktop convenience.
                        Let's keep it for now as it's useful. 
                    */}
                    <div className="hidden md:block">
                        <PostForm onSuccess={handlePostSuccess} defaultCategory={currentCategory} />
                    </div>

                    {/* Visibility Toggle */}
                    <div className="flex justify-end">
                        <PostVisibilityToggle
                            isVisible={isCurrentVisible}
                            onToggle={() => setIsCurrentVisible(!isCurrentVisible)}
                            category={currentCategory}
                        />
                    </div>

                    {/* Feed */}
                    {isCurrentVisible ? (
                        <div key={activeTab}> {/* Re-mount feed on tab change to reset state smoothly */}
                            <PostFeed
                                category={currentCategory}
                                key={`${activeTab}-${currentRefreshTrigger}`}
                                onlyOfficial={true}
                                onCommentClick={(post: any) => navigate(`/post/${post._id}`)}
                            />
                        </div>
                    ) : (
                        <div className="text-center py-12 glass rounded-xl">
                            <p className="text-light-muted dark:text-dark-muted">
                                Posts are hidden. Click "Show Posts" to view them.
                            </p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
