import { useEffect, useState } from 'react';
import { messaging } from '../lib/firebase';
import { getToken, onMessage } from 'firebase/messaging';
import { useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from './useAuth';

const VAPID_KEY = import.meta.env.VITE_FIREBASE_VAPID_KEY;

export function useNotifications() {
    const { user } = useAuth();
    const updateFcmToken = useMutation(api.users.updateFcmToken);
    const [permissionStatus, setPermissionStatus] = useState<NotificationPermission>(
        typeof window !== 'undefined' ? Notification.permission : 'default'
    );

    useEffect(() => {
        if (!user || !messaging || !VAPID_KEY) return;

        const setupNotifications = async () => {
            try {
                // 1. Request Permission
                const permission = await Notification.requestPermission();
                setPermissionStatus(permission);

                if (permission === 'granted') {
                    // 2. Get Token
                    const token = await getToken(messaging, { vapidKey: VAPID_KEY });
                    if (token) {
                        console.log('FCM Token:', token);
                        // 3. Sync with Convex
                        await updateFcmToken({ userId: user._id, fcmToken: token });
                    }
                }
            } catch (error) {
                console.error('Error setting up notifications:', error);
            }
        };

        setupNotifications();

        // 4. Handle Foreground Messages
        const unsubscribe = onMessage(messaging, (payload) => {
            console.log('Foreground message received:', payload);
            // In a real app, you'd show a toast here
            if (payload.notification) {
                new Notification(payload.notification.title || 'New Notification', {
                    body: payload.notification.body,
                    icon: payload.notification.image || '/logo192.png',
                });
            }
        });

        return () => unsubscribe();
    }, [user, updateFcmToken]);

    return { permissionStatus };
}
