import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

import { useAuth } from '@/hooks/useAuth';
import {
    Calendar,
    DollarSign,
    Shield,
} from 'lucide-react';
import { Card } from '@/components/ui/Card';

import { AttendanceTab } from '@/components/governance/AttendanceTab';
import { FinanceTab } from '@/components/governance/FinanceTab';

type TabType = 'attendance' | 'finance';

export function BoardAdmin() {
    const { user } = useAuth();
    const [activeTab, setActiveTab] = useState<TabType>('attendance');

    // Check if user is authorized
    if (!user || (user.role !== 'super_admin' && user.role !== 'board_member')) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <Card className="p-8 text-center">
                    <Shield className="w-16 h-16 mx-auto mb-4 text-rose-500" />
                    <h2 className="text-2xl font-bold mb-2">Access Denied</h2>
                    <p className="text-light-muted dark:text-dark-muted">
                        You don't have permission to access this page.
                    </p>
                </Card>
            </div>
        );
    }

    return (
        <div className="space-y-8">
            {/* Header */}
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-10"
            >
                <div className="flex items-center gap-2 mb-2">
                    <div className="w-8 h-1 bg-primary-500 rounded-full" />
                    <span className="text-[10px] font-black uppercase tracking-[0.3em] text-primary-500">
                        Governance System
                    </span>
                </div>
                <h1 className="text-4xl md:text-5xl font-black text-gradient font-display mb-2 tracking-tight">
                    Board Governance
                </h1>
                <p className="text-light-muted dark:text-dark-muted font-medium">
                    Professional attendance tracking and financial management system
                </p>
            </motion.div>

            {/* Tab Navigation */}
            <div className="flex gap-4 border-b border-light-border dark:border-dark-border">
                <button
                    onClick={() => setActiveTab('attendance')}
                    className={`px-6 py-3 font-bold text-sm transition-all relative ${activeTab === 'attendance'
                        ? 'text-primary-500'
                        : 'text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text'
                        }`}
                >
                    <div className="flex items-center gap-2">
                        <Calendar className="w-4 h-4" />
                        Attendance
                    </div>
                    {activeTab === 'attendance' && (
                        <motion.div
                            layoutId="activeTab"
                            className="absolute bottom-0 left-0 right-0 h-0.5 bg-primary-500"
                        />
                    )}
                </button>
                <button
                    onClick={() => setActiveTab('finance')}
                    className={`px-6 py-3 font-bold text-sm transition-all relative ${activeTab === 'finance'
                        ? 'text-primary-500'
                        : 'text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text'
                        }`}
                >
                    <div className="flex items-center gap-2">
                        <DollarSign className="w-4 h-4" />
                        Finance
                    </div>
                    {activeTab === 'finance' && (
                        <motion.div
                            layoutId="activeTab"
                            className="absolute bottom-0 left-0 right-0 h-0.5 bg-primary-500"
                        />
                    )}
                </button>
            </div>

            {/* Tab Content */}
            <AnimatePresence mode="wait">
                <motion.div
                    key={activeTab}
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -20 }}
                    transition={{ duration: 0.2 }}
                >
                    {activeTab === 'attendance' ? (
                        <AttendanceTab user={user} />
                    ) : (
                        <FinanceTab user={user} />
                    )}
                </motion.div>
            </AnimatePresence>
        </div>
    );
}
