import { useState } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Settings, Users, MessageCircle, Gem, CheckCircle2 } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

const RATE_OPTIONS = [
    { value: 200, label: '200', description: 'Default rate  1 Ruby = 200' },
    { value: 100, label: '100', description: '1 Ruby = 100' },
    { value: 50, label: '50', description: '1 Ruby = 50' },
    { value: 20, label: '20', description: '1 Ruby = 20' },
    { value: 10, label: '10', description: '1 Ruby = 10' },
] as const;

type AllowedRate = typeof RATE_OPTIONS[number]['value'];

export default function AdminSettings() {
    const { user } = useAuth();
    const [isUpdating, setIsUpdating] = useState(false);
    const [rateSuccess, setRateSuccess] = useState(false);

    // Queries
    const globalPostCreationEnabled = useQuery(api.settings.getUserPostCreationEnabled);
    const commentsEnabled = useQuery(api.settings.getCommentsEnabled);
    const questionsOnPostsEnabled = useQuery(api.settings.getQuestionsOnPostsEnabled);
    const chatEnabled = useQuery(api.settings.getChatEnabled);
    const activeRate = useQuery(api.settings.getActiveRubyRate);

    // Mutations
    const togglePostCreation = useMutation(api.settings.toggleUserPostCreation);
    const toggleComments = useMutation(api.settings.toggleCommentsEnabled);
    const toggleQuestionsOnPosts = useMutation(api.settings.toggleQuestionsOnPostsEnabled);
    const toggleChat = useMutation(api.settings.toggleChatEnabled);
    const setRubyRate = useMutation(api.settings.setRubyRate);

    const handleToggle = async (
        mutation: any,
        currentValue: boolean | undefined,
        key: string
    ) => {
        if (!user || currentValue === undefined) return;
        setIsUpdating(true);
        try {
            await mutation({ adminId: user._id, enabled: !currentValue });
        } catch (error) {
            console.error(`Failed to update ${key}:`, error);
            alert('Failed to update setting. Please try again.');
        } finally {
            setIsUpdating(false);
        }
    };

    const handleRateChange = async (rate: AllowedRate) => {
        if (!user) return;
        setIsUpdating(true);
        try {
            await setRubyRate({ adminId: user._id, rate });
            setRateSuccess(true);
            setTimeout(() => setRateSuccess(false), 2500);
        } catch (error: any) {
            alert(error.message || 'Failed to update rate.');
        } finally {
            setIsUpdating(false);
        }
    };

    if (!user) return null;

    return (
        <div className="min-h-screen bg-light-bg dark:bg-dark-bg pb-20 md:pb-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl pt-6">
                {/* Page Header */}
                <div className="flex items-center gap-3 mb-6">
                    <div className="p-3 rounded-xl bg-gradient-to-br from-primary-500/10 to-secondary-500/10">
                        <Settings className="w-6 h-6 text-primary-600 dark:text-primary-400" />
                    </div>
                    <div>
                        <h1 className="text-2xl font-bold text-light-text dark:text-dark-text font-display">
                            System Settings
                        </h1>
                        <p className="text-sm text-light-muted dark:text-dark-muted">
                            Manage global permissions and system configurations
                        </p>
                    </div>
                </div>

                <div className="space-y-6">

                    {/*  Ruby Economy Rate  */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="glass rounded-2xl p-6 shadow-lg border border-amber-500/20"
                    >
                        <div className="flex items-center gap-3 mb-2">
                            <Gem className="w-5 h-5 text-amber-500" />
                            <h2 className="text-lg font-bold text-light-text dark:text-dark-text">
                                Ruby  Naira Exchange Rate
                            </h2>
                        </div>
                        <p className="text-sm text-light-muted dark:text-dark-muted mb-5">
                            Set how much Naira () each Ruby is worth. This affects what users see on
                            the Banking page in real time. Ruby balances are never changed  only
                            the display value updates.
                        </p>

                        {/* Current rate badge */}
                        <div className="flex flex-wrap items-center gap-2 mb-5">
                            <span className="text-xs uppercase tracking-widest font-semibold text-light-muted dark:text-dark-muted">
                                Current active rate:
                            </span>
                            <span className="px-3 py-1 rounded-full bg-amber-500/15 text-amber-600 dark:text-amber-400 font-bold text-sm">
                                1 Ruby = {activeRate ?? ''}
                            </span>
                            <AnimatePresence>
                                {rateSuccess && (
                                    <motion.span
                                        key="ok"
                                        initial={{ opacity: 0, scale: 0.8 }}
                                        animate={{ opacity: 1, scale: 1 }}
                                        exit={{ opacity: 0 }}
                                        className="flex items-center gap-1 text-green-600 dark:text-green-400 text-xs font-semibold"
                                    >
                                        <CheckCircle2 className="w-4 h-4" /> Updated!
                                    </motion.span>
                                )}
                            </AnimatePresence>
                        </div>

                        {/* Rate selector  grid that fills width on all screens, no scroll */}
                        <div className="grid grid-cols-5 gap-2 sm:gap-3">
                            {RATE_OPTIONS.map((opt) => {
                                const isActive = activeRate === opt.value;
                                return (
                                    <button
                                        key={opt.value}
                                        id={`rate-btn-${opt.value}`}
                                        onClick={() => handleRateChange(opt.value)}
                                        disabled={isUpdating || isActive}
                                        className={`relative flex flex-col items-center justify-center aspect-square rounded-xl border-2 transition-all duration-200 min-w-0
                                            ${isActive
                                                ? 'border-amber-500 bg-amber-500/15 shadow-lg shadow-amber-500/20 scale-105'
                                                : 'border-light-border dark:border-dark-border hover:border-amber-400 hover:bg-amber-500/5'
                                            } disabled:cursor-not-allowed`}
                                    >
                                        {isActive && (
                                            <span className="absolute -top-2 -right-2 w-5 h-5 bg-amber-500 rounded-full flex items-center justify-center">
                                                <CheckCircle2 className="w-3.5 h-3.5 text-white" />
                                            </span>
                                        )}
                                        <span className={`text-sm sm:text-base font-display font-bold leading-tight text-center ${isActive ? 'text-amber-600 dark:text-amber-400' : 'text-light-text dark:text-dark-text'}`}>
                                            {opt.label}
                                        </span>
                                        <span className="text-[9px] sm:text-[10px] text-light-muted dark:text-dark-muted mt-0.5 text-center leading-tight">
                                            per Ruby
                                        </span>
                                    </button>
                                );
                            })}
                        </div>

                        {/* Impact note */}
                        <div className="mt-4 p-3 rounded-lg bg-blue-500/10 border border-blue-500/20">
                            <p className="text-xs text-blue-600 dark:text-blue-400">
                                <strong>Example at current rate:</strong>{' '}
                                24 Rubies (minimum withdrawal) = {(24 * (activeRate ?? 200)).toLocaleString()}.{' '}
                                5 Rubies (winner window) = {(5 * (activeRate ?? 200)).toLocaleString()}.
                            </p>
                        </div>
                    </motion.div>

                    {/*  Global Controls  */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.05 }}
                        className="glass rounded-2xl p-6 shadow-lg space-y-6"
                    >
                        <div className="flex items-center gap-3 mb-4">
                            <Users className="w-5 h-5 text-primary-600 dark:text-primary-400" />
                            <h2 className="text-lg font-bold text-light-text dark:text-dark-text">
                                Global Controls
                            </h2>
                        </div>

                        {[
                            {
                                label: 'Allow Users to Create Posts',
                                desc: 'Enable regular users to create posts. If disabled, only admins and tagged users can post.',
                                value: globalPostCreationEnabled,
                                mutation: togglePostCreation,
                                key: 'postCreation',
                                icon: null,
                            },
                            {
                                label: 'Enable Comments Globally',
                                desc: 'Allow users to comment on posts. Turning this off hides the comment section on all posts.',
                                value: commentsEnabled,
                                mutation: toggleComments,
                                key: 'comments',
                                icon: null,
                            },
                            {
                                label: 'Enable "Ask Question" on Posts',
                                desc: 'Allow users to ask questions directly on teaching posts.',
                                value: questionsOnPostsEnabled,
                                mutation: toggleQuestionsOnPosts,
                                key: 'questionsOnPosts',
                                icon: null,
                            },
                            {
                                label: 'Enable Chat System',
                                desc: 'Allow users to chat with each other and admins.',
                                value: chatEnabled,
                                mutation: toggleChat,
                                key: 'chat',
                                icon: <MessageCircle className="w-5 h-5 text-primary-500" />,
                            },
                        ].map((item) => (
                            <div key={item.key} className="border border-light-border dark:border-dark-border rounded-xl p-4">
                                <div className="flex items-start justify-between gap-4">
                                    <div className="flex-1">
                                        <div className="flex items-center gap-2 mb-2">
                                            {item.icon}
                                            <h3 className="font-semibold text-light-text dark:text-dark-text">
                                                {item.label}
                                            </h3>
                                        </div>
                                        <p className="text-sm text-light-muted dark:text-dark-muted">{item.desc}</p>
                                    </div>
                                    <div className="flex flex-col items-end gap-2">
                                        <button
                                            id={`toggle-${item.key}`}
                                            onClick={() => handleToggle(item.mutation, item.value, item.key)}
                                            disabled={isUpdating}
                                            className={`relative inline-flex h-8 w-14 items-center rounded-full transition-colors ${item.value ? 'bg-green-500' : 'bg-gray-300 dark:bg-gray-600'
                                                }`}
                                        >
                                            <span className={`inline-block h-6 w-6 transform rounded-full bg-white transition-transform ${item.value ? 'translate-x-7' : 'translate-x-1'
                                                }`} />
                                        </button>
                                        <span className="text-xs font-medium text-light-muted dark:text-dark-muted">
                                            {item.value ? 'ON' : 'OFF'}
                                        </span>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </motion.div>

                    {/* Info Card */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.1 }}
                        className="glass rounded-2xl p-6 shadow-lg border-l-4 border-primary-500"
                    >
                        <h3 className="font-bold text-light-text dark:text-dark-text mb-2"> How it works</h3>
                        <ul className="space-y-2 text-sm text-light-muted dark:text-dark-muted">
                            {[
                                ['Ruby Rate', 'Changing the rate only affects Naira display values. All Ruby balances and Talent conversions stay the same.'],
                                ['Global ON', 'Every user (even new signups) can immediately create question and prayer posts.'],
                                ['Global OFF', 'Users need to be individually tagged by you before they can create posts.'],
                                ['Admins', 'Always have permission to create posts, regardless of settings.'],
                                ['Chat System', 'Controls whether users can initiate chats. Admins can always chat.'],
                            ].map(([title, body]) => (
                                <li key={title} className="flex items-start gap-2">
                                    <span className="text-primary-500 mt-1"></span>
                                    <span><strong>{title}:</strong> {body}</span>
                                </li>
                            ))}
                        </ul>
                    </motion.div>

                </div>
            </div>
        </div>
    );
}
