import { motion } from 'framer-motion';
import { cn } from '@/lib/cn';

interface BadgeProps {
    children: React.ReactNode;
    variant?: 'default' | 'primary' | 'secondary' | 'success' | 'warning' | 'danger' | 'outline';
    className?: string;
    animate?: boolean;
}

export function Badge({ children, variant = 'default', className, animate = false }: BadgeProps) {
    const variants = {
        default: 'bg-light-border dark:bg-dark-border text-light-text dark:text-dark-text',
        primary: 'bg-primary-100 dark:bg-primary-950 text-primary-800 dark:text-primary-300 border border-primary-200 dark:border-primary-800',
        secondary: 'bg-secondary-100 dark:bg-secondary-950 text-secondary-700 dark:text-secondary-300 border border-secondary-200 dark:border-secondary-800',
        success: 'bg-green-100 dark:bg-green-950 text-green-700 dark:text-green-300 border border-green-200 dark:border-green-800',
        warning: 'bg-yellow-100 dark:bg-yellow-950 text-yellow-700 dark:text-yellow-300 border border-yellow-200 dark:border-yellow-800',
        danger: 'bg-red-100 dark:bg-red-950 text-red-700 dark:text-red-300 border border-red-200 dark:border-red-800',
        outline: 'bg-transparent border border-light-border dark:border-dark-border text-light-muted dark:text-dark-muted',
    };

    const Component = animate ? motion.span : 'span';
    const animationProps = animate
        ? {
            initial: { scale: 0 },
            animate: { scale: 1 },
            transition: { type: 'spring', stiffness: 500, damping: 30 },
        }
        : {};

    return (
        <Component
            {...animationProps}
            className={cn(
                'inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium',
                variants[variant],
                className
            )}
        >
            {children}
        </Component>
    );
}
