import { useState, useEffect } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useNavigate } from 'react-router-dom';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Card, CardContent } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Modal } from '@/components/ui/Modal';
import { Search, UserPlus, Shield, Users, Mail, Phone, AlertTriangle, UserCheck, ShieldCheck, ChevronLeft, ChevronRight, DollarSign } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { UserCardSkeleton } from '@/components/ui/Skeletons';

export function AdminUserRoles() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();
    const allUsers = useQuery(api.users.getAllUsers, user ? { adminId: user._id } : 'skip');
    const updateUserRole = useMutation(api.users.updateUserRole);
    const assignFinancialManager = useMutation(api.finance.assignFinancialManager);

    // Search and Modal State
    const [searchQuery, setSearchQuery] = useState('');
    const [confirmModalOpen, setConfirmModalOpen] = useState(false);
    const [financeModalOpen, setFinanceModalOpen] = useState(false);
    const [financeChangeUser, setFinanceChangeUser] = useState<{ id: string; name: string; isCurrentlyManager: boolean } | null>(null);
    const [roleChangeUser, setRoleChangeUser] = useState<{ id: string; name: string; action: string; newRole: string; icon: any; color: string } | null>(null);

    // Pagination State
    const [currentPage, setCurrentPage] = useState(1);
    const usersPerPage = 10;

    // Reset to page 1 when search query changes
    useEffect(() => {
        setCurrentPage(1);
    }, [searchQuery]);

    // Filtered Users
    const filteredUsers = allUsers?.filter((u: any) =>
        u.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        u.email.toLowerCase().includes(searchQuery.toLowerCase())
    ) || [];

    const indexOfLastUser = currentPage * usersPerPage;
    const indexOfFirstUser = indexOfLastUser - usersPerPage;
    const paginatedUsers = filteredUsers.slice(indexOfFirstUser, indexOfLastUser);
    const totalPages = Math.ceil(filteredUsers.length / usersPerPage);

    const initiateRoleChange = (userId: string, userName: string, currentRole: string, action: 'admin' | 'board', previousRole?: string) => {
        let actionText = '';
        let newRole = '';
        let icon = Shield;
        let color = 'text-primary-500';

        if (action === 'admin') {
            if (currentRole === 'admin') {
                if (previousRole === 'board_member') {
                    actionText = 'revert this user from Admin to Board Member';
                    newRole = 'board_member';
                    icon = Users;
                    color = 'text-blue-500';
                } else {
                    actionText = 'demote this user from Admin to Member';
                    newRole = 'member';
                    icon = Users;
                    color = 'text-gray-500';
                }
            } else {
                actionText = 'promote this user to Admin';
                newRole = 'admin';
                icon = ShieldCheck;
                color = 'text-primary-500';
            }
        } else if (action === 'board') {
            if (currentRole === 'board_member') {
                actionText = 'remove this user from Board Members';
                newRole = 'member';
                icon = Users;
                color = 'text-gray-500';
            } else {
                actionText = 'add this user to Board Members';
                newRole = 'board_member';
                icon = UserCheck;
                color = 'text-blue-500';
            }
        }

        setRoleChangeUser({ id: userId, name: userName, action: actionText, newRole, icon, color });
        setConfirmModalOpen(true);
    };

    const confirmRoleChange = async () => {
        if (!roleChangeUser || !user) return;
        try {
            await updateUserRole({
                adminId: user._id,
                userId: roleChangeUser.id as any,
                newRole: roleChangeUser.newRole as any
            });
            setConfirmModalOpen(false);
            setRoleChangeUser(null);
        } catch (error) {
            console.error("Failed to update role", error);
        }
    };

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-[60vh]">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user || (user.role !== 'admin' && user.role !== 'super_admin')) {
        return <Navigate to="/feed" replace />;
    }

    return (
        <div className="space-y-8 pb-12">
            <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                <div>
                    <h1 className="text-3xl font-bold text-gradient font-display mb-1">
                        Role Management
                    </h1>
                    <p className="text-sm text-light-muted dark:text-dark-muted">
                        Assign and manage administrative privileges for community members
                    </p>
                </div>
                <div className="w-full md:w-80">
                    <Input
                        placeholder="Search by name or email..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        icon={<Search className="w-5 h-5 text-light-muted" />}
                        className="bg-white/50 dark:bg-dark-card/50 backdrop-blur-sm border-light-border dark:border-dark-border"
                    />
                </div>
            </div>

            {/* Quick Stats */}
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
                <Card glass className="border-primary-500/20">
                    <CardContent className="p-4 flex items-center gap-4">
                        <div className="w-12 h-12 rounded-2xl bg-primary-500/10 flex items-center justify-center">
                            <Shield className="w-6 h-6 text-primary-500" />
                        </div>
                        <div>
                            <p className="text-sm text-light-muted dark:text-dark-muted">Admins</p>
                            <p className="text-2xl font-bold">{allUsers?.filter((u: any) => u.role === 'admin' || u.role === 'super_admin').length || 0}</p>
                        </div>
                    </CardContent>
                </Card>
                <Card glass className="border-blue-500/20">
                    <CardContent className="p-4 flex items-center gap-4">
                        <div className="w-12 h-12 rounded-2xl bg-blue-500/10 flex items-center justify-center">
                            <UserCheck className="w-6 h-6 text-blue-500" />
                        </div>
                        <div>
                            <p className="text-sm text-light-muted dark:text-dark-muted">Board Members</p>
                            <p className="text-2xl font-bold">{allUsers?.filter((u: any) => u.role === 'board_member').length || 0}</p>
                        </div>
                    </CardContent>
                </Card>
            </div>

            {/* Users Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
                <AnimatePresence mode="popLayout">
                    {allUsers === undefined ? (
                        Array.from({ length: 6 }).map((_, i) => (
                            <div key={i}>
                                <UserCardSkeleton />
                            </div>
                        ))
                    ) : paginatedUsers.length > 0 ? (
                        paginatedUsers.map((u: any) => (
                            <motion.div
                                key={u._id}
                                layout
                                initial={{ opacity: 0, scale: 0.95 }}
                                animate={{ opacity: 1, scale: 1 }}
                                exit={{ opacity: 0, scale: 0.95 }}
                            >
                                <Card glass className={`h-full group hover:border-primary-500/30 transition-all duration-300 overflow-hidden relative ${u.role === 'admin' || u.role === 'super_admin' ? 'border-primary-500/50' :
                                    u.role === 'board_member' ? 'border-blue-500/50' : ''
                                    }`}>
                                    <div className="absolute top-0 right-0 w-32 h-32 bg-gradient-to-br from-primary-500/5 to-transparent rounded-bl-full pointer-events-none" />

                                    <CardContent className="p-6">
                                        <div className="flex items-start justify-between mb-4">
                                            <div
                                                onClick={() => navigate(`/admin/user/${u._id}`)}
                                                className="w-14 h-14 rounded-2xl bg-gradient-to-br from-light-bg to-light-card dark:from-dark-bg dark:to-dark-card border border-light-border dark:border-dark-border flex items-center justify-center text-xl font-bold text-gradient overflow-hidden shadow-sm cursor-pointer hover:scale-105 transition-transform duration-200"
                                            >
                                                {u.avatarUrl ? (
                                                    <img src={u.avatarUrl} alt={u.name} className="w-full h-full object-cover" />
                                                ) : (
                                                    u.name[0]
                                                )}
                                            </div>
                                            <Badge variant={
                                                u.role === 'admin' || u.role === 'super_admin' ? 'primary' :
                                                    u.role === 'board_member' ? 'secondary' : 'default'
                                            } className="px-3 py-1">
                                                {u.role.replace('_', ' ')}
                                            </Badge>
                                        </div>

                                        <div className="space-y-1 mb-6">
                                            <h3 className="font-bold text-lg text-light-text dark:text-dark-text truncate">{u.name}</h3>
                                            <div className="flex items-center gap-2 text-xs text-light-muted dark:text-dark-muted">
                                                <Mail className="w-3.5 h-3.5" />
                                                <span className="truncate">{u.email}</span>
                                            </div>
                                            <div className="flex items-center gap-2 text-xs text-light-muted dark:text-dark-muted">
                                                <Phone className="w-3.5 h-3.5" />
                                                <span>{u.phone || 'No phone'}</span>
                                            </div>
                                        </div>

                                        {user.role === 'super_admin' && u.role !== 'super_admin' && (
                                            <div className="grid grid-cols-2 gap-3 pt-4 border-t border-light-border/50 dark:border-dark-border/50">
                                                <Button
                                                    variant={u.role === 'admin' ? 'outline' : 'primary'}
                                                    size="sm"
                                                    className="w-full text-[10px] font-bold h-9"
                                                    onClick={() => initiateRoleChange(u._id, u.name, u.role, 'admin', (u as any).previousRole)}
                                                >
                                                    {u.role === 'admin' ? (
                                                        <span className="flex items-center gap-1">
                                                            <Users className="w-3 h-3" />
                                                            Demote
                                                        </span>
                                                    ) : (
                                                        <span className="flex items-center gap-1">
                                                            <Shield className="w-3 h-3" />
                                                            Make Admin
                                                        </span>
                                                    )}
                                                </Button>
                                                <Button
                                                    variant={u.role === 'board_member' ? 'outline' : 'secondary'}
                                                    size="sm"
                                                    className={`w-full text-[10px] font-bold h-9 ${u.role === 'board_member' ? 'border-blue-500 text-blue-500 hover:bg-blue-50 dark:hover:bg-blue-900/10' : ''
                                                        }`}
                                                    onClick={() => initiateRoleChange(u._id, u.name, u.role, 'board')}
                                                >
                                                    {u.role === 'board_member' ? (
                                                        <span className="flex items-center gap-1">
                                                            <UserPlus className="w-3 h-3" />
                                                            Remove Board
                                                        </span>
                                                    ) : (
                                                        <span className="flex items-center gap-1">
                                                            <UserPlus className="w-3 h-3" />
                                                            Add Board
                                                        </span>
                                                    )}
                                                </Button>
                                            </div>
                                        )}

                                        {/* Financial Manager Toggle - Only for Board Members */}
                                        {user.role === 'super_admin' && u.role === 'board_member' && (
                                            <div className="pt-3">
                                                <Button
                                                    variant={(u as any).isFinancialManager ? 'primary' : 'outline'}
                                                    size="sm"
                                                    className="w-full text-[10px] font-bold h-9"
                                                    onClick={() => {
                                                        setFinanceChangeUser({
                                                            id: u._id,
                                                            name: u.name,
                                                            isCurrentlyManager: (u as any).isFinancialManager || false
                                                        });
                                                        setFinanceModalOpen(true);
                                                    }}
                                                >
                                                    <span className="flex items-center gap-1">
                                                        <DollarSign className="w-3 h-3" />
                                                        {(u as any).isFinancialManager ? 'Remove Finance Role' : 'Make Finance Manager'}
                                                    </span>
                                                </Button>
                                            </div>
                                        )}
                                    </CardContent>
                                </Card>
                            </motion.div>
                        ))
                    ) : (
                        <div className="col-span-full py-12 text-center text-light-muted">
                            <Users className="w-16 h-16 mx-auto mb-4 opacity-10" />
                            <p className="text-lg">No users found matching your search</p>
                        </div>
                    )}
                </AnimatePresence>
            </div>

            {/* Pagination Controls */}
            {totalPages > 1 && (
                <div className="flex items-center justify-center gap-2 mt-12 bg-light-card/30 dark:bg-dark-card/30 p-4 rounded-3xl border border-light-border/20 dark:border-dark-border/20 max-w-fit mx-auto">
                    <Button
                        variant="ghost"
                        size="sm"
                        onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                        disabled={currentPage === 1}
                        className="rounded-xl h-10 w-10 p-0"
                    >
                        <ChevronLeft className="w-5 h-5" />
                    </Button>

                    <div className="flex items-center gap-1.5 px-2">
                        {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => (
                            <button
                                key={page}
                                onClick={() => setCurrentPage(page)}
                                className={`
                                    w-10 h-10 rounded-xl text-sm font-bold transition-all
                                    ${currentPage === page
                                        ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                                        : 'text-light-muted hover:bg-light-card dark:hover:bg-dark-card hover:text-primary-500'
                                    }
                                `}
                            >
                                {page}
                            </button>
                        ))}
                    </div>

                    <Button
                        variant="ghost"
                        size="sm"
                        onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))}
                        disabled={currentPage === totalPages}
                        className="rounded-xl h-10 w-10 p-0"
                    >
                        <ChevronRight className="w-5 h-5" />
                    </Button>
                </div>
            )}

            {/* Confirm Role Change Modal */}
            <Modal
                isOpen={confirmModalOpen}
                onClose={() => setConfirmModalOpen(false)}
                title="Confirm Role Change"
            >
                <div className="space-y-6">
                    <div className="flex flex-col items-center text-center p-4">
                        <div className={`w-20 h-20 rounded-3xl bg-gradient-to-br from-light-bg to-light-card dark:from-dark-bg dark:to-dark-card border border-light-border dark:border-dark-border flex items-center justify-center mb-4 shadow-xl ${roleChangeUser?.color}`}>
                            {roleChangeUser && <roleChangeUser.icon className="w-10 h-10" />}
                        </div>
                        <h3 className="text-xl font-bold text-light-text dark:text-dark-text mb-2">Confirm Action</h3>
                        <p className="text-sm text-light-muted dark:text-dark-muted max-w-xs">
                            Are you sure you want to {roleChangeUser?.action} <strong>{roleChangeUser?.name}</strong>?
                        </p>
                    </div>

                    <div className="flex items-start gap-4 p-4 rounded-2xl bg-amber-500/5 border border-amber-500/20">
                        <div className="w-10 h-10 rounded-xl bg-amber-500/10 flex items-center justify-center shrink-0">
                            <AlertTriangle className="w-6 h-6 text-amber-500" />
                        </div>
                        <div>
                            <p className="text-sm font-bold text-amber-700 dark:text-amber-500 mb-1">Important Security Note</p>
                            <p className="text-xs text-amber-700/70 dark:text-amber-500/70">
                                This user will gain or lose access to administrative tools and sensitive data. This action is logged.
                            </p>
                        </div>
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                        <Button
                            variant="ghost"
                            className="w-full rounded-2xl h-12"
                            onClick={() => setConfirmModalOpen(false)}
                        >
                            Cancel
                        </Button>
                        <Button
                            variant="primary"
                            className="w-full rounded-2xl h-12 shadow-lg shadow-primary-500/20"
                            onClick={confirmRoleChange}
                        >
                            Yes, Update Role
                        </Button>
                    </div>
                </div>
            </Modal>

            {/* Financial Manager Confirmation Modal */}
            <Modal
                isOpen={financeModalOpen}
                onClose={() => setFinanceModalOpen(false)}
                title="Financial Manager Assignment"
            >
                <div className="space-y-6">
                    <div className="flex flex-col items-center text-center p-4">
                        <div className="w-20 h-20 rounded-3xl bg-gradient-to-br from-emerald-500/10 to-emerald-500/5 border border-emerald-500/20 flex items-center justify-center mb-4 shadow-xl">
                            <DollarSign className="w-10 h-10 text-emerald-500" />
                        </div>
                        <h3 className="text-xl font-bold text-light-text dark:text-dark-text mb-2">Confirm Action</h3>
                        <p className="text-sm text-light-muted dark:text-dark-muted max-w-xs">
                            {financeChangeUser?.isCurrentlyManager
                                ? `Remove ${financeChangeUser?.name} from Financial Manager role?`
                                : `Assign ${financeChangeUser?.name} as Financial Manager?`
                            }
                        </p>
                    </div>

                    <div className="flex items-start gap-4 p-4 rounded-2xl bg-blue-500/5 border border-blue-500/20">
                        <div className="w-10 h-10 rounded-xl bg-blue-500/10 flex items-center justify-center shrink-0">
                            <AlertTriangle className="w-6 h-6 text-blue-500" />
                        </div>
                        <div>
                            <p className="text-sm font-bold text-blue-700 dark:text-blue-500 mb-1">Financial Manager Permissions</p>
                            <p className="text-xs text-blue-700/70 dark:text-blue-500/70">
                                {financeChangeUser?.isCurrentlyManager
                                    ? 'This user will lose access to add/reverse financial transactions.'
                                    : 'This user will be able to add and reverse financial transactions (within 24 hours) but cannot create meetings.'
                                }
                            </p>
                        </div>
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                        <Button
                            variant="ghost"
                            className="w-full rounded-2xl h-12"
                            onClick={() => setFinanceModalOpen(false)}
                        >
                            Cancel
                        </Button>
                        <Button
                            variant="primary"
                            className="w-full rounded-2xl h-12 shadow-lg shadow-primary-500/20"
                            onClick={async () => {
                                if (financeChangeUser) {
                                    await assignFinancialManager({
                                        adminId: user._id,
                                        userId: financeChangeUser.id as any,
                                        assign: !financeChangeUser.isCurrentlyManager
                                    });
                                    setFinanceModalOpen(false);
                                    setFinanceChangeUser(null);
                                }
                            }}
                        >
                            Yes, {financeChangeUser?.isCurrentlyManager ? 'Remove' : 'Assign'}
                        </Button>
                    </div>
                </div>
            </Modal>
        </div>
    );
}

export default AdminUserRoles;
