import { useState, useEffect } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Card, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { motion, AnimatePresence } from 'framer-motion';
import { Clock, Trophy, CheckCircle, XCircle, AlertCircle, ChevronDown, ChevronUp, BarChart2 } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { cn } from '@/lib/cn';
import { Link } from 'react-router-dom';

export function WeeklyPollCard() {
    const { user } = useAuth();
    const poll = useQuery(api.polls.getActivePoll, user ? { userId: user._id } : "skip");
    const vote = useMutation(api.polls.vote);

    const [selectedOption, setSelectedOption] = useState<string | null>(null);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [timeLeft, setTimeLeft] = useState<string>('');
    const [isExpanded, setIsExpanded] = useState(false);

    // Timer Logic
    useEffect(() => {
        if (!poll || !poll.expiresAt) return;
        const updateTimer = () => {
            const now = Date.now();
            const diff = poll.expiresAt - now;
            if (diff <= 0) {
                setTimeLeft('Ended');
            } else {
                const hours = Math.floor(diff / (1000 * 60 * 60));
                const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
                setTimeLeft(`${hours}h ${minutes}m`);
            }
        };
        updateTimer();
        const interval = setInterval(updateTimer, 60000); // Update every minute
        return () => clearInterval(interval);
    }, [poll]);

    const [error, setError] = useState<string | null>(null);

    const handleVote = async () => {
        if (!user || !poll || !selectedOption) return;
        setIsSubmitting(true);
        setError(null);
        try {
            await vote({
                pollId: poll._id,
                userId: user._id,
                selectedOptionId: selectedOption
            });
            setIsExpanded(true);
        } catch (err: any) {
            console.error(err);
            setError(err.message || "Failed to submit vote");
        } finally {
            setIsSubmitting(false);
        }
    };

    if (poll === undefined) return null; // Loading state handled by parent or hidden
    if (poll === null) return null; // No active poll

    const isExpired = Date.now() > poll.expiresAt;

    return (
        <Card glass className="border-primary-500/20 overflow-hidden relative mb-6">
            <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-primary-500 to-secondary-500" />

            <CardContent className="p-6">
                {/* Header */}
                {/* Header Row */}
                <div className="flex flex-col sm:flex-row justify-between items-start gap-4 mb-2">
                    <div className="flex-1 cursor-pointer" onClick={() => setIsExpanded(!isExpanded)}>
                        <h3 className="text-lg sm:text-xl font-bold font-display text-gradient leading-tight flex items-center gap-2">
                            Weekly Wisdom Game
                            {isExpanded ? <ChevronUp className="w-5 h-5 text-light-muted" /> : <ChevronDown className="w-5 h-5 text-light-muted" />}
                        </h3>
                        <p className="text-xs sm:text-sm text-light-muted dark:text-dark-muted mt-1">
                            {isExpanded ? "Test your knowledge & earn Rubies" : "Tap to reveal the weekly question"}
                        </p>
                    </div>

                    <div className="flex items-center gap-2 self-start sm:self-auto flex-wrap">
                        {/* Timer Badge */}
                        <div className="flex items-center gap-1.5 bg-primary-50 dark:bg-primary-900/20 px-2.5 py-1.5 rounded-full border border-primary-100 dark:border-primary-900/50">
                            <Clock className="w-3.5 h-3.5 sm:w-4 sm:h-4 text-primary-600 dark:text-primary-400" />
                            <span className="text-xs font-bold text-primary-700 dark:text-primary-300 font-mono">
                                {timeLeft}
                            </span>
                        </div>

                        {/* Leaderboard Button */}
                        <Link to="/leaderboard" className="flex items-center gap-1.5 bg-light-card dark:bg-dark-card hover:bg-light-bg dark:hover:bg-dark-bg px-2.5 py-1.5 rounded-full border border-light-border dark:border-dark-border transition-colors">
                            <BarChart2 className="w-3.5 h-3.5 sm:w-4 sm:h-4 text-light-muted dark:text-dark-muted" />
                            <span className="text-xs font-bold text-light-muted dark:text-dark-muted">Leadersboard</span>
                        </Link>
                    </div>
                </div>

                {/* Question */}
                <AnimatePresence>
                    {isExpanded && (
                        <motion.div
                            initial={{ height: 0, opacity: 0 }}
                            animate={{ height: "auto", opacity: 1 }}
                            exit={{ height: 0, opacity: 0 }}
                            className="overflow-hidden"
                        >
                            <div className="pt-4 border-t border-light-border/50 dark:border-dark-border/50 mt-4">
                                <h4 className="text-lg font-medium mb-6 leading-relaxed">
                                    {poll.question}
                                </h4>

                                {/* Content based on state */}
                                <div className="space-y-4">
                                    {!poll.hasVoted ? (
                                        // Voting View
                                        <>
                                            <div className="space-y-3">
                                                {poll.options.map((opt: any) => (
                                                    <button
                                                        key={opt.id}
                                                        onClick={() => !isExpired && setSelectedOption(opt.id)}
                                                        disabled={isExpired}
                                                        className={cn(
                                                            "w-full p-4 rounded-xl text-left transition-all duration-200 border relative overflow-hidden group outline-none focus:ring-2 focus:ring-primary-500",
                                                            selectedOption === opt.id
                                                                ? "bg-primary-500 text-white border-primary-600 shadow-lg shadow-primary-500/25"
                                                                : "bg-light-bg/50 dark:bg-dark-bg/50 border-light-border dark:border-dark-border hover:border-primary-300 dark:hover:border-primary-700 hover:bg-light-bg dark:hover:bg-dark-bg"
                                                        )}
                                                    >
                                                        <span className="relative z-10 font-medium">{opt.text}</span>
                                                        {selectedOption === opt.id && (
                                                            <motion.div
                                                                layoutId="selected-indicator"
                                                                className="absolute inset-0 bg-primary-500 z-0"
                                                                initial={false}
                                                                transition={{ type: "spring", stiffness: 500, damping: 30 }}
                                                            />
                                                        )}
                                                    </button>
                                                ))}
                                            </div>

                                            {error && (
                                                <div className="mt-4 p-3 rounded-xl bg-red-500/10 border border-red-500/20 text-red-500 text-xs text-center flex items-center justify-center gap-2">
                                                    <AlertCircle className="w-4 h-4" />
                                                    {error}
                                                </div>
                                            )}

                                            <Button
                                                onClick={handleVote}
                                                disabled={!selectedOption || isSubmitting || isExpired}
                                                className="w-full mt-4 bg-gradient-to-r from-primary-600 to-primary-500 hover:from-primary-700 hover:to-primary-600 shadow-xl shadow-primary-500/20"
                                                size="lg"
                                                isLoading={isSubmitting}
                                            >
                                                {isExpired ? "Poll Closed" : "Submit Vote"}
                                            </Button>
                                        </>
                                    ) : (
                                        // Results View
                                        <div className="space-y-4 animate-in fade-in duration-500">
                                            <div className="grid grid-cols-2 gap-3">
                                                {poll.options.map((opt: any) => {
                                                    const isSelected = (poll as any).userVote?.selectedOptionId === opt.id;
                                                    const isCorrect = poll.correctOptionId === opt.id;
                                                    const percentage = poll.totalVotes > 0
                                                        ? Math.round((opt.voteCount / poll.totalVotes) * 100)
                                                        : 0;

                                                    // Styling based on result
                                                    let themeClass = "bg-light-bg/30 dark:bg-dark-bg/30 border-light-border dark:border-dark-border";
                                                    if (isCorrect) themeClass = "bg-green-500/10 border-green-500/50";
                                                    else if (isSelected && !isCorrect) themeClass = "bg-red-500/10 border-red-500/50";

                                                    return (
                                                        <div key={opt.id} className={cn("relative p-3 rounded-xl border overflow-hidden min-h-[6rem] flex flex-col justify-center", themeClass)}>
                                                            {/* Progress Bar Background */}
                                                            <motion.div
                                                                initial={{ height: 0 }}
                                                                animate={{ height: `${percentage}%` }}
                                                                transition={{ duration: 1, ease: "easeOut", delay: 0.2 }}
                                                                className={cn(
                                                                    "absolute bottom-0 left-0 w-full opacity-10",
                                                                    isCorrect ? "bg-green-500" : (isSelected ? "bg-red-500" : "bg-gray-400")
                                                                )}
                                                            />

                                                            <div className="relative z-10 text-center">
                                                                <div className="flex justify-center mb-1">
                                                                    {isCorrect && <CheckCircle className="w-5 h-5 text-green-500" />}
                                                                    {isSelected && !isCorrect && <XCircle className="w-5 h-5 text-red-500" />}
                                                                </div>
                                                                <span className={cn("block text-sm font-medium leading-tight mb-1", isCorrect ? "text-green-600 dark:text-green-400" : "")}>
                                                                    {opt.text}
                                                                </span>
                                                                <span className="font-bold text-lg">{percentage}%</span>
                                                            </div>
                                                        </div>
                                                    );
                                                })}
                                            </div>

                                            {/* Reward Message */}
                                            <div className="mt-6 p-4 rounded-xl bg-gradient-to-br from-primary-500/10 to-secondary-500/10 border border-primary-500/20 text-center relative overflow-hidden">
                                                <div className="absolute inset-0 bg-white/5 opacity-50 backdrop-blur-[1px]" />
                                                <div className="relative z-10">
                                                    {(poll as any).userVote?.isCorrect ? (
                                                        <>
                                                            <div className="flex justify-center mb-2">
                                                                <Trophy className="w-8 h-8 text-yellow-500 fill-yellow-500 animate-bounce" />
                                                            </div>
                                                            <h5 className="font-bold text-lg text-primary-600 dark:text-primary-400">Correct! +1 Ruby</h5>
                                                            <p className="text-xs text-light-muted">Added to Silver Pool</p>
                                                        </>
                                                    ) : (
                                                        <>
                                                            <div className="flex justify-center mb-2">
                                                                <AlertCircle className="w-8 h-8 text-primary-400" />
                                                            </div>
                                                            <h5 className="font-bold text-lg text-light-text dark:text-dark-text">Good Try! +0.3 Ruby</h5>
                                                            <p className="text-xs text-light-muted">Participation Reward Added</p>
                                                        </>
                                                    )}
                                                </div>
                                            </div>

                                            <div className="text-center text-xs text-light-muted mt-2">
                                                Total Votes: {poll.totalVotes}
                                            </div>
                                        </div>
                                    )}
                                </div>
                            </div>
                        </motion.div>
                    )}
                </AnimatePresence>
            </CardContent>
        </Card>
    );
}
