import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { MemberTicker } from '@/components/users/MemberTicker';
import { Users, TrendingUp, Megaphone, ExternalLink } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import { Badge } from '@/components/ui/Badge';
import { SidebarStatsSkeleton, SidebarAnnouncementSkeleton } from '@/components/ui/Skeletons';

export function FeedSidebarRight() {
    const navigate = useNavigate();
    const tickerData = useQuery(api.users.getTickerData);
    const announcements = useQuery(api.announcements.getAnnouncements, {});

    return (
        <aside className="hidden xl:block space-y-6 sticky top-24 max-h-[calc(100vh-120px)] overflow-y-auto pb-10 pr-1 transition-all custom-scrollbar-premium">
            <style>{`
                .custom-scrollbar-premium::-webkit-scrollbar {
                    width: 4px;
                }
                .custom-scrollbar-premium::-webkit-scrollbar-track {
                    background: #ffffff;
                }
                .dark .custom-scrollbar-premium::-webkit-scrollbar-track {
                    background: #111827;
                }
                .custom-scrollbar-premium::-webkit-scrollbar-thumb {
                    background: #374151;
                    border-radius: 10px;
                }
                .dark .custom-scrollbar-premium::-webkit-scrollbar-thumb {
                    background: #064e3b;
                }
                .custom-scrollbar-premium {
                    scrollbar-width: thin;
                    scrollbar-color: #374151 #ffffff;
                }
                .dark .custom-scrollbar-premium {
                    scrollbar-color: #064e3b #111827;
                }
            `}</style>

            {/* Community Progress */}
            {tickerData === undefined ? (
                <SidebarStatsSkeleton />
            ) : (
                <Card glass className="overflow-hidden border-emerald-500/10 hover:border-emerald-500/30 transition-all duration-300">
                    <CardHeader className="pb-2">
                        <CardTitle className="text-[10px] font-black uppercase tracking-[0.2em] text-light-muted flex items-center justify-between">
                            Realtime Stats
                            <TrendingUp className="w-3.5 h-3.5 text-emerald-500" />
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <div className="flex items-center justify-between p-3 rounded-2xl bg-emerald-500/5 border border-emerald-500/10">
                            <div>
                                <p className="text-2xl font-black text-emerald-500 font-display">
                                    {tickerData?.totalCount?.toLocaleString() || '...'}
                                </p>
                                <p className="text-[10px] font-bold text-light-muted uppercase tracking-wider">Total Members</p>
                            </div>
                            <div className="w-10 h-10 rounded-xl bg-emerald-500/10 flex items-center justify-center">
                                <Users className="w-5 h-5 text-emerald-500" />
                            </div>
                        </div>

                        <div className="pt-2">
                            <MemberTicker />
                        </div>
                    </CardContent>
                </Card>
            )}

            {/* Announcements Segment - Full Desktop View */}
            {announcements === undefined ? (
                <SidebarAnnouncementSkeleton />
            ) : (
                <Card glass className="border-primary-500/10">
                    <CardHeader className="pb-3 border-b border-light-border/30 dark:border-dark-border/30">
                        <CardTitle className="text-[10px] font-black uppercase tracking-[0.2em] text-light-muted flex items-center justify-between">
                            Announcements
                            <Megaphone className="w-3.5 h-3.5 text-primary-500" />
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="p-0">
                        <div className="divide-y divide-light-border/30 dark:divide-dark-border/30">
                            {announcements && announcements.length > 0 ? (
                                announcements.slice(0, 4).map((ann: any) => (
                                    <div
                                        key={ann._id}
                                        onClick={() => navigate(`/announcement/${ann._id}`)}
                                        className="p-4 hover:bg-light-card/50 dark:hover:bg-dark-card/50 transition-colors group relative overflow-hidden cursor-pointer"
                                    >
                                        {ann.imageUrl && (
                                            <div className="h-32 w-full rounded-xl overflow-hidden mb-3 border border-light-border/20 dark:border-dark-border/20">
                                                <img src={ann.imageUrl} alt={ann.title} className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500" />
                                            </div>
                                        )}
                                        <div className="flex items-start justify-between gap-2 mb-1">
                                            <h4 className="text-sm font-bold text-light-text dark:text-dark-text group-hover:text-primary-500 transition-colors">{ann.title}</h4>
                                            <Badge variant="outline" className="text-[8px] px-1.5 py-0 bg-primary-500/10 text-primary-500 border-primary-500/20">
                                                {ann.category || 'New'}
                                            </Badge>
                                        </div>
                                        <p className="text-xs text-light-muted line-clamp-2 leading-relaxed mb-3">{ann.description}</p>

                                        <div className="flex items-center justify-between">
                                            <span className="text-[10px] font-bold text-light-muted/60 uppercase tracking-tighter italic">
                                                By {ann.creatorRole?.replace('_', ' ') || 'Admin'}
                                            </span>
                                            <button className="text-[10px] font-bold text-primary-500 flex items-center gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                                                Read More <ExternalLink className="w-2.5 h-2.5" />
                                            </button>
                                        </div>
                                    </div>
                                ))
                            ) : (
                                <div className="p-8 text-center">
                                    <Megaphone className="w-8 h-8 text-light-muted/20 mx-auto mb-2" />
                                    <p className="text-xs text-light-muted italic">No official announcements yet.</p>
                                </div>
                            )}
                        </div>
                    </CardContent>
                </Card>
            )}
        </aside>
    );
}
