import { useState } from 'react';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/Card';
import { Button } from '../ui/Button';
import { Badge } from '../ui/Badge';
import {
    Calendar,
    CreditCard,
    History,
    TrendingUp,
    Plus,
    Eye
} from 'lucide-react';
import { ImageModal } from '../ui/ImageModal';
import { SubmitPaymentModal } from './SubmitPaymentModal';
import { StatsCardSkeleton, PostCardSkeleton } from '../ui/Skeletons';

import { MemberLedgerTab } from './MemberLedgerTab';

interface MyContributionsTabProps {
    user: any;
}

export function MyContributionsTab({ user }: MyContributionsTabProps) {
    const [activeView, setActiveView] = useState<'personal' | 'ledger'>('personal');
    const [showSubmitModal, setShowSubmitModal] = useState(false);
    const [selectedProof, setSelectedProof] = useState<{ url: string; title: string } | null>(null);
    const [selectedYear] = useState(new Date().getFullYear());

    const isSuperAdmin = user?.role === 'super_admin';

    const contributions = useQuery(api.finance.getMyContributions, {
        userId: user._id,
        year: selectedYear
    });
    const submissions = useQuery(api.finance.getMyPaymentSubmissions, {
        userId: user._id
    });

    const formatCurrency = (amount: number) => {
        return new Intl.NumberFormat('en-NG', {
            style: 'currency',
            currency: 'NGN',
        }).format(amount);
    };

    const getStatusBadge = (status: string) => {
        switch (status) {
            case 'cleared':
                return <Badge className="bg-emerald-500">Cleared</Badge>;
            case 'partial':
                return <Badge className="bg-amber-500">Partial</Badge>;
            case 'owing':
                return <Badge className="bg-rose-500">Owing</Badge>;
            default:
                return <Badge variant="outline">{status}</Badge>;
        }
    };

    const getMonthName = (month: number) => {
        return new Date(2024, month - 1).toLocaleDateString('en-US', { month: 'long' });
    };

    const isLoading = contributions === undefined || submissions === undefined;

    if (isLoading) {
        return (
            <div className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    {[1, 2, 3].map(i => <StatsCardSkeleton key={i} />)}
                </div>
                <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                    <div className="lg:col-span-2">
                        <PostCardSkeleton />
                    </div>
                    <div className="lg:col-span-1 border rounded-xl p-4 space-y-4">
                        {[1, 2, 3].map(i => <div key={i} className="h-20 bg-light-muted/10 animate-pulse rounded-xl" />)}
                    </div>
                </div>
            </div>
        );
    }

    if (activeView === 'ledger' && isSuperAdmin) {
        return (
            <div className="space-y-6">
                <div className="flex justify-end mb-4">
                    <div className="flex bg-light-bg/50 dark:bg-dark-bg/50 p-1 rounded-xl border border-light-border dark:border-dark-border">
                        <button
                            onClick={() => setActiveView('personal')}
                            className="px-4 py-1.5 rounded-lg text-xs font-bold transition-all text-light-muted hover:text-light-text"
                        >
                            Personal
                        </button>
                        <button
                            onClick={() => setActiveView('ledger')}
                            className="px-4 py-1.5 rounded-lg text-xs font-bold transition-all bg-white dark:bg-dark-card shadow-sm text-primary-500"
                        >
                            Member Ledger
                        </button>
                    </div>
                </div>
                {user && <MemberLedgerTab admin={user} />}
            </div>
        );
    }

    const totalPaidThisYear = contributions?.reduce((acc: number, curr: any) => acc + curr.totalPaid, 0) || 0;
    const totalSurplus = contributions?.reduce((acc: number, curr: any) => acc + curr.surplus, 0) || 0;

    return (
        <div className="space-y-6">
            {/* View Switcher for Super Admin */}
            {isSuperAdmin && (
                <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center bg-primary-500/5 p-3 md:p-4 rounded-2xl border border-primary-500/10 gap-4">
                    <div className="flex-1 min-w-0">
                        <h3 className="text-xs md:text-sm font-black text-primary-500 uppercase tracking-wider truncate">Contribution Management</h3>
                        <p className="text-[9px] md:text-[10px] text-light-muted font-bold leading-tight">Switch between personal and board records.</p>
                    </div>
                    <div className="grid grid-cols-2 bg-light-bg/50 dark:bg-dark-bg/50 p-1 rounded-xl border border-light-border dark:border-dark-border w-full sm:w-auto sm:flex shrink-0">
                        <button
                            onClick={() => setActiveView('personal')}
                            className={`px-2 md:px-4 py-2 sm:py-1.5 rounded-lg text-[10px] md:text-xs font-bold transition-all whitespace-nowrap ${activeView === 'personal' ? 'bg-white dark:bg-dark-card shadow-sm text-primary-500' : 'text-light-muted hover:text-light-text'}`}
                        >
                            Personal
                        </button>
                        <button
                            onClick={() => setActiveView('ledger')}
                            className={`px-2 md:px-4 py-2 sm:py-1.5 rounded-lg text-[10px] md:text-xs font-bold transition-all whitespace-nowrap ${activeView === 'ledger' ? 'bg-white dark:bg-dark-card shadow-sm text-primary-500' : 'text-light-muted hover:text-light-text'}`}
                        >
                            Member Ledger
                        </button>
                    </div>
                </div>
            )}

            {/* Stats Overview */}
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-3 md:gap-4">
                <Card className="bg-gradient-to-br from-primary-500/5 to-transparent border-primary-500/10">
                    <CardContent className="p-3 md:p-4">
                        <div className="flex items-center gap-2 md:gap-3 mb-1 md:mb-2">
                            <div className="p-1.5 rounded-lg bg-primary-500/10 text-primary-500 shrink-0">
                                <TrendingUp className="w-4 h-4 md:w-5 h-5" />
                            </div>
                            <span className="text-[10px] md:text-xs font-bold text-light-muted uppercase whitespace-nowrap">Total Paid {selectedYear}</span>
                        </div>
                        <p className="text-lg md:text-2xl font-black text-primary-500">{formatCurrency(totalPaidThisYear)}</p>
                    </CardContent>
                </Card>
                <Card className="bg-gradient-to-br from-emerald-500/5 to-transparent border-emerald-500/10">
                    <CardContent className="p-3 md:p-4">
                        <div className="flex items-center gap-2 md:gap-3 mb-1 md:mb-2">
                            <div className="p-1.5 rounded-lg bg-emerald-500/10 text-emerald-500 shrink-0">
                                <Plus className="w-4 h-4 md:w-5 h-5" />
                            </div>
                            <span className="text-[10px] md:text-xs font-bold text-light-muted uppercase whitespace-nowrap">Total Surplus</span>
                        </div>
                        <p className="text-lg md:text-2xl font-black text-emerald-500">{formatCurrency(totalSurplus)}</p>
                    </CardContent>
                </Card>
                <Card className="sm:col-span-2 md:col-span-1">
                    <CardContent className="p-3 md:p-4 flex flex-col justify-center">
                        <Button
                            onClick={() => setShowSubmitModal(true)}
                            className="w-full bg-primary-500 hover:bg-primary-600 h-11 md:h-full py-2 text-sm font-bold shadow-lg shadow-primary-500/10"
                        >
                            <CreditCard className="w-4 h-4 mr-2" />
                            Pay Monthly Due
                        </Button>
                    </CardContent>
                </Card>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                {/* Monthly Ledger */}
                <Card className="lg:col-span-2">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2">
                            <Calendar className="w-5 h-5" />
                            Monthly Contribution Ledger ({selectedYear})
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="px-1 md:px-6">
                        <div className="overflow-x-auto hide-scrollbar">
                            <table className="w-full text-left min-w-[500px] md:min-w-0">
                                <thead>
                                    <tr className="border-b border-light-border dark:border-dark-border">
                                        <th className="pb-3 px-3 font-bold text-[10px] md:text-xs uppercase tracking-wider text-light-muted">Month</th>
                                        <th className="pb-3 px-3 font-bold text-[10px] md:text-xs uppercase tracking-wider text-light-muted">Required</th>
                                        <th className="pb-3 px-3 font-bold text-[10px] md:text-xs uppercase tracking-wider text-light-muted">Paid</th>
                                        <th className="pb-3 px-3 font-bold text-[10px] md:text-xs uppercase tracking-wider text-light-muted">Status</th>
                                        <th className="pb-3 px-3 font-bold text-[10px] md:text-xs uppercase tracking-wider text-light-muted text-right">Surplus</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {contributions?.map((due: any, idx: number) => (
                                        <tr key={idx} className="border-b border-light-border/50 dark:border-dark-border/50 last:border-0 hover:bg-light-bg/30 dark:hover:bg-dark-bg/30 transition-colors">
                                            <td className="py-4 px-3">
                                                <span className="font-bold text-sm md:text-base">{getMonthName(due.month)}</span>
                                            </td>
                                            <td className="py-4 px-3 text-xs md:text-sm font-medium">{due.requiredAmount}</td>
                                            <td className="py-4 px-3">
                                                <div className="text-xs md:text-sm font-bold">{formatCurrency(due.totalPaid)}</div>
                                                {due.outstanding > 0 && (
                                                    <div className="text-[9px] md:text-[10px] text-rose-500 font-bold">Outstanding: {formatCurrency(due.outstanding)}</div>
                                                )}
                                            </td>
                                            <td className="py-4 px-3">
                                                {getStatusBadge(due.status)}
                                            </td>
                                            <td className="py-4 px-3 text-right">
                                                {due.surplus > 0 ? (
                                                    <div className="text-xs md:text-sm font-bold text-emerald-500">+{formatCurrency(due.surplus)}</div>
                                                ) : (
                                                    <span className="text-light-muted">--</span>
                                                )}
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </CardContent>
                </Card>

                {/* Recent Submissions */}
                <Card className="lg:col-span-1">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2">
                            <History className="w-5 h-5" />
                            Recent Submissions
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="p-3 md:p-6">
                        <div className="space-y-4">
                            {submissions && submissions.length > 0 ? (
                                submissions.map((sub: any) => (
                                    <div key={sub._id} className="p-3 rounded-xl border border-light-border dark:border-dark-border bg-light-bg/50 dark:bg-dark-bg/50">
                                        <div className="flex justify-between items-start mb-2">
                                            <div>
                                                <p className="font-bold text-sm">{getMonthName(sub.month)} {sub.year}</p>
                                                <p className="text-lg font-black text-primary-500">{formatCurrency(sub.amount)}</p>
                                            </div>
                                            <Badge
                                                className={
                                                    sub.status === 'approved' ? 'bg-emerald-500' :
                                                        sub.status === 'rejected' ? 'bg-rose-500' :
                                                            'bg-amber-500'
                                                }
                                            >
                                                {sub.status.charAt(0).toUpperCase() + sub.status.slice(1)}
                                            </Badge>
                                        </div>
                                        <div className="flex items-center justify-between text-[10px] text-light-muted">
                                            <span>{new Date(sub.createdAt).toLocaleDateString()}</span>
                                            {sub.proofUrl && (
                                                <button
                                                    onClick={() => setSelectedProof({ url: sub.proofUrl, title: `Proof - ${getMonthName(sub.month)} ${sub.year}` })}
                                                    className="flex items-center gap-1 text-primary-500 hover:text-primary-600 font-bold"
                                                >
                                                    <Eye className="w-3 h-3" /> View Proof
                                                </button>
                                            )}
                                        </div>
                                        {sub.rejectionReason && (
                                            <div className="mt-2 p-2 rounded bg-rose-500/10 text-rose-500 text-[10px]">
                                                <strong>Reason:</strong> {sub.rejectionReason}
                                            </div>
                                        )}
                                    </div>
                                ))
                            ) : (
                                <div className="text-center py-8 text-light-muted text-sm">
                                    No payment submissions yet
                                </div>
                            )}
                        </div>
                    </CardContent>
                </Card>
            </div>

            <SubmitPaymentModal
                isOpen={showSubmitModal}
                onClose={() => setShowSubmitModal(false)}
                user={user}
            />

            <ImageModal
                isOpen={!!selectedProof}
                onClose={() => setSelectedProof(null)}
                imageUrl={selectedProof?.url || ''}
                title={selectedProof?.title}
            />
        </div>
    );
}
