import { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Id } from '../../../convex/_generated/dataModel';
import { useAuth } from '@/hooks/useAuth';
import { Avatar } from '@/components/ui/Avatar';
import { Button } from '@/components/ui/Button';
import { Send, Lock, Globe, AlertCircle, CheckCircle } from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';
import { motion } from 'framer-motion';

interface QuestionSectionProps {
    postId: Id<"posts">;
}

export function QuestionSection({ postId }: QuestionSectionProps) {
    const { user } = useAuth();
    const [questionText, setQuestionText] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    // Queries
    const publicQuestions = useQuery(api.questions.getQuestionsByPost, { postId, publicOnly: true });
    const myQuestions = useQuery(api.questions.getUserQuestionsOnPost, user?._id ? { userId: user._id, postId } : 'skip');

    // Mutations
    const submitQuestion = useMutation(api.questions.submitQuestion);



    // Merge and Deduplicate Questions
    const allQuestions = [...(publicQuestions || [])];
    if (myQuestions) {
        myQuestions.forEach((myQ: any) => {
            if (!allQuestions.some(q => q._id === myQ._id)) {
                allQuestions.push({
                    ...myQ,
                    userName: user?.name || 'Me',
                    userAvatar: user?.avatarUrl,
                    adminName: undefined,
                    adminAvatar: undefined,
                });
            }
        });
    }

    // Sort by date desc
    allQuestions.sort((a, b) => b.createdAt - a.createdAt);

    const handleSubmitQuestion = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!user || !questionText.trim()) return;

        setIsSubmitting(true);
        try {
            await submitQuestion({
                userId: user._id,
                postId,
                questionText: questionText.trim(),
            });
            setQuestionText('');
        } catch (error) {
            console.error("Failed to submit question:", error);
            alert("Failed to submit question. Please try again.");
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="space-y-6">
            {/* Submission Form - Available to all logged-in users who haven't asked a question yet */}
            {user && (!myQuestions || myQuestions.length === 0) && (
                <form onSubmit={handleSubmitQuestion} className="relative">
                    <textarea
                        value={questionText}
                        onChange={(e) => setQuestionText(e.target.value)}
                        placeholder="Ask a question about this teaching..."
                        className="w-full min-h-[100px] p-4 pr-12 rounded-xl bg-gray-50 dark:bg-[#222] border-transparent focus:border-primary-500 focus:ring-0 text-sm resize-none"
                    />
                    <Button
                        type="submit"
                        disabled={!questionText.trim() || isSubmitting}
                        size="sm"
                        className="absolute bottom-3 right-3"
                    >
                        {isSubmitting ? 'Sending...' : <Send className="w-4 h-4" />}
                    </Button>
                </form>
            )}

            {user && myQuestions && myQuestions.length > 0 && (
                <div className="text-center py-4 text-green-600 dark:text-green-400 bg-green-50 dark:bg-green-900/10 rounded-xl border border-green-100 dark:border-green-900/30">
                    <CheckCircle className="w-5 h-5 mx-auto mb-2" />
                    <p className="font-medium">Question Submitted</p>
                    <p className="text-xs opacity-80">You can only ask one question per post.</p>
                </div>
            )}

            {!user && (
                <div className="text-center py-4 text-gray-500 dark:text-gray-400">
                    Please log in to ask a question
                </div>
            )}

            {/* Questions List */}
            <div className="space-y-4">
                <h3 className="font-bold text-lg text-light-text dark:text-dark-text flex items-center gap-2">
                    Questions ({allQuestions.length})
                </h3>

                {allQuestions.length === 0 ? (
                    <div className="text-center py-8 text-gray-500 dark:text-gray-400">
                        No questions yet. Be the first to ask!
                    </div>
                ) : (
                    <div className="space-y-4">
                        {allQuestions.map((q) => (
                            <motion.div
                                key={q._id}
                                initial={{ opacity: 0, y: 10 }}
                                animate={{ opacity: 1, y: 0 }}
                                className="p-4 rounded-xl border border-gray-100 dark:border-gray-800 bg-white dark:bg-[#1A1A1A] shadow-sm"
                            >
                                {/* Question Header */}
                                <div className="flex items-start justify-between gap-3 mb-2">
                                    <div className="flex items-center gap-2">
                                        <Avatar src={q.userAvatar} alt={q.userName || 'User'} size="sm" />
                                        <div>
                                            <p className="text-sm font-semibold text-light-text dark:text-dark-text">
                                                {q.userName}
                                            </p>
                                            <p className="text-[10px] text-light-muted">
                                                {formatDistanceToNow(q.createdAt, { addSuffix: true })}
                                            </p>
                                        </div>
                                    </div>
                                    {q.isPublic ? (
                                        <div title="Public" className="text-green-500"><Globe className="w-3.5 h-3.5" /></div>
                                    ) : (
                                        <div title="Private" className="text-gray-400"><Lock className="w-3.5 h-3.5" /></div>
                                    )}
                                </div>

                                {/* Question Content */}
                                <p className="text-sm text-gray-700 dark:text-gray-300 mb-3 pl-10">
                                    {q.questionText}
                                </p>

                                {/* Answer Section */}
                                {q.isAnswered ? (
                                    <div className="ml-10 mt-3 p-3 rounded-lg bg-primary-50 dark:bg-primary-900/10 border border-primary-100 dark:border-primary-900/30">
                                        <div className="flex items-center gap-2 mb-1">
                                            <Avatar src={q.adminAvatar} alt={q.adminName || 'Admin'} size="xs" className="w-5 h-5" />
                                            <span className="text-xs font-bold text-primary-700 dark:text-primary-400">
                                                {q.adminName || 'Admin Response'}
                                            </span>
                                        </div>
                                        <p className="text-sm text-gray-800 dark:text-gray-200">
                                            {q.answerText}
                                        </p>
                                    </div>
                                ) : (
                                    <div className="ml-10 mt-2 flex items-center gap-1.5 text-xs text-orange-500 bg-orange-50 dark:bg-orange-900/10 px-2 py-1 rounded-md w-fit">
                                        <AlertCircle className="w-3 h-3" />
                                        Waiting for response
                                    </div>
                                )}
                            </motion.div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
}
