import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { PostFeed } from '@/components/posts/PostFeed';
import { MobileAnnouncementCarousel } from '@/components/announcements/MobileAnnouncementCarousel';
import { PostCardSkeleton } from '@/components/ui/Skeletons';
import { FeedSidebarLeft } from '@/components/layout/FeedSidebarLeft';
import { FeedSidebarRight } from '@/components/layout/FeedSidebarRight';
import { useNavigate } from 'react-router-dom';
import { useRef, useState, useEffect } from 'react';
import { PostInput } from '@/components/posts/PostInput';
import { StickyHeader } from '@/components/layout/StickyHeader';
import { WeeklyPollCard } from '@/components/polls/WeeklyPollCard';

export function Feed() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();
    const announcements = useQuery(api.announcements.getAnnouncements, user ? { userId: user._id } : 'skip');

    // Sticky Logic
    const announcementRef = useRef<HTMLDivElement>(null);
    const gameRef = useRef<HTMLDivElement>(null);
    const [showStickyAnnouncement, setShowStickyAnnouncement] = useState(false);
    const [showStickyGame, setShowStickyGame] = useState(false);

    useEffect(() => {
        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach((entry) => {
                    if (entry.target === announcementRef.current) {
                        setShowStickyAnnouncement(!entry.isIntersecting && entry.boundingClientRect.top < 0);
                    }
                    if (entry.target === gameRef.current) {
                        setShowStickyGame(!entry.isIntersecting && entry.boundingClientRect.top < 0);
                    }
                });
            },
            { threshold: 0 }
        );

        if (announcementRef.current) observer.observe(announcementRef.current);
        if (gameRef.current) observer.observe(gameRef.current);

        return () => observer.disconnect();
    }, [announcements]);

    if (isLoading) {
        return (
            <div className="min-h-screen py-4 md:py-8 bg-light-bg dark:bg-dark-bg">
                <div className="max-w-[1440px] mx-auto px-4 sm:px-6 lg:px-8">
                    {/* ... Loading Skeleton ... */}
                    <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">
                        <div className="hidden lg:block lg:col-span-4 xl:col-span-3">
                            <div className="h-96 rounded-3xl bg-light-card/50 dark:bg-dark-card/50 animate-pulse" />
                        </div>
                        <div className="lg:col-span-8 xl:col-span-6 space-y-8">
                            <div className="h-40 rounded-3xl bg-light-card/50 dark:bg-dark-card/50 animate-pulse" />
                            <div className="space-y-4">
                                <PostCardSkeleton />
                                <PostCardSkeleton />
                            </div>
                        </div>
                        <div className="hidden xl:block xl:col-span-3">
                            <div className="h-96 rounded-3xl bg-light-card/50 dark:bg-dark-card/50 animate-pulse" />
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    return (
        <div className="min-h-screen py-4 md:py-8 bg-light-bg/50 dark:bg-dark-bg/50">
            <div className="max-w-[1440px] mx-auto px-4 sm:px-6 lg:px-8">
                <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">

                    {/* Left Sidebar - Navigation & Profile */}
                    <div className="hidden lg:block lg:col-span-4 xl:col-span-3">
                        <FeedSidebarLeft />
                    </div>

                    {/* Middle Content - Main Feed */}
                    <div className="lg:col-span-8 xl:col-span-6">
                        <div className="max-w-2xl mx-auto xl:max-w-none">

                            {/* Post Input Section - Desktop Premium Look */}
                            <PostInput />

                            {/* Mobile/Tablet Announcements */}
                            {announcements && announcements.length > 0 && (
                                <div ref={announcementRef} className="xl:hidden mb-8">
                                    <MobileAnnouncementCarousel announcements={announcements} />
                                </div>
                            )}

                            {/* Weekly Poll */}
                            <div ref={gameRef} className="mb-8">
                                <WeeklyPollCard />
                            </div>

                            {/* Sticky Header (Mobile Only) */}
                            <StickyHeader
                                announcement={showStickyAnnouncement && announcements && announcements.length > 0 ? announcements[0] : null}
                                showGame={showStickyGame}
                            />

                            {/* Main Feed */}
                            <PostFeed
                                onCommentClick={(post: any) => navigate(`/post/${post._id}`)}
                            />
                        </div>
                    </div>

                    {/* Right Sidebar - Community Display & Desktop Announcements */}
                    <div className="hidden xl:block xl:col-span-3">
                        <FeedSidebarRight />
                    </div>

                </div>
            </div>
        </div>
    );
}
