import { motion, AnimatePresence } from 'framer-motion';
import { X, Download, Maximize2 } from 'lucide-react';
import { useEffect } from 'react';

interface ImageModalProps {
    isOpen: boolean;
    onClose: () => void;
    imageUrl: string;
    title?: string;
}

export function ImageModal({ isOpen, onClose, imageUrl, title }: ImageModalProps) {
    // Close on escape key
    useEffect(() => {
        const handleEsc = (e: KeyboardEvent) => {
            if (e.key === 'Escape') onClose();
        };
        window.addEventListener('keydown', handleEsc);
        return () => window.removeEventListener('keydown', handleEsc);
    }, [onClose]);

    // Prevent scroll when open
    useEffect(() => {
        if (isOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'unset';
        }
        return () => {
            document.body.style.overflow = 'unset';
        };
    }, [isOpen]);

    return (
        <AnimatePresence>
            {isOpen && (
                <div className="fixed inset-0 z-[100] flex items-center justify-center p-4 md:p-8">
                    {/* Backdrop */}
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={onClose}
                        className="absolute inset-0 bg-black/90 backdrop-blur-sm"
                    />

                    {/* Content */}
                    <motion.div
                        initial={{ opacity: 0, scale: 0.9, y: 20 }}
                        animate={{ opacity: 1, scale: 1, y: 0 }}
                        exit={{ opacity: 0, scale: 0.9, y: 20 }}
                        className="relative max-w-5xl w-full max-h-full flex flex-col bg-light-bg dark:bg-dark-bg rounded-2xl md:rounded-3xl overflow-hidden shadow-2xl"
                        onClick={(e) => e.stopPropagation()}
                    >
                        {/* Header */}
                        <div className="flex items-center justify-between p-4 md:p-6 border-b border-light-border dark:border-dark-border bg-light-bg/80 dark:bg-dark-bg/80 backdrop-blur-md">
                            <div className="flex items-center gap-3">
                                <div className="p-2 rounded-xl bg-primary-500/10 text-primary-500">
                                    <Maximize2 className="w-5 h-5" />
                                </div>
                                <h3 className="font-display font-bold text-light-text dark:text-dark-text truncate max-w-[200px] md:max-w-md">
                                    {title || "View Proof"}
                                </h3>
                            </div>
                            <div className="flex items-center gap-2">
                                <a
                                    href={imageUrl}
                                    download
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="p-2.5 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border text-light-muted hover:text-primary-500 transition-colors"
                                    title="Download Image"
                                >
                                    <Download className="w-5 h-5" />
                                </a>
                                <button
                                    onClick={onClose}
                                    className="p-2.5 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border text-light-muted hover:text-rose-500 transition-colors"
                                >
                                    <X className="w-5 h-5" />
                                </button>
                            </div>
                        </div>

                        {/* Image Body */}
                        <div className="flex-1 overflow-auto p-2 md:p-4 bg-black/5 dark:bg-white/5 flex items-center justify-center min-h-[300px]">
                            <img
                                src={imageUrl}
                                alt={title || "Proof"}
                                className="max-w-full max-h-[70vh] object-contain rounded-lg shadow-lg"
                            />
                        </div>

                        {/* Footer */}
                        <div className="p-4 md:p-6 bg-light-bg/80 dark:bg-dark-bg/80 backdrop-blur-md border-t border-light-border dark:border-dark-border flex justify-center">
                            <Button
                                onClick={onClose}
                                variant="outline"
                                className="px-8 rounded-xl border-light-border dark:border-dark-border"
                            >
                                Close Preview
                            </Button>
                        </div>
                    </motion.div>
                </div>
            )}
        </AnimatePresence>
    );
}

// Support component
function Button({ children, onClick, variant = 'primary', className = '' }: any) {
    const baseStyles = "px-4 py-2 rounded-lg font-bold transition-all flex items-center justify-center";
    const variants: any = {
        primary: "bg-primary-500 text-white hover:bg-primary-600 shadow-lg shadow-primary-500/20",
        outline: "border border-light-border dark:border-dark-border text-light-text dark:text-dark-text hover:bg-light-card dark:hover:bg-dark-card",
        secondary: "bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text hover:bg-light-border dark:hover:bg-dark-border"
    };

    return (
        <button
            onClick={onClick}
            className={`${baseStyles} ${variants[variant]} ${className}`}
        >
            {children}
        </button>
    );
}
