import { Info } from 'lucide-react';
import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

interface InfoIconProps {
    content: string;
    title?: string;
}

export function InfoIcon({ content, title }: InfoIconProps) {
    const [showTooltip, setShowTooltip] = useState(false);

    return (
        <div className="relative inline-block">
            <button
                onClick={() => setShowTooltip(!showTooltip)}
                className="p-1 rounded-full hover:bg-light-bg dark:hover:bg-dark-bg transition-all focus:outline-none"
                aria-label="Information"
            >
                <Info className="w-4 h-4 md:w-5 md:h-5 text-primary-500" />
            </button>

            <AnimatePresence>
                {showTooltip && (
                    <>
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="fixed inset-0 z-50 bg-black/50 backdrop-blur-sm"
                            onClick={() => setShowTooltip(false)}
                        />
                        <motion.div
                            initial={{ opacity: 0, scale: 0.9, x: "-50%", y: "-40%" }}
                            animate={{ opacity: 1, scale: 1, x: "-50%", y: "-50%" }}
                            exit={{ opacity: 0, scale: 0.9, x: "-50%", y: "-40%" }}
                            className="fixed z-50 left-1/2 top-1/2 w-[90vw] max-w-sm"
                        >
                            <div className="glass rounded-xl p-6 shadow-2xl border border-light-border dark:border-dark-border bg-white/90 dark:bg-black/90">
                                {title && (
                                    <h4 className="font-bold text-lg text-primary-500 mb-2 font-display">
                                        {title}
                                    </h4>
                                )}
                                <p className="text-sm md:text-base text-light-text dark:text-dark-text leading-relaxed">
                                    {content}
                                </p>
                                <button
                                    onClick={() => setShowTooltip(false)}
                                    className="mt-4 w-full py-2 rounded-lg bg-primary-500/10 text-primary-500 hover:bg-primary-500/20 font-medium text-sm transition-colors"
                                >
                                    Got it
                                </button>
                            </div>
                        </motion.div>
                    </>
                )}
            </AnimatePresence>
        </div>
    );
}
