import { useState } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useNavigate } from 'react-router-dom';
import { PostForm } from '@/components/posts/PostForm';
import { motion } from 'framer-motion';
import { ArrowLeft, BookOpen, Sparkles, Lock } from 'lucide-react';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';


export function CreatePost() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();
    const [selectedOption, setSelectedOption] = useState<'Ancient Culture' | 'Wisdom of The Now' | 'question' | 'prayer' | null>(null);

    // Check if user can create posts (global setting OR tagged)
    const permission = useQuery(api.settings.canUserCreatePosts, user?._id ? { userId: user._id } : 'skip');

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    const isAdmin = user.role === 'admin' || user.role === 'super_admin';

    const handleSuccess = () => {
        // Redirect based on what they created? Or generic feed?
        // Admin -> teachings. User -> maybe feed?
        // Let's just go to feed for now as generic fallback, or teachings if admin.
        if (isAdmin) {
            navigate('/teachings');
        } else {
            navigate('/feed'); // Or wherever user posts go
        }
    };

    const getPostType = () => {
        if (selectedOption === 'question') return 'question';
        if (selectedOption === 'prayer') return 'prayer';
        return 'general';
    };

    const getDefaultCategory = () => {
        if (selectedOption === 'Ancient Culture' || selectedOption === 'Wisdom of The Now') {
            return selectedOption;
        }
        return undefined;
    };

    return (
        <div className="min-h-screen py-4 md:py-8 pb-20 md:pb-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-2xl">

                {/* Header */}
                <div className="flex items-center gap-4 mb-6 md:mb-8">
                    <button
                        onClick={() => selectedOption ? setSelectedOption(null) : navigate(-1)}
                        className="p-2 -ml-2 rounded-full hover:bg-light-card dark:hover:bg-dark-card transition-colors"
                    >
                        <ArrowLeft className="w-6 h-6 text-light-text dark:text-dark-text" />
                    </button>
                    <h1 className="text-2xl font-bold font-display text-light-text dark:text-dark-text">
                        {selectedOption ? 'Create Post' : 'Choose Category'}
                    </h1>
                </div>

                {/* Content */}
                <div className="space-y-6">
                    {!selectedOption ? (
                        <>
                            <p className="text-light-muted dark:text-dark-muted">
                                {isAdmin
                                    ? "Select the category where you want to share your thoughts."
                                    : "What would you like to share today?"}
                            </p>

                            <div className="grid gap-4">
                                {/* Wisdom of The Now */}
                                {(() => {
                                    const canCreate = isAdmin || permission?.canCreate || false;
                                    return (
                                        <motion.button
                                            whileHover={canCreate ? { scale: 1.02 } : {}}
                                            whileTap={canCreate ? { scale: 0.98 } : {}}
                                            onClick={() => canCreate && setSelectedOption('Wisdom of The Now')}
                                            disabled={!canCreate}
                                            className={`w-full p-6 rounded-2xl border ${canCreate
                                                ? 'border-secondary-500/20 bg-gradient-to-br from-secondary-500/5 to-secondary-500/10 hover:shadow-lg hover:shadow-secondary-500/10 cursor-pointer'
                                                : 'border-gray-300/20 bg-gray-50 dark:bg-gray-900/20 opacity-60 cursor-not-allowed'
                                                } transition-all text-left flex items-start gap-4`}
                                        >
                                            <div className={`p-3 rounded-xl ${canCreate ? 'bg-secondary-500/10 text-secondary-500' : 'bg-gray-300/10 text-gray-400'}`}>
                                                {canCreate ? <Sparkles className="w-8 h-8" /> : <Lock className="w-8 h-8" />}
                                            </div>
                                            <div className="flex-1">
                                                <h3 className={`text-xl font-bold font-display mb-1 ${canCreate ? 'text-secondary-600 dark:text-secondary-400' : 'text-gray-500 dark:text-gray-400'}`}>
                                                    Wisdom of The Now
                                                </h3>
                                                <p className="text-sm text-light-muted dark:text-dark-muted">
                                                    {canCreate
                                                        ? 'Share spiritual insights and identity from the New Testament'
                                                        : permission?.reason || 'No permission to create posts'}
                                                </p>
                                            </div>
                                        </motion.button>
                                    );
                                })()}

                                {/* Ancient Culture */}
                                {(() => {
                                    const canCreate = isAdmin || permission?.canCreate || false;
                                    return (
                                        <motion.button
                                            whileHover={canCreate ? { scale: 1.02 } : {}}
                                            whileTap={canCreate ? { scale: 0.98 } : {}}
                                            onClick={() => canCreate && setSelectedOption('Ancient Culture')}
                                            disabled={!canCreate}
                                            className={`w-full p-6 rounded-2xl border ${canCreate
                                                ? 'border-primary-500/20 bg-gradient-to-br from-primary-500/5 to-primary-500/10 hover:shadow-lg hover:shadow-primary-500/10 cursor-pointer'
                                                : 'border-gray-300/20 bg-gray-50 dark:bg-gray-900/20 opacity-60 cursor-not-allowed'
                                                } transition-all text-left flex items-start gap-4`}
                                        >
                                            <div className={`p-3 rounded-xl ${canCreate ? 'bg-primary-500/10 text-primary-500' : 'bg-gray-300/10 text-gray-400'}`}>
                                                {canCreate ? <BookOpen className="w-8 h-8" /> : <Lock className="w-8 h-8" />}
                                            </div>
                                            <div className="flex-1">
                                                <h3 className={`text-xl font-bold font-display mb-1 ${canCreate ? 'text-primary-600 dark:text-primary-400' : 'text-gray-500 dark:text-gray-400'}`}>
                                                    Ancient Culture
                                                </h3>
                                                <p className="text-sm text-light-muted dark:text-dark-muted">
                                                    {canCreate
                                                        ? 'Discuss prophetic wisdom from the Old Testament'
                                                        : permission?.reason || 'No permission to create posts'}
                                                </p>
                                            </div>
                                        </motion.button>
                                    );
                                })()}

                                {/* Ask a Question */}
                                {(() => {
                                    const canCreate = isAdmin || permission?.canCreate || false;
                                    return (
                                        <motion.button
                                            whileHover={canCreate ? { scale: 1.02 } : {}}
                                            whileTap={canCreate ? { scale: 0.98 } : {}}
                                            onClick={() => canCreate && setSelectedOption('question')}
                                            disabled={!canCreate}
                                            className={`w-full p-6 rounded-2xl border ${canCreate
                                                ? 'border-blue-500/20 bg-gradient-to-br from-blue-500/5 to-blue-500/10 hover:shadow-lg hover:shadow-blue-500/10 cursor-pointer'
                                                : 'border-gray-300/20 bg-gray-50 dark:bg-gray-900/20 opacity-60 cursor-not-allowed'
                                                } transition-all text-left flex items-start gap-4`}
                                        >
                                            <div className={`p-3 rounded-xl ${canCreate ? 'bg-blue-500/10 text-blue-500' : 'bg-gray-300/10 text-gray-400'}`}>
                                                {canCreate ? (
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><circle cx="12" cy="12" r="10" /><path d="M9.09 9a3 3 0 0 1 5.83 1c0 2-3 3-3 3" /><path d="M12 17h.01" /></svg>
                                                ) : (
                                                    <Lock className="w-8 h-8" />
                                                )}
                                            </div>
                                            <div className="flex-1">
                                                <h3 className={`text-xl font-bold font-display mb-1 ${canCreate ? 'text-blue-600 dark:text-blue-400' : 'text-gray-500 dark:text-gray-400'}`}>
                                                    Ask a Question
                                                </h3>
                                                <p className="text-sm text-light-muted dark:text-dark-muted">
                                                    {canCreate
                                                        ? 'Submit a question about a teaching or topic'
                                                        : permission?.reason || 'No permission to create posts'}
                                                </p>
                                            </div>
                                        </motion.button>
                                    );
                                })()}

                                {/* Prayer Point */}
                                {(() => {
                                    const canCreate = isAdmin || permission?.canCreate || false;
                                    return (
                                        <motion.button
                                            whileHover={canCreate ? { scale: 1.02 } : {}}
                                            whileTap={canCreate ? { scale: 0.98 } : {}}
                                            onClick={() => canCreate && setSelectedOption('prayer')}
                                            disabled={!canCreate}
                                            className={`w-full p-6 rounded-2xl border ${canCreate
                                                ? 'border-purple-500/20 bg-gradient-to-br from-purple-500/5 to-purple-500/10 hover:shadow-lg hover:shadow-purple-500/10 cursor-pointer'
                                                : 'border-gray-300/20 bg-gray-50 dark:bg-gray-900/20 opacity-60 cursor-not-allowed'
                                                } transition-all text-left flex items-start gap-4`}
                                        >
                                            <div className={`p-3 rounded-xl ${canCreate ? 'bg-purple-500/10 text-purple-500' : 'bg-gray-300/10 text-gray-400'}`}>
                                                {canCreate ? (
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M19 14c1.49-1.28 3.6-1.28 5.09 0 1.49 1.28 1.49 3.36 0 4.63-1.49 1.28-3.6 1.28-5.09 0-1.49-1.28-1.49-3.36 0-4.63z" /><path d="M24 6h-6a2 2 0 0 0-2 2v10.17a3 3 0 0 1-.5 1.7l-2 2.83" /><path d="M13 14c1.49-1.28 3.6-1.28 5.09 0 1.49 1.28 1.49 3.36 0 4.63-1.49 1.28-3.6 1.28-5.09 0-1.49-1.28-1.49-3.36 0-4.63z" /><path d="M7 6h6a2 2 0 0 1 2 2v10.17a3 3 0 0 0 .5 1.7l2 2.83" /></svg>
                                                ) : (
                                                    <Lock className="w-8 h-8" />
                                                )}
                                            </div>
                                            <div className="flex-1">
                                                <h3 className={`text-xl font-bold font-display mb-1 ${canCreate ? 'text-purple-600 dark:text-purple-400' : 'text-gray-500 dark:text-gray-400'}`}>
                                                    Prayer Point
                                                </h3>
                                                <p className="text-sm text-light-muted dark:text-dark-muted">
                                                    {canCreate
                                                        ? 'Share a prayer request with the community'
                                                        : permission?.reason || 'No permission to create posts'}
                                                </p>
                                            </div>
                                        </motion.button>
                                    );
                                })()}
                            </div>

                            {/* Inactive Note */}
                            {(!isAdmin && permission?.canCreate === false) && (
                                <motion.div
                                    initial={{ opacity: 0, y: 10 }}
                                    animate={{ opacity: 1, y: 0 }}
                                    className="mt-6 p-4 rounded-xl bg-orange-500/5 border border-orange-500/20 flex items-start gap-3"
                                >
                                    <div className="p-2 bg-orange-500/10 rounded-lg">
                                        <Lock className="w-4 h-4 text-orange-500" />
                                    </div>
                                    <div>
                                        <h4 className="text-sm font-bold text-orange-600 dark:text-orange-400 font-display">
                                            Post Creation Paused
                                        </h4>
                                        <p className="text-xs text-light-muted dark:text-dark-muted mt-0.5 leading-relaxed">
                                            This feature is currently inactive. It will be available once an administrator enables post creation permissions.
                                        </p>
                                    </div>
                                </motion.div>
                            )}
                        </>
                    ) : (
                        <motion.div
                            initial={{ opacity: 0, x: 20 }}
                            animate={{ opacity: 1, x: 0 }}
                            className="bg-light-bg dark:bg-dark-bg"
                        >
                            <PostForm
                                defaultCategory={getDefaultCategory()}
                                postType={getPostType()}
                                onSuccess={handleSuccess}
                                variant="default"
                            />
                        </motion.div>
                    )}
                </div>
            </div>
        </div>
    );
}
