import { useState } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Moon, Sun, LogOut, LayoutDashboard, Search, Bell } from 'lucide-react';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { useTheme } from '@/context/ThemeContext';
import { Button } from '@/components/ui/Button';
import { MenuModal } from './MenuModal';



function NotificationBell() {
    const { user } = useAuth();
    const navigate = useNavigate();

    // Don't render or query if no user
    if (!user?._id) {
        return null;
    }

    const unreadCount = useQuery(api.notifications.getUnreadCount, { userId: user._id }) || 0;

    return (
        <motion.button
            whileTap={{ scale: 0.9 }}
            onClick={() => navigate('/notifications')}
            className="p-2 md:p-3 relative text-light-text dark:text-dark-text hover:text-primary-500 transition-colors"
            title="Notifications"
        >
            <Bell className="w-6 h-6 md:w-6 md:h-6" />
            {(unreadCount ?? 0) > 0 && (
                <span className="absolute top-0.5 right-0.5 md:top-2 md:right-2 bg-green-500 text-white text-[10px] font-bold h-4 min-w-[16px] px-1 flex items-center justify-center rounded-full border-2 border-light-bg dark:border-dark-bg">
                    {unreadCount > 9 ? '9+' : unreadCount}
                </span>
            )}
        </motion.button>
    );
}

export function Header() {
    const { user, logout } = useAuth();
    const { theme, toggleTheme } = useTheme();
    const navigate = useNavigate();
    const location = useLocation();
    const [menuModalOpen, setMenuModalOpen] = useState(false);
    const badgeCounts = useQuery(api.stats.getBadgeCounts, user?._id ? { userId: user._id } : 'skip');

    // Main pages that show full header
    const isMainPage = location.pathname === '/' || location.pathname === '/feed' || location.pathname === '/login' || location.pathname === '/register';

    const handleLogout = () => {
        logout();
        navigate('/');
    };

    return (
        <>
            <header className={`fixed ${isMainPage ? 'top-2 md:top-0' : 'top-0'} left-0 right-0 z-40 ${isMainPage ? 'px-3 md:px-0' : 'px-0'} transition-all duration-300`}>
                <div className={`mx-auto ${isMainPage ? 'max-w-7xl md:max-w-full relative' : 'w-full'}`}>
                    <div className={`${isMainPage ? 'glass rounded-2xl md:rounded-b-3xl md:rounded-t-none border border-light-border/50 dark:border-dark-border/50 overflow-hidden' : 'bg-light-bg/95 dark:bg-dark-bg/95 backdrop-blur-sm border-b border-light-border dark:border-dark-border md:glass md:rounded-b-3xl md:border md:border-light-border/50 md:dark:border-dark-border/50'} md:overflow-hidden`}>
                        <div className={`${isMainPage ? 'px-4 md:px-12' : 'px-4 md:px-12'} h-14 md:h-20 flex items-center justify-between`}>

                            {/* Mobile: Hamburger + Notification */}
                            <div className="md:hidden flex items-center justify-between w-full">
                                <button
                                    onClick={() => setMenuModalOpen(true)}
                                    className="p-2 -ml-2 text-light-text dark:text-dark-text hover:text-primary-500 transition-colors relative"
                                >
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                        <line x1="3" y1="12" x2="21" y2="12"></line>
                                        <line x1="3" y1="6" x2="21" y2="6"></line>
                                        <line x1="3" y1="18" x2="21" y2="18"></line>
                                    </svg>
                                    {badgeCounts && (badgeCounts.messages > 0 || badgeCounts.banking > 0 || badgeCounts.leaderboard > 0) && (
                                        <span className="absolute top-2 right-2 w-2 h-2 rounded-full bg-green-500 shadow-[0_0_8px_rgba(34,197,94,0.5)] border border-light-bg dark:border-dark-bg" />
                                    )}
                                </button>

                                {user ? (
                                    <div className="flex items-center gap-1">
                                        <button
                                            onClick={() => navigate('/search')}
                                            className="p-2 text-light-text dark:text-dark-text hover:text-primary-500 transition-colors"
                                        >
                                            <Search className="w-6 h-6" />
                                        </button>

                                        <NotificationBell />

                                    </div>
                                ) : (
                                    isMainPage && (
                                        <Link
                                            to="/"
                                            className="flex items-center"
                                        >
                                            <span className="text-xl font-black text-gradient font-display tracking-tight">Baptizo</span>
                                        </Link>
                                    )
                                )}
                            </div>

                            {/* Desktop Navigation - Unchanged for Desktop */}
                            <div className="hidden md:flex items-center space-x-2 w-full">
                                {/* Logo */}
                                <Link
                                    to="/"
                                    state={{ manual: true }}
                                    className="flex items-center space-x-2 mr-auto"
                                >
                                    <motion.div
                                        whileHover={{ scale: 1.05 }}
                                        className="flex items-center gap-2"
                                    >
                                        <img
                                            src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/premium-logo.png"
                                            alt="Baptizo"
                                            className="h-8 md:h-12 w-auto"
                                        />
                                        <span className="text-xl md:text-3xl font-black text-gradient font-display tracking-tight">Baptizo</span>
                                    </motion.div>
                                </Link>

                                <nav className="hidden md:flex items-center space-x-8">
                                    {user ? (
                                        <>
                                            <Link
                                                to="/feed"
                                                className="text-light-text dark:text-dark-text hover:text-primary-500 transition-all font-semibold text-lg"
                                            >
                                                Feed
                                            </Link>

                                            <Link
                                                to="/profile"
                                                className="text-light-text dark:text-dark-text hover:text-primary-500 transition-all font-semibold text-lg"
                                            >
                                                Profile
                                            </Link>
                                            {user.role === 'admin' || user.role === 'super_admin' ? (
                                                <Link
                                                    to="/admin"
                                                    className="px-4 py-2 rounded-xl bg-primary-500/10 text-primary-500 hover:bg-primary-500 hover:text-white transition-all font-bold flex items-center gap-2"
                                                >
                                                    <LayoutDashboard className="w-5 h-5" />
                                                    Admin
                                                </Link>
                                            ) : null}
                                            {user.role === 'board_member' && (
                                                <Link
                                                    to="/board"
                                                    className="px-4 py-2 rounded-xl bg-secondary-500/10 text-secondary-500 hover:bg-secondary-500 hover:text-white transition-all font-bold flex items-center gap-2"
                                                >
                                                    <LayoutDashboard className="w-5 h-5" />
                                                    Board
                                                </Link>
                                            )}
                                        </>
                                    ) : (
                                        <div className="flex items-center gap-2">
                                            <Link
                                                to="/login"
                                                className="text-light-text dark:text-dark-text hover:text-primary-500 transition-all font-semibold text-lg"
                                            >
                                                Login
                                            </Link>
                                            <Link to="/register">
                                                <Button size="lg" className="font-bold px-8">Get Started</Button>
                                            </Link>
                                        </div>
                                    )}
                                </nav>

                                {/* Right Section Desktop */}
                                <div className="hidden md:flex items-center space-x-2 md:space-x-4 ml-auto">
                                    {user && (
                                        <>
                                            <motion.button
                                                whileTap={{ scale: 0.9 }}
                                                onClick={() => navigate('/search')}
                                                className="p-2 md:p-3 rounded-xl hover:bg-light-card/50 dark:hover:bg-dark-card/50 transition-all border border-transparent hover:border-light-border dark:hover:border-dark-border"
                                                title="Search Members"
                                            >
                                                <Search className="w-5 h-5 md:w-6 md:h-6 text-primary-500" />
                                            </motion.button>
                                            <NotificationBell />
                                        </>
                                    )}

                                    <motion.button
                                        whileTap={{ scale: 0.9 }}
                                        onClick={toggleTheme}
                                        className="p-2 md:p-3 rounded-xl hover:bg-light-card/50 dark:hover:bg-dark-card/50 transition-all border border-transparent hover:border-light-border dark:hover:border-dark-border"
                                    >
                                        {theme === 'dark' ? (
                                            <Sun className="w-5 h-5 md:w-6 md:h-6 text-yellow-500" />
                                        ) : (
                                            <Moon className="w-5 h-5 md:w-6 md:h-6 text-slate-700" />
                                        )}
                                    </motion.button>

                                    {user && (
                                        <div className="hidden md:flex items-center space-x-3">
                                            <div className="flex items-center space-x-3 p-1.5 rounded-xl bg-light-card/30 dark:bg-dark-card/30 border border-light-border/20 dark:border-dark-border/20 shadow-inner">
                                                <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-primary-500 to-secondary-500 flex items-center justify-center text-white font-bold overflow-hidden">
                                                    {user.avatarUrl ? (
                                                        <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover" />
                                                    ) : (
                                                        user.name[0]
                                                    )}
                                                </div>
                                                <div className="flex flex-col">
                                                    <span className="text-sm font-bold text-light-text dark:text-dark-text pr-2 leading-none">
                                                        {user.name}
                                                    </span>
                                                    {(user.role === 'board_member' || user.role === 'admin' || user.role === 'super_admin') && (
                                                        <span className="text-[9px] font-bold text-secondary-500 tracking-wide mt-0.5">
                                                            {user.role === 'board_member' ? 'BOARD MEMBER' : user.role.replace('_', ' ').toUpperCase()}
                                                        </span>
                                                    )}
                                                </div>
                                            </div>
                                            <motion.button
                                                whileHover={{ scale: 1.05 }}
                                                whileTap={{ scale: 0.95 }}
                                                onClick={handleLogout}
                                                className="p-3 rounded-xl bg-red-500/10 text-red-500 hover:bg-red-500 hover:text-white transition-all"
                                            >
                                                <LogOut className="w-5 h-5" />
                                            </motion.button>
                                        </div>
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </header>

            <MenuModal
                isOpen={menuModalOpen}
                onClose={() => setMenuModalOpen(false)}
            />
        </>
    );
}
