import { Modal } from '../ui/Modal';
import { Button } from '../ui/Button';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useToast } from '@/context/ToastContext';
import { StopCircle, AlertTriangle, Users } from 'lucide-react';
import { useState } from 'react';

interface EndMeetingModalProps {
    isOpen: boolean;
    onClose: () => void;
    meeting: any;
    userId: string;
}

export function EndMeetingModal({ isOpen, onClose, meeting, userId }: EndMeetingModalProps) {
    const [isEnding, setIsEnding] = useState(false);
    const endMeeting = useMutation(api.governance.endMeeting);
    const { showToast } = useToast();

    const handleEndMeeting = async () => {
        setIsEnding(true);
        try {
            await endMeeting({
                adminId: userId as any,
                meetingId: meeting._id,
            });
            onClose();
            showToast('success', 'Meeting ended successfully! Attendance has been processed.');
        } catch (error: any) {
            console.error('Failed to end meeting:', error);
            showToast('error', error.message || 'Failed to end meeting. Please try again.');
        } finally {
            setIsEnding(false);
        }
    };

    if (!meeting) return null;

    return (
        <Modal isOpen={isOpen} onClose={onClose} title="End Meeting">
            <div className="space-y-6">
                <div className="flex flex-col items-center text-center p-4">
                    <div className="w-20 h-20 rounded-3xl bg-gradient-to-br from-rose-500/10 to-rose-500/5 border border-rose-500/20 flex items-center justify-center mb-4 shadow-xl">
                        <StopCircle className="w-10 h-10 text-rose-500" />
                    </div>
                    <h3 className="text-xl font-bold text-light-text dark:text-dark-text mb-2">End This Meeting?</h3>
                    <p className="text-sm text-light-muted dark:text-dark-muted max-w-xs">
                        Are you sure you want to end "{meeting.title}"?
                    </p>
                </div>

                <div className="flex items-start gap-4 p-4 rounded-2xl bg-amber-500/5 border border-amber-500/20">
                    <div className="w-10 h-10 rounded-xl bg-amber-500/10 flex items-center justify-center shrink-0">
                        <AlertTriangle className="w-6 h-6 text-amber-500" />
                    </div>
                    <div>
                        <p className="text-sm font-bold text-amber-700 dark:text-amber-500 mb-1">What Happens Next</p>
                        <ul className="text-xs text-amber-700/70 dark:text-amber-500/70 space-y-1">
                            <li> Meeting will be marked as ended</li>
                            <li> Attendance will be automatically processed</li>
                            <li> Members who didn't clock in will be marked absent</li>
                            <li> Notifications will be sent to all board members</li>
                        </ul>
                    </div>
                </div>

                <div className="flex items-center gap-2 p-3 rounded-xl bg-light-bg dark:bg-dark-bg">
                    <Users className="w-4 h-4 text-light-muted" />
                    <p className="text-xs text-light-muted">
                        Started: {new Date(meeting.date).toLocaleDateString()} at {meeting.startTime}
                    </p>
                </div>

                <div className="grid grid-cols-2 gap-4">
                    <Button
                        variant="ghost"
                        className="w-full rounded-2xl h-12"
                        onClick={onClose}
                        disabled={isEnding}
                    >
                        Cancel
                    </Button>
                    <Button
                        variant="danger"
                        className="w-full rounded-2xl h-12 shadow-lg shadow-rose-500/20"
                        onClick={handleEndMeeting}
                        disabled={isEnding}
                    >
                        {isEnding ? 'Ending...' : 'Yes, End Meeting'}
                    </Button>
                </div>
            </div>
        </Modal>
    );
}
