import { useState } from 'react';
import { motion } from 'framer-motion';
import { useMutation, useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useNavigate } from 'react-router-dom';
import { Card, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
// import { BackButton } from '@/components/ui/BackButton';
import { Input } from '@/components/ui/Input';
import { Textarea } from '../../components/ui/Textarea';
import { PremiumSelect } from '@/components/ui/PremiumSelect';

export function CreateTrainingModule() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();
    const createModule = useMutation(api.training.createModule);
    const trainingStats = useQuery(
        api.training.getTrainingStats,
        user ? { userId: user._id } : 'skip'
    );

    const [moduleTitle, setModuleTitle] = useState('');
    const [moduleDescription, setModuleDescription] = useState('');
    const [moduleCategory, setModuleCategory] = useState<string>('Leadership');
    const [moduleContent, setModuleContent] = useState('');
    const [moduleVideoUrl, setModuleVideoUrl] = useState('');
    const [moduleDocumentUrl, setModuleDocumentUrl] = useState('');
    const [moduleDuration, setModuleDuration] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    if (isLoading) return <div className="flex justify-center p-8"><div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-500" /></div>;
    if (!user) return <Navigate to="/login" replace />;
    if (user.role !== 'admin' && user.role !== 'super_admin') return <Navigate to="/board" replace />;

    const handleCreateModule = async () => {
        if (!moduleTitle || !moduleDescription) return;
        setIsSubmitting(true);

        try {
            const moduleCount = trainingStats?.totalModules || 0;
            await createModule({
                adminId: user._id,
                title: moduleTitle,
                description: moduleDescription,
                category: moduleCategory as any,
                content: moduleContent,
                videoUrl: moduleVideoUrl || undefined,
                documentUrl: moduleDocumentUrl || undefined,
                duration: moduleDuration ? parseInt(moduleDuration) : undefined,
                order: moduleCount + 1,
            });
            navigate('/board/admin');
        } catch (error) {
            console.error('Failed to create module:', error);
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="max-w-3xl mx-auto space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-10 text-center"
            >
                <div className="flex items-center justify-center gap-2 mb-2">
                    <div className="w-8 h-1 bg-secondary-500 rounded-full" />
                    <span className="text-[10px] font-black uppercase tracking-[0.3em] text-secondary-500">Wisdom Forge</span>
                </div>
                <h1 className="text-4xl md:text-5xl font-black text-gradient font-display mb-2 tracking-tight">Author Wisdom</h1>
                <p className="text-light-muted dark:text-dark-muted font-medium">Create a transformative learning experience for the board.</p>
            </motion.div>

            <Card glass className="overflow-hidden border-secondary-500/20">
                <div className="h-2 bg-gradient-to-r from-secondary-500 via-primary-500 to-secondary-500" />
                <CardContent className="p-8 md:p-12 space-y-10">
                    <div className="space-y-8">
                        <Input
                            label="Module Title"
                            value={moduleTitle}
                            onChange={(e) => setModuleTitle(e.target.value)}
                            placeholder="Enter a compelling title..."
                            className="text-lg font-bold h-14 rounded-2xl border-2 focus:border-secondary-500 transition-all"
                        />

                        <Textarea
                            label="Executive Summary"
                            value={moduleDescription}
                            onChange={(e) => setModuleDescription(e.target.value)}
                            rows={2}
                            placeholder="What is the core essence of this wisdom?"
                            className="rounded-[24px] border-2 focus:border-secondary-500 transition-all p-6 text-base italic"
                        />
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                        <PremiumSelect
                            label="Sector Category"
                            value={moduleCategory}
                            onChange={setModuleCategory}
                            options={[
                                { value: "Leadership", label: "Leadership" },
                                { value: "Ministry Skills", label: "Ministry Skills" },
                                { value: "Spiritual Growth", label: "Spiritual Growth" },
                                { value: "Technical Skills", label: "Technical Skills" },
                                { value: "Team Building", label: "Team Building" }
                            ]}
                        />

                        <Input
                            label="Estimated Duration (MIN)"
                            type="number"
                            value={moduleDuration}
                            onChange={(e) => setModuleDuration(e.target.value)}
                            placeholder="e.g., 30"
                            className="h-14 rounded-2xl border-2 focus:border-secondary-500"
                        />
                    </div>

                    <div className="space-y-4">
                        <label className="text-[10px] font-black uppercase tracking-widest text-light-muted/60 ml-1">Deep Content (Markdown/HTML Compatible)</label>
                        <Textarea
                            value={moduleContent}
                            onChange={(e) => setModuleContent(e.target.value)}
                            rows={10}
                            placeholder="<h2>Core Principles</h2><p>Synthesize the wisdom here...</p>"
                            className="rounded-[32px] border-2 focus:border-secondary-500 transition-all p-8 text-base font-mono bg-light-muted/5"
                        />
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                        <Input
                            label="Visual Transmission (Video URL)"
                            value={moduleVideoUrl}
                            onChange={(e) => setModuleVideoUrl(e.target.value)}
                            placeholder="Embed URL (YT/Vimeo)..."
                            className="h-14 rounded-2xl border-2 focus:border-secondary-500"
                        />

                        <Input
                            label="Resource Reference (Document URL)"
                            value={moduleDocumentUrl}
                            onChange={(e) => setModuleDocumentUrl(e.target.value)}
                            placeholder="Cloud Link (Drive/Dropbox)..."
                            className="h-14 rounded-2xl border-2 focus:border-secondary-500"
                        />
                    </div>

                    <div className="flex flex-col-reverse sm:flex-row justify-end gap-4 pt-8 border-t border-light-border dark:border-dark-border">
                        <Button
                            variant="ghost"
                            onClick={() => navigate(-1)}
                            className="h-14 px-8 rounded-2xl font-black text-[10px] uppercase tracking-widest border-2 hover:bg-light-muted/10 transition-all"
                        >
                            Discard Draft
                        </Button>
                        <Button
                            variant="primary"
                            onClick={handleCreateModule}
                            disabled={isSubmitting}
                            className="h-14 px-12 rounded-2xl font-black text-[10px] uppercase tracking-[0.2em] bg-secondary-500 hover:bg-secondary-600 shadow-2xl shadow-secondary-500/40 border-none"
                        >
                            {isSubmitting ? (
                                <div className="flex items-center gap-2">
                                    <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                                    <span>Forging Module...</span>
                                </div>
                            ) : 'Authorize Wisdom'}
                        </Button>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}
