import { useState, useRef, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Check, ChevronDown } from 'lucide-react';
import { cn } from '@/lib/cn';

export interface Option {
    value: string;
    label: string;
    icon?: React.ReactNode;
    disabled?: boolean;
}

interface PremiumSelectProps {
    label?: string;
    options: Option[];
    value: string;
    onChange: (value: string) => void;
    placeholder?: string;
    className?: string;
    error?: string;
}

export function PremiumSelect({ label, options, value, onChange, placeholder = "Select an option", className, error }: PremiumSelectProps) {
    const [isOpen, setIsOpen] = useState(false);
    const containerRef = useRef<HTMLDivElement>(null);
    const selectedOption = options.find(opt => opt.value === value);

    useEffect(() => {
        function handleClickOutside(event: MouseEvent) {
            if (containerRef.current && !containerRef.current.contains(event.target as Node)) {
                setIsOpen(false);
            }
        }
        document.addEventListener("mousedown", handleClickOutside);
        return () => document.removeEventListener("mousedown", handleClickOutside);
    }, []);

    const handleSelect = (optionValue: string) => {
        onChange(optionValue);
        setIsOpen(false);
    };

    return (
        <div className={cn("w-full relative", className)} ref={containerRef}>
            {label && (
                <label className="block text-sm font-medium mb-2 text-light-text dark:text-dark-text">
                    {label}
                </label>
            )}

            <button
                type="button"
                onClick={() => setIsOpen(!isOpen)}
                className={cn(
                    "w-full flex items-center justify-between px-4 py-3 rounded-lg border transition-all duration-200",
                    "bg-light-bg dark:bg-dark-bg text-light-text dark:text-dark-text",
                    isOpen ? "border-primary-500 ring-2 ring-primary-500/20" : "border-light-border dark:border-dark-border hover:border-primary-500",
                    error && "border-red-500"
                )}
            >
                <span className={cn("flex items-center gap-2", !selectedOption && "text-light-muted dark:text-dark-muted")}>
                    {selectedOption?.icon}
                    {selectedOption?.label || placeholder}
                </span>
                <ChevronDown className={cn("w-4 h-4 transition-transform", isOpen && "rotate-180")} />
            </button>

            <AnimatePresence>
                {isOpen && (
                    <motion.div
                        initial={{ opacity: 0, y: -10 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: -10 }}
                        transition={{ duration: 0.15 }}
                        className="absolute z-50 w-full mt-2 bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border rounded-lg shadow-xl overflow-hidden max-h-60 overflow-y-auto thin-scrollbar"
                    >
                        {options.map((option) => (
                            option.disabled ? (
                                <div
                                    key={option.value}
                                    className="px-4 py-2 text-xs font-bold text-light-muted dark:text-dark-muted uppercase tracking-wide pointer-events-none"
                                >
                                    {option.label}
                                </div>
                            ) : (
                                <button
                                    key={option.value}
                                    onClick={() => handleSelect(option.value)}
                                    className={cn(
                                        "w-full flex items-center justify-between px-4 py-3 text-sm transition-colors",
                                        "hover:bg-primary-50 dark:hover:bg-primary-900/10",
                                        value === option.value
                                            ? "text-primary-600 dark:text-primary-400 font-medium bg-primary-50/50 dark:bg-primary-900/5"
                                            : "text-light-text dark:text-dark-text"
                                    )}
                                >
                                    <span className="flex items-center gap-2">
                                        {option.icon}
                                        {option.label}
                                    </span>
                                    {value === option.value && <Check className="w-4 h-4" />}
                                </button>
                            )
                        ))}
                    </motion.div>
                )}
            </AnimatePresence>

            {error && <p className="mt-1 text-sm text-red-500">{error}</p>}
        </div>
    );
}
