import { useState, useRef, useEffect } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Avatar } from '@/components/ui/Avatar';
import { Button } from '@/components/ui/Button';
import {
    Send, ArrowLeft, Search, MessageSquare, X, MessageCircle, Sparkles,
    Reply, Edit2, Trash2, ChevronDown, Image as ImageIcon, Loader2,
    Users, Lock, Unlock, Shield, Crown, Eye
} from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';
import { useNavigate, Link } from 'react-router-dom';
import { ChatListSkeleton, MessagesSkeleton } from '@/components/ui/Skeletons';
import { motion, AnimatePresence } from 'framer-motion';
import { uploadToImageKit } from '@/lib/imagekit';
import { Id } from '../../convex/_generated/dataModel';

interface ChatPageProps {
    isEmbedded?: boolean;
}

type TabType = 'direct' | 'group';

export function ChatPage({ isEmbedded = false }: ChatPageProps) {
    const { user } = useAuth();
    const navigate = useNavigate();

    // Tab state
    const [activeTab, setActiveTab] = useState<TabType>('direct');

    // Direct chat state
    const [activeConversationId, setActiveConversationId] = useState<string | null>(null);
    const [messageText, setMessageText] = useState('');
    const [searchTerm, setSearchTerm] = useState('');
    const [userSearchTerm, setUserSearchTerm] = useState('');
    const [showUserSearch, setShowUserSearch] = useState(false);
    const [replyingTo, setReplyingTo] = useState<any>(null);
    const [editingMessage, setEditingMessage] = useState<any>(null);
    const [hoveredMessageId, setHoveredMessageId] = useState<any>(null);
    const [contextMenuMsg, setContextMenuMsg] = useState<any>(null);

    // Group chat state
    const [groupMsgText, setGroupMsgText] = useState('');
    const [groupReplyingTo, setGroupReplyingTo] = useState<any>(null);
    const [groupEditingMsg, setGroupEditingMsg] = useState<any>(null);
    const [groupHoveredId, setGroupHoveredId] = useState<any>(null);
    const [groupContextMsg, setGroupContextMsg] = useState<any>(null);

    // Wallpaper State
    const [isUploadingWallpaper, setIsUploadingWallpaper] = useState(false);

    //  Queries 
    const conversations = useQuery(api.chat.getConversations, user ? { userId: user._id } : 'skip');
    const messages = useQuery(
        api.chat.getMessages,
        activeConversationId && user
            ? { conversationId: activeConversationId as Id<'conversations'>, userId: user._id }
            : 'skip'
    );
    // Use getChatContacts (filtered: admins + followed users for members)
    const chatContacts = useQuery(api.users.getChatContacts, user ? { userId: user._id } : 'skip');
    const globalWallpaper = useQuery(api.settings.getGlobalChatWallpaper);

    // Group
    const communityGroup = useQuery(api.chat.getCommunityGroup);
    const groupMessages = useQuery(
        api.chat.getGroupMessages,
        communityGroup ? { groupId: communityGroup._id } : 'skip'
    );

    //  Mutations 
    const sendMessage = useMutation(api.chat.sendMessage);
    const markAsRead = useMutation(api.chat.markAsRead);
    const editMessage = useMutation(api.chat.editMessage);
    const deleteMessage = useMutation(api.chat.deleteMessage);
    const toggleReaction = useMutation(api.chat.toggleReaction);
    const updateChatWallpaper = useMutation(api.users.updateChatWallpaper);
    const getOrCreateCommunityGroup = useMutation(api.chat.getOrCreateCommunityGroup);
    const sendGroupMessage = useMutation(api.chat.sendGroupMessage);
    const editGroupMessage = useMutation(api.chat.editGroupMessage);
    const deleteGroupMessage = useMutation(api.chat.deleteGroupMessage);
    const toggleGroupReaction = useMutation(api.chat.toggleGroupReaction);
    const toggleGroupLock = useMutation(api.chat.toggleGroupLock);
    const recordGroupMessageView = useMutation(api.chat.recordGroupMessageView);

    // Seen-by modal state
    const [seenByMessageId, setSeenByMessageId] = useState<string | null>(null);
    const seenByData = useQuery(
        api.chat.getGroupMessageViews,
        seenByMessageId ? { messageId: seenByMessageId as Id<'groupMessages'> } : 'skip'
    );

    const scrollRef = useRef<HTMLDivElement>(null);
    const groupScrollRef = useRef<HTMLDivElement>(null);
    const textareaRef = useRef<HTMLTextAreaElement>(null);
    const groupTextareaRef = useRef<HTMLTextAreaElement>(null);
    const wallpaperInputRef = useRef<HTMLInputElement>(null);

    //  Mobile keyboard offset (keeps input above keyboard) 
    const [keyboardOffset, setKeyboardOffset] = useState(0);

    useEffect(() => {
        const vv = window.visualViewport;
        if (!vv) return;

        const handleVVResize = () => {
            // gap between the layout viewport and the visual viewport = keyboard height
            const offset = Math.max(0, window.innerHeight - vv.height - vv.offsetTop);
            setKeyboardOffset(offset);

            // When keyboard opens, scroll messages to bottom so input stays visible
            if (offset > 0) {
                setTimeout(() => {
                    scrollRef.current?.scrollTo({ top: scrollRef.current.scrollHeight, behavior: 'smooth' });
                    groupScrollRef.current?.scrollTo({ top: groupScrollRef.current.scrollHeight, behavior: 'smooth' });
                }, 100);
            }
        };

        vv.addEventListener('resize', handleVVResize);
        vv.addEventListener('scroll', handleVVResize);
        return () => {
            vv.removeEventListener('resize', handleVVResize);
            vv.removeEventListener('scroll', handleVVResize);
        };
    }, []);

    const isAdmin = user?.role === 'admin' || user?.role === 'super_admin';

    //  Auto-ensure group chat exists 
    useEffect(() => {
        if (user && communityGroup === null) {
            getOrCreateCommunityGroup({ userId: user._id }).catch(console.error);
        }
    }, [communityGroup, user]);

    //  Auto-scroll 
    useEffect(() => {
        if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }, [messages, activeConversationId, replyingTo]);

    useEffect(() => {
        if (groupScrollRef.current) groupScrollRef.current.scrollTop = groupScrollRef.current.scrollHeight;
    }, [groupMessages, activeTab]);

    //  Auto-record group message views via IntersectionObserver 
    useEffect(() => {
        if (!groupMessages || !user || activeTab !== 'group') return;

        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        const msgId = (entry.target as HTMLElement).dataset.msgid;
                        const senderId = (entry.target as HTMLElement).dataset.senderid;
                        if (msgId && senderId && senderId !== user._id) {
                            recordGroupMessageView({
                                messageId: msgId as Id<'groupMessages'>,
                                userId: user._id,
                            }).catch(() => {/* ignore */ });
                        }
                    }
                });
            },
            { threshold: 0.5 } // 50% visible counts as seen
        );

        // Observe all visible group message elements
        const els = document.querySelectorAll('[data-groupmsg]');
        els.forEach((el) => observer.observe(el));

        return () => observer.disconnect();
    }, [groupMessages, user, activeTab]);

    //  Auto-resize textareas 
    useEffect(() => {
        if (textareaRef.current) {
            textareaRef.current.style.height = 'auto';
            textareaRef.current.style.height = `${Math.min(textareaRef.current.scrollHeight, 120)}px`;
        }
    }, [messageText]);

    useEffect(() => {
        if (groupTextareaRef.current) {
            groupTextareaRef.current.style.height = 'auto';
            groupTextareaRef.current.style.height = `${Math.min(groupTextareaRef.current.scrollHeight, 120)}px`;
        }
    }, [groupMsgText]);

    //  Mark as read 
    useEffect(() => {
        if (activeConversationId && user) {
            markAsRead({ conversationId: activeConversationId as Id<'conversations'>, userId: user._id });
        }
    }, [activeConversationId, messages, user]);

    //  Wallpaper 
    const handleWallpaperUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file || !user) return;
        setIsUploadingWallpaper(true);
        try {
            const url = await uploadToImageKit(file, 'announcements');
            await updateChatWallpaper({ userId: user._id, wallpaperUrl: url });
        } catch {
            alert('Failed to update wallpaper');
        } finally {
            setIsUploadingWallpaper(false);
        }
    };

    //  Direct chat handlers 
    const handleSendMessage = async (e?: React.FormEvent) => {
        e?.preventDefault();
        if (!messageText.trim() || !activeConversationId || !user) return;
        try {
            if (editingMessage) {
                await editMessage({ messageId: editingMessage._id, userId: user._id, newText: messageText });
                setEditingMessage(null);
            } else {
                const activeConv = conversations?.find((c: any) => c._id === activeConversationId);
                const recipientId = activeConv?.participantOne === user._id ? activeConv.participantTwo : activeConv?.participantOne;
                if (!recipientId) return;
                await sendMessage({ senderId: user._id, recipientId, text: messageText, replyToId: replyingTo?._id });
                setReplyingTo(null);
            }
            setMessageText('');
        } catch (error: any) {
            console.error('Failed to send message:', error);
            alert(error.message || 'Failed to send message');
        }
    };

    const handleKeyDown = (e: React.KeyboardEvent) => {
        if (e.key === 'Enter' && !e.shiftKey) { e.preventDefault(); handleSendMessage(); }
    };

    const handleStartConversation = async (recipientId: string) => {
        if (!user) return;
        try {
            const conversationId = await sendMessage({ senderId: user._id, recipientId: recipientId as Id<'users'>, text: 'Hi!' });
            setActiveConversationId(conversationId);
            setShowUserSearch(false);
            setUserSearchTerm('');
        } catch (error: any) {
            console.error('Failed to start conversation:', error);
            alert(error.message || 'Failed to start conversation');
        }
    };

    //  Group chat handlers 
    const handleSendGroupMessage = async (e?: React.FormEvent) => {
        e?.preventDefault();
        if (!groupMsgText.trim() || !communityGroup || !user) return;
        try {
            if (groupEditingMsg) {
                await editGroupMessage({ messageId: groupEditingMsg._id, userId: user._id, newText: groupMsgText });
                setGroupEditingMsg(null);
            } else {
                await sendGroupMessage({
                    groupId: communityGroup._id,
                    senderId: user._id,
                    text: groupMsgText,
                    replyToId: groupReplyingTo?._id,
                });
                setGroupReplyingTo(null);
            }
            setGroupMsgText('');
        } catch (error: any) {
            console.error('Failed to send group message:', error);
            alert(error.message || 'Failed to send group message');
        }
    };

    const handleGroupKeyDown = (e: React.KeyboardEvent) => {
        if (e.key === 'Enter' && !e.shiftKey) { e.preventDefault(); handleSendGroupMessage(); }
    };

    const handleToggleLock = async () => {
        if (!communityGroup || !user || !isAdmin) return;
        await toggleGroupLock({ groupId: communityGroup._id, adminId: user._id, locked: !communityGroup.isLocked });
    };

    //  Long press for mobile 
    const longPressTimer = useRef<any>(null);
    const isLongPress = useRef(false);
    const handleTouchStart = (msg: any, isGroup = false) => {
        isLongPress.current = false;
        longPressTimer.current = setTimeout(() => {
            isLongPress.current = true;
            if (isGroup) setGroupContextMsg(msg); else setContextMenuMsg(msg);
        }, 500);
    };
    const handleTouchEnd = () => {
        if (longPressTimer.current) { clearTimeout(longPressTimer.current); longPressTimer.current = null; }
    };

    //  Filter helpers 
    const handleChatsClick = () => {
        if (showUserSearch) setShowUserSearch(false);
        else if (activeConversationId) setActiveConversationId(null);
    };

    const filteredConversations = conversations?.filter((c: any) =>
        c.otherUser?.name.toLowerCase().includes(searchTerm.toLowerCase())
    );

    const filteredContacts = chatContacts?.filter((u: any) =>
        u._id !== user?._id && u.name.toLowerCase().includes(userSearchTerm.toLowerCase())
    );

    const activeConversation = conversations?.find((c: any) => c._id === activeConversationId);

    if (!user) { navigate('/login'); return null; }

    //  Shared message bubble renderer 
    const renderGroupMessage = (msg: any) => {
        const isMe = msg.senderId === user._id;
        const isDeleted = msg.isDeleted;

        if (isDeleted) {
            return (
                <div key={msg._id} className={`flex ${isMe ? 'justify-end' : 'justify-start'}`}>
                    <div className="px-3 py-2 rounded-lg bg-gray-100 dark:bg-gray-900 border border-gray-200 dark:border-gray-800 text-xs text-gray-500 italic flex items-center gap-2">
                        <Trash2 className="w-3 h-3" /> This message was deleted
                    </div>
                </div>
            );
        }

        const roleColor = msg.senderRole === 'super_admin' ? 'text-amber-500' :
            msg.senderRole === 'admin' ? 'text-primary-500' : 'text-gray-500';

        return (
            <div
                key={msg._id}
                className={`group relative flex ${isMe ? 'justify-end' : 'justify-start'} mb-3`}
                onMouseEnter={() => setGroupHoveredId(msg._id)}
                onMouseLeave={() => setGroupHoveredId(null)}
                data-groupmsg="true"
                data-msgid={msg._id}
                data-senderid={msg.senderId}
            >
                {/* Avatar for others */}
                {!isMe && (
                    <div className="flex-shrink-0 mr-2 mt-1">
                        <div className="w-7 h-7 rounded-full bg-gradient-to-br from-primary-500 to-secondary-500 flex items-center justify-center text-white text-xs font-bold overflow-hidden">
                            {msg.senderAvatar
                                ? <img src={msg.senderAvatar} alt={msg.senderName} className="w-full h-full object-cover" />
                                : msg.senderName?.[0]}
                        </div>
                    </div>
                )}

                <motion.div
                    drag="x"
                    dragConstraints={{ left: 0, right: 0 }}
                    onDragEnd={(_e, info) => { if (info.offset.x < -50) setGroupReplyingTo(msg); }}
                    whileDrag={{ scale: 1.02 }}
                    className="relative max-w-[78%] md:max-w-[65%]"
                    onTouchStart={() => handleTouchStart(msg, true)}
                    onTouchEnd={handleTouchEnd}
                >
                    {/* Hover action arrow */}
                    {groupHoveredId === msg._id && (
                        <div className={`absolute top-0 ${isMe ? '-left-8' : '-right-8'} z-20`}>
                            <button
                                className="p-1 rounded-full bg-gray-200 dark:bg-gray-700 shadow-sm hover:bg-gray-300 dark:hover:bg-gray-600 transition-colors"
                                onClick={(e) => { e.stopPropagation(); setGroupContextMsg(msg); }}
                            >
                                <ChevronDown className="w-3 h-3 text-gray-600 dark:text-gray-300" />
                            </button>
                        </div>
                    )}

                    <div className={`flex flex-col ${isMe ? 'items-end' : 'items-start'}`}>
                        {/* Sender name for others */}
                        {!isMe && (
                            <span className={`text-[10px] font-bold mb-0.5 flex items-center gap-1 ${roleColor}`}>
                                {msg.senderRole === 'super_admin' && <Crown className="w-3 h-3" />}
                                {msg.senderRole === 'admin' && <Shield className="w-3 h-3" />}
                                {msg.senderName}
                            </span>
                        )}

                        {/* Reply context */}
                        {msg.replyTo && (
                            <div className={`mb-1 text-xs px-2 py-1 rounded bg-gray-200 dark:bg-gray-800 border-l-2 border-primary-500 w-full opacity-80 ${msg.replyTo.isDeleted ? 'italic' : ''}`}>
                                <span className="font-bold block text-primary-500">{msg.replyTo.senderName}</span>
                                {msg.replyTo.isDeleted ? 'Message deleted' : msg.replyTo.text.length > 50 ? msg.replyTo.text.substring(0, 50) + '...' : msg.replyTo.text}
                            </div>
                        )}

                        <div className={`px-4 py-2 rounded-2xl text-sm whitespace-pre-wrap break-words shadow-sm ${isMe
                            ? 'bg-primary-500 text-white rounded-br-none'
                            : 'bg-white dark:bg-gray-800 text-gray-800 dark:text-gray-200 rounded-bl-none'}`}
                            style={{ wordBreak: 'break-word', overflowWrap: 'break-word' }}
                        >
                            {msg.text}
                            <div className={`text-[10px] mt-1 flex items-center justify-end gap-1.5 ${isMe ? 'text-white/70' : 'text-gray-400'}`}>
                                {msg.isEdited && <span className="opacity-70">(edited)</span>}
                                <span className="opacity-70">{formatDistanceToNow(msg.createdAt, { addSuffix: false })}</span>
                                {isMe && (
                                    <button
                                        onClick={(e) => { e.stopPropagation(); setSeenByMessageId(seenByMessageId === msg._id ? null : msg._id); }}
                                        className="flex items-center gap-0.5 opacity-75 hover:opacity-100 transition-opacity ml-0.5"
                                        title="See who viewed this"
                                    >
                                        <Eye className="w-3 h-3" />
                                        {msg._viewCount != null && msg._viewCount > 0 && (
                                            <span className="text-[9px] font-semibold">{msg._viewCount}</span>
                                        )}
                                    </button>
                                )}
                            </div>
                        </div>

                        {/* Reactions */}
                        {msg.reactions && msg.reactions.length > 0 && (
                            <motion.div initial={{ scale: 0 }} animate={{ scale: 1 }} className={`flex -mt-2 ${isMe ? 'mr-2' : 'ml-2'} z-10`}>
                                {msg.reactions.map((r: any, i: number) => (
                                    <span key={i} className="text-xs bg-white dark:bg-gray-800 rounded-full shadow border border-gray-100 dark:border-gray-700 w-5 h-5 flex items-center justify-center">
                                        {r.emoji}
                                    </span>
                                ))}
                            </motion.div>
                        )}
                    </div>
                </motion.div>
            </div>
        );
    };
    //  Seen-By Modal 
    const SeenByModal = seenByMessageId ? (
        <AnimatePresence>
            <motion.div
                key="seenby-backdrop"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                className="fixed inset-0 z-[200] flex items-end sm:items-center justify-center"
                onClick={() => setSeenByMessageId(null)}
            >
                {/* Backdrop */}
                <div className="absolute inset-0 bg-black/40 backdrop-blur-sm" />

                <motion.div
                    initial={{ y: 60, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    exit={{ y: 60, opacity: 0 }}
                    transition={{ type: 'spring', damping: 22 }}
                    className="relative z-10 w-full max-w-sm mx-4 mb-4 sm:mb-0 bg-white dark:bg-gray-900 rounded-2xl shadow-2xl overflow-hidden"
                    onClick={(e) => e.stopPropagation()}
                >
                    {/* Header */}
                    <div className="flex items-center justify-between px-5 py-4 border-b border-gray-100 dark:border-gray-800">
                        <div className="flex items-center gap-2 text-gray-700 dark:text-gray-200">
                            <Eye className="w-4 h-4 text-primary-500" />
                            <h2 className="text-sm font-bold">Seen by</h2>
                            {seenByData && (
                                <span className="text-xs bg-primary-100 dark:bg-primary-900/40 text-primary-600 dark:text-primary-400 rounded-full px-2 py-0.5 font-semibold">
                                    {seenByData.length}
                                </span>
                            )}
                        </div>
                        <button
                            onClick={() => setSeenByMessageId(null)}
                            className="p-1.5 rounded-full hover:bg-gray-100 dark:hover:bg-gray-800 transition-colors"
                        >
                            <X className="w-4 h-4 text-gray-500" />
                        </button>
                    </div>

                    {/* Viewer list */}
                    <div className="max-h-72 overflow-y-auto custom-thin-scrollbar">
                        {!seenByData ? (
                            <div className="flex items-center justify-center h-20">
                                <Loader2 className="w-5 h-5 animate-spin text-primary-400" />
                            </div>
                        ) : seenByData.length === 0 ? (
                            <div className="flex flex-col items-center justify-center py-10 text-gray-400 dark:text-gray-600">
                                <Eye className="w-10 h-10 mb-2 opacity-30" />
                                <p className="text-sm">No views yet</p>
                            </div>
                        ) : (
                            <ul className="divide-y divide-gray-50 dark:divide-gray-800">
                                {seenByData.map((viewer: any, idx: number) => (
                                    <motion.li
                                        key={viewer.userId}
                                        initial={{ opacity: 0, x: -10 }}
                                        animate={{ opacity: 1, x: 0 }}
                                        transition={{ delay: idx * 0.04 }}
                                        className="flex items-center gap-3 px-5 py-3"
                                    >
                                        <div className="w-9 h-9 rounded-full bg-gradient-to-br from-primary-400 to-secondary-400 flex items-center justify-center text-white text-sm font-bold overflow-hidden flex-shrink-0">
                                            {viewer.avatarUrl
                                                ? <img src={viewer.avatarUrl} alt={viewer.name} className="w-full h-full object-cover" />
                                                : viewer.name?.[0]}
                                        </div>
                                        <div className="flex-1 min-w-0">
                                            <p className="text-sm font-semibold text-gray-800 dark:text-gray-100 truncate">{viewer.name}</p>
                                            <p className="text-[10px] text-gray-400 dark:text-gray-500">
                                                Seen {formatDistanceToNow(viewer.seenAt, { addSuffix: true })}
                                            </p>
                                        </div>
                                        <Eye className="w-3.5 h-3.5 text-primary-400 flex-shrink-0" />
                                    </motion.li>
                                ))}
                            </ul>
                        )}
                    </div>
                </motion.div>
            </motion.div>
        </AnimatePresence>
    ) : null;

    return (
        <div
            className={`${isEmbedded ? 'relative w-full h-full' : 'fixed inset-0 z-[100]'} bg-light-bg dark:bg-dark-bg flex flex-col`}
            style={{ paddingBottom: keyboardOffset }}
        >
            {/* Seen-By overlay  renders on top of everything */}
            {SeenByModal}
            <input type="file" ref={wallpaperInputRef} onChange={handleWallpaperUpload} className="hidden" accept="image/*" />
            <style>{`
                .custom-thin-scrollbar::-webkit-scrollbar { width: 4px; }
                .custom-thin-scrollbar::-webkit-scrollbar-track { background: transparent; }
                .custom-thin-scrollbar::-webkit-scrollbar-thumb { background: #9ca3af; border-radius: 10px; }
                .dark .custom-thin-scrollbar::-webkit-scrollbar-thumb { background: #4b5563; }
                /* Prevent body scroll bounce when mobile keyboard opens */
                body { overscroll-behavior: none; }
                /* Hide scrollbar on textareas */
                .scrollbar-none::-webkit-scrollbar { display: none; }
                .scrollbar-none { -ms-overflow-style: none; scrollbar-width: none; }
            `}</style>

            <div className="flex-1 flex overflow-hidden">
                {/*  SIDEBAR  */}
                <div className={`${(activeConversationId && activeTab === 'direct') ? 'hidden md:flex' : 'flex'} md:w-[380px] flex-col border-r border-light-border dark:border-dark-border bg-white dark:bg-dark-card flex-shrink-0 w-full relative`}>

                    {/* Sidebar Header */}
                    <div className="p-4 border-b border-light-border dark:border-dark-border bg-white dark:bg-dark-card flex-shrink-0">
                        <div className="flex items-center justify-between mb-3">
                            <div className="flex items-center gap-3">
                                <button onClick={() => navigate('/feed')} className="md:hidden p-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-800 -ml-2">
                                    <ArrowLeft className="w-5 h-5" />
                                </button>
                                <h1 className="text-xl md:text-2xl font-bold text-light-text dark:text-dark-text">Messages</h1>
                            </div>
                        </div>

                        {/* Tab Switcher */}
                        <div className="flex gap-1 p-1 bg-gray-100 dark:bg-gray-800 rounded-xl">
                            <button
                                onClick={() => setActiveTab('direct')}
                                className={`flex-1 flex items-center justify-center gap-1.5 py-2 rounded-lg text-xs font-bold transition-all ${activeTab === 'direct' ? 'bg-white dark:bg-gray-700 text-primary-500 shadow-sm' : 'text-gray-500 hover:text-gray-700 dark:hover:text-gray-300'}`}
                            >
                                <MessageCircle className="w-4 h-4" /> Direct
                            </button>
                            <button
                                onClick={() => { setActiveTab('group'); if (!communityGroup && user) getOrCreateCommunityGroup({ userId: user._id }); }}
                                className={`flex-1 flex items-center justify-center gap-1.5 py-2 rounded-lg text-xs font-bold transition-all ${activeTab === 'group' ? 'bg-white dark:bg-gray-700 text-primary-500 shadow-sm' : 'text-gray-500 hover:text-gray-700 dark:hover:text-gray-300'}`}
                            >
                                <Users className="w-4 h-4" /> Community
                                {communityGroup?.isLocked && <Lock className="w-3 h-3 text-amber-500" />}
                            </button>
                        </div>
                    </div>

                    {/*  DIRECT TAB CONTENT  */}
                    {activeTab === 'direct' && (
                        <>
                            {/* Search Bar */}
                            <div className="p-3 border-b border-light-border dark:border-dark-border flex-shrink-0 bg-white dark:bg-dark-card">
                                <div className="relative">
                                    <Search className="w-4 h-4 absolute left-3 top-3 text-gray-400" />
                                    <input
                                        type="text"
                                        placeholder="Search conversations..."
                                        value={searchTerm}
                                        onChange={(e) => setSearchTerm(e.target.value)}
                                        className="w-full pl-10 pr-4 py-2 rounded-xl bg-gray-100 dark:bg-gray-800 border-none text-sm focus:ring-2 focus:ring-primary-500"
                                    />
                                </div>
                            </div>

                            {/* New Chat Overlay */}
                            {showUserSearch && (
                                <div className="absolute inset-x-0 top-0 bottom-[60px] z-20 bg-white dark:bg-dark-card flex flex-col">
                                    <div className="p-4 border-b border-light-border dark:border-dark-border flex-shrink-0 bg-white dark:bg-dark-card">
                                        <div className="flex items-center gap-3 mb-3">
                                            <button onClick={() => setShowUserSearch(false)} className="p-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-800">
                                                <ArrowLeft className="w-5 h-5" />
                                            </button>
                                            <h2 className="text-lg font-bold">New Message</h2>
                                        </div>
                                        {!isAdmin && (
                                            <p className="text-xs text-gray-500 mb-3 bg-gray-50 dark:bg-gray-800 px-3 py-2 rounded-lg">
                                                 You can message <strong>admins</strong> and <strong>members you follow</strong>.
                                            </p>
                                        )}
                                        <div className="relative">
                                            <Search className="w-4 h-4 absolute left-3 top-3 text-gray-400" />
                                            <input
                                                type="text"
                                                placeholder="Search contacts..."
                                                value={userSearchTerm}
                                                onChange={(e) => setUserSearchTerm(e.target.value)}
                                                className="w-full pl-10 pr-4 py-2 rounded-xl bg-gray-100 dark:bg-gray-800 border-none text-sm focus:ring-2 focus:ring-primary-500"
                                                autoFocus
                                            />
                                        </div>
                                    </div>
                                    <div className="flex-1 overflow-y-auto custom-thin-scrollbar">
                                        {!chatContacts ? (
                                            <div className="p-4"><ChatListSkeleton count={8} /></div>
                                        ) : filteredContacts?.length === 0 ? (
                                            <div className="flex flex-col items-center justify-center h-full p-8 text-center text-gray-500 text-sm">
                                                <MessageSquare className="w-12 h-12 mb-3 text-gray-300" />
                                                <p>No contacts found.</p>
                                                {!isAdmin && <p className="text-xs mt-2">Follow other members to chat with them.</p>}
                                            </div>
                                        ) : (
                                            filteredContacts?.map((u: any) => (
                                                <div
                                                    key={u._id}
                                                    onClick={() => handleStartConversation(u._id)}
                                                    className="flex items-center gap-3 p-4 hover:bg-gray-50 dark:hover:bg-gray-800 cursor-pointer transition-colors border-b border-light-border/50 dark:border-dark-border/50"
                                                >
                                                    <Avatar src={u.avatarUrl} alt={u.name} className="w-12 h-12" />
                                                    <div className="flex-1 min-w-0">
                                                        <div className="flex items-center gap-1.5">
                                                            <h4 className="font-semibold text-sm text-gray-900 dark:text-gray-100 truncate">{u.name}</h4>
                                                            {(u.role === 'admin' || u.role === 'super_admin') && (
                                                                <span className="text-[9px] font-bold px-1.5 py-0.5 rounded-full bg-primary-100 dark:bg-primary-900/30 text-primary-600 uppercase">
                                                                    {u.role === 'super_admin' ? 'Super Admin' : 'Admin'}
                                                                </span>
                                                            )}
                                                        </div>
                                                        <p className="text-xs text-gray-500 truncate">{u.email}</p>
                                                    </div>
                                                </div>
                                            ))
                                        )}
                                    </div>
                                </div>
                            )}

                            {/* Conversation List */}
                            <div className="flex-1 overflow-y-auto custom-thin-scrollbar">
                                {!conversations ? (
                                    <div className="p-4"><ChatListSkeleton count={6} /></div>
                                ) : filteredConversations?.length === 0 ? (
                                    <div className="flex flex-col items-center justify-center h-full text-center p-8">
                                        <MessageSquare className="w-16 h-16 text-gray-300 dark:text-gray-700 mb-4" />
                                        <p className="text-gray-500 text-sm">No conversations yet</p>
                                        <button
                                            onClick={() => setShowUserSearch(true)}
                                            className="mt-4 px-4 py-2 bg-primary-500 text-white rounded-xl text-sm font-semibold hover:bg-primary-600 transition-colors"
                                        >
                                            Start a conversation
                                        </button>
                                    </div>
                                ) : (
                                    filteredConversations?.map((conv: any) => (
                                        <div
                                            key={conv._id}
                                            onClick={() => setActiveConversationId(conv._id)}
                                            className={`flex items-center gap-3 p-4 cursor-pointer transition-colors border-b border-light-border/50 dark:border-dark-border/50 ${activeConversationId === conv._id ? 'bg-primary-50 dark:bg-primary-900/20' : 'hover:bg-gray-50 dark:hover:bg-gray-800'}`}
                                        >
                                            <Avatar src={conv.otherUser?.avatarUrl} alt={conv.otherUser?.name || 'User'} className="w-12 h-12" />
                                            <div className="flex-1 min-w-0">
                                                <div className="flex justify-between items-baseline mb-1">
                                                    <h4 className="font-semibold text-sm text-gray-900 dark:text-gray-100 truncate">{conv.otherUser?.name}</h4>
                                                    <span className="text-[10px] text-gray-500 ml-2 flex-shrink-0">{formatDistanceToNow(conv.updatedAt, { addSuffix: false })}</span>
                                                </div>
                                                <p className={`text-xs truncate ${!conv.isRead && conv.lastMessageSenderId !== user?._id ? 'font-bold text-gray-900 dark:text-gray-100' : 'text-gray-500'}`}>
                                                    {conv.lastMessageSenderId === user?._id ? 'You: ' : ''}{conv.lastMessageText || 'Start a conversation'}
                                                </p>
                                            </div>
                                            {!conv.isRead && conv.lastMessageSenderId !== user?._id && (
                                                <div className="w-2.5 h-2.5 bg-primary-500 rounded-full flex-shrink-0" />
                                            )}
                                        </div>
                                    ))
                                )}
                            </div>

                            {/* Sidebar Footer Nav */}
                            <div className="flex-shrink-0 bg-white dark:bg-dark-card border-t border-light-border dark:border-dark-border py-2 px-6 flex justify-center gap-10 md:gap-16 items-center">
                                <button
                                    onClick={handleChatsClick}
                                    className={`flex flex-col items-center gap-1 ${!showUserSearch ? 'text-primary-500' : 'text-light-muted dark:text-dark-muted hover:text-primary-500'}`}
                                >
                                    <MessageCircle className="w-6 h-6 md:w-7 md:h-7 fill-current" />
                                    <span className="text-[10px] font-bold">Chats</span>
                                </button>
                                <button
                                    onClick={() => setShowUserSearch(true)}
                                    className={`flex flex-col items-center gap-1 -mt-6 ${showUserSearch ? 'text-primary-500' : 'text-light-muted dark:text-dark-muted hover:text-primary-500'}`}
                                >
                                    <div className={`w-12 h-12 md:w-14 md:h-14 rounded-full flex items-center justify-center text-white shadow-lg ${showUserSearch ? 'bg-primary-600 ring-4 ring-primary-100' : 'bg-primary-500 shadow-primary-500/30'}`}>
                                        <MessageSquare className="w-5 h-5 md:w-6 md:h-6" />
                                    </div>
                                    <span className="text-[10px] font-bold mt-1">New Chat</span>
                                </button>
                                <Link to="/feed" className="flex flex-col items-center gap-1 text-light-muted dark:text-dark-muted hover:text-primary-500">
                                    <Sparkles className="w-6 h-6 md:w-7 md:h-7" />
                                    <span className="text-[10px] font-bold">Feed</span>
                                </Link>
                            </div>
                        </>
                    )}

                    {/*  GROUP TAB CONTENT (sidebar area is just the group chat itself)  */}
                    {activeTab === 'group' && (
                        <div className="flex-1 flex flex-col overflow-hidden">
                            {/* Group header */}
                            <div className="p-3 border-b border-light-border dark:border-dark-border bg-white dark:bg-dark-card flex items-center gap-3 flex-shrink-0">
                                <div className="w-10 h-10 rounded-full bg-gradient-to-br from-primary-500 to-secondary-500 flex items-center justify-center text-white font-bold text-lg flex-shrink-0">
                                    <Users className="w-5 h-5" />
                                </div>
                                <div className="flex-1 min-w-0">
                                    <h3 className="font-bold text-sm text-gray-900 dark:text-gray-100">
                                        {communityGroup?.name || 'Community'}
                                    </h3>
                                    <p className="text-xs text-gray-500 truncate">{communityGroup?.description || 'All members'}</p>
                                </div>
                                {/* Lock toggle  admins only */}
                                {isAdmin && communityGroup && (
                                    <button
                                        onClick={handleToggleLock}
                                        className={`p-2 rounded-lg transition-all text-sm font-bold flex items-center gap-1 ${communityGroup.isLocked
                                            ? 'bg-amber-100 dark:bg-amber-900/30 text-amber-600 hover:bg-amber-200'
                                            : 'bg-gray-100 dark:bg-gray-800 text-gray-500 hover:bg-gray-200 dark:hover:bg-gray-700'}`}
                                        title={communityGroup.isLocked ? 'Unlock group (allow all to message)' : 'Lock group (admins only)'}
                                    >
                                        {communityGroup.isLocked ? <Lock className="w-4 h-4" /> : <Unlock className="w-4 h-4" />}
                                    </button>
                                )}
                            </div>

                            {/* Lock status banner */}
                            {communityGroup?.isLocked && (
                                <div className="px-4 py-2 bg-amber-50 dark:bg-amber-900/20 border-b border-amber-200 dark:border-amber-800 flex items-center gap-2 text-xs text-amber-700 dark:text-amber-400 font-medium">
                                    <Lock className="w-3 h-3" />
                                    Group is locked  only admins can send messages
                                </div>
                            )}

                            {/* Messages */}
                            <div
                                ref={groupScrollRef}
                                className="flex-1 p-4 overflow-y-auto custom-thin-scrollbar overflow-x-hidden relative"
                                style={{ backgroundImage: globalWallpaper ? `url(${globalWallpaper})` : undefined, backgroundSize: 'cover', backgroundPosition: 'center' }}
                            >
                                {globalWallpaper && <div className="absolute inset-0 bg-white/70 dark:bg-black/60 pointer-events-none z-0" />}
                                <div className="relative z-10 space-y-1">
                                    {!groupMessages ? (
                                        <MessagesSkeleton />
                                    ) : groupMessages.length === 0 ? (
                                        <div className="flex items-center justify-center h-full pt-20">
                                            <div className="text-center text-gray-500 text-sm">
                                                <Users className="w-12 h-12 mx-auto mb-3 text-gray-300" />
                                                <p>No messages yet. Say hi to the community! </p>
                                            </div>
                                        </div>
                                    ) : (
                                        groupMessages.map(renderGroupMessage)
                                    )}
                                </div>
                            </div>

                            {/* Group Input */}
                            <div className="p-3 bg-white/90 dark:bg-dark-card/90 backdrop-blur-md border-t border-light-border dark:border-dark-border flex-shrink-0">
                                <AnimatePresence>
                                    {(groupReplyingTo || groupEditingMsg) && (
                                        <motion.div
                                            initial={{ opacity: 0, y: 10 }} animate={{ opacity: 1, y: 0 }} exit={{ opacity: 0, y: 10 }}
                                            className="flex items-center justify-between px-3 py-2 mb-2 bg-gray-100 dark:bg-gray-800 rounded-lg text-sm border-l-4 border-primary-500"
                                        >
                                            <div className="flex flex-col justify-center text-gray-600 dark:text-gray-300">
                                                <span className="font-bold text-xs text-primary-500">
                                                    {groupEditingMsg ? 'Editing message' : `Replying to ${groupReplyingTo.senderName}`}
                                                </span>
                                                <span className="text-xs truncate max-w-[200px] opacity-70">
                                                    {groupEditingMsg ? groupEditingMsg.text : groupReplyingTo.text}
                                                </span>
                                            </div>
                                            <button onClick={() => { setGroupReplyingTo(null); setGroupEditingMsg(null); setGroupMsgText(''); }} className="p-1 hover:bg-gray-200 dark:hover:bg-gray-700 rounded-full">
                                                <X className="w-4 h-4" />
                                            </button>
                                        </motion.div>
                                    )}
                                </AnimatePresence>

                                {communityGroup?.isLocked && !isAdmin ? (
                                    <div className="flex items-center justify-center gap-2 py-3 text-amber-600 dark:text-amber-400 text-sm font-medium">
                                        <Lock className="w-4 h-4" />
                                        Group is locked. Only admins can send messages.
                                    </div>
                                ) : (
                                    <div className="flex gap-2 items-end">
                                        <div className="flex-1 bg-gray-100 dark:bg-gray-800 rounded-2xl border-none focus-within:ring-2 focus-within:ring-primary-500 transition-all min-h-[44px]">
                                            <textarea
                                                ref={groupTextareaRef}
                                                value={groupMsgText}
                                                onChange={(e) => setGroupMsgText(e.target.value)}
                                                onKeyDown={handleGroupKeyDown}
                                                onFocus={() => {
                                                    setTimeout(() => {
                                                        groupScrollRef.current?.scrollTo({ top: groupScrollRef.current.scrollHeight, behavior: 'smooth' });
                                                    }, 300);
                                                }}
                                                placeholder={groupEditingMsg ? 'Edit your message...' : 'Message the community...'}
                                                className="w-full px-4 py-3 bg-transparent border-none focus:ring-0 resize-none overflow-hidden text-sm text-gray-900 dark:text-gray-100 scrollbar-none"
                                                style={{ minHeight: '44px' }}
                                                rows={1}
                                            />
                                        </div>
                                        <Button
                                            onClick={() => handleSendGroupMessage()}
                                            size="icon"
                                            disabled={!groupMsgText.trim()}
                                            className="rounded-full w-12 h-12 flex-shrink-0 mb-[1px]"
                                        >
                                            <Send className="w-5 h-5" />
                                        </Button>
                                    </div>
                                )}
                            </div>

                            {/* Group Context Menu */}
                            <AnimatePresence>
                                {groupContextMsg && (
                                    <>
                                        <motion.div
                                            initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }}
                                            className="absolute inset-0 bg-black/50 z-[110]"
                                            onClick={() => setGroupContextMsg(null)}
                                        />
                                        <motion.div
                                            initial={{ opacity: 0, scale: 0.9, y: 20 }} animate={{ opacity: 1, scale: 1, y: 0 }} exit={{ opacity: 0, scale: 0.9, y: 20 }}
                                            className="absolute bottom-4 left-4 right-4 bg-white dark:bg-[#222] rounded-2xl p-2 shadow-2xl z-[120] flex flex-col gap-1"
                                        >
                                            <div className="flex items-center justify-around p-2 border-b border-gray-100 dark:border-gray-800 pb-2">
                                                {['', '', '', '', '', ''].map(emoji => (
                                                    <button key={emoji} onClick={() => { toggleGroupReaction({ messageId: groupContextMsg._id, userId: user._id, emoji }); setGroupContextMsg(null); }} className="text-xl hover:scale-125 transition-transform">{emoji}</button>
                                                ))}
                                            </div>
                                            <button className="w-full text-left p-3 rounded-xl hover:bg-gray-100 dark:hover:bg-gray-700 flex items-center gap-3 text-sm font-medium" onClick={() => { setGroupReplyingTo(groupContextMsg); setGroupContextMsg(null); }}>
                                                <Reply className="w-4 h-4" /> Reply
                                            </button>
                                            {groupContextMsg.senderId === user._id && (
                                                <>
                                                    <button className="w-full text-left p-3 rounded-xl hover:bg-gray-100 dark:hover:bg-gray-700 flex items-center gap-3 text-sm font-medium" onClick={() => { setGroupEditingMsg(groupContextMsg); setGroupMsgText(groupContextMsg.text); setGroupContextMsg(null); }}>
                                                        <Edit2 className="w-4 h-4" /> Edit
                                                    </button>
                                                    <button className="w-full text-left p-3 rounded-xl hover:bg-primary-50 dark:hover:bg-primary-900/20 text-primary-600 dark:text-primary-400 flex items-center gap-3 text-sm font-medium" onClick={() => { setSeenByMessageId(groupContextMsg._id); setGroupContextMsg(null); }}>
                                                        <Eye className="w-4 h-4" /> Seen by
                                                    </button>
                                                    <button className="w-full text-left p-3 rounded-xl hover:bg-red-50 dark:hover:bg-red-900/20 text-red-500 flex items-center gap-3 text-sm font-medium" onClick={() => { deleteGroupMessage({ messageId: groupContextMsg._id, userId: user._id }); setGroupContextMsg(null); }}>
                                                        <Trash2 className="w-4 h-4" /> Delete
                                                    </button>
                                                </>
                                            )}

                                        </motion.div>
                                    </>
                                )}
                            </AnimatePresence>
                        </div>
                    )}
                </div>

                {/*  DIRECT CHAT WINDOW  */}
                {activeTab === 'direct' && (
                    <div
                        className={`${activeConversationId ? 'flex' : 'hidden md:flex'} flex-1 flex-col bg-gray-50 dark:bg-gray-900 ${activeConversationId ? 'fixed inset-0 md:relative z-[200] md:z-auto' : ''} relative overflow-hidden`}
                        style={activeConversationId ? { bottom: keyboardOffset } : undefined}
                    >
                        {/* Wallpaper Background */}
                        {(user?.chatWallpaperUrl || globalWallpaper) && (
                            <div
                                className="absolute inset-0 z-0 bg-cover bg-center opacity-30 dark:opacity-20 pointer-events-none"
                                style={{ backgroundImage: `url(${user?.chatWallpaperUrl || globalWallpaper})`, filter: 'blur(3px)' }}
                            />
                        )}

                        {activeConversationId && activeConversation ? (
                            <>
                                {/* Chat Header */}
                                <div className="p-4 bg-white/90 dark:bg-dark-card/90 backdrop-blur-md border-b border-light-border dark:border-dark-border flex items-center gap-3 flex-shrink-0 z-10">
                                    <button onClick={() => setActiveConversationId(null)} className="p-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-800 -ml-2">
                                        <ArrowLeft className="w-5 h-5" />
                                    </button>
                                    <Avatar src={activeConversation.otherUser?.avatarUrl} alt={activeConversation.otherUser?.name || 'User'} className="w-10 h-10" />
                                    <div className="flex-1 min-w-0">
                                        <h3 className="font-semibold text-gray-900 dark:text-gray-100 truncate">{activeConversation.otherUser?.name}</h3>
                                        <p className="text-xs text-gray-500">{activeConversation.otherUser?.role || 'Member'}</p>
                                    </div>
                                    <button
                                        onClick={() => wallpaperInputRef.current?.click()}
                                        disabled={isUploadingWallpaper}
                                        className="p-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-800 text-gray-500 transition-colors"
                                        title="Change Chat Wallpaper"
                                    >
                                        {isUploadingWallpaper ? <Loader2 className="w-5 h-5 animate-spin" /> : <ImageIcon className="w-5 h-5" />}
                                    </button>
                                </div>

                                {/* Messages */}
                                <div className="flex-1 p-4 overflow-y-auto custom-thin-scrollbar overflow-x-hidden relative z-10" ref={scrollRef}>
                                    {!messages ? (
                                        <MessagesSkeleton />
                                    ) : messages.length === 0 ? (
                                        <div className="flex items-center justify-center h-full">
                                            <div className="text-center text-gray-500 text-sm">No messages yet. Say hi!</div>
                                        </div>
                                    ) : (
                                        <div className="space-y-4 pb-2">
                                            {messages.map((msg: any) => {
                                                const isMe = msg.senderId === user?._id;
                                                const isDeleted = msg.isDeleted;

                                                if (isDeleted) {
                                                    return (
                                                        <div key={msg._id} className={`flex ${isMe ? 'justify-end' : 'justify-start'}`}>
                                                            <div className="px-3 py-2 rounded-lg bg-gray-100 dark:bg-gray-900 border border-gray-200 dark:border-gray-800 text-xs text-gray-500 italic flex items-center gap-2">
                                                                <Trash2 className="w-3 h-3" /> This message was deleted
                                                            </div>
                                                        </div>
                                                    );
                                                }

                                                return (
                                                    <div
                                                        key={msg._id}
                                                        className={`group relative flex ${isMe ? 'justify-end' : 'justify-start'} mb-2`}
                                                        onMouseEnter={() => setHoveredMessageId(msg._id)}
                                                        onMouseLeave={() => setHoveredMessageId(null)}
                                                    >
                                                        <motion.div
                                                            drag="x"
                                                            dragConstraints={{ left: 0, right: 0 }}
                                                            onDragEnd={(_event, info) => { if (info.offset.x < -50) setReplyingTo(msg); }}
                                                            whileDrag={{ scale: 1.02 }}
                                                            className={`relative max-w-[85%] md:max-w-[70%]`}
                                                            onTouchStart={() => handleTouchStart(msg)}
                                                            onTouchEnd={handleTouchEnd}
                                                        >
                                                            {hoveredMessageId === msg._id && (
                                                                <div className={`absolute top-0 ${isMe ? '-left-8' : '-right-8'} z-20 flex flex-col`}>
                                                                    <button
                                                                        className="p-1 rounded-full bg-gray-200 dark:bg-gray-700 shadow-sm hover:bg-gray-300 dark:hover:bg-gray-600 transition-colors"
                                                                        onClick={(e) => { e.stopPropagation(); setContextMenuMsg(msg); }}
                                                                    >
                                                                        <ChevronDown className="w-4 h-4 text-gray-600 dark:text-gray-300" />
                                                                    </button>
                                                                </div>
                                                            )}
                                                            <div className={`flex flex-col ${isMe ? 'items-end' : 'items-start'}`}>
                                                                {msg.replyTo && (
                                                                    <div className={`mb-1 text-xs px-2 py-1 rounded bg-gray-200 dark:bg-gray-800 border-l-2 border-primary-500 w-full opacity-80 ${msg.replyTo.isDeleted ? 'italic' : ''}`}>
                                                                        <span className="font-bold block text-primary-500">{msg.replyTo.senderName}</span>
                                                                        {msg.replyTo.isDeleted ? 'Message deleted' : msg.replyTo.text.length > 50 ? msg.replyTo.text.substring(0, 50) + '...' : msg.replyTo.text}
                                                                    </div>
                                                                )}
                                                                <div className={`px-4 py-2 rounded-2xl text-sm whitespace-pre-wrap break-words shadow-sm ${isMe ? 'bg-primary-500 text-white rounded-br-none' : 'bg-white dark:bg-gray-800 text-gray-800 dark:text-gray-200 rounded-bl-none shadow-sm'}`} style={{ wordBreak: 'break-word', overflowWrap: 'break-word' }}>
                                                                    {msg.text}
                                                                    <div className={`text-[10px] mt-1 flex items-center justify-end gap-1 opacity-70 ${isMe ? 'text-white/80' : 'text-gray-500'}`}>
                                                                        {msg.isEdited && <span>(edited)</span>}
                                                                        {formatDistanceToNow(msg.createdAt, { addSuffix: false })}
                                                                    </div>
                                                                </div>
                                                                {msg.reactions && msg.reactions.length > 0 && (
                                                                    <motion.div initial={{ scale: 0 }} animate={{ scale: 1 }} className={`flex -mt-2 ${isMe ? 'mr-2' : 'ml-2'} z-10`}>
                                                                        {msg.reactions.map((r: any, i: number) => (
                                                                            <span key={i} className="text-xs bg-white dark:bg-gray-800 rounded-full shadow border border-gray-100 dark:border-gray-700 w-5 h-5 flex items-center justify-center">{r.emoji}</span>
                                                                        ))}
                                                                    </motion.div>
                                                                )}
                                                            </div>
                                                        </motion.div>
                                                    </div>
                                                );
                                            })}
                                        </div>
                                    )}
                                </div>

                                {/* Input */}
                                <div className="p-3 md:p-4 bg-white/90 dark:bg-dark-card/90 backdrop-blur-md border-t border-light-border dark:border-dark-border flex-shrink-0 w-full z-10">
                                    <AnimatePresence>
                                        {(replyingTo || editingMessage) && (
                                            <motion.div
                                                initial={{ opacity: 0, y: 10 }} animate={{ opacity: 1, y: 0 }} exit={{ opacity: 0, y: 10 }}
                                                className="flex items-center justify-between px-3 py-2 mb-2 bg-gray-100 dark:bg-gray-800 rounded-lg text-sm border-l-4 border-primary-500"
                                            >
                                                <div className="flex flex-col justify-center text-gray-600 dark:text-gray-300">
                                                    <span className="font-bold text-xs text-primary-500">
                                                        {editingMessage ? 'Editing Message' : `Replying to ${replyingTo.senderId === user?._id ? 'yourself' : 'them'}`}
                                                    </span>
                                                    <span className="text-xs truncate max-w-[200px] opacity-70">
                                                        {editingMessage ? editingMessage.text : replyingTo.text}
                                                    </span>
                                                </div>
                                                <button onClick={() => { setReplyingTo(null); setEditingMessage(null); setMessageText(''); }} className="p-1 hover:bg-gray-200 dark:hover:bg-gray-700 rounded-full">
                                                    <X className="w-4 h-4" />
                                                </button>
                                            </motion.div>
                                        )}
                                    </AnimatePresence>
                                    <div className="flex gap-2 items-end">
                                        <div className="flex-1 bg-gray-100 dark:bg-gray-800 rounded-2xl border-none focus-within:ring-2 focus-within:ring-primary-500 transition-all min-h-[44px]">
                                            <textarea
                                                ref={textareaRef}
                                                value={messageText}
                                                onChange={(e) => setMessageText(e.target.value)}
                                                onKeyDown={handleKeyDown}
                                                onFocus={() => {
                                                    setTimeout(() => {
                                                        scrollRef.current?.scrollTo({ top: scrollRef.current.scrollHeight, behavior: 'smooth' });
                                                    }, 300);
                                                }}
                                                placeholder={editingMessage ? 'Edit your message...' : 'Type a message...'}
                                                className="w-full px-4 py-3 bg-transparent border-none focus:ring-0 resize-none overflow-hidden text-sm text-gray-900 dark:text-gray-100 scrollbar-none"
                                                style={{ minHeight: '44px' }}
                                                rows={1}
                                            />
                                        </div>
                                        <Button onClick={() => handleSendMessage()} size="icon" disabled={!messageText.trim()} className="rounded-full w-12 h-12 flex-shrink-0 mb-[1px]">
                                            <Send className="w-5 h-5" />
                                        </Button>
                                    </div>
                                </div>

                                {/* Direct Chat Context Menu */}
                                <AnimatePresence>
                                    {contextMenuMsg && (
                                        <>
                                            <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }} className="absolute inset-0 bg-black/50 z-[110]" onClick={() => setContextMenuMsg(null)} />
                                            <motion.div
                                                initial={{ opacity: 0, scale: 0.9, y: 20 }} animate={{ opacity: 1, scale: 1, y: 0 }} exit={{ opacity: 0, scale: 0.9, y: 20 }}
                                                className="absolute bottom-4 left-4 right-4 md:left-auto md:right-auto md:w-[300px] md:bottom-20 bg-white dark:bg-[#222] rounded-2xl p-2 shadow-2xl z-[120] flex flex-col gap-1 md:left-1/2 md:-translate-x-1/2"
                                            >
                                                <div className="flex items-center justify-around p-2 border-b border-gray-100 dark:border-gray-800 pb-2">
                                                    {['', '', '', '', '', ''].map(emoji => (
                                                        <button key={emoji} onClick={() => { toggleReaction({ messageId: contextMenuMsg._id, userId: user!._id, emoji }); setContextMenuMsg(null); }} className="text-xl hover:scale-125 transition-transform">{emoji}</button>
                                                    ))}
                                                </div>
                                                <button className="w-full text-left p-3 rounded-xl hover:bg-gray-100 dark:hover:bg-gray-700 flex items-center gap-3 text-sm font-medium" onClick={() => { setReplyingTo(contextMenuMsg); setContextMenuMsg(null); }}>
                                                    <Reply className="w-4 h-4" /> Reply
                                                </button>
                                                {contextMenuMsg.senderId === user?._id && (
                                                    <>
                                                        <button className="w-full text-left p-3 rounded-xl hover:bg-gray-100 dark:hover:bg-gray-700 flex items-center gap-3 text-sm font-medium" onClick={() => { setEditingMessage(contextMenuMsg); setMessageText(contextMenuMsg.text); setContextMenuMsg(null); }}>
                                                            <Edit2 className="w-4 h-4" /> Edit
                                                        </button>
                                                        <button className="w-full text-left p-3 rounded-xl hover:bg-red-50 dark:hover:bg-red-900/20 text-red-500 flex items-center gap-3 text-sm font-medium" onClick={() => { deleteMessage({ messageId: contextMenuMsg._id, userId: user!._id }); setContextMenuMsg(null); }}>
                                                            <Trash2 className="w-4 h-4" /> Delete
                                                        </button>
                                                    </>
                                                )}
                                            </motion.div>
                                        </>
                                    )}
                                </AnimatePresence>
                            </>
                        ) : (
                            <div className="flex items-center justify-center h-full">
                                <div className="text-center">
                                    <MessageSquare className="w-24 h-24 text-gray-300 dark:text-gray-700 mx-auto mb-4" />
                                    <h3 className="text-lg font-semibold text-gray-600 dark:text-gray-400 mb-2">Select a conversation</h3>
                                    <p className="text-sm text-gray-500">Choose from your existing conversations or start a new one</p>
                                </div>
                            </div>
                        )}
                    </div>
                )}

                {/* On group tab: no separate right panel  group chat is in the sidebar area and goes full width on mobile */}
                {activeTab === 'group' && (
                    <div className="hidden md:flex flex-1 items-center justify-center bg-gray-50 dark:bg-gray-900">
                        <div className="text-center text-gray-400 dark:text-gray-600">
                            <Users className="w-20 h-20 mx-auto mb-4 opacity-40" />
                            <p className="font-medium">Join the conversation on the left</p>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}
