import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Link } from 'react-router-dom';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Avatar } from '@/components/ui/Avatar';
import { Send, Shield, HeartHandshake, MessageCircleOff, CheckCircle2 } from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';
import { Id } from '../../../convex/_generated/dataModel';
import { CommentSkeleton } from '../skeletons/CommentSkeleton';

interface CommentSectionProps {
    postId: Id<"posts">;
    isPrayer?: boolean;
    isAdminPost?: boolean; // one-comment rule + locked UI only applies to admin posts
}

export function CommentSection({ postId, isPrayer = false, isAdminPost = false }: CommentSectionProps) {
    const { user } = useAuth();
    const [commentText, setCommentText] = useState('');
    const [replyText, setReplyText] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [replyingTo, setReplyingTo] = useState<Id<"comments"> | null>(null);

    const comments = useQuery(api.comments.getComments, { postId });
    const commentsEnabled = useQuery(api.comments.getCommentSettings, {});
    const alreadyCommented = useQuery(
        api.comments.hasUserCommented,
        // Only query this on admin posts  restriction doesn't apply elsewhere
        user?._id && isAdminPost ? { postId, userId: user._id } : 'skip'
    );

    const createComment = useMutation(api.comments.createComment);

    //  Submit top-level comment 
    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!user || !commentText.trim() || isSubmitting) return;
        if (isAdminPost && alreadyCommented) return; // safety guard

        setIsSubmitting(true);
        try {
            await createComment({
                postId,
                authorId: user._id,
                content: commentText.trim(),
            });
            setCommentText('');
        } catch (error) {
            console.error('Failed to create comment:', error);
            alert(error instanceof Error ? error.message : 'Failed to post comment');
        } finally {
            setIsSubmitting(false);
        }
    };

    //  Submit reply 
    const handleReplySubmit = async (e: React.FormEvent, parentId: Id<"comments">) => {
        e.preventDefault();
        if (!user || !replyText.trim() || isSubmitting) return;

        setIsSubmitting(true);
        try {
            await createComment({
                postId,
                authorId: user._id,
                content: replyText.trim(),
                parentId,
            });
            setReplyText('');
            setReplyingTo(null);
        } catch (error) {
            console.error('Failed to post reply:', error);
            alert(error instanceof Error ? error.message : 'Failed to post reply');
        } finally {
            setIsSubmitting(false);
        }
    };

    //  Build threaded comment tree 
    const getThreadedComments = () => {
        if (!comments) return [];
        const map = new Map<string, any>();
        comments.forEach((c: any) => map.set(c._id, { ...c, replies: [] }));
        const threads: any[] = [];
        comments.forEach((c: any) => {
            if (c.parentId) {
                const parent = map.get(c.parentId);
                if (parent) parent.replies.push(map.get(c._id));
            } else {
                threads.push(map.get(c._id));
            }
        });
        return threads;
    };

    const threadedComments = getThreadedComments();

    //  Single comment item 
    function CommentItem({ comment, isReply = false }: { comment: any; isReply?: boolean }) {
        const authorName: string = comment.author?.name || 'U';
        const initial = authorName.charAt(0).toUpperCase();
        const avatarUrl: string | undefined = comment.author?.avatarUrl;

        return (
            <div className={`flex gap-2 md:gap-3 ${isReply ? 'ml-8 md:ml-12 mt-2' : ''} group`}>
                <Link to={`/profile/${comment.author?._id}`} className="shrink-0 mt-1">
                    {avatarUrl ? (
                        <Avatar
                            src={avatarUrl}
                            alt={authorName}
                            size={isReply ? 'xs' : 'sm'}
                        />
                    ) : (
                        // Fallback: coloured initial circle
                        <div className={`flex items-center justify-center rounded-full bg-primary-500/20 text-primary-600 dark:text-primary-400 font-bold flex-shrink-0 ${isReply ? 'w-6 h-6 text-[10px]' : 'w-8 h-8 text-sm'
                            }`}>
                            {initial}
                        </div>
                    )}
                </Link>

                <div className="flex-1 min-w-0">
                    <div className="flex flex-col items-start">
                        {/* Bubble */}
                        <div className="bg-light-card dark:bg-dark-card rounded-2xl px-3 py-2 md:px-4 md:py-2.5 inline-block max-w-full shadow-sm border border-light-border/50 dark:border-dark-border/50">
                            <Link
                                to={`/profile/${comment.author?._id}`}
                                className="font-bold text-sm text-light-text dark:text-dark-text hover:underline block mb-0.5 truncate"
                            >
                                {authorName}
                                {comment.author?.role === 'admin' && (
                                    <Shield className="w-3 h-3 inline-block ml-1 text-primary-500" />
                                )}
                            </Link>
                            <p className="text-sm text-light-text dark:text-dark-text break-words leading-relaxed whitespace-pre-wrap">
                                {comment.content}
                            </p>
                        </div>

                        {/* Actions */}
                        <div className="flex items-center gap-3 mt-1 ml-2">
                            <span className="text-[11px] md:text-xs text-light-muted dark:text-dark-muted font-medium">
                                {formatDistanceToNow(comment.createdAt, { addSuffix: true })}
                            </span>
                            {!isReply && user && (
                                <button
                                    onClick={() => {
                                        setReplyingTo(replyingTo === comment._id ? null : comment._id);
                                        setReplyText('');
                                    }}
                                    className="text-[11px] md:text-xs font-bold text-light-muted dark:text-dark-muted hover:text-primary-500 transition-colors"
                                >
                                    Reply
                                </button>
                            )}
                        </div>
                    </div>

                    {/* Reply input */}
                    <AnimatePresence>
                        {replyingTo === comment._id && (
                            <motion.form
                                key="reply-form"
                                initial={{ opacity: 0, y: -8 }}
                                animate={{ opacity: 1, y: 0 }}
                                exit={{ opacity: 0, y: -8 }}
                                onSubmit={(e) => handleReplySubmit(e, comment._id)}
                                className="mt-3 flex gap-2"
                            >
                                <Avatar src={user?.avatarUrl} size="xs" className="hidden md:block shrink-0 mt-1" />
                                <div className="flex-1 relative">
                                    <input
                                        type="text"
                                        value={replyText}
                                        onChange={(e) => setReplyText(e.target.value)}
                                        placeholder={`Reply to ${authorName}...`}
                                        className="w-full pl-3 pr-10 py-2 text-sm rounded-xl border border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg focus:ring-1 focus:ring-primary-500 outline-none"
                                        autoFocus
                                    />
                                    <button
                                        type="submit"
                                        disabled={!replyText.trim() || isSubmitting}
                                        className="absolute right-1 top-1/2 -translate-y-1/2 p-1.5 text-primary-500 hover:bg-primary-50 dark:hover:bg-primary-900/20 rounded-lg disabled:opacity-50"
                                    >
                                        <Send className="w-4 h-4" />
                                    </button>
                                </div>
                            </motion.form>
                        )}
                    </AnimatePresence>

                    {/* Nested replies */}
                    {comment.replies?.length > 0 && (
                        <div className="space-y-3 mt-3 relative before:absolute before:left-[-1.5rem] before:top-2 before:bottom-4 before:w-px before:bg-light-border/50 dark:before:bg-dark-border/50 md:before:hidden">
                            {comment.replies.map((reply: any) => (
                                <CommentItem key={reply._id} comment={reply} isReply />
                            ))}
                        </div>
                    )}
                </div>
            </div>
        );
    }

    //  Comments disabled globally 
    if (commentsEnabled === false) {
        return (
            <div className="mt-4 p-4 rounded-lg bg-light-card dark:bg-dark-card text-center">
                <p className="text-sm text-light-muted dark:text-dark-muted">
                    Comments are currently disabled
                </p>
            </div>
        );
    }

    return (
        <div className="mt-4 pt-4 border-t border-light-border dark:border-dark-border space-y-4">

            {/*  Comment Input (or locked state for admin posts)  */}
            {user && (
                <AnimatePresence mode="wait">
                    {alreadyCommented && isAdminPost ? (
                        /* Already commented on an admin post  show locked notice */
                        <motion.div
                            key="locked"
                            initial={{ opacity: 0, y: -6 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0 }}
                            className="flex items-center gap-3 px-4 py-3 rounded-2xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border"
                        >
                            <div className="w-8 h-8 rounded-full bg-green-500/15 flex items-center justify-center flex-shrink-0">
                                <CheckCircle2 className="w-4 h-4 text-green-500" />
                            </div>
                            <div className="flex-1 min-w-0">
                                <p className="text-sm font-semibold text-light-text dark:text-dark-text">
                                    You've already commented 
                                </p>
                                <p className="text-xs text-light-muted dark:text-dark-muted">
                                    Each user can only comment once per admin post to keep things fair. You can still reply to others below.
                                </p>
                            </div>
                            <MessageCircleOff className="w-5 h-5 text-light-muted dark:text-dark-muted flex-shrink-0" />
                        </motion.div>
                    ) : (
                        /* Normal comment input */
                        <motion.form
                            key="input"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            onSubmit={handleSubmit}
                            className="flex items-start gap-2 md:gap-3"
                        >
                            <div className="mt-1 flex-shrink-0">
                                <Avatar src={user.avatarUrl} alt={user.name} size="sm" />
                            </div>
                            <div className="flex-1 min-w-0 relative">
                                <textarea
                                    value={commentText}
                                    onChange={(e) => setCommentText(e.target.value)}
                                    placeholder={
                                        isPrayer
                                            ? 'Join us in praying this prayer...'
                                            : isAdminPost
                                                ? 'Write a comment... (once per post)'
                                                : 'Write a comment...'
                                    }
                                    className={`w-full px-4 py-2.5 pr-12 rounded-2xl border bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text focus:ring-2 focus:border-transparent resize-none overflow-hidden min-h-[44px] max-h-32 text-sm md:text-base transition-all ${isPrayer
                                            ? 'border-purple-200 dark:border-purple-900/30 focus:ring-purple-500 placeholder:text-purple-400/70'
                                            : 'border-light-border dark:border-dark-border focus:ring-primary-500'
                                        }`}
                                    disabled={isSubmitting}
                                    rows={1}
                                    onInput={(e) => {
                                        const t = e.target as HTMLTextAreaElement;
                                        t.style.height = 'auto';
                                        t.style.height = `${t.scrollHeight}px`;
                                    }}
                                />
                                <button
                                    type="submit"
                                    disabled={!commentText.trim() || isSubmitting}
                                    className="absolute right-1.5 bottom-1.5 p-2 rounded-xl bg-primary-500 hover:bg-primary-600 text-white transition-all disabled:opacity-30 disabled:cursor-not-allowed flex items-center justify-center shadow-lg shadow-primary-500/20 active:scale-95"
                                >
                                    {isPrayer
                                        ? <HeartHandshake className="w-4 h-4 md:w-5 md:h-5" />
                                        : <Send className="w-4 h-4 md:w-5 md:h-5" />
                                    }
                                </button>
                            </div>
                        </motion.form>
                    )}
                </AnimatePresence>
            )}

            {/*  Comments List  */}
            {!comments ? (
                <div className="space-y-4">
                    <CommentSkeleton />
                    <CommentSkeleton />
                    <CommentSkeleton />
                </div>
            ) : threadedComments.length > 0 ? (
                <div className="space-y-4">
                    {threadedComments.map((comment) => (
                        <CommentItem key={comment._id} comment={comment} />
                    ))}
                </div>
            ) : (
                <div className="text-center py-8 text-light-muted dark:text-dark-muted">
                    <p>
                        {isPrayer
                            ? 'No prayers yet. Be the first to join in prayer!'
                            : 'No comments yet. Be the first to share your thoughts!'}
                    </p>
                </div>
            )}
        </div>
    );
}
