import { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { useSearchParams } from 'react-router-dom';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Badge } from '@/components/ui/Badge';
import {
    Wallet,
    Users,
    TrendingUp,
    DollarSign,
    CheckCircle,
    Clock,
    XCircle,
    Search,
    Filter,
    Trophy,
    AlertTriangle
} from 'lucide-react';
import { cn } from '@/lib/cn';
import { ConfirmationModal } from '@/components/ui/ConfirmationModal';
import { PremiumSelect } from '@/components/ui/PremiumSelect';
import { AdminPageSkeleton } from '@/components/ui/Skeletons';

type TabType = 'overview' | 'withdrawals' | 'wallets' | 'rewards';

export default function AdminBankingPage() {
    const { user } = useAuth();
    const [searchParams, setSearchParams] = useSearchParams();
    const activeTab = (searchParams.get('tab') as TabType) || 'overview';
    const [searchTerm, setSearchTerm] = useState('');
    const [statusFilter, setStatusFilter] = useState<'all' | 'pending' | 'paid'>('all');

    const setActiveTab = (tab: TabType) => {
        setSearchParams({ tab });
    };

    // Get all wallets for overview
    const allWallets = useQuery(api.wallets.getAllWallets); // We'll need to create this

    // For now, using withdrawals query - we'll need to create admin-specific queries
    const allWithdrawals = useQuery(api.wallets.getAllWithdrawals); // We'll need to create this

    // Get Master Pool stats
    const masterPool = useQuery(api.masterPool.getMasterPool);

    if (allWallets === undefined || allWithdrawals === undefined || masterPool === undefined) {
        return <AdminPageSkeleton />;
    }

    return (
        <div className="space-y-6">
            {/* Admin Header */}
            <div>
                <h1 className="text-3xl font-display font-bold text-light-text dark:text-dark-text mb-2">
                    Banking System Management
                </h1>
                <p className="text-light-muted dark:text-dark-muted">
                    Manage user wallets, withdrawals, and reward distribution
                </p>
            </div>

            {/* Stats Overview Cards */}
            <div className="grid sm:grid-cols-2 lg:grid-cols-4 gap-4">
                <Card glass>
                    <CardContent className="p-6">
                        <div className="flex items-center justify-between mb-2">
                            <Users className="w-5 h-5 text-primary-500" />
                            <span className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {allWallets?.length || 0}
                            </span>
                        </div>
                        <p className="text-sm text-light-muted dark:text-dark-muted">Active Wallets</p>
                    </CardContent>
                </Card>

                <Card glass>
                    <CardContent className="p-6">
                        <div className="flex items-center justify-between mb-2">
                            <Clock className="w-5 h-5 text-yellow-500" />
                            <span className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {allWithdrawals?.filter((w: any) => w.status === 'pending').length || 0}
                            </span>
                        </div>
                        <p className="text-sm text-light-muted dark:text-dark-muted">Pending Withdrawals</p>
                    </CardContent>
                </Card>

                <Card glass>
                    <CardContent className="p-6">
                        <div className="flex items-center justify-between mb-2">
                            <TrendingUp className="w-5 h-5 text-green-500" />
                            <span className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {allWallets?.reduce((sum: number, w: any) => sum + (w.totalRubies || 0), 0).toFixed(0) || 0}
                            </span>
                        </div>
                        <p className="text-sm text-light-muted dark:text-dark-muted">Total Rubies Distributed</p>
                    </CardContent>
                </Card>

                <Card glass>
                    <CardContent className="p-6">
                        <div className="flex items-center justify-between mb-2">
                            <DollarSign className="w-5 h-5 text-amber-500" />
                            <span className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {allWallets?.reduce((sum: number, w: any) => sum + (w.totalTalents || 0), 0).toFixed(2) || 0}
                            </span>
                        </div>
                        <p className="text-sm text-light-muted dark:text-dark-muted">Total Talents</p>
                    </CardContent>
                </Card>
            </div>

            {/* Tabs */}
            <div className="flex gap-2 overflow-x-auto pb-2">
                {[
                    { key: 'overview' as TabType, label: 'Overview', icon: Wallet },
                    { key: 'withdrawals' as TabType, label: 'Withdrawals', icon: DollarSign },
                    { key: 'wallets' as TabType, label: 'User Wallets', icon: Users },
                    { key: 'rewards' as TabType, label: 'Reward Activity', icon: Trophy },
                ].map((tab) => {
                    const Icon = tab.icon;
                    const isActive = activeTab === tab.key;
                    return (
                        <button
                            key={tab.key}
                            onClick={() => setActiveTab(tab.key)}
                            className={cn(
                                'flex items-center gap-2 px-6 py-3 rounded-xl font-semibold transition-all whitespace-nowrap',
                                isActive
                                    ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                                    : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text hover:bg-primary-500/10'
                            )}
                        >
                            <Icon className="w-5 h-5" />
                            {tab.label}
                        </button>
                    );
                })}
            </div>

            {/* Tab Content */}
            <div>
                {activeTab === 'overview' && (
                    <OverviewTab
                        wallets={allWallets}
                        withdrawals={allWithdrawals}
                        masterPool={masterPool}
                    />
                )}
                {activeTab === 'withdrawals' && (
                    <WithdrawalsTab
                        withdrawals={allWithdrawals}
                        statusFilter={statusFilter}
                        setStatusFilter={setStatusFilter}
                        user={user}
                    />
                )}
                {activeTab === 'wallets' && (
                    <WalletsTab
                        wallets={allWallets}
                        searchTerm={searchTerm}
                        setSearchTerm={setSearchTerm}
                    />
                )}
                {activeTab === 'rewards' && <RewardsTab />}
            </div>
        </div>
    );
}

// Overview Tab Component
function OverviewTab({ wallets, withdrawals, masterPool }: { wallets: any; withdrawals: any; masterPool: any }) {
    if (!wallets || !withdrawals || !masterPool) {
        return <div className="text-center py-12 text-light-muted dark:text-dark-muted">Loading...</div>;
    }

    const poolStats = [
        { name: 'Diamond', icon: '', value: masterPool.diamondDistributed, color: 'text-blue-500', bg: 'bg-blue-500/10' },
        { name: 'Gold', icon: '', value: masterPool.goldDistributed, color: 'text-yellow-500', bg: 'bg-yellow-500/10' },
        { name: 'Silver', icon: '', value: masterPool.silverDistributed, color: 'text-gray-500', bg: 'bg-gray-500/10' },
        { name: 'Bronze', icon: '', value: masterPool.bronzeDistributed, color: 'text-orange-500', bg: 'bg-orange-500/10' },
    ];

    return (
        <div className="space-y-6">
            {/* Master Pool Metrics */}
            <div className="grid lg:grid-cols-3 gap-6">
                <Card glass className="lg:col-span-2">
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2">
                            <TrendingUp className="w-5 h-5 text-primary-500" />
                            Master Pool Resource Tracking
                        </CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-6">
                            <div>
                                <div className="flex justify-between text-sm mb-2">
                                    <span className="text-light-muted dark:text-dark-muted">Total Distributed (Engagement)</span>
                                    <span className="font-bold text-light-text dark:text-dark-text">
                                        {masterPool.totalDistributed.toLocaleString()} / {masterPool.totalInitial.toLocaleString()} Rubies
                                    </span>
                                </div>
                                <div className="h-4 bg-light-border dark:bg-dark-border rounded-full overflow-hidden">
                                    <div
                                        className="h-full bg-primary-500 transition-all duration-1000"
                                        style={{ width: `${(masterPool.totalDistributed / masterPool.totalInitial) * 100}%` }}
                                    />
                                </div>
                            </div>

                            <div className="grid grid-cols-2 gap-4">
                                <div className="p-4 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border">
                                    <p className="text-xs text-light-muted dark:text-dark-muted mb-1">Total Withdrawn</p>
                                    <p className="text-xl font-bold text-green-500">{(masterPool.totalWithdrawn * 200).toLocaleString()}</p>
                                    <p className="text-[10px] text-light-muted dark:text-dark-muted mt-1">{masterPool.totalWithdrawn.toLocaleString()} Rubies Paid</p>
                                </div>
                                <div className="p-4 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border">
                                    <p className="text-xs text-light-muted dark:text-dark-muted mb-1">Remaining in Pool</p>
                                    <p className="text-xl font-bold text-primary-500">{masterPool.remaining.toLocaleString()}</p>
                                    <p className="text-[10px] text-light-muted dark:text-dark-muted mt-1">Available for distribution</p>
                                </div>
                            </div>
                        </div>
                    </CardContent>
                </Card>

                <Card glass>
                    <CardHeader>
                        <CardTitle>Pool Engagement</CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        {poolStats.map((pool: any) => (
                            <div key={pool.name} className="flex items-center justify-between p-3 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border">
                                <div className="flex items-center gap-3">
                                    <span className="text-xl">{pool.icon}</span>
                                    <span className="font-semibold text-light-text dark:text-dark-text">{pool.name}</span>
                                </div>
                                <span className={cn("font-bold", pool.color)}>{pool.value.toFixed(1)}</span>
                            </div>
                        ))}
                    </CardContent>
                </Card>
            </div>
            {/* Recent Withdrawals */}
            <Card glass>
                <CardHeader>
                    <CardTitle>Recent Withdrawal Requests</CardTitle>
                </CardHeader>
                <CardContent>
                    {withdrawals && withdrawals.length > 0 ? (
                        <div className="space-y-3">
                            {withdrawals.slice(0, 5).map((w: any) => (
                                <div
                                    key={w._id}
                                    className="flex items-center justify-between p-4 rounded-lg bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border"
                                >
                                    <div className="flex items-center gap-3">
                                        <div className="w-10 h-10 rounded-full overflow-hidden bg-primary-500/10 flex items-center justify-center shrink-0 border border-primary-500/20">
                                            {w.user?.avatarUrl ? (
                                                <img src={w.user.avatarUrl} alt={w.user.name} className="w-full h-full object-cover" />
                                            ) : (
                                                <span className="font-bold text-primary-500">{(w.user?.name || '?')[0]}</span>
                                            )}
                                        </div>
                                        <div>
                                            <p className="font-semibold text-light-text dark:text-dark-text capitalize">
                                                {w.user?.name || 'Unknown User'}
                                            </p>
                                            <p className="text-[10px] text-light-muted dark:text-dark-muted">
                                                {w.type.replace('_', ' ')}  {new Date(w.createdAt).toLocaleDateString()}
                                            </p>
                                        </div>
                                    </div>
                                    <div className="text-right flex items-center gap-3">
                                        <div>
                                            <p className="font-bold text-light-text dark:text-dark-text">
                                                {w.amountRubies} Rubies
                                            </p>
                                            <p className="text-xs text-light-muted dark:text-dark-muted">
                                                {w.nairaAmount || 0}
                                            </p>
                                        </div>
                                        <Badge
                                            variant={w.status === 'paid' ? 'success' : w.status === 'pending' ? 'warning' : 'default'}
                                        >
                                            {w.status}
                                        </Badge>
                                    </div>
                                </div>
                            ))}
                        </div>
                    ) : (
                        <p className="text-center py-8 text-light-muted dark:text-dark-muted">
                            No withdrawal requests yet
                        </p>
                    )}
                </CardContent>
            </Card>
        </div>
    );
}

// Withdrawals Tab Component
function WithdrawalsTab({ withdrawals, statusFilter, setStatusFilter, user }: any) {
    const approveWithdrawal = useMutation(api.wallets.approveWithdrawal);
    const rejectWithdrawal = useMutation(api.wallets.rejectWithdrawal);

    // Modal State
    const [modalConfig, setModalConfig] = useState<{
        isOpen: boolean;
        type: 'approve' | 'reject' | null;
        withdrawalId: string | null;
        amount: number;
    }>({
        isOpen: false,
        type: null,
        withdrawalId: null,
        amount: 0
    });

    if (!withdrawals) {
        return <div className="text-center py-12 text-light-muted dark:text-dark-muted">Loading withdrawals...</div>;
    }

    const filteredWithdrawals = statusFilter === 'all'
        ? withdrawals
        : withdrawals.filter((w: any) => w.status === statusFilter);

    const handleActionClick = (type: 'approve' | 'reject', withdrawalId: string, amount: number) => {
        setModalConfig({
            isOpen: true,
            type,
            withdrawalId,
            amount
        });
    };

    const handleConfirmAction = async () => {
        if (!modalConfig.withdrawalId || !modalConfig.type) return;

        try {
            if (modalConfig.type === 'approve' && user) {
                await approveWithdrawal({
                    withdrawalId: modalConfig.withdrawalId as any,
                    adminId: user._id
                });
            } else {
                await rejectWithdrawal({ withdrawalId: modalConfig.withdrawalId as any });
            }
        } catch (error: any) {
            console.error("Action failed:", error);
            // In a real app, use a toast here
        } finally {
            setModalConfig({ ...modalConfig, isOpen: false });
        }
    };

    return (
        <div className="space-y-4">
            {/* Filter Bar */}
            <Card glass>
                <CardContent className="p-4">
                    <div className="flex items-center gap-4">
                        <Filter className="w-5 h-5 text-light-muted dark:text-dark-muted" />
                        <div className="w-48">
                            <PremiumSelect
                                value={statusFilter}
                                onChange={(val: any) => setStatusFilter(val)}
                                options={[
                                    { value: "all", label: "All Statuses" },
                                    { value: "pending", label: "Pending" },
                                    { value: "paid", label: "Paid" },
                                    { value: "rejected", label: "Rejected" }
                                ]}
                            />
                        </div>
                        <span className="text-sm text-light-muted dark:text-dark-muted border-l border-light-border dark:border-dark-border pl-4 ml-2">
                            {filteredWithdrawals.length} request{filteredWithdrawals.length !== 1 ? 's' : ''}
                        </span>
                    </div>
                </CardContent>
            </Card>

            {/* Withdrawals List */}
            <div className="space-y-3">
                {filteredWithdrawals.length > 0 ? (
                    filteredWithdrawals.map((w: any) => (
                        <Card key={w._id} glass>
                            <CardContent className="p-5">
                                <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
                                    <div className="flex-1">
                                        <div className="flex items-center gap-3 mb-2">
                                            <div className="w-12 h-12 rounded-full overflow-hidden bg-primary-500/10 flex items-center justify-center shrink-0 border-2 border-primary-500/20 shadow-sm">
                                                {w.user?.avatarUrl ? (
                                                    <img src={w.user.avatarUrl} alt={w.user.name} className="w-full h-full object-cover" />
                                                ) : (
                                                    <span className="text-lg font-bold text-primary-500">{(w.user?.name || '?')[0]}</span>
                                                )}
                                            </div>
                                            <div>
                                                <p className="font-bold text-lg text-light-text dark:text-dark-text">
                                                    {w.user?.name || 'Unknown User'}
                                                </p>
                                                <div className="flex items-center gap-2">
                                                    <Badge variant={w.status === 'paid' ? 'success' : w.status === 'pending' ? 'warning' : 'danger'}>
                                                        {w.status}
                                                    </Badge>
                                                    <span className="text-xs text-light-muted dark:text-dark-muted uppercase font-bold tracking-tight">
                                                        {w.type === 'big_pull' ? ' Big Pull' : ' Winner'}
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-y-2 gap-x-8 text-sm bg-light-bg/30 dark:bg-dark-bg/30 p-4 rounded-2xl border border-light-border dark:border-dark-border mt-3">
                                            <p className="text-light-muted dark:text-dark-muted">
                                                <span className="font-bold text-[10px] uppercase text-primary-500 block mb-0.5">Contact</span>
                                                <span className="font-medium text-light-text dark:text-dark-text">{w.user?.email || 'No Email'}</span>
                                            </p>
                                            <p className="text-light-muted dark:text-dark-muted">
                                                <span className="font-bold text-[10px] uppercase text-primary-500 block mb-0.5">Bank Information</span>
                                                <span className="font-semibold text-light-text dark:text-dark-text">{w.bankName}  {w.accountNumber}</span>
                                            </p>
                                            <p className="text-light-muted dark:text-dark-muted">
                                                <span className="font-bold text-[10px] uppercase text-primary-500 block mb-0.5">Asset Summary</span>
                                                <span className="font-bold text-emerald-500">{w.amountRubies} Rubies</span>
                                                <span className="mx-2 text-light-muted">/</span>
                                                <span className="font-black text-light-text dark:text-dark-text">{w.nairaAmount?.toLocaleString()}</span>
                                            </p>
                                        </div>
                                    </div>

                                    {w.status === 'pending' && (
                                        <div className="flex gap-3">
                                            <Button
                                                onClick={() => handleActionClick('approve', w._id, w.amountRubies)}
                                                variant="primary"
                                                size="sm"
                                                className="bg-green-500 hover:bg-green-600 border-none shadow-sm shadow-green-500/20"
                                            >
                                                <CheckCircle className="w-4 h-4 mr-1.5" />
                                                Approve
                                            </Button>
                                            <Button
                                                onClick={() => handleActionClick('reject', w._id, w.amountRubies)}
                                                variant="outline"
                                                size="sm"
                                                className="text-red-500 border-red-500 hover:bg-red-500/10 hover:text-red-600 hover:border-red-600"
                                            >
                                                <XCircle className="w-4 h-4 mr-1.5" />
                                                Reject
                                            </Button>
                                        </div>
                                    )}
                                </div>
                            </CardContent>
                        </Card>
                    ))
                ) : (
                    <Card glass>
                        <CardContent className="p-12 text-center">
                            <AlertTriangle className="w-12 h-12 text-light-muted dark:text-dark-muted mx-auto mb-3 opacity-50" />
                            <p className="text-light-muted dark:text-dark-muted">No {statusFilter !== 'all' ? statusFilter : ''} withdrawals found</p>
                        </CardContent>
                    </Card>
                )}
            </div>

            <ConfirmationModal
                isOpen={modalConfig.isOpen}
                onClose={() => setModalConfig({ ...modalConfig, isOpen: false })}
                onConfirm={handleConfirmAction}
                title={modalConfig.type === 'approve' ? 'Approve Withdrawal' : 'Reject Withdrawal'}
                description={
                    modalConfig.type === 'approve'
                        ? `Are you sure you want to approve this withdrawal of ${modalConfig.amount} Rubies? This will mark it as paid.`
                        : `Are you sure you want to reject this withdrawal of ${modalConfig.amount} Rubies? The amount will be refunded to the user's wallet.`
                }
                confirmText={modalConfig.type === 'approve' ? 'Approve & Pay' : 'Reject & Refund'}
                variant={modalConfig.type === 'reject' ? 'danger' : 'primary'}
            />
        </div>
    );
}

// Wallets Tab Component
function WalletsTab({ wallets, searchTerm, setSearchTerm }: any) {
    if (!wallets) {
        return <div className="text-center py-12 text-light-muted dark:text-dark-muted">Loading wallets...</div>;
    }

    const filteredWallets = wallets.filter((w: any) =>
        (w.user?.name || "").toLowerCase().includes(searchTerm.toLowerCase()) ||
        (w.user?.email || "").toLowerCase().includes(searchTerm.toLowerCase())
    );

    return (
        <div className="space-y-4">
            {/* Search Bar */}
            <Card glass>
                <CardContent className="p-4">
                    <div className="flex items-center gap-2">
                        <Search className="w-5 h-5 text-light-muted dark:text-dark-muted" />
                        <input
                            type="text"
                            placeholder="Search by name or email..."
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                            className="flex-1 px-4 py-2 rounded-lg bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border text-light-text dark:text-dark-text"
                        />
                    </div>
                </CardContent>
            </Card>

            {/* Wallets List */}
            <div className="space-y-3">
                {filteredWallets.length > 0 ? (
                    filteredWallets.map((w: any) => (
                        <Card key={w._id} glass>
                            <CardContent className="p-5">
                                <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
                                    <div className="flex-1">
                                        <div className="flex items-center gap-4 mb-4">
                                            <div className="w-14 h-14 rounded-full overflow-hidden bg-primary-500/10 flex items-center justify-center border-2 border-primary-500/20 shadow-md transform hover:scale-105 transition-transform">
                                                {w.user?.avatarUrl ? (
                                                    <img src={w.user.avatarUrl} alt={w.user.name} className="w-full h-full object-cover" />
                                                ) : (
                                                    <span className="text-xl font-bold text-primary-500">{(w.user?.name || '?')[0]}</span>
                                                )}
                                            </div>
                                            <div>
                                                <p className="font-black text-xl text-light-text dark:text-dark-text leading-tight">
                                                    {w.user?.name || 'Unknown User'}
                                                </p>
                                                <p className="text-xs text-light-muted dark:text-dark-muted font-medium">
                                                    {w.user?.email || 'N/A'}
                                                </p>
                                            </div>
                                            {/* Safeguard: Ensure w has the property before checking */}
                                            {['diamond', 'gold', 'silver', 'bronze'].some(
                                                (p) => (w[`${p}MonthlyEarned`] ?? 0) >= 21.6
                                            ) && (
                                                    <Badge className="bg-orange-500 text-white animate-pulse border-none shadow-lg shadow-orange-500/20">
                                                        CAP ALERT
                                                    </Badge>
                                                )}
                                        </div>
                                        <div className="grid grid-cols-2 lg:grid-cols-4 gap-3 text-sm">
                                            {['diamond', 'gold', 'silver', 'bronze'].map((pool: string) => {
                                                const earned = w[`${pool}MonthlyEarned`] ?? 0;
                                                return (
                                                    <div key={pool} className={cn(
                                                        "p-3 rounded-xl border transition-colors",
                                                        earned >= 21.6
                                                            ? "bg-red-500/10 border-red-500/30"
                                                            : "bg-light-card dark:bg-dark-card border-light-border dark:border-dark-border"
                                                    )}>
                                                        <p className="text-[10px] text-light-muted dark:text-dark-muted uppercase font-bold">
                                                            {pool} Monthly
                                                        </p>
                                                        <p className={cn(
                                                            "text-lg font-bold",
                                                            earned >= 21.6 ? "text-red-500" : "text-light-text dark:text-dark-text"
                                                        )}>
                                                            {earned.toFixed(1)} / 24
                                                        </p>
                                                    </div>
                                                )
                                            })}
                                        </div>
                                    </div>
                                    <div className="text-right p-4 rounded-2xl bg-primary-500/5 border border-primary-500/10 min-w-[150px]">
                                        <p className="text-xs text-primary-500 font-bold uppercase mb-1">Total Assets</p>
                                        <p className="text-2xl font-bold text-light-text dark:text-dark-text">
                                            {w.totalTalents.toFixed(2)} <span className="text-sm font-normal text-light-muted">Talents</span>
                                        </p>
                                        <p className="text-sm font-medium text-green-500 mt-1">
                                            {(w.actualizedNairaBalance || 0).toLocaleString()} Paid
                                        </p>
                                    </div>
                                </div>
                            </CardContent>
                        </Card>
                    ))
                ) : (
                    <Card glass>
                        <CardContent className="p-12 text-center">
                            <Search className="w-12 h-12 text-light-muted dark:text-dark-muted mx-auto mb-3" />
                            <p className="text-light-muted dark:text-dark-muted">No wallets found</p>
                        </CardContent>
                    </Card>
                )}
            </div>
        </div>
    );
}

