import { motion } from 'framer-motion';
import { BackButton } from '@/components/ui/BackButton';
import { Mail, MessageSquare, HelpCircle } from 'lucide-react';
import { useState } from 'react';
import { useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';

export function Contact() {
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        subject: '',
        message: ''
    });
    const [submitted, setSubmitted] = useState(false);
    const [submitting, setSubmitting] = useState(false);
    const submitContact = useMutation(api.contacts.submitContact);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setSubmitting(true);
        try {
            await submitContact(formData);
            setSubmitted(true);
            setFormData({ name: '', email: '', subject: '', message: '' });
            setTimeout(() => setSubmitted(false), 3000);
        } catch (error) {
            console.error('Failed to submit contact:', error);
        } finally {
            setSubmitting(false);
        }
    };

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <BackButton />

                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mt-6 space-y-8"
                >
                    <div className="text-center">
                        <h1 className="text-3xl md:text-5xl font-bold text-primary-600 dark:text-primary-400 mb-4">
                            Contact Us
                        </h1>
                        <p className="text-lg text-light-text dark:text-dark-text">
                            We'd love to hear from you
                        </p>
                    </div>

                    <div className="grid md:grid-cols-3 gap-4 mb-8">
                        <div className="glass rounded-xl p-6 text-center hover:shadow-lg transition-shadow">
                            <Mail className="w-10 h-10 text-primary-500 mx-auto mb-3" />
                            <h3 className="font-semibold text-light-text dark:text-dark-text mb-2">Email</h3>
                            <p className="text-sm text-light-muted dark:text-dark-muted">
                                support@baptizo.com
                            </p>
                        </div>
                        <div className="glass rounded-xl p-6 text-center hover:shadow-lg transition-shadow">
                            <MessageSquare className="w-10 h-10 text-secondary-500 mx-auto mb-3" />
                            <h3 className="font-semibold text-light-text dark:text-dark-text mb-2">Community</h3>
                            <p className="text-sm text-light-muted dark:text-dark-muted">
                                Ask in the Feed
                            </p>
                        </div>
                        <div className="glass rounded-xl p-6 text-center hover:shadow-lg transition-shadow">
                            <HelpCircle className="w-10 h-10 text-accent-500 mx-auto mb-3" />
                            <h3 className="font-semibold text-light-text dark:text-dark-text mb-2">FAQ</h3>
                            <p className="text-sm text-light-muted dark:text-dark-muted">
                                Check our FAQ page
                            </p>
                        </div>
                    </div>

                    {/* Contact Form */}
                    <div className="glass rounded-xl p-6 md:p-8">
                        <h2 className="text-2xl font-bold text-light-text dark:text-dark-text mb-6">
                            Send us a message
                        </h2>

                        {submitted ? (
                            <div className="glass rounded-lg p-6 bg-secondary-50/50 dark:bg-secondary-900/10 border border-secondary-500/20 text-center">
                                <h3 className="text-lg font-semibold text-secondary-600 dark:text-secondary-400 mb-2">
                                    Message Sent!
                                </h3>
                                <p className="text-light-muted dark:text-dark-muted">
                                    We'll get back to you as soon as possible.
                                </p>
                            </div>
                        ) : (
                            <form onSubmit={handleSubmit} className="space-y-4">
                                <div className="grid md:grid-cols-2 gap-4">
                                    <div>
                                        <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-2">
                                            Name
                                        </label>
                                        <input
                                            type="text"
                                            required
                                            value={formData.name}
                                            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                                            className="w-full px-4 py-2 rounded-lg border border-light-border dark:border-dark-border bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none"
                                            placeholder="Your name"
                                        />
                                    </div>
                                    <div>
                                        <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-2">
                                            Email
                                        </label>
                                        <input
                                            type="email"
                                            required
                                            value={formData.email}
                                            onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                                            className="w-full px-4 py-2 rounded-lg border border-light-border dark:border-dark-border bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none"
                                            placeholder="your@email.com"
                                        />
                                    </div>
                                </div>

                                <div>
                                    <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-2">
                                        Subject
                                    </label>
                                    <input
                                        type="text"
                                        required
                                        value={formData.subject}
                                        onChange={(e) => setFormData({ ...formData, subject: e.target.value })}
                                        className="w-full px-4 py-2 rounded-lg border border-light-border dark:border-dark-border bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none"
                                        placeholder="What's this about?"
                                    />
                                </div>

                                <div>
                                    <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-2">
                                        Message
                                    </label>
                                    <textarea
                                        required
                                        value={formData.message}
                                        onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                                        rows={6}
                                        className="w-full px-4 py-2 rounded-lg border border-light-border dark:border-dark-border bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none resize-none"
                                        placeholder="Tell us more..."
                                    />
                                </div>

                                <button
                                    type="submit"
                                    disabled={submitting}
                                    className="w-full md:w-auto px-8 py-3 rounded-lg bg-primary-500 hover:bg-primary-600 disabled:bg-primary-300 text-white font-semibold transition-colors shadow-lg shadow-primary-500/20"
                                >
                                    {submitting ? 'Sending...' : 'Send Message'}
                                </button>
                            </form>
                        )}
                    </div>
                </motion.div>
            </div>
        </div>
    );
}
