import { useState } from 'react';
import { useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Button } from '@/components/ui/Button';
import { Card, CardContent } from '@/components/ui/Card'; // Assuming these exist
import { Shield, CheckCircle, AlertTriangle } from 'lucide-react';


function SuperAdminSetup() {
    const { user } = useAuth();
    const claimSuperAdmin = useMutation(api.users.claimSuperAdmin);


    const [status, setStatus] = useState<'idle' | 'loading' | 'success' | 'error'>('idle');
    const [errorMessage, setErrorMessage] = useState('');

    const handleClaim = async () => {
        if (!user) return;
        setStatus('loading');
        try {
            await claimSuperAdmin({ userId: user._id });
            setStatus('success');
            // update local user state if needed, or wait for auth refresh?
            // Force reload might be needed to refresh auth context role from DB if it pulls fresh on mount?
            // Actually useAuth might listen to something? Or localStorage.
            // AuthContext relies on localStorage. We need to update user context manually or force re-login.
            // But mutation updates DB. The AuthContext usually doesn't auto-sync role unless we fetch `me` query.
            // We should reload.
            setTimeout(() => {
                window.location.href = '/admin';
            }, 2000);
        } catch (error: any) {
            setStatus('error');
            setErrorMessage(error.message || "Failed to claim super admin.");
        }
    };

    if (!user) {
        return <div className="p-8 text-center">Please login first.</div>;
    }

    return (
        <div className="min-h-screen flex items-center justify-center p-4">
            <Card className="max-w-md w-full bg-light-card dark:bg-dark-card border-light-border dark:border-dark-border">
                <CardContent className="p-8 text-center space-y-6">
                    <div className="flex justify-center mb-4">
                        <img
                            src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/Gemini_Generated_Image_zcvetszcvetszcve%20(1).png"
                            alt="Baptizo"
                            className="h-20 w-auto rounded-3xl shadow-2xl"
                        />
                    </div>
                    <div className="mx-auto w-12 h-12 bg-primary-100 dark:bg-primary-900/30 rounded-full flex items-center justify-center">
                        <Shield className="w-6 h-6 text-primary-500" />
                    </div>

                    <div>
                        <h1 className="text-2xl font-bold text-light-text dark:text-dark-text mb-2">
                            Super Admin Setup
                        </h1>
                        <p className="text-light-muted dark:text-dark-muted">
                            Initialize the system by claiming the first Super Admin role.
                            This is a one-time setup step.
                        </p>
                    </div>

                    {status === 'idle' && (
                        <Button
                            variant="primary"
                            size="lg"
                            className="w-full"
                            onClick={handleClaim}
                        >
                            Claim Super Admin Role
                        </Button>
                    )}

                    {status === 'loading' && (
                        <div className="flex justify-center">
                            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-500" />
                        </div>
                    )}

                    {status === 'success' && (
                        <div className="bg-green-50 dark:bg-green-900/20 text-green-600 dark:text-green-400 p-4 rounded-lg flex flex-col items-center gap-2">
                            <CheckCircle className="w-6 h-6" />
                            <p className="font-medium">Success! Redirecting...</p>
                        </div>
                    )}

                    {status === 'error' && (
                        <div className="bg-red-50 dark:bg-red-900/20 text-red-600 dark:text-red-400 p-4 rounded-lg flex flex-col items-center gap-2">
                            <AlertTriangle className="w-6 h-6" />
                            <p className="font-medium">{errorMessage}</p>
                            <Button
                                variant="outline"
                                size="sm"
                                onClick={() => setStatus('idle')}
                                className="mt-2"
                            >
                                Try Again
                            </Button>
                        </div>
                    )}
                </CardContent>
            </Card>
        </div>
    );
}

export default SuperAdminSetup;
