import React, { useState } from 'react';
import { useNavigate, Link, useLocation } from 'react-router-dom';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Input } from '@/components/ui/Input';
import { Button } from '@/components/ui/Button';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Mail, Lock } from 'lucide-react';
import { motion } from 'framer-motion';

export function LoginForm() {
    const navigate = useNavigate();
    const location = useLocation();
    const { login } = useAuth();
    const loginUser = useMutation(api.auth.loginUser);

    const [formData, setFormData] = useState({
        email: '',
        password: '',
    });
    const [errors, setErrors] = useState<Record<string, string>>({});
    const [isLoading, setIsLoading] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setErrors({});
        setIsLoading(true);

        try {
            const result = await loginUser({
                email: formData.email.toLowerCase().trim(),
                password: formData.password,
            });

            if (result?.success && result?.user) {
                login(result.user);
                const from = (location.state as any)?.from?.pathname || '/feed';
                navigate(from, { replace: true });
            } else {
                setErrors({ general: 'Invalid email or password' });
            }
        } catch (error) {
            setErrors({ general: 'Login failed. Please try again.' });
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="w-full max-w-md mx-auto"
        >
            <Card glass>
                <CardHeader className="pt-8">
                    <div className="flex justify-center mb-4">
                        <img
                            src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/premium-logo.png"
                            alt="Baptizo"
                            className="h-16 w-auto rounded-2xl shadow-xl"
                        />
                    </div>
                    <CardTitle className="text-center text-3xl">Welcome Back</CardTitle>
                    <p className="text-center text-light-muted dark:text-dark-muted mt-2">
                        Sign in to continue to Baptizo Community
                    </p>
                </CardHeader>
                <CardContent>
                    <form onSubmit={handleSubmit} className="space-y-4">
                        {errors.general && (
                            <div className="p-3 rounded-lg bg-red-50 dark:bg-red-950 border border-red-200 dark:border-red-800">
                                <p className="text-sm text-red-700 dark:text-red-300">{errors.general}</p>
                            </div>
                        )}

                        <Input
                            label="Email"
                            type="email"
                            value={formData.email}
                            onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                            icon={<Mail className="w-5 h-5" />}
                            placeholder="your@email.com"
                            required
                        />

                        <Input
                            label="Password"
                            type="password"
                            value={formData.password}
                            onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                            icon={<Lock className="w-5 h-5" />}
                            placeholder=""
                            required
                        />

                        <Button
                            type="submit"
                            variant="primary"
                            className="w-full"
                            isLoading={isLoading}
                        >
                            Sign In
                        </Button>

                        <p className="text-center text-sm text-light-muted dark:text-dark-muted">
                            Don't have an account?{' '}
                            <Link
                                to="/register"
                                state={{ from: (location.state as any)?.from }}
                                className="text-primary-500 hover:text-primary-600 font-medium"
                            >
                                Sign Up
                            </Link>
                        </p>
                    </form>
                </CardContent>
            </Card>
        </motion.div>
    );
}
