// Format account number to mask sensitive digits
export const formatAccountNumber = (accountNumber: string): string => {
    if (!accountNumber) return '';

    // Show only last 4 digits
    if (accountNumber.length <= 4) return accountNumber;

    const lastFour = accountNumber.slice(-4);
    const masked = '*'.repeat(accountNumber.length - 4);
    return masked + lastFour;
};

// Basic validation for account number (10 digits for Nigerian banks)
export const validateAccountNumber = (accountNumber: string): boolean => {
    // Remove any spaces or dashes
    const cleaned = accountNumber.replace(/[\s-]/g, '');

    // Must be exactly 10 digits for most Nigerian banks
    return /^\d{10}$/.test(cleaned);
};

// Validate bank name
export const validateBankName = (bankName: string): boolean => {
    // Bank name should not be empty and should be reasonable length
    return bankName.trim().length >= 2 && bankName.trim().length <= 100;
};

// Validate account holder name
export const validateAccountName = (accountName: string): boolean => {
    // Account name should not be empty and should be reasonable length
    return accountName.trim().length >= 2 && accountName.trim().length <= 100;
};

