import { useState } from 'react';
import { motion } from 'framer-motion';
import { BadgeCheck, Crown, Eye, EyeOff, Trash2, ArrowRight } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Id } from '../../../convex/_generated/dataModel';
import { useNavigate } from 'react-router-dom';

interface AnnouncementCardProps {
    id: Id<"announcements">;
    title: string;
    description: string;
    imageUrl: string;
    creatorRole?: string;
    isActive: boolean;
    category?: string;
}

export function AnnouncementCard({ id, title, description, imageUrl, creatorRole = 'admin', isActive, category }: AnnouncementCardProps) {
    const { user } = useAuth();
    const navigate = useNavigate();
    const updateAnnouncement = useMutation(api.announcements.updateAnnouncement);
    const deleteAnnouncement = useMutation(api.announcements.deleteAnnouncement);
    const [isDeleting, setIsDeleting] = useState(false);

    const isSuperAdmin = creatorRole === 'super_admin';
    const canManage = user && (user.role === 'admin' || user.role === 'super_admin');

    const handleToggleVisibility = async (e: React.MouseEvent) => {
        e.stopPropagation();
        if (!canManage) return;
        try {
            await updateAnnouncement({
                adminId: user._id,
                announcementId: id,
                isActive: !isActive
            });
        } catch (error) {
            console.error("Failed to toggle visibility:", error);
        }
    };

    const handleDelete = async (e: React.MouseEvent) => {
        e.stopPropagation();
        if (!canManage || !window.confirm("Are you sure you want to delete this announcement?")) return;
        setIsDeleting(true);
        try {
            await deleteAnnouncement({
                adminId: user._id,
                announcementId: id
            });
        } catch (error) {
            console.error("Failed to delete announcement:", error);
            setIsDeleting(false);
        }
    };

    if (isDeleting) return null;

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: isActive ? 1 : 0.6, y: 0 }}
            onClick={() => navigate(`/announcement/${id}`)}
            className={`glass rounded-2xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-300 border-2 relative group cursor-pointer ${isSuperAdmin
                ? 'border-secondary-500/50 shadow-secondary-500/10'
                : 'border-transparent'
                } ${!isActive ? 'grayscale' : ''}`}
        >
            {/* Admin Controls Overlay */}
            {canManage && (
                <div className="absolute top-4 right-4 z-20 flex items-center gap-2 opacity-0 group-hover:opacity-100 transition-opacity">
                    <button
                        onClick={handleToggleVisibility}
                        className="p-2 rounded-full bg-black/50 text-white hover:bg-black/70 backdrop-blur-md transition-colors"
                        title={isActive ? "Hide from users" : "Show to users"}
                    >
                        {isActive ? <Eye className="w-4 h-4" /> : <EyeOff className="w-4 h-4" />}
                    </button>
                    <button
                        onClick={handleDelete}
                        className="p-2 rounded-full bg-red-500/80 text-white hover:bg-red-600 backdrop-blur-md transition-colors"
                        title="Delete Announcement"
                    >
                        <Trash2 className="w-4 h-4" />
                    </button>
                </div>
            )}

            {/* Hidden Badge for Admins */}
            {!isActive && canManage && (
                <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 z-20 bg-black/80 text-white px-4 py-2 rounded-lg font-bold backdrop-blur-md border border-white/20">
                    HIDDEN
                </div>
            )}

            {/* Image */}
            <div className="relative h-48 md:h-60 overflow-hidden">
                <img
                    src={imageUrl}
                    alt={title}
                    className="w-full h-full object-cover transition-transform duration-500 group-hover:scale-110"
                />
                <div className={`absolute inset-0 bg-gradient-to-t ${isSuperAdmin ? 'from-secondary-900/80' : 'from-black/60'
                    } to-transparent`} />

                {/* Badge */}
                <div className="absolute bottom-3 left-3 md:bottom-4 md:left-4 flex items-center gap-2">
                    {isSuperAdmin ? (
                        <span className="px-2 py-0.5 md:px-3 md:py-1 rounded-full bg-secondary-500 text-white text-[9px] md:text-[10px] font-bold flex items-center gap-1 md:gap-1.5 shadow-lg">
                            <Crown className="w-2.5 h-2.5 md:w-3 md:h-3 fill-current" />
                            OFFICIAL ANNOUNCEMENT
                        </span>
                    ) : (
                        <span className="px-2 py-0.5 md:px-3 md:py-1 rounded-full bg-primary-500 text-white text-[9px] md:text-[10px] font-bold flex items-center gap-1 md:gap-1.5 shadow-lg">
                            <BadgeCheck className="w-2.5 h-2.5 md:w-3 md:h-3" />
                            ADMIN UPDATE
                        </span>

                    )}
                    {category && category !== 'General' && (
                        <span className="px-2 py-0.5 md:px-3 md:py-1 rounded-full bg-blue-500 text-white text-[9px] md:text-[10px] font-bold shadow-lg uppercase">
                            {category}
                        </span>
                    )}
                </div>
            </div>

            {/* Content */}
            <div className={`p-4 md:p-6 ${isSuperAdmin ? 'bg-secondary-500/5 dark:bg-secondary-500/5' : ''}`}>
                <h3 className={`text-lg md:text-2xl font-black mb-2 md:mb-3 font-display tracking-tight group-hover:text-primary-500 transition-colors ${isSuperAdmin ? 'text-secondary-600 dark:text-secondary-400' : 'text-light-text dark:text-dark-text'
                    }`}>
                    {title}
                </h3>
                <p className="text-light-muted dark:text-dark-muted leading-relaxed line-clamp-2 text-xs md:text-sm">
                    {description}
                </p>
                <div className="mt-4 md:mt-6 pt-3 md:pt-4 border-t border-light-border/20 dark:border-dark-border/20 flex items-center justify-between">
                    <span className="text-[9px] md:text-[10px] font-bold text-light-muted/60 uppercase tracking-widest italic">Read More Details</span>
                    <div className="w-6 h-6 md:w-8 md:h-8 rounded-full bg-primary-500/10 flex items-center justify-center text-primary-500 group-hover:translate-x-1 transition-transform">
                        <ArrowRight className="w-3 h-3 md:w-4 md:h-4" />
                    </div>
                </div>
            </div>
        </motion.div >
    );
}
