import { useState, useEffect, useRef } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Button } from '@/components/ui/Button';
import { Card, CardContent } from '@/components/ui/Card';
import { Brain, Gem, Trophy, Clock } from 'lucide-react';
import { motion } from 'framer-motion';
import { useNavigate } from 'react-router-dom';
import { cn } from '@/lib/cn';
import { useAuth } from '@/hooks/useAuth';

export function WordUnderPressure() {
    const { user } = useAuth();
    const navigate = useNavigate();
    const statusData = useQuery(api.wordUnderPressure.getActivePassageStatus, user ? { userId: user._id } : "skip");
    const startAttempt = useMutation(api.wordUnderPressure.startAttempt);
    const getQuestion = useMutation(api.wordUnderPressure.getQuestion);
    const submitAnswer = useMutation(api.wordUnderPressure.submitAnswer);

    const [status, setStatus] = useState<'loading' | 'landing' | 'viewing' | 'answering' | 'result' | 'error' | 'explaining'>('loading');
    const [errorMsg, setErrorMsg] = useState('');
    const [explanation, setExplanation] = useState('');
    const explanationRef = useRef('');
    const [explanationTimer, setExplanationTimer] = useState(20);
    const [timeLeft, setTimeLeft] = useState(0);
    const [textLines, setTextLines] = useState<string[]>([]);
    const [question, setQuestion] = useState('');
    const [options, setOptions] = useState<{ id: string, text: string }[]>([]);
    const [selectedOption, setSelectedOption] = useState<string | null>(null);
    const [result, setResult] = useState<{ isCorrect: boolean, participationRuby: number } | null>(null);
    const [attemptId, setAttemptId] = useState<any>(null);

    const timerRef = useRef<any>(null);

    const [initialLoad, setInitialLoad] = useState(true);
    const submittingRef = useRef(false);

    // Initial Status Check
    useEffect(() => {
        if (statusData !== undefined && initialLoad) {
            setStatus('landing');
            setInitialLoad(false);
        }
    }, [statusData, initialLoad]);

    // Anti-Cheat: Visibility Check
    useEffect(() => {
        const handleVisibilityChange = () => {
            if (document.hidden && (status === 'viewing' || status === 'answering')) {
                // User switched tabs/app
                failAttempt("Focus lost. Challenge failed.");
            }
        };

        document.addEventListener('visibilitychange', handleVisibilityChange);
        return () => {
            document.removeEventListener('visibilitychange', handleVisibilityChange);
        };
    }, [status]);

    const failAttempt = async (reason: string) => {
        if (submittingRef.current) return;
        submittingRef.current = true;

        setStatus('error');
        setErrorMsg(reason);
        if (timerRef.current) clearInterval(timerRef.current);
        if (attemptId) {
            try {
                await submitAnswer({ attemptId, selectedOptionId: "failed_focus_lost", userId: user?._id });
            } catch (e) {
                console.error("Failed to report failure", e);
            }
        }
    };

    const handleStart = async () => {
        if (!statusData?.hasActivePassage || !user) return;
        if (timerRef.current) clearInterval(timerRef.current);

        submittingRef.current = false;
        try {
            const res = await startAttempt({ userId: user._id });
            setAttemptId(res.attemptId);
            setTextLines(res.textLines);
            setTimeLeft(res.displayTime);
            setStatus('viewing');

            // Start Display Timer
            timerRef.current = setInterval(() => {
                setTimeLeft((prev) => {
                    if (prev <= 1) {
                        clearInterval(timerRef.current!);
                        handleFetchQuestion(res.attemptId);
                        return 0;
                    }
                    return prev - 1;
                });
            }, 1000);

        } catch (err: any) {
            setErrorMsg(err.message || "Failed to start");
            setStatus('error');
        }
    };

    const handleFetchQuestion = async (id: any) => {
        if (timerRef.current) clearInterval(timerRef.current);
        setTextLines([]);

        try {
            setStatus('loading');
            await new Promise(r => setTimeout(r, 800)); // slightly longer delay to be safe

            const res = await getQuestion({ attemptId: id, userId: user?._id });
            setQuestion(res.question);
            setOptions(res.options);
            setTimeLeft(res.timeLimit);
            setStatus('answering');
            submittingRef.current = false; // Allow answer submission

            // Start Question Timer
            timerRef.current = setInterval(() => {
                setTimeLeft((prev) => {
                    if (prev <= 1) {
                        clearInterval(timerRef.current!);
                        handleTimeout(id);
                        return 0;
                    }
                    return prev - 1;
                });
            }, 1000);

        } catch (err: any) {
            setErrorMsg(err.message || "Failed to fetch question");
            setStatus('error');
        }
    };

    const handleTimeout = async (id: any) => {
        if (submittingRef.current || status !== 'answering') return;
        submittingRef.current = true;

        if (timerRef.current) clearInterval(timerRef.current);
        setStatus('error');
        setErrorMsg("Time expired.");
        try {
            await submitAnswer({ attemptId: id, selectedOptionId: "timeout", userId: user?._id });
        } catch (e) { }
    };
    const handleAnswer = async (optionId: string) => {
        if (submittingRef.current || status !== 'answering') return;
        submittingRef.current = true;

        setSelectedOption(optionId);
        if (timerRef.current) clearInterval(timerRef.current);

        try {
            const res = await submitAnswer({ attemptId: attemptId!, selectedOptionId: optionId, userId: user?._id });
            setResult({ isCorrect: res.isCorrect || false, participationRuby: res.participationRuby || 0 });

            if (res.skipExplanation) {
                setStatus('result');
                return;
            }

            // Transition to explaining phase
            setStatus('explaining');
            setExplanationTimer(20);

            // Start Explanation Timer
            if (timerRef.current) clearInterval(timerRef.current);
            timerRef.current = setInterval(() => {
                setExplanationTimer((prev) => {
                    if (prev <= 1) {
                        clearInterval(timerRef.current!);
                        handleExplanationSubmit(); // closure should have latest state or use a ref
                        return 0;
                    }
                    return prev - 1;
                });
            }, 1000);

        } catch (err: any) {
            setErrorMsg(err.message || "Failed to submit answer");
            setStatus('error');
        }
    };

    const submitExplanation = useMutation(api.wordUnderPressure.submitExplanation);

    const handleExplanationSubmit = async (finalExp?: string) => {
        if (timerRef.current) clearInterval(timerRef.current);

        // Prevent double submit
        if (status === 'result') return;

        const textToSubmit = finalExp !== undefined ? finalExp : explanationRef.current;

        try {
            await submitExplanation({
                attemptId: attemptId!,
                explanation: textToSubmit,
                userId: user?._id
            });
            setStatus('result');
        } catch (err: any) {
            console.error(err);
            setStatus('result'); // Even if it fails, go to result to avoid getting stuck
        }
    };

    if (statusData === undefined) return <div className="min-h-screen bg-black/95 flex items-center justify-center text-white">Loading...</div>;

    return (
        <div className="min-h-screen bg-black text-white font-sans selection:bg-none">
            {/* Main Container */}
            <div className="max-w-md mx-auto min-h-screen flex flex-col p-6 items-center justify-center relative">

                {/* Header / Logo */}
                <div className="absolute top-6 left-0 w-full flex justify-between px-6 opacity-40">
                    <span className="text-[10px] tracking-[0.3em] font-medium uppercase text-emerald-500">Word Under Pressure</span>
                    <div className="flex items-center gap-1.5">
                        <Trophy className="w-3 h-3 text-emerald-400" />
                        <span className="text-[10px] font-mono font-medium text-emerald-200/50">
                            {statusData?.rewardClaimed ? 'PRACTICE MODE' : 'MONTHLY CHALLENGE'}
                        </span>
                    </div>
                </div>

                {status === 'landing' && (
                    <motion.div
                        initial={{ opacity: 0 }} animate={{ opacity: 1 }}
                        className="text-center w-full"
                    >
                        <div className="w-20 h-20 rounded-full border border-emerald-500/20 flex items-center justify-center mx-auto mb-8 bg-emerald-950/20 shadow-[0_0_40px_rgba(16,185,129,0.05)]">
                            <Brain className="w-8 h-8 text-emerald-400" />
                        </div>

                        <h1 className="text-4xl font-display font-light mb-2 tracking-tight">Focus.</h1>
                        <p className="text-emerald-200/40 text-sm mb-10 font-light">Memorize the Word accurately.</p>

                        {!statusData?.hasActivePassage ? (
                            <Card className="bg-emerald-950/20 border-emerald-500/10 backdrop-blur-xl">
                                <CardContent className="p-8 text-center">
                                    <Brain className="w-12 h-12 text-emerald-500/10 mx-auto mb-4" />
                                    <h3 className="text-emerald-100 text-lg font-light mb-2">No Active Challenge</h3>
                                    <p className="text-emerald-200/30 text-xs mb-8 leading-relaxed">
                                        The scribes are preparing the next monthly portion. Check back soon.
                                    </p>
                                    <Button
                                        variant="ghost"
                                        className="w-full text-emerald-400/50 hover:text-emerald-300 hover:bg-emerald-500/5 text-xs tracking-widest uppercase font-bold"
                                        onClick={() => navigate('/leaderboard')}
                                    >
                                        View Leaderboard
                                    </Button>
                                </CardContent>
                            </Card>
                        ) : (
                            <div className="space-y-6">
                                <div className="flex items-center justify-center gap-3 text-[10px] text-emerald-500/60 mb-10 font-mono tracking-[0.2em]">
                                    <span className="bg-emerald-500/10 px-2 py-0.5 rounded">FREE ENTRY</span>
                                    <span className="opacity-30"></span>
                                    <span className="bg-emerald-500/10 px-2 py-0.5 rounded">
                                        {statusData.rewardClaimed ? 'PRACTICE ONLY' : 'WINNER: 11.5 RUBIES'}
                                    </span>
                                </div>

                                {statusData.hasAttempted && (
                                    <div className="bg-emerald-950/40 border border-emerald-500/10 text-emerald-200/60 text-[10px] p-4 rounded-xl mb-6 font-mono leading-relaxed">
                                        {statusData.rewardClaimed ? (
                                            <span className="text-emerald-400">Monthly reward claimed. </span>
                                        ) : (
                                            <span>Participated. </span>
                                        )}
                                        Improve your time to win the month! <br />
                                        {statusData.bestTime ? <span className="text-emerald-400 mt-1 block">Your Best: {statusData.bestTime}ms</span> : ''}
                                    </div>
                                )}

                                <Button
                                    className="w-full h-16 bg-emerald-600 hover:bg-emerald-500 text-white tracking-[0.3em] text-xs font-bold shadow-[0_10px_30px_rgba(5,150,105,0.2)] transition-all active:scale-[0.98] rounded-2xl"
                                    onClick={handleStart}
                                >
                                    {statusData.hasAttempted ? 'RETRY CHALLENGE' : 'BEGIN CHALLENGE'}
                                </Button>
                                <p className="text-[10px] text-emerald-200/20 max-w-[220px] mx-auto leading-relaxed uppercase tracking-widest font-medium">
                                    No interruptions. No screenshots.
                                    <br />{statusData.memoTime}s study  {statusData.recallTime}s recall.
                                </p>
                            </div>
                        )}
                    </motion.div>
                )}

                {status === 'viewing' && (
                    <div className="w-full h-full flex flex-col justify-center select-none" onContextMenu={e => e.preventDefault()}>
                        <div className="mb-12 flex justify-center">
                            <div className="relative w-20 h-20 rounded-full border border-emerald-500/10 flex items-center justify-center bg-emerald-950/20">
                                <span className="font-mono text-3xl font-light text-emerald-400">{timeLeft}</span>
                                <svg className="absolute w-full h-full -rotate-90">
                                    <circle
                                        cx="40" cy="40" r="38"
                                        fill="none" stroke="currentColor" strokeWidth="1"
                                        className="text-emerald-500/10"
                                    />
                                    <circle
                                        cx="40" cy="40" r="38"
                                        fill="none" stroke="currentColor" strokeWidth="2"
                                        strokeDasharray="238.7"
                                        strokeDashoffset={238.7 - (238.7 * timeLeft) / (statusData?.memoTime || 20)}
                                        className="text-emerald-500 transition-all duration-1000 ease-linear shadow-[0_0_10px_rgba(16,185,129,0.5)]"
                                    />
                                </svg>
                            </div>
                        </div>

                        <motion.div
                            initial={{ opacity: 0, y: 10 }}
                            animate={{ opacity: 1, y: 0 }}
                            className="space-y-8 text-center"
                        >
                            <div className="space-y-4">
                                {textLines.map((line, idx) => (
                                    <p key={idx} className="text-xl md:text-2xl font-serif text-emerald-50/90 leading-relaxed tracking-wide italic">
                                        {line}
                                    </p>
                                ))}
                            </div>
                            <div className="pt-8 opacity-20">
                                <div className="h-px w-12 bg-emerald-500 mx-auto mb-4" />
                                <p className="text-[10px] uppercase tracking-[0.4em] text-emerald-200 font-medium">Internalize</p>
                            </div>
                        </motion.div>
                    </div>
                )}

                {status === 'answering' && (
                    <div className="w-full max-w-sm">
                        <div className="mb-12 text-center">
                            <div className="inline-flex items-center gap-4 px-4 py-2 bg-emerald-500/10 rounded-full border border-emerald-500/20 mb-8">
                                <Clock className="w-4 h-4 text-emerald-400" />
                                <span className="font-mono text-lg font-medium text-emerald-400">{timeLeft}s</span>
                            </div>
                            <h2 className="text-2xl font-light leading-snug text-emerald-50 px-4">
                                {question}
                            </h2>
                        </div>

                        <div className="grid grid-cols-1 gap-4">
                            {options.map((opt) => (
                                <Button
                                    key={opt.id}
                                    variant="outline"
                                    className={cn(
                                        "h-16 justify-start px-6 text-sm font-light tracking-wide rounded-2xl transition-all border-emerald-500/10 bg-emerald-950/10 hover:bg-emerald-500/10 hover:border-emerald-500/30",
                                        selectedOption === opt.id ? "bg-emerald-500/20 border-emerald-500" : "text-emerald-200/80"
                                    )}
                                    onClick={() => handleAnswer(opt.id)}
                                >
                                    <span className="w-6 text-[10px] font-mono text-emerald-500/50 uppercase">{opt.id}</span>
                                    <span className="flex-1 text-left">{opt.text}</span>
                                </Button>
                            ))}
                        </div>
                    </div>
                )}

                {status === 'explaining' && (
                    <motion.div
                        initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }}
                        className="w-full max-w-sm"
                    >
                        <div className="text-center mb-8">
                            <div className="inline-flex items-center gap-4 px-4 py-2 bg-emerald-500/10 rounded-full border border-emerald-500/20 mb-8">
                                <Clock className="w-4 h-4 text-emerald-400" />
                                <span className="font-mono text-lg font-medium text-emerald-400">{explanationTimer}s</span>
                            </div>
                            <h2 className="text-2xl font-light mb-2 text-emerald-50">Reasoning.</h2>
                            <p className="text-emerald-200/40 text-sm">Why did you choose this answer? Explain in less than 250 words.</p>
                        </div>

                        <div className="space-y-6">
                            <textarea
                                className="w-full h-40 p-4 rounded-2xl bg-emerald-950/20 border border-emerald-500/10 text-emerald-50 text-sm font-light leading-relaxed focus:outline-none focus:border-emerald-500/30 transition-all resize-none"
                                placeholder="Explain your logic..."
                                value={explanation}
                                onChange={(e) => {
                                    const words = e.target.value.trim().split(/\s+/).filter(w => w !== '');
                                    if (words.length <= 250 || e.target.value.length < explanation.length) {
                                        setExplanation(e.target.value);
                                        explanationRef.current = e.target.value;
                                    }
                                }}
                                onPaste={(e) => {
                                    e.preventDefault(); // Anti-cheat: prevent paste
                                }}
                            />

                            <div className="flex justify-between items-center px-1">
                                <span className={cn(
                                    "text-[10px] font-mono tracking-widest",
                                    explanation.trim().split(/\s+/).filter(w => w !== '').length > 240 ? "text-amber-500" : "text-emerald-500/40"
                                )}>
                                    {explanation.trim().split(/\s+/).filter(w => w !== '').length} / 250 WORDS
                                </span>
                                <Button
                                    className="bg-emerald-600 hover:bg-emerald-500 text-white px-8 rounded-xl h-10 text-[10px] tracking-widest uppercase font-bold"
                                    onClick={() => handleExplanationSubmit()}
                                    disabled={explanation.trim().length < 5}
                                >
                                    SUBMIT
                                </Button>
                            </div>
                        </div>
                    </motion.div>
                )}

                {status === 'result' && result && (
                    <motion.div
                        initial={{ opacity: 0, scale: 0.95 }}
                        animate={{ opacity: 1, scale: 1 }}
                        className="text-center w-full"
                    >
                        {result.isCorrect ? (
                            <div className="mb-10">
                                <div className="w-20 h-20 rounded-full bg-emerald-500/20 flex items-center justify-center mx-auto mb-6">
                                    <Trophy className="w-10 h-10 text-emerald-400" />
                                </div>
                                <h2 className="text-3xl font-display font-light mb-2">Well Done.</h2>
                                <p className="text-emerald-200/50 text-sm mb-10">Focus rewarded.</p>

                                {result.participationRuby > 0 ? (
                                    <div className="inline-flex flex-col items-center p-6 bg-emerald-500/10 rounded-3xl border border-emerald-500/20 shadow-[0_10px_40px_rgba(16,185,129,0.1)]">
                                        <div className="flex items-center gap-2 mb-1">
                                            <Gem className="w-4 h-4 text-emerald-400" />
                                            <span className="text-4xl font-mono text-emerald-400">+{result.participationRuby}</span>
                                        </div>
                                        <span className="text-[10px] uppercase tracking-[0.2em] text-emerald-500/60 font-bold">Rubies Earned</span>
                                    </div>
                                ) : (
                                    <div className="inline-flex flex-col items-center p-6 bg-emerald-950/40 rounded-3xl border border-white/5">
                                        <span className="text-xs uppercase tracking-[0.2em] text-white/30 font-bold mb-1">Practice Complete</span>
                                        <p className="text-[10px] text-emerald-500/40">Monthly reward already claimed.</p>
                                    </div>
                                )}
                            </div>
                        ) : (
                            <div className="mb-10">
                                <div className="w-20 h-20 rounded-full bg-red-500/10 flex items-center justify-center mx-auto mb-6">
                                    <Brain className="w-10 h-10 text-red-400" />
                                </div>
                                <h2 className="text-3xl font-display font-light mb-2 text-red-200">Refocus.</h2>
                                <p className="text-red-200/40 text-sm mb-10">Try again to improve your standing.</p>
                            </div>
                        )}

                        <div className="flex flex-col gap-4 w-full max-w-[240px] mx-auto">
                            <Button
                                className="w-full h-14 bg-white text-emerald-950 hover:bg-emerald-50 rounded-2xl font-bold tracking-widest text-[10px] uppercase"
                                onClick={() => setStatus('landing')}
                            >
                                Play Again
                            </Button>
                            <Button
                                variant="outline"
                                className="w-full h-14 border-emerald-500/20 text-emerald-400 hover:bg-emerald-500/5 rounded-2xl font-bold tracking-widest text-[10px] uppercase"
                                onClick={() => navigate('/leaderboard')}
                            >
                                Leaderboard
                            </Button>
                        </div>
                    </motion.div>
                )}

                {status === 'error' && (
                    <div className="text-center w-full">
                        <div className="w-16 h-16 rounded-full bg-red-500/20 flex items-center justify-center mx-auto mb-6">
                            <Clock className="w-8 h-8 text-red-500" />
                        </div>
                        <h2 className="text-2xl font-light mb-4">Interrupted.</h2>
                        <p className="text-red-200/40 text-sm mb-10 bg-red-500/5 p-4 rounded-xl border border-red-500/10">{errorMsg}</p>
                        <Button
                            className="bg-emerald-600 hover:bg-emerald-500 text-white rounded-2xl px-8 h-12"
                            onClick={() => setStatus('landing')}
                        >
                            Return to Start
                        </Button>
                    </div>
                )}
            </div>
        </div>
    );
}

export default WordUnderPressure;
