import { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Card, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Textarea } from '@/components/ui/Textarea';
import { Badge } from '@/components/ui/Badge';
import { Modal } from '@/components/ui/Modal';
import { MediaUpload } from '@/components/ui/MediaUpload';
import { PremiumSelect } from '@/components/ui/PremiumSelect';
import { ConfirmationModal } from '@/components/ui/ConfirmationModal';
import { Plus, Edit2, Trash2, Eye, EyeOff, Megaphone, Image as ImageIcon, ChevronRight, ChevronLeft, Check, XCircle, CheckCircle2 } from 'lucide-react';
import { Id } from '../../../convex/_generated/dataModel';
import { cn } from '@/lib/cn';
import { motion, AnimatePresence } from 'framer-motion';

import { AnnouncementCardSkeleton } from '@/components/ui/Skeletons';

export function AdminAnnouncements() {
    const { user } = useAuth();
    const announcements = useQuery(api.announcements.getAllAnnouncements, user ? { adminId: user._id } : "skip");

    const create = useMutation(api.announcements.createAnnouncement);
    const update = useMutation(api.announcements.updateAnnouncement);
    const remove = useMutation(api.announcements.deleteAnnouncement);

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [editId, setEditId] = useState<Id<"announcements"> | null>(null);
    const [step, setStep] = useState(1);
    const [notification, setNotification] = useState<{ type: 'success' | 'error', message: string } | null>(null);
    const [deleteModal, setDeleteModal] = useState<{ isOpen: boolean, id: Id<"announcements"> | null }>({ isOpen: false, id: null });

    const showNotification = (type: 'success' | 'error', message: string) => {
        setNotification({ type, message });
        setTimeout(() => setNotification(null), 5000);
    };

    // Form state
    const [formData, setFormData] = useState({
        title: '',
        description: '',
        imageUrl: '',
        category: 'General',
        isActive: true
    });

    const totalSteps = 3;

    const resetForm = () => {
        setFormData({ title: '', description: '', imageUrl: '', category: 'General', isActive: true });
        setStep(1);
    };

    const handleOpenCreate = () => {
        setEditId(null);
        resetForm();
        setIsModalOpen(true);
    };

    const handleOpenEdit = (item: any) => {
        setEditId(item._id);
        const validCategory = ["General", "Ancient Culture", "Wisdom of The Now", "Ads", "Product", "Event", "Update"].includes(item.category)
            ? item.category
            : "General";

        setFormData({
            title: item.title,
            description: item.description,
            imageUrl: item.imageUrl,
            category: validCategory,
            isActive: item.isActive
        });
        setStep(1);
        setIsModalOpen(true);
    };

    const handleNext = () => setStep(prev => Math.min(prev + 1, totalSteps));
    const handleBack = () => setStep(prev => Math.max(prev - 1, 1));

    const handleSubmit = async () => {
        if (!user) return;
        try {
            if (editId) {
                await update({
                    adminId: user._id,
                    announcementId: editId,
                    ...formData,
                    category: formData.category as any
                });
                console.log('Announcement updated');
            } else {
                await create({
                    adminId: user._id,
                    ...formData,
                    category: formData.category as any
                });
                console.log('Announcement created');
            }
            setIsModalOpen(false);
            resetForm();
        } catch (e) {
            console.error(e);
            alert(editId ? 'Failed to update' : 'Failed to create');
        }
    };

    const handleDelete = async () => {
        if (!deleteModal.id || !user) return;
        try {
            await remove({ adminId: user._id, announcementId: deleteModal.id });
            showNotification('success', 'Announcement deleted');
            setDeleteModal({ isOpen: false, id: null });
        } catch (e) {
            showNotification('error', 'Failed to delete');
        }
    };

    const handleToggleActive = async (item: any) => {
        if (!user) return;
        try {
            await update({
                adminId: user._id,
                announcementId: item._id,
                isActive: !item.isActive
            });
            showNotification('success', item.isActive ? 'Deactivated' : 'Activated');
        } catch (e) {
            showNotification('error', 'Failed to toggle status');
        }
    };

    if (announcements === undefined) {
        return (
            <div className="space-y-6 pb-20 md:pb-0">
                <div className="flex justify-between items-center flex-wrap gap-4">
                    <div>
                        <div className="h-10 w-64 bg-light-muted/20 dark:bg-dark-muted/20 rounded mb-2 animate-pulse" />
                        <div className="h-4 w-96 bg-light-muted/20 dark:bg-dark-muted/20 rounded animate-pulse" />
                    </div>
                </div>
                <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
                    {[1, 2, 3, 4, 5, 6].map((i) => (
                        <AnnouncementCardSkeleton key={i} />
                    ))}
                </div>
            </div>
        );
    }

    const renderStepContent = () => {
        switch (step) {
            case 1:
                return (
                    <div className="space-y-4 animate-in fade-in slide-in-from-right-4 duration-300">
                        <div>
                            <label className="text-sm font-medium mb-1 block">Title</label>
                            <Input
                                placeholder="Enter announcement title"
                                value={formData.title}
                                onChange={e => setFormData({ ...formData, title: e.target.value })}
                                autoFocus
                            />
                        </div>

                        <div>
                            <label className="text-sm font-medium mb-1 block">Category</label>
                            <PremiumSelect
                                value={formData.category}
                                onChange={val => setFormData({ ...formData, category: val })}
                                options={[
                                    { value: "General", label: "General" },
                                    { value: "Ancient Culture", label: "Ancient Culture" },
                                    { value: "Wisdom of The Now", label: "Wisdom of The Now" },
                                    { value: "Ads", label: "Ads" },
                                    { value: "Product", label: "Product" },
                                    { value: "Event", label: "Event" },
                                    { value: "Update", label: "Update" }
                                ]}
                            />
                        </div>

                        <div className="flex items-center gap-3 p-3 rounded-xl bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border dark:border-dark-border">
                            <input
                                type="checkbox"
                                id="isActive"
                                checked={formData.isActive}
                                onChange={e => setFormData({ ...formData, isActive: e.target.checked })}
                                className="w-5 h-5 rounded border-gray-300 text-primary-600 focus:ring-primary-500"
                            />
                            <label htmlFor="isActive" className="text-sm font-medium cursor-pointer flex-1">
                                Active immediately
                                <span className="block text-xs text-light-muted dark:text-dark-muted font-normal">
                                    If unchecked, the announcement will be hidden from users until you activate it.
                                </span>
                            </label>
                        </div>
                    </div>
                );
            case 2:
                return (
                    <div className="space-y-4 animate-in fade-in slide-in-from-right-4 duration-300">
                        <div>
                            <label className="text-sm font-medium mb-1 block">Description</label>
                            <Textarea
                                placeholder="Enter full description..."
                                value={formData.description}
                                onChange={e => setFormData({ ...formData, description: e.target.value })}
                                className="min-h-[200px] text-base leading-relaxed"
                            />
                            <p className="text-xs text-right text-light-muted mt-1">
                                {formData.description.length} characters
                            </p>
                        </div>
                    </div>
                );
            case 3:
                return (
                    <div className="space-y-6 animate-in fade-in slide-in-from-right-4 duration-300">
                        <div>
                            <label className="text-sm font-medium mb-2 block">Cover Image</label>
                            <MediaUpload
                                onUploadComplete={(url) => setFormData(prev => ({ ...prev, imageUrl: url }))}
                                currentMedia={formData.imageUrl ? { url: formData.imageUrl, type: 'image' } : undefined}
                                allowVideo={false}
                            />
                        </div>

                        {formData.title && (
                            <div className="p-4 rounded-xl bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border dark:border-dark-border">
                                <h4 className="font-bold text-sm text-light-muted mb-2 uppercase tracking-wider">Preview Summary</h4>
                                <div className="flex gap-3">
                                    {formData.imageUrl && (
                                        <div className="w-16 h-16 rounded-lg overflow-hidden flex-shrink-0 bg-gray-100 dark:bg-gray-800">
                                            <img src={formData.imageUrl} alt="Preview" className="w-full h-full object-cover" />
                                        </div>
                                    )}
                                    <div className="flex-1 min-w-0">
                                        <p className="font-bold truncate">{formData.title}</p>
                                        <Badge variant="outline" className="text-[10px] mt-1">{formData.category}</Badge>
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                );
            default:
                return null;
        }
    };

    return (
        <div className="space-y-6 pb-20 md:pb-0">
            <ConfirmationModal
                isOpen={deleteModal.isOpen}
                onClose={() => setDeleteModal({ isOpen: false, id: null })}
                onConfirm={handleDelete}
                title="Delete Announcement"
                description="Are you sure you want to delete this announcement? This action cannot be undone."
                variant="danger"
                confirmText="Delete"
            />

            {/* Notification Bar */}
            <AnimatePresence>
                {notification && (
                    <motion.div
                        initial={{ opacity: 0, y: -20 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: -20 }}
                        className={cn(
                            "fixed top-4 right-4 z-[100] px-6 py-4 rounded-xl shadow-2xl flex items-center gap-3 border backdrop-blur-md",
                            notification.type === 'success'
                                ? "bg-emerald-500 text-white border-emerald-400"
                                : "bg-red-500 text-white border-red-400"
                        )}
                    >
                        {notification.type === 'success' ? <CheckCircle2 className="w-5 h-5" /> : <XCircle className="w-5 h-5" />}
                        <p className="font-bold">{notification.message}</p>
                    </motion.div>
                )}
            </AnimatePresence>
            <div className="flex justify-between items-center flex-wrap gap-4">
                <div>
                    <h1 className="text-3xl font-bold font-display text-gradient">Announcements</h1>
                    <p className="text-light-muted dark:text-dark-muted">Manage community announcements and ads</p>
                </div>
                <Button onClick={handleOpenCreate} className="w-full md:w-auto">
                    <Plus className="w-4 h-4 mr-2" /> New Announcement
                </Button>
            </div>

            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
                {announcements.map((item: any) => (
                    <Card key={item._id} glass className="relative group overflow-hidden flex flex-col h-full">
                        <div className="aspect-video bg-gray-100 dark:bg-gray-800 relative overflow-hidden">
                            {item.imageUrl ? (
                                <img src={item.imageUrl} alt={item.title} className="w-full h-full object-cover transition-transform group-hover:scale-105" />
                            ) : (
                                <div className="w-full h-full flex items-center justify-center text-gray-400">
                                    <ImageIcon className="w-10 h-10" />
                                </div>
                            )}
                            <div className="absolute top-2 right-2 flex gap-2">
                                <Badge variant={item.isActive ? 'success' : 'secondary'}>
                                    {item.isActive ? 'Active' : 'Hidden'}
                                </Badge>
                                <Badge variant="outline" className="bg-black/50 backdrop-blur-sm text-white border-none">
                                    {item.category}
                                </Badge>
                            </div>
                        </div>

                        <CardContent className="flex-1 flex flex-col p-4">
                            <h3 className="font-bold text-lg mb-2 line-clamp-1">{item.title}</h3>
                            <p className="text-sm text-light-muted dark:text-dark-muted line-clamp-3 mb-4 flex-1">
                                {item.description}
                            </p>

                            <div className="flex items-center justify-between pt-4 border-t border-light-border dark:border-dark-border">
                                <div className="flex gap-2">
                                    <Button size="sm" variant="outline" onClick={() => handleToggleActive(item)} title={item.isActive ? "Hide" : "Show"}>
                                        {item.isActive ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                                    </Button>
                                    <Button size="sm" variant="outline" onClick={() => handleOpenEdit(item)}>
                                        <Edit2 className="w-4 h-4" />
                                    </Button>
                                </div>
                                <Button size="sm" variant="danger" onClick={() => setDeleteModal({ isOpen: true, id: item._id })}>
                                    <Trash2 className="w-4 h-4" />
                                </Button>
                            </div>
                        </CardContent>
                    </Card>
                ))}

                {announcements.length === 0 && (
                    <div className="col-span-full text-center py-12 bg-light-card/30 dark:bg-dark-card/30 rounded-2xl border border-dashed border-light-border dark:border-dark-border">
                        <Megaphone className="w-12 h-12 text-light-muted mx-auto mb-4" />
                        <p className="text-lg font-semibold text-light-muted">No announcements yet</p>
                        <Button variant="outline" className="mt-4" onClick={handleOpenCreate}>Create First Announcement</Button>
                    </div>
                )}
            </div>

            <Modal
                isOpen={isModalOpen}
                onClose={() => setIsModalOpen(false)}
                title={editId ? "Edit Announcement" : "Create Announcement"}
                className="max-w-lg"
            >
                <div className="flex flex-col h-full">
                    {/* Progress Indicator */}
                    <div className="flex items-center justify-between mb-6 px-1">
                        {[1, 2, 3].map((s) => (
                            <div key={s} className="flex flex-col items-center gap-1 w-1/3 relative z-10">
                                <div className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-bold transition-all duration-300 ${step >= s ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30' : 'bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-light-muted'
                                    }`}>
                                    {step > s ? <Check className="w-4 h-4" /> : s}
                                </div>
                                <span className={`text-[10px] uppercase tracking-wider font-bold mt-1 ${step >= s ? 'text-primary-500' : 'text-light-muted'
                                    }`}>
                                    {s === 1 ? 'Details' : s === 2 ? 'Content' : 'Media'}
                                </span>

                                {/* Connector Line */}
                                {s !== 3 && (
                                    <div className={`absolute top-4 left-1/2 w-full h-0.5 -z-10 ${step > s ? 'bg-primary-500' : 'bg-light-border dark:bg-dark-border'
                                        }`} />
                                )}
                            </div>
                        ))}
                    </div>

                    {/* Step Content */}
                    <div className="flex-1 overflow-y-auto px-1 py-2 min-h-[300px]">
                        {renderStepContent()}
                    </div>

                    {/* Footer Actions */}
                    <div className="flex items-center justify-between pt-6 mt-2 border-t border-light-border dark:border-dark-border">
                        <Button
                            variant="outline"
                            onClick={step === 1 ? () => setIsModalOpen(false) : handleBack}
                            className="w-[100px]"
                        >
                            {step === 1 ? 'Cancel' : (
                                <><ChevronLeft className="w-4 h-4 mr-1" /> Back</>
                            )}
                        </Button>

                        <div className="flex gap-2">
                            {step < totalSteps ? (
                                <Button onClick={handleNext} className="w-[100px]">
                                    Next <ChevronRight className="w-4 h-4 ml-1" />
                                </Button>
                            ) : (
                                <Button onClick={handleSubmit} className="w-[120px] bg-green-500 hover:bg-green-600 border-none shadow-lg shadow-green-500/20">
                                    {editId ? 'Update' : 'Create'} <Check className="w-4 h-4 ml-2" />
                                </Button>
                            )}
                        </div>
                    </div>
                </div>
            </Modal>
        </div>
    );
}
