import { useState, useEffect } from 'react';
import { useParams, useSearchParams } from 'react-router-dom';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Id } from '../../convex/_generated/dataModel';
import { useAuth } from '@/hooks/useAuth';
import { Avatar } from '@/components/ui/Avatar';
import { Badge } from '@/components/ui/Badge';
import { BackButton } from '@/components/ui/BackButton';
import { CommentSection } from '@/components/posts/CommentSection';
import { Heart, MessageCircle, Shield, User as UserIcon, Share2, ChevronDown, ChevronUp, Calendar, HeartHandshake } from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';
import { motion, AnimatePresence } from 'framer-motion';
import { PostCardSkeleton } from '@/components/ui/Skeletons';
import { CommentSkeleton } from '@/components/skeletons/CommentSkeleton';
import { QuestionSection } from '@/components/posts/QuestionSection';

export function PostDetails() {
    const { postId } = useParams<{ postId: string }>();
    const [searchParams, setSearchParams] = useSearchParams();
    const activeTab = searchParams.get('tab') || 'comments';

    const { user } = useAuth();

    const post = useQuery(api.posts.getPost, { postId: postId as Id<"posts"> });
    const likePost = useMutation(api.posts.likePost);


    const [isLiking, setIsLiking] = useState(false);

    // Optimistic Heart State
    const [localLiked, setLocalLiked] = useState(false);
    const [localLikeCount, setLocalLikeCount] = useState(0);

    const [expansionLevel, setExpansionLevel] = useState(0); // 0: Collapsed, 1: Semi-expanded, 2: Full

    useEffect(() => {
        if (post) {
            setLocalLikeCount(post.likeCount);
            setExpansionLevel(0);
        }
    }, [post?._id, post?.likeCount]);

    if (!post) {
        return (
            <div className="min-h-screen pb-20 md:pb-8">
                <div className="container mx-auto px-4 pt-4 mb-4">
                    <div className="w-24 h-8 bg-gray-200 dark:bg-gray-700 rounded animate-pulse" />
                </div>
                <div className="container mx-auto px-4 max-w-6xl">
                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                        <div className="aspect-[4/5] bg-gray-200 dark:bg-gray-700 rounded-2xl animate-pulse" />
                        <div className="space-y-6">
                            <PostCardSkeleton />
                            <div className="p-6 glass rounded-2xl border border-light-border dark:border-dark-border space-y-4">
                                <CommentSkeleton />
                                <CommentSkeleton />
                                <CommentSkeleton />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    const isAdminPost = post.author?.role === 'admin' || post.author?.role === 'super_admin';

    const handleLike = async () => {
        if (!user || isLiking) return;
        if (isAdminPost && localLiked) return; // one-way lock on admin posts

        const newLiked = !localLiked;
        setLocalLiked(newLiked);
        setLocalLikeCount((prev: number) => newLiked ? prev + 1 : prev - 1);

        setIsLiking(true);
        try {
            await likePost({ userId: user._id, postId: post._id });
        } catch (error) {
            const msg = error instanceof Error ? error.message : '';
            if (!msg.includes('already liked')) {
                setLocalLiked(!newLiked);
                setLocalLikeCount((prev: number) => !newLiked ? prev + 1 : prev - 1);
            }
            console.error('Failed to like post:', error);
        } finally {
            setIsLiking(false);
        }
    };


    const isLongContent = post.content.length > 250;
    const isSuperLongContent = post.content.length > 1200; // ~20 lines

    return (
        <div className="min-h-screen pb-20 md:pb-8">
            <div className="container mx-auto px-4 pt-4 mb-4">
                <BackButton />
            </div>

            <div className="container mx-auto px-4 max-w-6xl">
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 items-start">

                    {/* Left Side: Media Display (Half Page) */}
                    <div className="lg:sticky lg:top-24 space-y-4">
                        {post.imageUrl ? (
                            <motion.div
                                layoutId={`post-image-${post._id}`}
                                className="rounded-2xl overflow-hidden glass border border-light-border dark:border-dark-border shadow-2xl"
                            >
                                <img
                                    src={post.imageUrl}
                                    alt="Post"
                                    className="w-full h-auto max-h-[70vh] object-contain bg-black/5"
                                />
                            </motion.div>
                        ) : post.videoUrl ? (
                            <div className="rounded-2xl overflow-hidden bg-black shadow-2xl">
                                <video src={post.videoUrl} controls className="w-full max-h-[70vh]" />
                            </div>
                        ) : (
                            <div className="aspect-square flex items-center justify-center glass rounded-2xl border border-dashed border-light-border dark:border-dark-border opacity-50">
                                <p className="text-light-muted">No visual media</p>
                            </div>
                        )}

                        {/* Post Metadata Card (Desktop Only) */}
                        <div className="hidden lg:block p-6 glass rounded-2xl border border-light-border dark:border-dark-border">
                            <div className="flex items-center justify-between mb-4">
                                <div className="flex items-center gap-4">
                                    <button
                                        onClick={handleLike}
                                        className={`flex items-center gap-2 transition-all duration-300 ${localLiked ? 'text-red-500' : 'text-light-text dark:text-dark-text hover:text-red-500'}`}
                                    >
                                        <Heart className={`w-6 h-6 transition-all duration-300 ${localLiked ? 'fill-red-500 text-red-500 scale-110' : ''}`} />
                                        <span className="font-bold">{localLikeCount} Likes</span>
                                    </button>
                                    <div className="flex items-center gap-2 text-light-muted">
                                        <MessageCircle className="w-6 h-6" />
                                        <span className="font-bold">{post.commentCount} Comments</span>
                                    </div>
                                </div>
                                <button className="p-2 rounded-full hover:bg-light-bg dark:hover:bg-dark-bg transition-colors">
                                    <Share2 className="w-5 h-5" />
                                </button>
                            </div>
                            <div className="h-px bg-light-border dark:bg-dark-border mb-4" />
                            <p className="text-xs text-light-muted flex items-center gap-2">
                                <Calendar className="w-3 h-3" />
                                Posted on {new Date(post.createdAt).toLocaleDateString(undefined, { dateStyle: 'long' })} at {new Date(post.createdAt).toLocaleTimeString(undefined, { timeStyle: 'short' })}
                            </p>
                        </div>
                    </div>

                    {/* Right Side: Content & Comments (Half Page) */}
                    <div className="space-y-6">
                        {/* Author Header */}
                        <div className="flex items-center justify-between p-4 glass rounded-2xl border border-light-border dark:border-dark-border">
                            <div className="flex items-center gap-3">
                                <Avatar src={post.author?.avatarUrl} alt={post.author?.name || 'User'} size="md" />
                                <div>
                                    <h2 className="font-bold text-lg text-light-text dark:text-dark-text flex items-center gap-2">
                                        {post.author?.name}
                                        {post.author?.role === 'admin' && (
                                            <Shield className="w-4 h-4 text-primary-500" />
                                        )}
                                    </h2>
                                    <div className="flex items-center gap-2">
                                        <Badge variant="outline" className="text-[10px]">
                                            {post.category}
                                        </Badge>
                                        <span className="text-xs text-light-muted">
                                            {formatDistanceToNow(post.createdAt, { addSuffix: true })}
                                        </span>
                                    </div>
                                </div>
                            </div>
                            {post.author?.role === 'admin' ? (
                                <Badge variant="primary" className="hidden sm:flex">Official</Badge>
                            ) : (
                                <Badge variant="default" className="hidden sm:flex">Member</Badge>
                            )}
                        </div>

                        {/* Post Content with Expandable Text */}
                        <div className="p-6 glass rounded-2xl border border-light-border dark:border-dark-border relative overflow-hidden">
                            <div className="absolute top-0 right-0 p-2 opacity-10">
                                <UserIcon className="w-20 h-20" />
                            </div>

                            <AnimatePresence initial={false}>
                                <motion.p
                                    key={expansionLevel}
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    className={`text-light-text dark:text-dark-text leading-relaxed text-base md:text-lg break-words cursor-pointer ${expansionLevel === 0 ? 'line-clamp-[6]' :
                                        expansionLevel === 1 ? 'line-clamp-[20]' : ''
                                        }`}
                                    onClick={() => {
                                        if (!isLongContent) return;
                                        if (isSuperLongContent) {
                                            if (expansionLevel === 0) setExpansionLevel(1);
                                            else if (expansionLevel === 1) setExpansionLevel(2);
                                            // Don't collapse on click if fully expanded (optional, but usually better UX to only expand)
                                        } else {
                                            setExpansionLevel(expansionLevel === 0 ? 2 : 0);
                                        }
                                    }}
                                >
                                    {post.content}
                                </motion.p>
                            </AnimatePresence>

                            {isLongContent && (
                                <button
                                    onClick={() => {
                                        if (isSuperLongContent) {
                                            // 3-stage expansion for very long posts
                                            if (expansionLevel === 0) setExpansionLevel(1);
                                            else if (expansionLevel === 1) setExpansionLevel(2);
                                            else setExpansionLevel(0);
                                        } else {
                                            // 2-stage expansion for moderately long posts
                                            setExpansionLevel(expansionLevel === 0 ? 2 : 0);
                                        }
                                    }}
                                    className="mt-4 flex items-center gap-1 text-primary-500 font-bold hover:underline transition-all"
                                >
                                    {expansionLevel === 2 ? (
                                        <>Show Less <ChevronUp className="w-4 h-4" /></>
                                    ) : expansionLevel === 1 ? (
                                        <>Read Full Message <ChevronDown className="w-4 h-4" /></>
                                    ) : (
                                        <>Read More <ChevronDown className="w-4 h-4" /></>
                                    )}
                                </button>
                            )}
                        </div>
                    </div>


                    {/* Discussion / Questions Section */}
                    <div className="p-4 md:p-6 glass rounded-2xl border border-light-border dark:border-dark-border min-h-[400px]">
                        {post.author?.role === 'admin' || post.author?.role === 'super_admin' ? (
                            <>
                                <div className="flex items-center gap-6 border-b border-light-border dark:border-dark-border mb-6">
                                    <button
                                        onClick={() => setSearchParams({ tab: 'comments' })}
                                        className={`pb-3 font-semibold text-sm transition-colors border-b-2 ${activeTab === 'comments'
                                            ? 'border-primary-500 text-primary-500'
                                            : 'border-transparent text-gray-500 hover:text-gray-700 dark:hover:text-gray-300'}`}
                                    >
                                        <div className="flex items-center gap-2">
                                            <MessageCircle className="w-4 h-4" />
                                            Comments ({post.commentCount})
                                        </div>
                                    </button>
                                    <button
                                        onClick={() => setSearchParams({ tab: 'questions' })}
                                        className={`pb-3 font-semibold text-sm transition-colors border-b-2 ${activeTab === 'questions'
                                            ? 'border-purple-500 text-purple-500'
                                            : 'border-transparent text-gray-500 hover:text-gray-700 dark:hover:text-gray-300'}`}
                                    >
                                        <div className="flex items-center gap-2">
                                            <Shield className="w-4 h-4" />
                                            Questions ({(post as any).questionCount || 0})
                                        </div>
                                    </button>
                                </div>

                                {activeTab === 'comments' ? (
                                    <CommentSection
                                        postId={post._id}
                                        isPrayer={post.postType === 'prayer'}
                                        isAdminPost={isAdminPost}
                                    />
                                ) : (
                                    <QuestionSection postId={post._id} />
                                )}
                            </>
                        ) : (
                            <>
                                <h3 className="text-xl font-bold text-gradient mb-6 flex items-center gap-2">
                                    {post.postType === 'prayer' ? (
                                        <>
                                            <HeartHandshake className="w-6 h-6 text-purple-500" />
                                            Community Prayers
                                        </>
                                    ) : (
                                        <>
                                            <MessageCircle className="w-6 h-6 text-primary-500" />
                                            Community Discussion
                                        </>
                                    )}
                                </h3>
                                <CommentSection
                                    postId={post._id}
                                    isPrayer={post.postType === 'prayer'}
                                    isAdminPost={isAdminPost}
                                />
                            </>
                        )}
                    </div>
                </div>
            </div>
        </div>

    );
}
