import { useState, useEffect } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useLocation, useNavigate } from 'react-router-dom';
import { AdminSidebar } from './AdminSidebar';
import { Menu, Bell, Moon, Sun, Search } from 'lucide-react';
import { useTheme } from '@/context/ThemeContext';
import { motion, AnimatePresence } from 'framer-motion';

interface AdminLayoutProps {
    children: React.ReactNode;
}

export function AdminLayout({ children }: AdminLayoutProps) {
    const { user, isLoading } = useAuth();
    const { theme, toggleTheme } = useTheme();
    const navigate = useNavigate();
    const [isSidebarOpen, setIsSidebarOpen] = useState(false);
    const location = useLocation();

    // Close sidebar on navigation for mobile
    useEffect(() => {
        setIsSidebarOpen(false);
    }, [location.pathname]);

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user || (user.role !== 'admin' && user.role !== 'super_admin' && user.role !== 'board_member')) {
        return <Navigate to="/feed" replace />;
    }

    return (
        <div className="min-h-screen bg-light-bg dark:bg-dark-bg transition-colors duration-300">
            {/* Sidebar */}
            <AdminSidebar isOpen={isSidebarOpen} onClose={() => setIsSidebarOpen(false)} />

            {/* Main Content Area */}
            <div className="lg:ml-64 flex flex-col min-h-screen">
                {/* Header for Admin Area */}
                <header className="sticky top-0 z-40 bg-white/80 dark:bg-dark-bg/80 backdrop-blur-md border-b border-light-border dark:border-dark-border px-4 md:px-8 py-4">
                    <div className="flex items-center justify-between">
                        <div className="flex items-center gap-4">
                            <button
                                onClick={() => setIsSidebarOpen(true)}
                                className="lg:hidden p-2 hover:bg-light-card dark:hover:bg-dark-card rounded-lg"
                            >
                                <Menu className="w-6 h-6" />
                            </button>
                            <div className="hidden md:block">
                                <h2 className="text-xl font-bold text-light-text dark:text-dark-text capitalize">
                                    {location.pathname.split('/').pop()?.replace('-', ' ') || 'Dashboard'}
                                </h2>
                            </div>
                        </div>

                        <div className="flex items-center gap-2 md:gap-4">
                            {/* Search (Icon only for now) - Hidden on mobile */}
                            <button
                                onClick={() => navigate('/search')}
                                className="hidden md:flex p-2.5 text-light-muted dark:text-dark-muted hover:bg-light-card dark:hover:bg-dark-card rounded-xl transition-all"
                            >
                                <Search className="w-5 h-5" />
                            </button>

                            {/* Theme Toggle - Hidden on mobile */}
                            <button
                                onClick={toggleTheme}
                                className="hidden md:flex p-2.5 text-light-muted dark:text-dark-muted hover:bg-light-card dark:hover:bg-dark-card rounded-xl transition-all"
                            >
                                {theme === 'dark' ? <Sun className="w-5 h-5 text-yellow-500" /> : <Moon className="w-5 h-5 text-slate-700" />}
                            </button>

                            {/* Notifications */}
                            <button
                                onClick={() => navigate('/notifications')}
                                className="p-2.5 text-light-muted dark:text-dark-muted hover:bg-light-card dark:hover:bg-dark-card rounded-xl transition-all relative"
                            >
                                <Bell className="w-5 h-5" />
                                <span className="absolute top-2 right-2 w-2 h-2 bg-primary-500 rounded-full border-2 border-white dark:border-dark-bg" />
                            </button>

                            {/* User Profile - Hidden on mobile */}
                            <div className="hidden md:flex ml-2 pl-4 border-l border-light-border dark:border-dark-border items-center gap-3">
                                <div className="hidden sm:block text-right">
                                    <p className="text-xs font-bold truncate">{user?.name}</p>
                                    <p className="text-[10px] text-light-muted uppercase font-medium">{user?.role}</p>
                                </div>
                                <div className="w-9 h-9 rounded-xl bg-primary-500/10 dark:bg-primary-500/20 flex items-center justify-center text-primary-500 font-bold border border-primary-500/20 overflow-hidden">
                                    {user?.avatarUrl ? (
                                        <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover rounded-xl" />
                                    ) : (
                                        user?.name[0]
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </header>

                {/* Content */}
                <main className="flex-1 p-4 md:p-8">
                    <AnimatePresence mode="wait">
                        <motion.div
                            key={location.pathname}
                            initial={{ opacity: 0, y: 10 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: -10 }}
                            transition={{ duration: 0.2 }}
                        >
                            {children}
                        </motion.div>
                    </AnimatePresence>
                </main>
            </div>
        </div>
    );
}
