import { useAuth } from '@/hooks/useAuth';
import { Link, Navigate } from 'react-router-dom';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Card } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { AvatarUpload } from '@/components/ui/AvatarUpload';
import { Trophy, FileText } from 'lucide-react';
import { formatPhoneNumber } from '@/utils/phoneNetwork';
import { BankingCredentials } from '@/components/banking/BankingCredentials';
import { CoverUpload } from '@/components/ui/CoverUpload';
import { BackButton } from '@/components/ui/BackButton';
import { PageHeaderSkeleton, StatsCardSkeleton } from '@/components/ui/Skeletons';


export function Profile() {
    const { user, isLoading, updateUser } = useAuth();
    const updateAvatar = useMutation(api.presence.updateAvatar);
    const updateProfile = useMutation(api.users.updateProfile);




    const userStats = user ? useQuery(api.users.getUserStats, { userId: user._id }) : undefined;

    const handleAvatarUpload = async (url: string) => {
        if (!user) return;

        try {
            await updateAvatar({
                userId: user._id,
                avatarUrl: url,
            });
            // Update the user context with new avatar
            updateUser({ avatarUrl: url });
        } catch (error) {
            console.error('Failed to update avatar:', error);
        }
    };

    const handleCoverUpload = async (url: string) => {
        if (!user) return;
        try {
            await updateProfile({
                userId: user._id,
                coverImageUrl: url,
            });
        } catch (error) {
            console.error('Failed to update cover image:', error);
        }
    };



    if (isLoading) {
        return (
            <div className="min-h-screen py-4 md:py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                    <PageHeaderSkeleton />
                    <div className="glass rounded-xl h-64 animate-pulse mb-6" /> {/* Profile header skeleton */}
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                        <StatsCardSkeleton />
                        <StatsCardSkeleton />
                        <StatsCardSkeleton />
                    </div>
                </div>
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    return (
        <div className="min-h-screen py-4 md:py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <BackButton />
                <div className="mb-4 md:mb-8">
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient font-display mb-2">
                        My Profile
                    </h1>
                    <p className="text-sm md:text-base text-light-muted dark:text-dark-muted">
                        View your account details and community stats
                    </p>
                </div>

                <div className="space-y-6">
                    {/* Facebook-style Header */}
                    <div className="glass rounded-xl overflow-hidden">
                        <div className="h-32 md:h-48 relative bg-gray-100 dark:bg-gray-800">
                            <CoverUpload
                                currentCoverUrl={user.coverImageUrl}
                                onUploadComplete={handleCoverUpload}
                            />
                        </div>
                        <div className="px-4 pb-4 md:px-8 md:pb-8 relative">
                            <div className="relative -mt-10 md:-mt-16 mb-3 md:mb-4 flex flex-col items-center md:items-start whitespace-nowrap">
                                <AvatarUpload
                                    currentAvatarUrl={user.avatarUrl}
                                    onUploadComplete={handleAvatarUpload}
                                    size="xl"
                                />
                            </div>

                            <div className="flex flex-col md:flex-row md:items-start md:justify-between gap-4 text-center md:text-left">
                                <div className="flex-1 min-w-0">
                                    <div className="flex flex-col md:flex-row items-center gap-2 mb-1">
                                        <h1 className="text-xl md:text-3xl font-bold text-light-text dark:text-dark-text truncate">
                                            {user.name}
                                        </h1>
                                        <Badge
                                            variant={user.role === 'super_admin' ? 'secondary' : (user.role === 'admin' ? 'primary' : 'default')}
                                            className="h-5 text-[10px]"
                                        >
                                            {user.role === 'super_admin' ? 'Super Admin' : (user.role === 'admin' ? 'Admin' : 'Member')}
                                        </Badge>
                                    </div>

                                    <div className="flex flex-col md:flex-row items-center justify-center md:justify-start gap-1 md:gap-4 text-sm text-light-muted dark:text-dark-muted mb-4">
                                        <span>{user.email}</span>
                                        <span className="hidden md:inline"></span>
                                        <span>{formatPhoneNumber(user.phone)}</span>
                                    </div>

                                    {/* Stats Row */}
                                    {/* Stats Row */}
                                    <div className="flex items-center justify-center md:justify-start gap-6 mb-4 md:mb-0">
                                        <Link to="/profile/followers" className="flex flex-col items-center md:items-start hover:opacity-80 transition-opacity">
                                            <span className="text-lg font-bold text-light-text dark:text-dark-text leading-none">{userStats?.followerCount || 0}</span>
                                            <span className="text-xs text-light-muted dark:text-dark-muted">Followers</span>
                                        </Link>
                                        <Link to="/profile/following" className="flex flex-col items-center md:items-start hover:opacity-80 transition-opacity">
                                            <span className="text-lg font-bold text-light-text dark:text-dark-text leading-none">{userStats?.followingCount || 0}</span>
                                            <span className="text-xs text-light-muted dark:text-dark-muted">Following</span>
                                        </Link>
                                    </div>
                                </div>

                                {/* Theme Toggle (Mobile) - moved here or kept? User wanted structure. I'll keep it integrated cleanly if needed or remove if distinct. Making it a small icon button if strictly needed, but let's stick to the Profile structure primarily. */}
                            </div>
                        </div>
                    </div>

                    {/* Stats Grid */}
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                        <Card glass className="p-6 text-center">
                            <FileText className="w-8 h-8 mx-auto mb-2 text-primary-500" />
                            <div className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {userStats?.totalPosts || 0}
                            </div>
                            <div className="text-sm text-light-muted dark:text-dark-muted">Posts</div>
                        </Card>
                        <Card glass className="p-6 text-center">
                            <Trophy className="w-8 h-8 mx-auto mb-2 text-accent-500" />
                            <div className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {userStats?.totalRewards || 0}
                            </div>
                            <div className="text-sm text-light-muted dark:text-dark-muted">Awards Won</div>
                        </Card>
                        <Card glass className="p-6 text-center">
                            <div className="w-8 h-8 mx-auto mb-2 flex items-center justify-center text-xl"></div>
                            <div className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {(userStats as any)?.totalEarned || 0}
                            </div>
                            <div className="text-sm text-light-muted dark:text-dark-muted">Rubies Earned</div>
                        </Card>
                    </div>
                </div>

                {/* Banking Information - Full Width */}
                <div className="mt-6">
                    <BankingCredentials />
                </div>
            </div>
        </div >
    );
}
