import React from 'react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { ConvexProvider, ConvexReactClient } from 'convex/react';
import { ThemeProvider } from './context/ThemeContext';
import { AuthProvider } from './context/AuthContext';
import { ToastProvider } from './context/ToastContext';
import { Header } from './components/layout/Header';
import { Footer } from './components/layout/Footer';
import { MobileNav } from './components/layout/MobileNav';
import { DesktopNav } from './components/layout/DesktopNav';
import { Home } from './pages/Home';
import { Login } from './pages/Login';
import { Register } from './pages/Register';
import { Feed } from './pages/Feed';
import { SavedPosts } from './pages/SavedPosts';
import { AncientCulture } from './pages/AncientCulture';
import { WisdomNow } from './pages/WisdomNow';
import { Profile } from './pages/Profile';
import { FollowList } from './pages/FollowList';
import { AdminDashboard } from './pages/AdminDashboard';
import { Notifications } from './pages/Notifications';
import UserTasksPage from './pages/UserTasksPage';
import SuperAdminSetup from './pages/SuperAdminSetup';
import AdminBanking from './pages/admin/AdminBanking';
import AdminDiamondChallenge from './pages/admin/AdminDiamondChallenge';
import AdminQuestionsPage from './pages/admin/AdminQuestions';
import AdminTagsPage from './pages/admin/AdminTags';
import AdminMasterPoolPage from './pages/admin/AdminMasterPool';
import AdminSettings from './pages/admin/AdminSettings';
import { AdminAnnouncements } from './pages/admin/AdminAnnouncements';
import { AdminPolls } from './pages/admin/AdminPolls';
import { AdminChat } from './pages/admin/AdminChat';

import MyResponses from './pages/MyResponses';
import Leaderboard from './pages/Leaderboard';
import { PostResponses } from './pages/PostResponses';
import { PostDetails } from './pages/PostDetails';
import { SearchPage } from './pages/SearchPage';
import { MemberProfile } from './pages/MemberProfile';
import { About } from './pages/About';
import { Privacy } from './pages/Privacy';
import { Terms } from './pages/Terms';
import { Contact } from './pages/Contact';
import { FAQ } from './pages/FAQ';
import { Guidelines } from './pages/Guidelines';

import { AdminUserRoles } from './pages/AdminUserRoles';
import { AdminUserDetail } from './pages/AdminUserDetail';
import { AnnouncementDetails } from './pages/AnnouncementDetails';
import { BoardDashboard } from './pages/board/BoardDashboard';
import { BoardTasks } from './pages/board/BoardTasks';
import { TrainingCenter } from './pages/board/TrainingCenter';
import { BoardAdmin } from './pages/board/BoardAdmin';
import { BoardAttendance } from './pages/board/BoardAttendance';
import { BoardFinance } from './pages/board/BoardFinance';
import { BoardContributions } from './pages/board/BoardContributions';
import { MemberOnboarding } from './pages/MemberOnboarding';
import { OfficialResponsePage } from './pages/OfficialResponsePage';
import { AdminLayout } from './components/admin/AdminLayout';
import { BankingPage } from './pages/BankingPage';

const convex = new ConvexReactClient(import.meta.env.VITE_CONVEX_URL as string);

import { PageLoader } from './components/ui/PageLoader';
import { useNotifications } from './hooks/useNotifications';
import { InstallPrompt } from '@/components/pwa/InstallPrompt';

import { CreateBoardTask } from './pages/board/CreateBoardTask';
import { CreateTrainingModule } from './pages/board/CreateTrainingModule';
import { Teachings } from './pages/Teachings';
import { CreatePost } from './pages/CreatePost';
import { ChatPage } from './pages/ChatPage';

import { WordUnderPressure } from './pages/WordUnderPressure';

function AppContent() {
    const location = useLocation();
    const [isLoading, setIsLoading] = React.useState(true);
    useNotifications(); // Call the hook here

    React.useEffect(() => {
        // Simulate initial load for premium feel
        const timer = setTimeout(() => setIsLoading(false), 2000);
        return () => clearTimeout(timer);
    }, []);

    if (isLoading) {
        return <PageLoader />;
    }

    // Check if current path is admin or board member portal
    const isDashboardPath = location.pathname.startsWith('/admin') || location.pathname.startsWith('/board');

    // Only show footer on the Landing/Home page
    const shouldShowFooter = location.pathname === '/';

    // Show mobile nav only when logged in and on feed pages
    const hiddenFooterPaths = ['/feed', '/ancient-culture', '/wisdom-now', '/teachings', '/profile', '/admin', '/board', '/notifications', '/tasks', '/leaderboard', '/admin/reward-responses', '/my-responses', '/admin/responses', '/post', '/search', '/banking', '/chat'];
    const showMobileNav = !isDashboardPath && hiddenFooterPaths.some(path => location.pathname.startsWith(path)) && location.pathname !== '/chat';

    // Show desktop nav on feed-related pages
    const showDesktopNav = false; // Disabled as per user request to remove the sub-header

    return (
        <div className="flex flex-col min-h-screen">
            {!isDashboardPath && location.pathname !== '/chat' && <Header />}
            {showDesktopNav && <DesktopNav />}
            <main className={`flex-1 ${!isDashboardPath && location.pathname !== '/chat' ? 'pt-14 md:pt-20' : ''} ${showMobileNav ? 'pb-20 md:pb-0' : ''}`}>
                <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="/login" element={<Login />} />
                    <Route path="/register" element={<Register />} />
                    <Route path="/feed" element={<Feed />} />
                    <Route path="/ancient-culture" element={<AncientCulture />} />
                    <Route path="/wisdom-now" element={<WisdomNow />} />
                    <Route path="/profile" element={<Profile />} />
                    <Route path="/profile/followers" element={<FollowList />} />
                    <Route path="/profile/following" element={<FollowList />} />
                    <Route path="/profile/:userId/followers" element={<FollowList />} />
                    <Route path="/profile/:userId/following" element={<FollowList />} />
                    <Route path="/saved-posts" element={<SavedPosts />} />

                    {/* Admin Routes Wrapped in Layout */}
                    <Route path="/admin/*" element={
                        <AdminLayout>
                            <Routes>
                                <Route index element={<AdminDashboard />} />
                                <Route path="banking" element={<AdminBanking />} />
                                <Route path="master-pool" element={<AdminMasterPoolPage />} />
                                <Route path="announcements" element={<AdminAnnouncements />} />
                                <Route path="polls" element={<AdminPolls />} />
                                <Route path="diamond-challenge" element={<AdminDiamondChallenge />} />
                                <Route path="questions" element={<AdminQuestionsPage />} />
                                <Route path="user-tags" element={<AdminTagsPage />} />
                                <Route path="contacts" element={<AdminChat />} />
                                <Route path="settings" element={<AdminSettings />} />
                                <Route path="roles" element={<AdminUserRoles />} />
                                <Route path="user/:userId" element={<AdminUserDetail />} />
                            </Routes>
                        </AdminLayout>
                    } />

                    <Route path="/my-responses" element={<MyResponses />} />
                    <Route path="/banking" element={<BankingPage />} />
                    <Route path="/tasks" element={<UserTasksPage />} />
                    <Route path="/leaderboard" element={<Leaderboard />} />
                    <Route path="/notifications" element={<Notifications />} />
                    <Route path="/setup" element={<SuperAdminSetup />} />
                    <Route path="/search" element={<SearchPage />} />
                    <Route path="/member-onboarding" element={<MemberOnboarding />} />

                    {/* Board Member Routes Wrapped in Layout */}
                    <Route path="/board/*" element={
                        <AdminLayout>
                            <Routes>
                                <Route index element={<BoardDashboard />} />
                                <Route path="tasks" element={<BoardTasks />} />
                                <Route path="task/new" element={<CreateBoardTask />} />
                                <Route path="training" element={<TrainingCenter />} />
                                <Route path="training/new" element={<CreateTrainingModule />} />
                                <Route path="attendance" element={<BoardAttendance />} />
                                <Route path="finance" element={<BoardFinance />} />
                                <Route path="contributions" element={<BoardContributions />} />
                                <Route path="admin" element={<BoardAdmin />} />
                            </Routes>
                        </AdminLayout>
                    } />

                    {/* Footer Pages */}
                    <Route path="/profile/:userId" element={<MemberProfile />} />
                    <Route path="/about" element={<About />} />
                    <Route path="/privacy" element={<Privacy />} />
                    <Route path="/terms" element={<Terms />} />
                    <Route path="/contact" element={<Contact />} />
                    <Route path="/faq" element={<FAQ />} />
                    <Route path="/guidelines" element={<Guidelines />} />
                    <Route path="/post/:postId/responses" element={<PostResponses />} />

                    <Route path="/post/:postId" element={<PostDetails />} />
                    <Route path="/official-response/:postId" element={<OfficialResponsePage />} />

                    {/* New Routes */}
                    <Route path="/teachings" element={<Teachings />} />
                    <Route path="/create-post" element={<CreatePost />} />
                    <Route path="/announcement/:id" element={<AnnouncementDetails />} />
                    <Route path="/chat" element={<ChatPage />} />
                    <Route path="/diamond-challenge" element={<WordUnderPressure />} />
                </Routes>
            </main>
            {shouldShowFooter && <Footer />}
            {showMobileNav && <MobileNav />}
        </div>
    );
}

function App() {
    return (
        <ConvexProvider client={convex}>
            <ThemeProvider>
                <AuthProvider>
                    <ToastProvider>
                        <Router future={{ v7_startTransition: true, v7_relativeSplatPath: true }}>
                            <InstallPrompt />
                            <AppContent />
                        </Router>
                    </ToastProvider>
                </AuthProvider>
            </ThemeProvider>
        </ConvexProvider>
    );
}

export default App;

