import { useState, useEffect } from 'react';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { motion, AnimatePresence } from 'framer-motion';
import { Users } from 'lucide-react';

export function MemberTicker() {
    const data = useQuery(api.users.getTickerData);
    const [currentIndex, setCurrentIndex] = useState(0);

    useEffect(() => {
        if (!data?.recentUsers || data.recentUsers.length === 0) return;

        const interval = setInterval(() => {
            setCurrentIndex((prev) => (prev + 2) % data.recentUsers.length);
        }, 4000);

        return () => clearInterval(interval);
    }, [data]);

    // Skeleton loader
    if (!data) {
        return (
            <div className="flex items-center gap-3 bg-light-card/50 dark:bg-dark-card/50 backdrop-blur-sm px-3 py-1.5 rounded-full border border-light-border/20 dark:border-dark-border/20 shadow-sm overflow-hidden w-[240px] xl:w-[280px] animate-pulse">
                <div className="flex items-center gap-2 pr-3 border-r border-light-border/20 dark:border-dark-border/20 shrink-0">
                    <div className="bg-light-bg dark:bg-dark-bg p-1 rounded-full w-5 h-5" />
                    <div className="flex flex-col gap-1 leading-none">
                        <div className="h-2 w-12 bg-light-bg dark:bg-dark-bg rounded" />
                        <div className="h-3 w-8 bg-light-bg dark:bg-dark-bg rounded" />
                    </div>
                </div>
                <div className="flex items-center gap-2">
                    <div className="w-5 h-5 rounded-full bg-light-bg dark:bg-dark-bg" />
                    <div className="h-2 w-12 bg-light-bg dark:bg-dark-bg rounded" />
                </div>
            </div>
        );
    }

    const visibleUsers = [
        data.recentUsers[currentIndex % data.recentUsers.length],
        data.recentUsers[(currentIndex + 1) % data.recentUsers.length]
    ].filter(Boolean);

    return (
        <div className="flex items-center gap-3 bg-light-card/50 dark:bg-dark-card/50 backdrop-blur-sm px-3 py-1.5 rounded-full border border-light-border/20 dark:border-dark-border/20 shadow-sm overflow-hidden w-[240px] xl:w-[280px]">
            {/* Total Count Badge */}
            <div className="flex items-center gap-2 pr-3 border-r border-light-border/20 dark:border-dark-border/20 shrink-0">
                <div className="bg-primary-500/10 p-1 rounded-full">
                    <Users className="w-3 h-3 text-primary-500" />
                </div>
                <div className="flex flex-col leading-none">
                    <span className="text-[9px] uppercase font-bold text-light-muted dark:text-dark-muted">Community</span>
                    <span className="text-xs font-black text-light-text dark:text-dark-text font-display">
                        {data.totalCount.toLocaleString()}
                    </span>
                </div>
            </div>

            {/* Cycling Users */}
            <div className="flex-1 relative h-7 overflow-hidden">
                <AnimatePresence mode="popLayout">
                    <motion.div
                        key={currentIndex}
                        initial={{ y: 20, opacity: 0 }}
                        animate={{ y: 0, opacity: 1 }}
                        exit={{ y: -20, opacity: 0 }}
                        transition={{ duration: 0.5, ease: "easeInOut" }}
                        className="absolute inset-0 flex items-center gap-2"
                    >
                        {visibleUsers.map((user) => (
                            <div key={user._id} className="flex items-center gap-1.5 min-w-0 max-w-[90px]">
                                <div className="w-5 h-5 rounded-full bg-gradient-to-br from-primary-400 to-secondary-400 p-[1px] flex-shrink-0">
                                    <div className="w-full h-full rounded-full bg-light-bg dark:bg-dark-bg overflow-hidden">
                                        {user.avatarUrl ? (
                                            <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover" />
                                        ) : (
                                            <div className="w-full h-full flex items-center justify-center text-[8px] font-bold bg-light-muted/10">
                                                {user.name.charAt(0)}
                                            </div>
                                        )}
                                    </div>
                                </div>
                                <span className="text-[10px] font-medium text-light-text dark:text-dark-text truncate">
                                    {user.name.split(' ')[0]}
                                </span>
                            </div>
                        ))}
                    </motion.div>
                </AnimatePresence>
            </div>
        </div>
    );
}
