import { motion, AnimatePresence } from 'framer-motion';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '@/hooks/useAuth';
import { useTheme } from '@/context/ThemeContext';
import { X, LogOut, Sun, Moon, LayoutDashboard, User, Home, Trophy, Bookmark, Brain, Wallet, MessageCircle } from 'lucide-react';
import { Button } from '@/components/ui/Button';
import { MemberTicker } from '../users/MemberTicker';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';

interface MenuModalProps {
    isOpen: boolean;
    onClose: () => void;
}

export function MenuModal({ isOpen, onClose }: MenuModalProps) {
    const { user, logout } = useAuth();
    const { theme, toggleTheme } = useTheme();
    const navigate = useNavigate();

    const badgeCounts = useQuery(api.stats.getBadgeCounts, user?._id ? { userId: user._id } : 'skip');
    const updateLastViewed = useMutation(api.users.updateLastViewed);

    const handleNavClick = async (path: string, field?: any) => {
        if (field && user) {
            await updateLastViewed({ userId: user._id, field });
        }
        onClose();
        navigate(path);
    };

    const handleLogout = () => {
        logout();
        onClose();
        navigate('/');
    };

    return (
        <AnimatePresence>
            {isOpen && (
                <>
                    {/* Backdrop */}
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={onClose}
                        className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50 md:hidden"
                    />

                    {/* Modal Content */}
                    <motion.div
                        initial={{ x: '-100%' }}
                        animate={{ x: 0 }}
                        exit={{ x: '-100%' }}
                        transition={{ type: 'spring', damping: 30, stiffness: 300 }}
                        className="fixed top-0 bottom-0 left-0 w-[85%] max-w-[320px] bg-light-bg dark:bg-dark-bg z-[60] shadow-2xl overflow-hidden"
                    >
                        <div className="flex flex-col h-full p-4">

                            {/* Header */}
                            <div className="flex items-center justify-between mb-4">
                                <Link to="/" onClick={onClose} className="flex items-center gap-2">
                                    <img
                                        src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/premium-logo.png"
                                        alt="Baptizo"
                                        className="h-8 w-auto"
                                    />
                                    <span className="text-xl font-bold font-display text-gradient">Baptizo</span>
                                </Link>
                                <button
                                    onClick={onClose}
                                    className="p-2 -mr-2 text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text transition-colors"
                                >
                                    <X className="w-6 h-6" />
                                </button>
                            </div>

                            {/* User Profile Summary */}
                            {user && (
                                <div className="mb-4 p-3 rounded-2xl bg-gradient-to-br from-primary-500/10 to-secondary-500/10 border border-light-border dark:border-dark-border">
                                    <div className="flex items-center gap-3 mb-2">
                                        <div className="w-10 h-10 rounded-full bg-gradient-to-br from-primary-500 to-secondary-500 flex items-center justify-center text-white font-bold text-sm overflow-hidden shrink-0">
                                            {user.avatarUrl ? (
                                                <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover" />
                                            ) : (
                                                user.name[0]
                                            )}
                                        </div>
                                        <div className="min-w-0">
                                            <h3 className="font-bold text-light-text dark:text-dark-text truncate text-sm">
                                                {user.name}
                                            </h3>
                                            <p className="text-[10px] text-light-muted dark:text-dark-muted truncate">
                                                {user.email}
                                            </p>
                                        </div>
                                    </div>
                                    <Link to="/profile" onClick={onClose}>
                                        <Button variant="outline" size="sm" className="w-full rounded-lg text-[10px] h-7 font-semibold">
                                            View Profile
                                        </Button>
                                    </Link>
                                </div>
                            )}

                            {/* Navigation Links */}
                            <nav className="space-y-2 flex-1 overflow-y-auto green-scrollbar">
                                {user ? (
                                    <>
                                        <button
                                            onClick={() => handleNavClick('/feed')}
                                            className="w-full flex items-center gap-3 p-3 rounded-xl hover:bg-light-card dark:hover:bg-dark-card transition-colors text-light-text dark:text-dark-text font-semibold"
                                        >
                                            <Home className="w-5 h-5 text-primary-500" />
                                            Feed
                                        </button>

                                        <button
                                            onClick={() => handleNavClick('/chat')}
                                            className="w-full flex items-center justify-between p-3 rounded-xl hover:bg-light-card dark:hover:bg-dark-card transition-colors text-light-text dark:text-dark-text font-semibold"
                                        >
                                            <div className="flex items-center gap-3">
                                                <MessageCircle className="w-5 h-5 text-blue-500" />
                                                Messages
                                            </div>
                                            {(badgeCounts?.messages || 0) > 0 && (
                                                <span className="bg-green-500 text-white text-[10px] font-bold h-4 min-w-[16px] px-1 flex items-center justify-center rounded-full">
                                                    {badgeCounts?.messages}
                                                </span>
                                            )}
                                        </button>
                                        <button
                                            onClick={() => handleNavClick('/banking', 'lastViewedBanking')}
                                            className="w-full flex items-center justify-between p-3 rounded-xl hover:bg-light-card dark:hover:bg-dark-card transition-colors text-light-text dark:text-dark-text font-semibold"
                                        >
                                            <div className="flex items-center gap-3">
                                                <Wallet className="w-5 h-5 text-emerald-500" />
                                                Banking
                                            </div>
                                            {(badgeCounts?.banking || 0) > 0 && (
                                                <div className="w-2 h-2 rounded-full bg-green-500 shadow-[0_0_8px_rgba(34,197,94,0.5)]" />
                                            )}
                                        </button>
                                        <button
                                            onClick={() => handleNavClick('/leaderboard', 'lastViewedLeaderboard')}
                                            className="w-full flex items-center justify-between p-3 rounded-xl hover:bg-light-card dark:hover:bg-dark-card transition-colors text-light-text dark:text-dark-text font-semibold"
                                        >
                                            <div className="flex items-center gap-3">
                                                <Trophy className="w-5 h-5 text-amber-500" />
                                                Leaderboard
                                            </div>
                                            {(badgeCounts?.leaderboard || 0) > 0 && (
                                                <div className="w-2 h-2 rounded-full bg-green-500 shadow-[0_0_8px_rgba(34,197,94,0.5)]" />
                                            )}
                                        </button>
                                        <Link
                                            to="/saved-posts"
                                            onClick={onClose}
                                            className="flex items-center gap-3 p-3 rounded-xl hover:bg-light-card dark:hover:bg-dark-card transition-colors text-light-text dark:text-dark-text font-semibold"
                                        >
                                            <Bookmark className="w-5 h-5 text-purple-500" />
                                            Saved Posts
                                        </Link>
                                        <Link
                                            to="/diamond-challenge"
                                            onClick={onClose}
                                            className="flex items-center gap-3 p-3 rounded-xl hover:bg-light-card dark:hover:bg-dark-card transition-colors text-light-text dark:text-dark-text font-semibold"
                                        >
                                            <Brain className="w-5 h-5 text-cyan-500" />
                                            Diamond Challenge
                                        </Link>

                                        {(user.role === 'admin' || user.role === 'super_admin') && (
                                            <Link
                                                to="/admin"
                                                onClick={onClose}
                                                className="flex items-center gap-3 p-3 rounded-xl hover:bg-primary-500/10 text-primary-500 transition-colors font-bold mt-4"
                                            >
                                                <LayoutDashboard className="w-5 h-5" />
                                                Admin Dashboard
                                            </Link>
                                        )}

                                        {user.role === 'board_member' && (
                                            <Link
                                                to="/board"
                                                onClick={onClose}
                                                className="flex items-center gap-3 p-3 rounded-xl hover:bg-secondary-500/10 text-secondary-500 transition-colors font-bold mt-4"
                                            >
                                                <LayoutDashboard className="w-5 h-5" />
                                                Board Dashboard
                                            </Link>
                                        )}

                                        <div className="mt-6 flex justify-center">
                                            <MemberTicker />
                                        </div>
                                    </>
                                ) : (
                                    <>
                                        <Link to="/login" onClick={onClose} className="block mb-4">
                                            <div className="flex items-center gap-3 p-3 rounded-xl hover:bg-light-card dark:hover:bg-dark-card transition-colors text-light-text dark:text-dark-text font-semibold">
                                                <User className="w-5 h-5" />
                                                Login
                                            </div>
                                        </Link>
                                        <Link to="/register" onClick={onClose} className="block">
                                            <Button className="w-full font-bold">Get Started</Button>
                                        </Link>
                                    </>
                                )}
                            </nav>

                            {/* Footer Actions */}
                            <div className="pt-6 border-t border-light-border dark:border-dark-border mt-auto">
                                <div className="flex items-center justify-between mb-4">
                                    <span className="text-sm font-medium text-light-text dark:text-dark-text">
                                        Appearance
                                    </span>
                                    <button
                                        onClick={toggleTheme}
                                        className="p-2 rounded-lg hover:bg-light-card dark:hover:bg-dark-card transition-colors text-light-text dark:text-dark-text"
                                    >
                                        {theme === 'dark' ? (
                                            <div className="flex items-center gap-2">
                                                <Sun className="w-5 h-5 text-yellow-500" />
                                                <span className="text-xs">Dark</span>
                                            </div>
                                        ) : (
                                            <div className="flex items-center gap-2">
                                                <Moon className="w-5 h-5 text-slate-700" />
                                                <span className="text-xs">Light</span>
                                            </div>
                                        )}
                                    </button>
                                </div>

                                {user && (
                                    <button
                                        onClick={handleLogout}
                                        className="w-full p-3 rounded-xl bg-red-500/10 text-red-500 hover:bg-red-500 hover:text-white transition-all flex items-center justify-center gap-2 font-bold"
                                    >
                                        <LogOut className="w-5 h-5" />
                                        Logout
                                    </button>
                                )}
                            </div>
                        </div>
                    </motion.div>
                </>
            )}
        </AnimatePresence>
    );
}
