import { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { PageLoader } from '../components/ui/PageLoader';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { CheckCircle, Clock, FileText } from 'lucide-react';
import { BackButton } from '../components/ui/BackButton';

export default function UserTasksPage() {
    const { user } = useAuth();
    const [submissionContent, setSubmissionContent] = useState('');
    const [activeTask, setActiveTask] = useState<string | null>(null);

    const assignments = useQuery(
        api.assignments.getUserAssignments,
        user ? { userId: user._id } : 'skip'
    );

    const submitAssignment = useMutation(api.assignments.submitAssignment);

    if (!user) return null;
    if (assignments === undefined) return <PageLoader />;

    const pendingAssignments = assignments.filter((a: any) => a.status === 'pending');
    const completedAssignments = assignments.filter((a: any) => a.status !== 'pending');

    const handleSubmit = async (assignmentId: string) => {
        if (!submissionContent.trim()) return;

        try {
            // @ts-ignore - ID type mismatch in quick scaffold
            await submitAssignment({
                assignmentId: assignmentId as any,
                userId: user._id,
                content: submissionContent,
            });
            setActiveTask(null);
            setSubmissionContent('');
        } catch (error) {
            console.error("Failed to submit:", error);
            alert("Failed to submit assignment");
        }
    };

    return (
        <div className="container max-w-4xl mx-auto px-4 py-8 pb-20">
            <BackButton />
            <h1 className="text-3xl md:text-4xl font-bold mb-6 text-gradient font-display">My Tasks</h1>

            {/* Pending Tasks */}
            <div className="mb-8">
                <h2 className="text-xl font-semibold mb-4 text-light-text dark:text-dark-text flex items-center gap-2">
                    <Clock className="w-5 h-5 text-amber-500" />
                    Pending ({pendingAssignments.length})
                </h2>

                {pendingAssignments.length === 0 ? (
                    <p className="text-light-muted dark:text-dark-muted italic">No pending tasks.</p>
                ) : (
                    <div className="space-y-4">
                        {pendingAssignments.map((task: any) => (
                            <div key={task._id} className="glass p-6 rounded-xl shadow-sm border border-amber-500/20">
                                <div className="flex justify-between items-start mb-4">
                                    <div>
                                        <h3 className="font-medium text-lg text-light-text dark:text-dark-text mb-1">
                                            Instruction from {task.adminName}
                                        </h3>
                                        <p className="text-sm text-light-muted dark:text-dark-muted">
                                            Re: "{task.postContent?.substring(0, 50)}..."
                                        </p>
                                    </div>
                                    <Badge variant="warning">Pending</Badge>
                                </div>

                                <div className="bg-light-bg dark:bg-dark-bg p-4 rounded-lg mb-4 text-light-text dark:text-dark-text">
                                    {task.instructions}
                                </div>

                                {activeTask === task._id ? (
                                    <div className="space-y-3">
                                        <textarea
                                            value={submissionContent}
                                            onChange={(e) => setSubmissionContent(e.target.value)}
                                            placeholder="Type your response here..."
                                            className="w-full min-h-[150px] p-4 rounded-lg border border-light-border dark:border-dark-border bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text focus:ring-2 focus:ring-primary-500"
                                        />
                                        <div className="flex gap-2 justify-end">
                                            <Button variant="ghost" onClick={() => setActiveTask(null)}>Cancel</Button>
                                            <Button
                                                variant="primary"
                                                onClick={() => handleSubmit(task._id)}
                                                disabled={!submissionContent.trim()}
                                            >
                                                Submit Response
                                            </Button>
                                        </div>
                                    </div>
                                ) : (
                                    <Button variant="primary" onClick={() => setActiveTask(task._id)}>
                                        Start Task
                                    </Button>
                                )}
                            </div>
                        ))}
                    </div>
                )}
            </div>

            {/* Completed Tasks */}
            <div>
                <h2 className="text-xl font-semibold mb-4 text-light-text dark:text-dark-text flex items-center gap-2">
                    <FileText className="w-5 h-5 text-primary-500" />
                    History
                </h2>

                <div className="space-y-4">
                    {completedAssignments.map((task: any) => (
                        <div key={task._id} className="glass p-6 rounded-xl shadow-sm opacity-80 hover:opacity-100 transition-opacity">
                            <div className="flex justify-between items-start mb-4">
                                <div>
                                    <h3 className="font-medium text-lg text-light-text dark:text-dark-text mb-1">
                                        Task from {task.adminName}
                                    </h3>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">
                                        Re: "{task.postContent?.substring(0, 30)}..."
                                    </p>
                                </div>
                                <Badge variant={
                                    task.status === 'approved' ? 'success' :
                                        task.status === 'rejected' ? 'danger' : 'default'
                                }>
                                    {task.status.charAt(0).toUpperCase() + task.status.slice(1)}
                                </Badge>
                            </div>

                            <div className="mb-4 space-y-2">
                                <p className="text-sm font-semibold text-light-text dark:text-dark-text">Instructions:</p>
                                <p className="text-sm text-light-muted dark:text-dark-muted bg-light-bg dark:bg-dark-bg p-2 rounded">
                                    {task.instructions}
                                </p>
                            </div>

                            <div className="mb-4 space-y-2">
                                <p className="text-sm font-semibold text-light-text dark:text-dark-text">Your Submission:</p>
                                <p className="text-sm text-light-text dark:text-dark-text bg-light-bg dark:bg-dark-bg p-3 rounded">
                                    {task.submissionContent}
                                </p>
                            </div>

                            {(task.grade || task.feedback) && (
                                <div className={`p-4 rounded-lg ${task.status === 'approved' ? 'bg-green-500/10 border border-green-500/20' :
                                    task.status === 'rejected' ? 'bg-red-500/10 border border-red-500/20' :
                                        'bg-primary-500/10 border border-primary-500/20'
                                    }`}>
                                    {task.grade && (
                                        <div className="flex items-center gap-2 mb-2">
                                            <CheckCircle className="w-4 h-4" />
                                            <span className="font-bold">Grade: {task.grade}</span>
                                        </div>
                                    )}
                                    {task.feedback && (
                                        <p className="text-sm italic">"{task.feedback}"</p>
                                    )}
                                </div>
                            )}
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}
