import { useNavigate } from 'react-router-dom';
import { useAuth } from '@/hooks/useAuth';
import { Card, CardContent } from '@/components/ui/Card';
import { Image, Smile, Search } from 'lucide-react';

export function PostInput() {
    const navigate = useNavigate();
    const { user } = useAuth();

    if (!user) return null;

    return (
        <Card glass className="hidden md:block mb-6 overflow-hidden border-primary-500/10 hover:border-primary-500/20 transition-all duration-300 group">
            <CardContent className="p-6">
                <div className="flex items-center gap-4 mb-6">
                    <div className="w-12 h-12 rounded-2xl bg-gradient-to-br from-primary-500 to-secondary-500 p-[2px] shadow-lg shadow-primary-500/20 shrink-0">
                        <div className="w-full h-full rounded-2xl bg-white dark:bg-dark-bg overflow-hidden flex items-center justify-center font-bold text-xl text-gradient">
                            {user.avatarUrl ? (
                                <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover" />
                            ) : (
                                user.name[0]
                            )}
                        </div>
                    </div>

                    <button
                        onClick={() => navigate('/create-post')}
                        className="flex-1 h-12 px-6 rounded-2xl bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border/50 dark:border-dark-border/50 text-left text-light-muted dark:text-dark-muted hover:bg-light-bg dark:hover:bg-dark-bg hover:border-primary-500/30 transition-all text-base font-medium flex items-center justify-between group/input"
                    >
                        <span>What's on your mind, {user.name.split(' ')[0]}?</span>
                        <div className="flex items-center gap-2 px-3 py-1.5 rounded-xl bg-primary-500 text-white text-[10px] font-bold uppercase tracking-wider group-hover/input:scale-105 transition-transform">
                            <Search className="w-3 h-3" /> Post
                        </div>
                    </button>
                </div>

                <div className="grid grid-cols-2 gap-4 pt-6 border-t border-light-border/20 dark:border-dark-border/20">
                    <button
                        onClick={() => navigate('/create-post')}
                        className="flex items-center justify-center gap-2 p-3 rounded-xl hover:bg-red-500/10 transition-colors group/btn"
                    >
                        <Image className="w-5 h-5 text-red-500 group-hover/btn:scale-110 transition-transform" />
                        <span className="text-sm font-bold text-light-muted dark:text-dark-muted group-hover/btn:text-red-500">Media</span>
                    </button>
                    <button
                        onClick={() => navigate('/create-post')}
                        className="flex items-center justify-center gap-2 p-3 rounded-xl hover:bg-emerald-500/10 transition-colors group/btn"
                    >
                        <Smile className="w-5 h-5 text-emerald-500 group-hover/btn:scale-110 transition-transform" />
                        <span className="text-sm font-bold text-light-muted dark:text-dark-muted group-hover/btn:text-emerald-500">Activity</span>
                    </button>
                </div>
            </CardContent>
        </Card>
    );
}
