import { X, Download, Share2, ArrowLeft } from 'lucide-react';
import { motion } from 'framer-motion';

interface ImageLightboxProps {
    src: string;
    alt: string;
    onClose: () => void;
    layoutId?: string;
}

export function ImageLightbox({ src, alt, onClose, layoutId }: ImageLightboxProps) {
    const handleShare = async () => {
        if (navigator.share) {
            try {
                await navigator.share({
                    title: alt || 'Baptizo Image',
                    text: 'Check out this image',
                    url: src
                });
            } catch (err) {
                console.error('Error sharing:', err);
            }
        } else {
            try {
                await navigator.clipboard.writeText(src);
                alert('Image link copied to clipboard!');
            } catch (err) {
                console.error('Failed to copy:', err);
            }
        }
    };

    const handleDownload = async () => {
        try {
            const response = await fetch(src);
            const blob = await response.blob();
            const url = window.URL.createObjectURL(blob);
            const link = document.createElement('a');
            link.href = url;
            link.download = alt || 'baptizo-image.jpg';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            window.URL.revokeObjectURL(url);
        } catch (err) {
            console.error('Failed to download:', err);
            // Fallback: open in new tab
            window.open(src, '_blank');
        }
    };

    return (
        <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-[100] flex flex-col items-center justify-center bg-black/95 backdrop-blur-sm shadow-2xl overflow-hidden"
            onClick={onClose}
        >
            {/* Top Bar - WhatsApp Style */}
            <div
                className="absolute top-0 left-0 right-0 z-[110] flex items-center justify-between p-4 bg-gradient-to-b from-black/60 to-transparent"
                onClick={(e) => e.stopPropagation()}
            >
                <div className="flex items-center gap-3">
                    <button
                        onClick={onClose}
                        className="p-2 rounded-full hover:bg-white/10 transition-colors text-white"
                        aria-label="Back"
                    >
                        <ArrowLeft className="w-6 h-6" />
                    </button>
                    <span className="text-white font-medium truncate max-w-[200px]">{alt || 'Image'}</span>
                </div>

                <div className="flex items-center gap-2">
                    <button
                        onClick={handleShare}
                        className="p-2 rounded-full hover:bg-white/10 transition-colors text-white"
                        title="Share image"
                    >
                        <Share2 className="w-5 h-5" />
                    </button>
                    <button
                        onClick={handleDownload}
                        className="p-2 rounded-full hover:bg-white/10 transition-colors text-white"
                        title="Download image"
                    >
                        <Download className="w-5 h-5" />
                    </button>
                    <button
                        onClick={onClose}
                        className="p-2 rounded-full hover:bg-white/10 transition-colors text-white"
                    >
                        <X className="w-6 h-6" />
                    </button>
                </div>
            </div>

            {/* Image Container with Pop Animation */}
            <motion.div
                className="relative z-[105] w-full h-full flex items-center justify-center p-4"
                onClick={(e) => e.stopPropagation()}
            >
                <motion.img
                    layoutId={layoutId}
                    src={src}
                    alt={alt}
                    className="max-w-full max-h-full object-contain shadow-2xl rounded-sm"
                    draggable={false}
                />
            </motion.div>

            {/* Footer - Optional extra space for caption if needed */}
            <div className="absolute bottom-0 left-0 right-0 p-8 bg-gradient-to-t from-black/60 to-transparent pointer-events-none">
                <p className="text-white/60 text-sm text-center max-w-2xl mx-auto">
                    {alt}
                </p>
            </div>
        </motion.div>
    );
}

