import { useState, useEffect } from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { useAuth } from '@/hooks/useAuth';
import { useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';

import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Badge } from '@/components/ui/Badge';
import { Modal } from '@/components/ui/Modal';
import { PremiumSelect } from '@/components/ui/PremiumSelect';
import { Phone, Briefcase, CreditCard, Check } from 'lucide-react';
import { BackButton } from '@/components/ui/BackButton';
import { detectPhoneNetwork } from '@/utils/phoneNetwork';

const BANK_NAMES = [
    "Access Bank", "GTBank", "First Bank", "UBA", "Zenith Bank",
    "Kuda Bank", "Opay", "PalmPay", "Moniepoint", "Stanbic IBTC",
    "Fidelity Bank", "Union Bank", "FCMB", "Sterling Bank", "Wema Bank",
    "Other"
];

export function MemberOnboarding() {
    const { user, isLoading: authLoading } = useAuth();
    const updateProfile = useMutation(api.users.updateProfile);
    const location = useLocation();

    // Wait for auth to load
    if (authLoading) {
        return (
            <div className="min-h-screen flex items-center justify-center">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    // Redirect if not logged in
    if (!user) {
        return <Navigate to="/login" state={{ from: location }} replace />;
    }

    const [isLoading, setIsLoading] = useState(false);

    const [formData, setFormData] = useState({
        phone: '',
        network: '',
        whatsappVerified: false,
        profession: '',
        bankName: '',
        accountNumber: '',
        accountName: '',
        customBankName: '',
        sameAsPhone: false
    });

    useEffect(() => {
        if (user) {
            setFormData(prev => ({
                ...prev,
                phone: user.phone || '',
                network: user.network || '',
                whatsappVerified: user.whatsappVerified || false,
                profession: user.profession || '',
                bankName: user.bankDetails?.bankName || '',
                accountNumber: user.bankDetails?.accountNumber || '',
                accountName: user.bankDetails?.accountName || user.name || '',
                customBankName: !BANK_NAMES.includes(user.bankDetails?.bankName || '') ? user.bankDetails?.bankName || '' : ''
            }));
        }
    }, [user]);

    const handlePhoneChange = (value: string) => {
        const network = detectPhoneNetwork(value);
        setFormData(prev => ({
            ...prev,
            phone: value,
            network,
            accountNumber: prev.sameAsPhone ? value : prev.accountNumber
        }));
    };

    const handleSameAsPhone = (checked: boolean) => {
        setFormData(prev => ({
            ...prev,
            sameAsPhone: checked,
            accountNumber: checked ? prev.phone : prev.accountNumber
        }));
    };

    const [successModalOpen, setSuccessModalOpen] = useState(false);

    // ... (rest of the code)

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);
        try {
            const finalBankName = formData.bankName === 'Other' ? formData.customBankName : formData.bankName;

            await updateProfile({
                userId: user!._id,
                phone: formData.phone,
                network: formData.network as any,
                whatsappVerified: formData.whatsappVerified,
                profession: formData.profession,
                bankDetails: {
                    bankName: finalBankName,
                    accountNumber: formData.accountNumber,
                    accountName: formData.accountName
                }
            });
            setSuccessModalOpen(true);
        } catch (error) {
            console.error('Failed to update profile', error);
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="min-h-screen py-8 px-4">
            <div className="max-w-2xl mx-auto">
                <div className="flex items-center gap-4 mb-8">
                    <BackButton />
                    <h1 className="text-3xl font-bold text-gradient">Member Details</h1>
                </div>

                <Card glass>
                    <CardHeader>
                        <CardTitle>Update Your Information</CardTitle>
                        <p className="text-light-muted dark:text-dark-muted">
                            Help us keep our community records up to date.
                        </p>
                    </CardHeader>
                    <CardContent>
                        <form onSubmit={handleSubmit} className="space-y-6">
                            {/* Contact Info */}
                            <div className="space-y-4">
                                <h3 className="text-lg font-semibold flex items-center gap-2">
                                    <Phone className="w-5 h-5 text-primary-500" />
                                    Contact Details
                                </h3>
                                <div className="grid md:grid-cols-2 gap-4">
                                    <div>
                                        <Input
                                            label="Phone Number"
                                            value={formData.phone}
                                            onChange={(e) => handlePhoneChange(e.target.value)}
                                            placeholder="08012345678"
                                            required
                                        />
                                        {formData.network && formData.network !== 'Unknown' && (
                                            <div className="mt-1">
                                                <Badge variant="primary">{formData.network}</Badge>
                                            </div>
                                        )}
                                    </div>
                                    <div className="flex items-end pb-3">
                                        <label className="flex items-center gap-2 cursor-pointer">
                                            <input
                                                type="checkbox"
                                                checked={formData.whatsappVerified}
                                                onChange={(e) => setFormData({ ...formData, whatsappVerified: e.target.checked })}
                                                className="w-4 h-4 rounded border-primary-500 text-primary-500 focus:ring-primary-500"
                                            />
                                            <span className="text-sm">Is this your WhatsApp number?</span>
                                        </label>
                                    </div>
                                </div>
                            </div>

                            <hr className="border-light-border/20 dark:border-dark-border/20" />

                            {/* Professional Info */}
                            <div className="space-y-4">
                                <h3 className="text-lg font-semibold flex items-center gap-2">
                                    <Briefcase className="w-5 h-5 text-secondary-500" />
                                    Professional Info
                                </h3>
                                <Input
                                    label="Profession / Occupation"
                                    value={formData.profession}
                                    onChange={(e) => setFormData({ ...formData, profession: e.target.value })}
                                    placeholder="e.g. Software Engineer, Student, Business Owner"
                                />
                            </div>

                            <hr className="border-light-border/20 dark:border-dark-border/20" />

                            {/* Bank Details */}
                            <div className="space-y-4">
                                <h3 className="text-lg font-semibold flex items-center gap-2">
                                    <CreditCard className="w-5 h-5 text-accent-500" />
                                    Bank Details
                                </h3>

                                <div className="space-y-3">
                                    <PremiumSelect
                                        label="Bank Name"
                                        value={formData.bankName}
                                        onChange={(val) => setFormData({ ...formData, bankName: val })}
                                        options={BANK_NAMES.map(bank => ({ value: bank, label: bank }))}
                                    />

                                    {formData.bankName === 'Other' && (
                                        <Input
                                            label="Enter Bank Name"
                                            value={formData.customBankName}
                                            onChange={(e) => setFormData({ ...formData, customBankName: e.target.value })}
                                            placeholder="Bank Name"
                                            required
                                        />
                                    )}

                                    <div className="space-y-2">
                                        <Input
                                            label="Account Number"
                                            value={formData.accountNumber}
                                            onChange={(e) => setFormData({ ...formData, accountNumber: e.target.value })}
                                            placeholder="0123456789"
                                            required
                                        />
                                        <label className="flex items-center gap-2 cursor-pointer text-sm text-light-muted dark:text-dark-muted">
                                            <input
                                                type="checkbox"
                                                checked={formData.sameAsPhone}
                                                onChange={(e) => handleSameAsPhone(e.target.checked)}
                                                className="w-4 h-4 rounded border-primary-500"
                                            />
                                            Use my phone number as account number
                                        </label>
                                    </div>

                                    <Input
                                        label="Account Name"
                                        value={formData.accountName}
                                        onChange={(e) => setFormData({ ...formData, accountName: e.target.value })}
                                        placeholder="Account Holder Name"
                                        required
                                    />
                                </div>
                            </div>

                            <Button type="submit" variant="primary" className="w-full" isLoading={isLoading}>
                                Save Details
                            </Button>
                        </form>
                    </CardContent>
                </Card>

                <Modal
                    isOpen={successModalOpen}
                    onClose={() => setSuccessModalOpen(false)}
                    title="Profile Updated!"
                >
                    <div className="text-center p-4">
                        <div className="w-16 h-16 bg-green-100 dark:bg-green-900 rounded-full flex items-center justify-center mx-auto mb-4">
                            <Check className="w-8 h-8 text-green-600 dark:text-green-400" />
                        </div>
                        <p className="text-light-muted dark:text-dark-muted mb-6">
                            Thank you for updating your member details.
                        </p>
                        <Button
                            variant="primary"
                            className="w-full"
                            onClick={() => {
                                setSuccessModalOpen(false);
                                // Optional: navigate back or somewhere else
                            }}
                        >
                            Close
                        </Button>
                    </div>
                </Modal>
            </div>
        </div>
    );
}
