import React, { useState, useRef } from 'react';
import { Upload, X, Loader2, Check } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { uploadToImageKit, validateImageFile, getOptimizedImageUrl } from '@/lib/imagekit';
import { Button } from '@/components/ui/Button';

interface AvatarUploadProps {
    currentAvatarUrl?: string;
    onUploadComplete: (url: string) => void;
    size?: 'sm' | 'md' | 'lg' | 'xl';
}

export function AvatarUpload({ currentAvatarUrl, onUploadComplete, size = 'lg' }: AvatarUploadProps) {
    const [isUploading, setIsUploading] = useState(false);
    const [progress, setProgress] = useState(0);
    const [previewUrl, setPreviewUrl] = useState(currentAvatarUrl);
    const [showSuccess, setShowSuccess] = useState(false);
    const fileInputRef = useRef<HTMLInputElement>(null);

    const sizeClasses = {
        sm: 'w-16 h-16',
        md: 'w-24 h-24',
        lg: 'w-32 h-32',
        xl: 'w-40 h-40',
    };

    const handleFileSelect = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file) return;

        if (!validateImageFile(file)) {
            alert('Please upload a valid image file (JPG, PNG, GIF, or WebP)');
            return;
        }

        // Show preview
        const reader = new FileReader();
        reader.onloadend = () => {
            setPreviewUrl(reader.result as string);
        };
        reader.readAsDataURL(file);

        // Upload to ImageKit
        setIsUploading(true);
        setProgress(0);
        setShowSuccess(false);

        try {
            const url = await uploadToImageKit(file, 'avatars', setProgress);
            onUploadComplete(url);
            setPreviewUrl(url);

            // Show success message
            setShowSuccess(true);
            setTimeout(() => setShowSuccess(false), 3000);
        } catch (error) {
            console.error('Upload failed:', error);
            alert('Failed to upload avatar. Please try again.');
            setPreviewUrl(currentAvatarUrl);
        } finally {
            setIsUploading(false);
            setProgress(0);
        }
    };

    const handleRemove = () => {
        setPreviewUrl(undefined);
        onUploadComplete('');
        if (fileInputRef.current) {
            fileInputRef.current.value = '';
        }
    };

    return (
        <div className="flex flex-col items-center gap-4">
            <div className="relative">
                <div
                    className={`${sizeClasses[size]} rounded-full overflow-hidden border-4 border-light-border dark:border-dark-border bg-light-card dark:bg-dark-card flex items-center justify-center`}
                >
                    {previewUrl ? (
                        <img
                            src={getOptimizedImageUrl(previewUrl, 200, 200)}
                            alt="Avatar"
                            className="w-full h-full object-cover"
                        />
                    ) : (
                        <Upload className="w-8 h-8 text-light-muted dark:text-dark-muted" />
                    )}
                </div>

                {isUploading && (
                    <div className="absolute inset-0 rounded-full bg-black/50 flex items-center justify-center">
                        <div className="text-center">
                            <Loader2 className="w-6 h-6 text-white animate-spin mx-auto mb-1" />
                            <span className="text-xs text-white font-medium">{progress}%</span>
                        </div>
                    </div>
                )}

                {previewUrl && !isUploading && (
                    <button
                        onClick={handleRemove}
                        className="absolute -top-2 -right-2 w-6 h-6 rounded-full bg-red-500 hover:bg-red-600 flex items-center justify-center transition-colors"
                    >
                        <X className="w-4 h-4 text-white" />
                    </button>
                )}
            </div>

            <input
                ref={fileInputRef}
                type="file"
                accept="image/*"
                onChange={handleFileSelect}
                className="hidden"
                disabled={isUploading}
            />

            <Button
                onClick={() => fileInputRef.current?.click()}
                variant="outline"
                size="sm"
                disabled={isUploading}
            >
                <Upload className="w-4 h-4 mr-2" />
                {previewUrl ? 'Change Avatar' : 'Upload Avatar'}
            </Button>

            <AnimatePresence>
                {showSuccess && (
                    <motion.div
                        initial={{ opacity: 0, y: -10 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: -10 }}
                        className="flex items-center gap-2 px-3 py-2 rounded-lg bg-green-50 dark:bg-green-950 border border-green-200 dark:border-green-800"
                    >
                        <Check className="w-4 h-4 text-green-600 dark:text-green-400" />
                        <span className="text-sm font-medium text-green-700 dark:text-green-300">
                            Avatar saved successfully!
                        </span>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    );
}
