import { useState, useRef, useEffect } from 'react';
import { Modal } from '../ui/Modal';
import { Button } from '../ui/Button';
import { Input } from '../ui/Input';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useToast } from '@/context/ToastContext';
import { Calendar, Image, X, Upload, ChevronDown, Check } from 'lucide-react';
import { AnimatePresence, motion } from 'framer-motion';

interface SubmitPaymentModalProps {
    isOpen: boolean;
    onClose: () => void;
    user: any;
}

//  Reusable custom dropdown 
interface DropdownOption { label: string; value: number; }
interface CustomDropdownProps {
    label: string;
    options: DropdownOption[];
    value: number;
    onChange: (v: number) => void;
}

function CustomDropdown({ label, options, value, onChange }: CustomDropdownProps) {
    const [open, setOpen] = useState(false);
    const ref = useRef<HTMLDivElement>(null);
    const selectedLabel = options.find(o => o.value === value)?.label ?? '';

    // Close on outside click
    useEffect(() => {
        const handler = (e: MouseEvent) => {
            if (ref.current && !ref.current.contains(e.target as Node)) setOpen(false);
        };
        document.addEventListener('mousedown', handler);
        return () => document.removeEventListener('mousedown', handler);
    }, []);

    return (
        <div ref={ref} className="relative">
            <label className="block text-sm font-bold mb-2 uppercase tracking-wide text-light-muted">
                {label}
            </label>

            {/* Trigger */}
            <button
                type="button"
                onClick={() => setOpen(prev => !prev)}
                className={`
                    w-full flex items-center justify-between gap-2
                    px-4 py-3 rounded-xl border text-sm font-bold
                    bg-light-bg dark:bg-dark-bg
                    text-light-text dark:text-dark-text
                    transition-all duration-200
                    ${open
                        ? 'border-primary-500 ring-2 ring-primary-500/20'
                        : 'border-light-border dark:border-dark-border hover:border-primary-400'}
                `}
            >
                <span>{selectedLabel}</span>
                <ChevronDown className={`w-4 h-4 text-light-muted transition-transform duration-300 ${open ? 'rotate-180' : ''}`} />
            </button>

            {/* Options panel */}
            <AnimatePresence>
                {open && (
                    <motion.div
                        initial={{ opacity: 0, y: -6, scale: 0.97 }}
                        animate={{ opacity: 1, y: 0, scale: 1 }}
                        exit={{ opacity: 0, y: -6, scale: 0.97 }}
                        transition={{ duration: 0.15, ease: 'easeOut' }}
                        className="
                            absolute z-50 left-0 right-0 mt-1
                            bg-white dark:bg-dark-card
                            border border-light-border dark:border-dark-border
                            rounded-xl shadow-xl shadow-black/10
                            overflow-hidden
                        "
                    >
                        <div className="max-h-48 overflow-y-auto premium-scrollbar py-1">
                            {options.map(opt => {
                                const isSelected = opt.value === value;
                                return (
                                    <button
                                        key={opt.value}
                                        type="button"
                                        onClick={() => { onChange(opt.value); setOpen(false); }}
                                        className={`
                                            w-full flex items-center justify-between
                                            px-4 py-2.5 text-sm font-semibold text-left
                                            transition-colors duration-150
                                            ${isSelected
                                                ? 'bg-primary-500/10 text-primary-600 dark:text-primary-400'
                                                : 'text-light-text dark:text-dark-text hover:bg-light-bg dark:hover:bg-dark-bg'
                                            }
                                        `}
                                    >
                                        {opt.label}
                                        {isSelected && <Check className="w-4 h-4 shrink-0 text-primary-500" />}
                                    </button>
                                );
                            })}
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    );
}

//  Month & Year option arrays 
const MONTH_OPTIONS: DropdownOption[] = Array.from({ length: 12 }, (_, i) => ({
    value: i + 1,
    label: new Date(2024, i).toLocaleDateString('en-US', { month: 'long' }),
}));

const YEAR_OPTIONS: DropdownOption[] = [2024, 2025, 2026, 2027].map(y => ({
    value: y,
    label: String(y),
}));

//  Main Modal 
export function SubmitPaymentModal({ isOpen, onClose, user }: SubmitPaymentModalProps) {
    const [amount, setAmount] = useState('1000');
    const [month, setMonth] = useState(new Date().getMonth() + 1);
    const [year, setYear] = useState(new Date().getFullYear());
    const [proofFile, setProofFile] = useState<File | null>(null);
    const [isUploading, setIsUploading] = useState(false);
    const [isSubmitting, setIsSubmitting] = useState(false);

    const generateUploadUrl = useMutation(api.files.generateUploadUrl);
    const submitPayment = useMutation(api.finance.submitPaymentSubmission);
    const { showToast } = useToast();

    const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file) return;
        if (!file.type.startsWith('image/')) {
            showToast('error', 'Please upload an image file');
            return;
        }
        if (file.size > 5 * 1024 * 1024) {
            showToast('error', 'File size must be less than 5MB');
            return;
        }
        setProofFile(file);
    };

    const handleSubmit = async () => {
        if (!proofFile) {
            showToast('error', 'Please upload a proof of payment');
            return;
        }
        setIsSubmitting(true);
        try {
            setIsUploading(true);
            const uploadUrl = await generateUploadUrl();
            const result = await fetch(uploadUrl, {
                method: 'POST',
                headers: { 'Content-Type': proofFile.type },
                body: proofFile,
            });
            const { storageId } = await result.json();
            setIsUploading(false);

            await submitPayment({
                userId: user._id,
                amount: parseFloat(amount),
                month,
                year,
                proofStorageId: storageId,
            });

            showToast('success', 'Payment submission sent for approval!');
            handleClose();
        } catch (error: any) {
            console.error('Failed to submit payment:', error);
            showToast('error', error.message || 'Failed to submit payment');
        } finally {
            setIsUploading(false);
            setIsSubmitting(false);
        }
    };

    const handleClose = () => {
        setAmount('1000');
        setMonth(new Date().getMonth() + 1);
        setYear(new Date().getFullYear());
        setProofFile(null);
        onClose();
    };

    const monthName = new Date(year, month - 1).toLocaleDateString('en-US', { month: 'long', year: 'numeric' });

    return (
        <Modal isOpen={isOpen} onClose={handleClose} title="Submit Board Due Payment">
            <div className="space-y-5">

                {/* Paying-for banner */}
                <div className="p-4 rounded-2xl bg-primary-500/5 border border-primary-500/10 flex items-center gap-4">
                    <div className="w-11 h-11 rounded-full bg-primary-500/10 flex items-center justify-center shrink-0">
                        <Calendar className="w-5 h-5 text-primary-500" />
                    </div>
                    <div>
                        <p className="text-[10px] font-bold text-light-muted uppercase tracking-widest">Paying For</p>
                        <p className="font-bold text-light-text dark:text-dark-text">{monthName}</p>
                    </div>
                </div>

                {/* Amount + Month + Year row */}
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                    {/* Amount */}
                    <div>
                        <label className="block text-sm font-bold mb-2 uppercase tracking-wide text-light-muted">
                            Amount ()
                        </label>
                        <Input
                            type="number"
                            value={amount}
                            onChange={(e) => setAmount(e.target.value)}
                            placeholder="1000"
                            className="font-bold text-lg"
                        />
                    </div>

                    {/* Month & Year side by side */}
                    <div className="grid grid-cols-2 gap-2">
                        <CustomDropdown
                            label="Month"
                            options={MONTH_OPTIONS}
                            value={month}
                            onChange={setMonth}
                        />
                        <CustomDropdown
                            label="Year"
                            options={YEAR_OPTIONS}
                            value={year}
                            onChange={setYear}
                        />
                    </div>
                </div>

                {/* Proof upload */}
                <div>
                    <label className="block text-sm font-bold mb-2 uppercase tracking-wide text-light-muted">
                        Payment Proof (Screenshot)
                    </label>

                    {!proofFile ? (
                        <label className="flex flex-col items-center justify-center w-full h-36 border-2 border-dashed border-light-border dark:border-dark-border rounded-2xl hover:border-primary-500 cursor-pointer bg-light-bg/30 dark:bg-dark-bg/30 transition-all group">
                            <div className="flex flex-col items-center gap-2">
                                <div className="p-3 rounded-full bg-primary-500/10 text-primary-500 group-hover:bg-primary-500/20 transition-colors">
                                    <Upload className="w-5 h-5" />
                                </div>
                                <p className="text-sm font-bold text-light-text dark:text-dark-text">Click to upload proof</p>
                                <p className="text-xs text-light-muted">PNG or JPG (Max 5MB)</p>
                            </div>
                            <input type="file" className="hidden" accept="image/*" onChange={handleFileChange} />
                        </label>
                    ) : (
                        <div className="relative group rounded-2xl overflow-hidden border border-light-border dark:border-dark-border bg-black/5">
                            <img
                                src={URL.createObjectURL(proofFile)}
                                alt="Proof Preview"
                                className="w-full h-44 object-contain"
                            />
                            <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                                <button
                                    onClick={() => setProofFile(null)}
                                    className="p-3 rounded-full bg-rose-500 text-white shadow-lg hover:scale-110 transition-transform"
                                >
                                    <X className="w-5 h-5" />
                                </button>
                            </div>
                            <div className="p-3 bg-light-bg dark:bg-dark-bg border-t border-light-border dark:border-dark-border flex items-center justify-between">
                                <div className="flex items-center gap-2">
                                    <Image className="w-4 h-4 text-light-muted" />
                                    <span className="text-xs font-bold truncate max-w-[150px]">{proofFile.name}</span>
                                </div>
                                <span className="text-[10px] text-light-muted">{(proofFile.size / 1024 / 1024).toFixed(2)} MB</span>
                            </div>
                        </div>
                    )}
                </div>

                {/* Actions */}
                <div className="pt-2 flex gap-3">
                    <Button
                        variant="ghost"
                        className="flex-1 rounded-2xl h-12"
                        onClick={handleClose}
                        disabled={isSubmitting}
                    >
                        Cancel
                    </Button>
                    <Button
                        className="flex-1 rounded-2xl h-12 bg-primary-500 hover:bg-primary-600 shadow-lg shadow-primary-500/20"
                        onClick={handleSubmit}
                        disabled={isSubmitting || isUploading || !proofFile}
                    >
                        {isUploading ? 'Uploading' : isSubmitting ? 'Submitting' : 'Submit Payment'}
                    </Button>
                </div>
            </div>
        </Modal>
    );
}
