import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { AnnouncementCard } from './AnnouncementCard';
import { Id } from '../../../convex/_generated/dataModel';

interface Announcement {
    _id: Id<"announcements">;
    title: string;
    description: string;
    imageUrl: string;
    creatorRole?: string;
    isActive: boolean;
    category?: string;
}

interface MobileAnnouncementCarouselProps {
    announcements: Announcement[];
}

export function MobileAnnouncementCarousel({ announcements }: MobileAnnouncementCarouselProps) {
    const [currentIndex, setCurrentIndex] = useState(0);

    useEffect(() => {
        if (announcements.length <= 1) return;

        const interval = setInterval(() => {
            setCurrentIndex((prev) => (prev + 1) % announcements.length);
        }, 5000); // Change every 5 seconds

        return () => clearInterval(interval);
    }, [announcements.length]);

    if (!announcements || announcements.length === 0) return null;

    return (
        <div className="relative overflow-hidden w-full">
            <div className="relative w-full">
                <AnimatePresence mode="wait">
                    <motion.div
                        key={currentIndex}
                        initial={{ opacity: 0, x: 50 }}
                        animate={{ opacity: 1, x: 0 }}
                        exit={{ opacity: 0, x: -50 }}
                        transition={{ duration: 0.5, ease: "easeInOut" }}
                        className="w-full"
                    >
                        <AnnouncementCard
                            id={announcements[currentIndex]._id}
                            title={announcements[currentIndex].title}
                            description={announcements[currentIndex].description}
                            imageUrl={announcements[currentIndex].imageUrl}
                            creatorRole={announcements[currentIndex].creatorRole}
                            isActive={announcements[currentIndex].isActive}
                            category={announcements[currentIndex].category}
                        />
                    </motion.div>
                </AnimatePresence>
            </div>

            {/* Pagination Indicators */}
            {announcements.length > 1 && (
                <div className="absolute bottom-4 right-4 flex gap-1.5 z-10">
                    {announcements.map((_, idx) => (
                        <div
                            key={idx}
                            className={`w-1.5 h-1.5 rounded-full transition-all duration-300 ${idx === currentIndex
                                ? 'bg-white w-4'
                                : 'bg-white/50'
                                }`}
                        />
                    ))}
                </div>
            )}
        </div>
    );
}
