import { Modal } from '../ui/Modal';
import { Button } from '../ui/Button';
import { Bot, Download, FileText, Sparkles } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

interface AIReportModalProps {
    isOpen: boolean;
    onClose: () => void;
    report: string | null;
    isLoading: boolean;
    month: number;
    year: number;
}

export function AIReportModal({ isOpen, onClose, report, isLoading, month, year }: AIReportModalProps) {
    const monthName = new Intl.DateTimeFormat('en-US', { month: 'long' }).format(new Date(year, month - 1));

    return (
        <Modal
            isOpen={isOpen}
            onClose={onClose}
            title={
                <div className="flex items-center gap-2">
                    <Sparkles className="w-5 h-5 text-primary-500" />
                    <span>AI Financial Insights</span>
                </div>
            }
            maxWidth="3xl"
        >
            <div className="space-y-6">
                {/* Header Banner */}
                <div className="p-6 rounded-3xl bg-gradient-to-br from-primary-500 to-indigo-600 text-white relative overflow-hidden shadow-xl shadow-primary-500/20">
                    <div className="absolute top-0 right-0 p-8 opacity-10 pointer-events-none">
                        <Bot size={120} />
                    </div>
                    <div className="relative z-10">
                        <div className="flex items-center gap-2 mb-2">
                            <Badge className="bg-white/20 hover:bg-white/30 text-white border-white/20 backdrop-blur-md">
                                Gemini 2.0 Flash
                            </Badge>
                            <Badge className="bg-emerald-500/20 text-emerald-100 border-emerald-500/20 backdrop-blur-md">
                                Financial Analysis
                            </Badge>
                        </div>
                        <h2 className="text-2xl md:text-3xl font-black mb-1">
                            {monthName} {year} Report
                        </h2>
                        <p className="text-sm text-primary-100 max-w-md font-medium">
                            Automated deep-dive into community liquidity, transaction trends, and sustainability metrics.
                        </p>
                    </div>
                </div>

                {/* Content Area */}
                <div className="relative min-h-[400px] max-h-[60vh] overflow-y-auto premium-scrollbar px-1">
                    <AnimatePresence mode="wait">
                        {isLoading ? (
                            <motion.div
                                key="loading"
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                exit={{ opacity: 0 }}
                                className="absolute inset-0 flex flex-col items-center justify-center p-12 text-center"
                            >
                                <div className="relative w-20 h-20 mb-6">
                                    <div className="absolute inset-0 rounded-full border-4 border-primary-500/20" />
                                    <div className="absolute inset-0 rounded-full border-t-4 border-primary-500 animate-spin" />
                                    <div className="absolute inset-0 flex items-center justify-center text-primary-500">
                                        <Bot className="w-8 h-8 animate-pulse" />
                                    </div>
                                </div>
                                <h3 className="text-xl font-bold mb-2">Analyzing Data...</h3>
                                <p className="text-sm text-light-muted dark:text-dark-muted max-w-xs mx-auto">
                                    Gemini is processing your transactions and calculating growth metrics.
                                </p>
                            </motion.div>
                        ) : report ? (
                            <motion.div
                                key="content"
                                initial={{ opacity: 0, y: 10 }}
                                animate={{ opacity: 1, y: 0 }}
                                className="prose prose-sm dark:prose-invert max-w-none ai-report-content pb-8"
                            >
                                <div
                                    className="p-4 md:p-6 rounded-2xl bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border dark:border-dark-border"
                                    dangerouslySetInnerHTML={{ __html: formatReportHtml(report) }}
                                />
                            </motion.div>
                        ) : (
                            <div className="flex flex-col items-center justify-center h-full p-12 text-center">
                                <FileText className="w-12 h-12 text-light-muted mb-4 opacity-50" />
                                <p className="text-light-muted">No report data found.</p>
                            </div>
                        )}
                    </AnimatePresence>
                </div>

                {/* Footer Actions */}
                <div className="pt-4 flex flex-col sm:flex-row gap-3">
                    <Button
                        variant="ghost"
                        className="flex-1 rounded-2xl h-12 border border-light-border dark:border-dark-border"
                        onClick={onClose}
                    >
                        Close Analysis
                    </Button>
                    {!isLoading && report && (
                        <Button
                            className="flex-1 rounded-2xl h-12 bg-primary-500 hover:bg-primary-600 shadow-lg shadow-primary-500/20 gap-2"
                            onClick={() => window.print()}
                        >
                            <Download className="w-4 h-4" />
                            Export PDF
                        </Button>
                    )}
                </div>
            </div>
        </Modal>
    );
}

function Badge({ children, className }: { children: React.ReactNode; className?: string }) {
    return (
        <span className={`px-2.5 py-0.5 rounded-full text-[10px] font-black uppercase tracking-wider border ${className}`}>
            {children}
        </span>
    );
}

// Simple formatter to handle common Markdown patterns Gemini might use while keeping it safe
function formatReportHtml(report: string) {
    // Remove markdown code blocks if any
    let cleaned = report.replace(/```html|```/g, '');

    // Convert markdown bold to strong with color if not already HTML
    if (!cleaned.includes('<strong') && !cleaned.includes('<span')) {
        cleaned = cleaned.replace(/\*\*(.*?)\*\*/g, '<strong class="text-primary-600 dark:text-primary-400">$1</strong>');
        cleaned = cleaned.replace(/^\s*\*\s+(.*)$/gm, '<li class="ml-4 mb-2">$1</li>');
        cleaned = cleaned.replace(/^\s*#\s+(.*)$/gm, '<h3 class="text-lg font-bold mt-6 mb-3">$1</h3>');
        cleaned = cleaned.replace(/^\s*##\s+(.*)$/gm, '<h4 class="text-base font-bold mt-4 mb-2">$1</h4>');
    }

    return cleaned;
}
