import { Link, useLocation } from 'react-router-dom';
import { Home, User, Wallet, PlusCircle, MessageCircle } from 'lucide-react';
import { cn } from '@/lib/cn';
import { useAuth } from '@/hooks/useAuth';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';

export function MobileNav() {
    const location = useLocation();
    const { user } = useAuth();
    const badgeCounts = useQuery(api.stats.getBadgeCounts, user?._id ? { userId: user._id } : 'skip');

    // Check if path is active (starts with path to account for sub-routes)
    const isActive = (path: string) => {
        if (path === '/feed' && location.pathname === '/feed') return true;
        if (path !== '/feed' && location.pathname.startsWith(path)) return true;
        return false;
    };

    return (
        <nav className="md:hidden fixed bottom-0 left-0 right-0 z-40 border-t border-light-border dark:border-dark-border glass pb-safe">
            <div className="flex items-center justify-around h-16 px-2">
                {/* Feed */}
                <Link
                    to="/feed"
                    className={cn(
                        'flex flex-col items-center justify-center flex-1 min-w-0 transition-all relative py-1 mx-0.5 rounded-xl',
                        isActive('/feed')
                            ? 'text-primary-500 bg-primary-500/5'
                            : 'text-light-muted dark:text-dark-muted hover:text-primary-500'
                    )}
                >
                    <div className="relative">
                        <Home className={cn('w-5 h-5 md:w-6 md:h-6', isActive('/feed') && 'fill-primary-500/20')} />
                    </div>
                    <span className={cn(
                        "text-[10px] font-semibold mt-1 truncate w-full text-center",
                        isActive('/feed') ? "opacity-100" : "opacity-70"
                    )}>
                        Feed
                    </span>
                </Link>

                {/* Chat */}
                <Link
                    to="/chat"
                    className={cn(
                        'flex flex-col items-center justify-center flex-1 min-w-0 transition-all relative py-1 mx-0.5 rounded-xl',
                        isActive('/chat')
                            ? 'text-primary-500 bg-primary-500/5'
                            : 'text-light-muted dark:text-dark-muted hover:text-primary-500'
                    )}
                >
                    <div className="relative">
                        <MessageCircle className={cn('w-5 h-5 md:w-6 md:h-6', isActive('/chat') && 'fill-primary-500/20')} />
                        {(badgeCounts?.messages || 0) > 0 && (
                            <span className="absolute -top-2 -right-2 bg-green-500 text-white text-[9px] font-bold h-4 min-w-[16px] px-0.5 flex items-center justify-center rounded-full border border-white dark:border-[#1A1A1A] z-10 animate-bounce">
                                {(badgeCounts?.messages || 0) > 9 ? '9+' : badgeCounts?.messages}
                            </span>
                        )}
                    </div>
                    <span className={cn(
                        "text-[10px] font-semibold mt-1 truncate w-full text-center",
                        isActive('/chat') ? "opacity-100" : "opacity-70"
                    )}>
                        Chat
                    </span>
                </Link>

                {user && (
                    <Link
                        to="/create-post"
                        className={cn(
                            'flex flex-col items-center justify-center flex-1 min-w-0 transition-all relative py-1 mx-0.5 rounded-xl',
                            isActive('/create-post')
                                ? 'text-primary-500 bg-primary-500/5'
                                : 'text-light-muted dark:text-dark-muted hover:text-primary-500'
                        )}
                    >
                        <div className="relative p-1">
                            <PlusCircle className={cn('w-6 h-6 md:w-8 md:h-8 text-primary-500 fill-primary-500/10')} />
                        </div>
                        <span className={cn(
                            "text-[10px] font-semibold mt-1 truncate w-full text-center",
                            isActive('/create-post') ? "opacity-100 font-bold" : "opacity-70"
                        )}>
                            Post
                        </span>
                    </Link>
                )}

                {/* Banking */}
                <Link
                    to="/banking"
                    className={cn(
                        'flex flex-col items-center justify-center flex-1 min-w-0 transition-all relative py-1 mx-0.5 rounded-xl',
                        isActive('/banking')
                            ? 'text-primary-500 bg-primary-500/5'
                            : 'text-light-muted dark:text-dark-muted hover:text-primary-500'
                    )}
                >
                    <div className="relative">
                        <Wallet className={cn('w-5 h-5 md:w-6 md:h-6', isActive('/banking') && 'fill-primary-500/20')} />
                    </div>
                    <span className={cn(
                        "text-[10px] font-semibold mt-1 truncate w-full text-center",
                        isActive('/banking') ? "opacity-100" : "opacity-70"
                    )}>
                        Banking
                    </span>
                </Link>

                {/* Profile */}
                <Link
                    to="/profile"
                    className={cn(
                        'flex flex-col items-center justify-center flex-1 min-w-0 transition-all relative py-1 mx-0.5 rounded-xl',
                        isActive('/profile')
                            ? 'text-primary-500 bg-primary-500/5'
                            : 'text-light-muted dark:text-dark-muted hover:text-primary-500'
                    )}
                >
                    <div className="relative">
                        <User className={cn('w-5 h-5 md:w-6 md:h-6', isActive('/profile') && 'fill-primary-500/20')} />
                    </div>
                    <span className={cn(
                        "text-[10px] font-semibold mt-1 truncate w-full text-center",
                        isActive('/profile') ? "opacity-100" : "opacity-70"
                    )}>
                        Profile
                    </span>
                </Link>
            </div>
        </nav>
    );
}
