import React, { createContext, useContext, useEffect, useState } from 'react';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Id } from '../../convex/_generated/dataModel';

interface User {
    _id: Id<"users">;
    email: string;
    name: string;
    phone: string;
    network: "MTN" | "Glo" | "Airtel" | "9Mobile" | "Unknown";
    role: "admin" | "member" | "super_admin" | "board_member";
    whatsappVerified: boolean;
    avatarUrl?: string;
    coverImageUrl?: string;
    profession?: string;
    bankDetails?: {
        bankName: string;
        accountNumber: string;
        accountName: string;
    };
    createdAt: number;
    chatWallpaperUrl?: string;
    lastViewedBanking?: number;
    lastViewedLeaderboard?: number;
    lastViewedMessages?: number;
}

interface AuthContextType {
    user: User | null;
    isLoading: boolean;
    login: (user: User) => void;
    logout: () => void;
    updateUser: (updates: Partial<User>) => void;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

const AUTH_STORAGE_KEY = 'baptizo-auth-user';

export function AuthProvider({ children }: { children: React.ReactNode }) {
    const [user, setUser] = useState<User | null>(null);
    const [isLoading, setIsLoading] = useState(true);

    // Load user from localStorage on mount
    useEffect(() => {
        const stored = localStorage.getItem(AUTH_STORAGE_KEY);
        if (stored) {
            try {
                const parsedUser = JSON.parse(stored);
                setUser(parsedUser);
            } catch (error) {
                console.error('Failed to parse stored user:', error);
                localStorage.removeItem(AUTH_STORAGE_KEY);
            }
        }
        setIsLoading(false);
    }, []);

    // Fetch fresh user data from DB to ensure roles/data are in sync
    const freshUser = useQuery(api.users.getUser, user?._id ? { userId: user._id } : 'skip');

    // Sync fresh DB data with local state
    useEffect(() => {
        if (freshUser && user) {
            // Check if important fields changed (e.g. role update)
            if (freshUser.role !== user.role ||
                freshUser.name !== user.name ||
                freshUser.avatarUrl !== user.avatarUrl ||
                freshUser.coverImageUrl !== user.coverImageUrl) {

                const updatedUser = { ...user, ...freshUser };
                console.log("Syncing fresh user data from DB:", updatedUser);
                setUser(updatedUser);
                // localStorage update is handled by the other useEffect
            }
        }
    }, [freshUser, user]);

    // Save user to localStorage whenever it changes
    useEffect(() => {
        if (user) {
            localStorage.setItem(AUTH_STORAGE_KEY, JSON.stringify(user));
        } else {
            localStorage.removeItem(AUTH_STORAGE_KEY);
        }
    }, [user]);

    const login = (newUser: User) => {
        setUser(newUser);
    };

    const logout = () => {
        setUser(null);
    };

    const updateUser = (updates: Partial<User>) => {
        if (user) {
            setUser({ ...user, ...updates });
        }
    };

    return (
        <AuthContext.Provider value={{ user, isLoading, login, logout, updateUser }}>
            {children}
        </AuthContext.Provider>
    );
}

export function useAuth() {
    const context = useContext(AuthContext);
    if (context === undefined) {
        throw new Error('useAuth must be used within an AuthProvider');
    }
    return context;
}
