import { useState } from 'react';
import { motion } from 'framer-motion';
import { AdminContacts } from '../AdminContacts';
import { ChatPage } from '../ChatPage';
import { Mail, MessageCircle } from 'lucide-react';

export function AdminChat() {
    const [activeTab, setActiveTab] = useState<'contacts' | 'live-chat'>('contacts');

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="h-[calc(100vh-theme(spacing.24))] flex flex-col overflow-hidden"
        >
            {/* Header / Tabs */}
            <div className="flex-shrink-0 mb-4 px-1">
                <div className="flex items-center justify-between mb-4">
                    <div>
                        <h1 className="text-3xl md:text-4xl font-bold text-primary-600 dark:text-primary-400">
                            Communications
                        </h1>
                        <p className="text-light-muted dark:text-dark-muted">
                            Manage contact requests and live chat interactions
                        </p>
                    </div>
                </div>

                <div className="flex gap-2 p-1 bg-light-card dark:bg-dark-card rounded-xl border border-light-border dark:border-dark-border w-fit">
                    <button
                        onClick={() => setActiveTab('contacts')}
                        className={`
                            flex items-center gap-2 px-4 py-2 rounded-lg text-sm font-bold transition-all
                            ${activeTab === 'contacts'
                                ? 'bg-primary-500 text-white shadow-md'
                                : 'text-light-muted dark:text-dark-muted hover:bg-gray-100 dark:hover:bg-gray-800'
                            }
                        `}
                    >
                        <Mail className="w-4 h-4" />
                        Contact Requests
                    </button>
                    <button
                        onClick={() => setActiveTab('live-chat')}
                        className={`
                             flex items-center gap-2 px-4 py-2 rounded-lg text-sm font-bold transition-all
                            ${activeTab === 'live-chat'
                                ? 'bg-primary-500 text-white shadow-md'
                                : 'text-light-muted dark:text-dark-muted hover:bg-gray-100 dark:hover:bg-gray-800'
                            }
                        `}
                    >
                        <MessageCircle className="w-4 h-4" />
                        Live Chat
                    </button>
                </div>
            </div>

            {/* Content Area */}
            <div className="flex-1 overflow-hidden relative rounded-2xl border border-light-border dark:border-dark-border bg-white dark:bg-dark-card shadow-sm">
                <div className="absolute inset-0 overflow-hidden">
                    {activeTab === 'contacts' ? (
                        <div className="h-full overflow-y-auto p-6 custom-thin-scrollbar">
                            <AdminContacts />
                        </div>
                    ) : (
                        <div className="h-full w-full">
                            <ChatPage isEmbedded={true} />
                        </div>
                    )}
                </div>
            </div>
        </motion.div>
    );
}
