import { useParams, useLocation, useNavigate } from 'react-router-dom';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { BackButton } from '@/components/ui/BackButton';
import { Button } from '@/components/ui/Button';
import { useAuth } from '@/hooks/useAuth';
import { Id } from '../../convex/_generated/dataModel';
import { Avatar } from '@/components/ui/Avatar';

export function FollowList() {
    const { userId } = useParams();
    const { user: currentUser } = useAuth();
    const location = useLocation();
    const navigate = useNavigate();

    const type = location.pathname.includes('following') ? 'following' : 'followers';
    const targetUserId = (userId as Id<"users">) || currentUser?._id;

    const list = useQuery(api.follows.getFollowList, targetUserId ? {
        userId: targetUserId,
        type,
        currentUserId: currentUser?._id
    } : 'skip');

    const toggleFollow = useMutation(api.follows.toggleFollow);

    const handleFollow = async (id: Id<"users">) => {
        if (!currentUser) return;
        await toggleFollow({ followerId: currentUser._id, followingId: id });
    };

    if (!targetUserId) return null;

    return (
        <div className="min-h-screen py-4 md:py-8">
            <div className="container mx-auto px-4 max-w-2xl">
                <BackButton />
                <h1 className="text-2xl font-bold mb-6 capitalize text-light-text dark:text-dark-text">
                    {type}
                </h1>

                <div className="space-y-4">
                    {list === undefined ? (
                        [1, 2, 3].map(i => (
                            <div key={i} className="h-16 glass rounded-xl animate-pulse" />
                        ))
                    ) : list.length === 0 ? (
                        <div className="text-center py-12 text-light-muted dark:text-dark-muted">
                            No {type} found.
                        </div>
                    ) : (
                        list.map((u: any) => (
                            <div key={u._id} className="flex items-center justify-between p-4 glass rounded-xl hover:bg-black/5 dark:hover:bg-white/5 transition-colors">
                                <div
                                    className="flex items-center gap-3 cursor-pointer flex-1"
                                    onClick={() => navigate(`/profile/${u._id}`)}
                                >
                                    <Avatar src={u.avatarUrl} alt={u.name} className="w-10 h-10 border border-light-border dark:border-dark-border" />
                                    <div>
                                        <div className="font-bold text-light-text dark:text-dark-text">{u.name}</div>
                                        <div className="text-xs text-light-muted dark:text-dark-muted">@{u.username || 'user'}</div>
                                    </div>
                                </div>
                                {currentUser?._id !== u._id && (
                                    <Button
                                        size="sm"
                                        variant={u.isFollowing ? "outline" : "primary"}
                                        onClick={() => handleFollow(u._id)}
                                        className={u.isFollowing ? "hover:bg-red-500 hover:text-white hover:border-red-500" : ""}
                                    >
                                        {u.isFollowing ? "Following" : "Follow"}
                                    </Button>
                                )}
                            </div>
                        ))
                    )}
                </div>
            </div>
        </div>
    );
}
