import { Link, useLocation } from 'react-router-dom';
import {
    Sparkles,
    BookOpen,
    Wallet,
    LayoutDashboard,
    Shield,
    MessageCircle,
    Trophy,
    Bookmark,
    Brain,
} from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Card, CardContent } from '@/components/ui/Card';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { SidebarProfileSkeleton } from '@/components/ui/Skeletons';

export function FeedSidebarLeft() {
    const location = useLocation();
    const navigate = useNavigate();
    const { user } = useAuth();

    if (!user?._id) {
        return null;
    }

    const stats = useQuery(api.users.getUserStats, { userId: user._id });
    const badgeCounts = useQuery(api.stats.getBadgeCounts, { userId: user._id });
    const updateLastViewed = useMutation(api.users.updateLastViewed);

    const handleNavClick = async (path: string, field?: any) => {
        if (field && user) {
            await updateLastViewed({ userId: user._id, field });
        }
        navigate(path);
    };

    const getBadgeCount = (label: string) => {
        if (!badgeCounts) return null;
        if (label === 'Messages') return badgeCounts.messages > 0 ? badgeCounts.messages : null;
        if (label === 'Banking') return badgeCounts.banking > 0 ? true : null;
        if (label === 'Leaderboard') return badgeCounts.leaderboard > 0 ? true : null;
        return null;
    };

    if (!user) return null;

    const navItems = [
        { path: '/feed', label: 'Home Feed', icon: Sparkles, color: 'text-primary-500' },
        { path: '/teachings', label: 'All Posts', icon: BookOpen, color: 'text-blue-500' },
        { path: '/chat', label: 'Messages', icon: MessageCircle, color: 'text-indigo-500' },
        { path: '/banking', label: 'Banking', icon: Wallet, color: 'text-emerald-500' },
        { path: '/leaderboard', label: 'Leaderboard', icon: Trophy, color: 'text-amber-500' },
        { path: '/diamond-challenge', label: 'Diamond Challenge', icon: Brain, color: 'text-cyan-500' },
        { path: '/saved-posts', label: 'Saved Posts', icon: Bookmark, color: 'text-purple-500' },
    ];

    const adminItems = [
        { path: '/admin', label: 'Admin Hub', icon: Shield, color: 'text-orange-500' },
        { path: '/admin/roles', label: 'Role Manager', icon: Shield, color: 'text-red-500' },
        // { path: '/admin/tasks', label: 'Task Reviews', icon: ClipboardList, color: 'text-indigo-500' },
    ];

    const boardItems = [
        { path: '/board', label: 'Board Dash', icon: LayoutDashboard, color: 'text-amber-500' },
    ];

    const isAdmin = user.role === 'admin' || user.role === 'super_admin';
    const isBoard = user.role === 'board_member';

    return (
        <aside className="hidden lg:block space-y-6 sticky top-24 max-h-[calc(100vh-120px)] overflow-y-auto pb-10 pr-1 transition-all custom-scrollbar-premium">
            <style>{`
                .custom-scrollbar-premium::-webkit-scrollbar {
                    width: 4px;
                }
                .custom-scrollbar-premium::-webkit-scrollbar-track {
                    background: #ffffff;
                }
                .dark .custom-scrollbar-premium::-webkit-scrollbar-track {
                    background: #111827;
                }
                .custom-scrollbar-premium::-webkit-scrollbar-thumb {
                    background: #374151;
                    border-radius: 10px;
                }
                .dark .custom-scrollbar-premium::-webkit-scrollbar-thumb {
                    background: #064e3b;
                }
                .custom-scrollbar-premium {
                    scrollbar-width: thin;
                    scrollbar-color: #374151 #ffffff;
                }
                .dark .custom-scrollbar-premium {
                    scrollbar-color: #064e3b #111827;
                }
            `}</style>

            {/* Profile Card */}
            {stats === undefined ? (
                <SidebarProfileSkeleton />
            ) : (
                <Card glass className="overflow-hidden border-primary-500/10 hover:border-primary-500/30 transition-all duration-300">
                    <div className="h-20 bg-gradient-to-br from-primary-500/20 via-primary-500/5 to-transparent relative">
                        <div className="absolute inset-0 bg-grid-white/5 opacity-20" />
                    </div>
                    <CardContent className="px-6 pb-6 -mt-10 relative">
                        <div className="flex flex-col items-center text-center">
                            <div className="w-20 h-20 rounded-2xl bg-gradient-to-br from-primary-500 to-secondary-500 p-1 shadow-xl shadow-primary-500/20 mb-3">
                                <div className="w-full h-full rounded-2xl bg-white dark:bg-dark-bg overflow-hidden flex items-center justify-center font-bold text-2xl text-gradient">
                                    {user.avatarUrl ? (
                                        <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover" />
                                    ) : (
                                        user.name[0]
                                    )}
                                </div>
                            </div>
                            <h3 className="font-bold text-lg text-light-text dark:text-dark-text truncate w-full">{user.name}</h3>
                            <p className="text-[10px] font-bold text-primary-500 uppercase tracking-widest mt-1">
                                {user.role.replace('_', ' ')}
                            </p>
                        </div>

                        <div className="mt-6 pt-6 border-t border-light-border/50 dark:border-dark-border/50 grid grid-cols-2 gap-4">
                            <div className="text-center">
                                <p className="text-sm font-bold">{stats?.totalPosts || 0}</p>
                                <p className="text-[10px] text-light-muted uppercase font-black">Posts</p>
                            </div>
                            <div className="text-center">
                                <p className="text-sm font-bold">{stats?.totalRewards || 0}</p>
                                <p className="text-[10px] text-light-muted uppercase font-black">Rewards</p>
                            </div>
                        </div>
                    </CardContent>
                </Card>
            )}

            {/* Navigation Card */}
            <Card glass className="p-2">
                <nav className="space-y-1">
                    {navItems.map((item: any) => {
                        const Icon = item.icon;
                        const isActive = location.pathname === item.path;
                        return (
                            <button
                                key={item.path}
                                onClick={() => {
                                    const field = item.label === 'Banking' ? 'lastViewedBanking' :
                                        item.label === 'Leaderboard' ? 'lastViewedLeaderboard' : undefined;
                                    handleNavClick(item.path, field);
                                }}
                                className={`
                                    w-full flex items-center justify-between px-4 py-3 rounded-xl transition-all duration-200 group
                                    ${isActive
                                        ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30 font-bold'
                                        : 'text-light-text dark:text-dark-text hover:bg-light-card dark:hover:bg-dark-card'
                                    }
                                `}
                            >
                                <div className="flex items-center gap-3">
                                    <Icon className={`w-5 h-5 ${isActive ? 'text-white' : item.color} transition-transform group-hover:scale-110`} />
                                    <span className="text-sm font-medium">{item.label}</span>
                                </div>

                                {getBadgeCount(item.label) === true ? (
                                    <div className={`w-2 h-2 rounded-full ${isActive ? 'bg-white' : 'bg-primary-500 shadow-[0_0_8px_rgba(249,115,22,0.5)]'}`} />
                                ) : typeof getBadgeCount(item.label) === 'number' ? (
                                    <span className={`px-1.5 py-0.5 rounded-full text-[10px] font-bold ${isActive ? 'bg-white text-primary-500' : 'bg-primary-500 text-white'}`}>
                                        {getBadgeCount(item.label)}
                                    </span>
                                ) : isActive && (
                                    <motion.div
                                        layoutId="activeFeedIndicator"
                                        className="w-1.5 h-1.5 rounded-full bg-white shadow-[0_0_8px_white]"
                                    />
                                )}
                            </button>
                        );
                    })}

                    {(isAdmin || isBoard) && (
                        <div className="mt-4 pt-4 border-t border-light-border/50 dark:border-dark-border/50 space-y-1">
                            <p className="px-4 mb-2 text-[10px] font-black text-light-muted dark:text-dark-muted uppercase tracking-[0.2em]">Management</p>

                            {isAdmin && adminItems.map((item: any) => {
                                const Icon = item.icon;
                                const isActive = location.pathname === item.path;
                                return (
                                    <Link
                                        key={item.path}
                                        to={item.path}
                                        className={`
                                            flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200 group
                                            ${isActive
                                                ? 'bg-primary-500 text-white shadow-lg'
                                                : 'text-light-text dark:text-dark-text hover:bg-light-card dark:hover:bg-dark-card'
                                            }
                                        `}
                                    >
                                        <Icon className={`w-5 h-5 ${isActive ? 'text-white' : item.color} transition-transform group-hover:scale-110`} />
                                        <span className="text-sm font-medium">{item.label}</span>
                                    </Link>
                                );
                            })}

                            {(isAdmin || isBoard) && boardItems.map((item: any) => {
                                const Icon = item.icon;
                                const isActive = location.pathname === item.path;
                                return (
                                    <Link
                                        key={item.path}
                                        to={item.path}
                                        className={`
                                            flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200 group
                                            ${isActive
                                                ? 'bg-primary-500 text-white shadow-lg'
                                                : 'text-light-text dark:text-dark-text hover:bg-light-card dark:hover:bg-dark-card'
                                            }
                                        `}
                                    >
                                        <Icon className={`w-5 h-5 ${isActive ? 'text-white' : item.color} transition-transform group-hover:scale-110`} />
                                        <span className="text-sm font-medium">{item.label}</span>
                                    </Link>
                                );
                            })}
                        </div>
                    )}
                </nav>
            </Card>

            {/* Quick Actions / Shortcuts */}
            <Card glass className="p-6">
                <h4 className="font-bold text-xs uppercase tracking-widest text-light-muted mb-4">Quick Links</h4>
                <div className="flex flex-col gap-2">
                    <Link to="/profile" className="px-3 py-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-xs font-bold hover:border-primary-500 transition-colors flex items-center justify-center">My Profile</Link>
                    <Link to="/notifications" className="px-3 py-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-xs font-bold hover:border-primary-500 transition-colors flex items-center justify-center">Notifications</Link>
                </div>
            </Card>
        </aside >
    );
}
