// Get WhatsApp group invite URL from environment
export function getWhatsAppGroupUrl(): string {
    return import.meta.env.VITE_WHATSAPP_GROUP_URL || 'https://chat.whatsapp.com/IEd7nZVPdUiAvYF7CLw7jU';
}

// Redirect to WhatsApp group
export function redirectToWhatsAppGroup(): void {
    const url = getWhatsAppGroupUrl();
    window.open(url, '_blank');
}

// Check if WhatsApp is installed (basic detection)
export function isWhatsAppInstalled(): boolean {
    // This is a basic check - in reality, we can't reliably detect WhatsApp installation
    // We'll use manual verification instead
    return true;
}

// Generate WhatsApp chat URL for phone number
export function getWhatsAppChatUrl(phone: string): string {
    const cleanPhone = phone.replace(/\D/g, '');
    // Remove leading 0 and add country code
    const internationalPhone = cleanPhone.startsWith('0')
        ? '234' + cleanPhone.substring(1)
        : '234' + cleanPhone;

    return `https://wa.me/${internationalPhone}`;
}
