import { useState } from 'react';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Card, CardContent } from '@/components/ui/Card';
import { Trophy, Medal, MessageSquare, Gamepad2, Brain, ArrowLeft, Heart } from 'lucide-react';
import { Link, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { cn } from '@/lib/cn';

// Custom CSS for thin scrollbar
const scrollbarStyles = `
  .premium-scrollbar::-webkit-scrollbar {
    height: 5px;
    width: 5px;
  }
  .premium-scrollbar::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
  }
  .premium-scrollbar::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.3);
    border-radius: 10px;
  }
  .premium-scrollbar::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.5);
  }
`;

type TabType = 'gold' | 'silver' | 'diamond';

const TABS = [
    { key: 'gold' as TabType, label: 'Comments', icon: MessageSquare, color: 'from-yellow-500 to-amber-600' },
    { key: 'silver' as TabType, label: 'Games', icon: Gamepad2, color: 'from-gray-400 to-gray-600' },
    { key: 'diamond' as TabType, label: 'Challenge', icon: Brain, color: 'from-emerald-500 to-teal-600' },
];

function Leaderboard() {
    const [activeTab, setActiveTab] = useState<TabType>('gold');
    const navigate = useNavigate();

    // Get leaderboard data for the active tab
    const walletLeaderboard = useQuery(api.wallets.getLeaderboard, activeTab !== 'diamond' ? { poolType: activeTab as any, limit: 50 } : "skip");
    const diamondLeaderboard = useQuery(api.wordUnderPressure.getDiamondLeaderboard, activeTab === 'diamond' ? { limit: 50 } : "skip");

    const leaderboard = activeTab === 'diamond' ? diamondLeaderboard : walletLeaderboard;

    const activeTabConfig = TABS.find(t => t.key === activeTab)!;

    return (
        <div className="min-h-screen bg-light-bg dark:bg-dark-bg">
            <style>{scrollbarStyles}</style>
            {/* Header */}
            <div className="gradient-primary text-white py-8 px-4 md:px-8">
                <div className="max-w-5xl mx-auto">
                    <button
                        onClick={() => navigate('/feed')}
                        className="flex items-center gap-2 text-white/80 hover:text-white mb-4 transition-colors"
                    >
                        <ArrowLeft className="w-5 h-5" />
                        Back to Feed
                    </button>

                    <div className="flex items-center gap-3 mb-6">
                        <div className="w-14 h-14 rounded-full bg-white/20 backdrop-blur-md flex items-center justify-center">
                            <Trophy className="w-8 h-8" />
                        </div>
                        <div>
                            <h1 className="text-3xl md:text-4xl font-display font-bold">Leaderboard</h1>
                            <p className="text-white/80">Top performers across all categories</p>
                            {activeTab === 'diamond' && (
                                <button
                                    onClick={() => navigate('/diamond-challenge')}
                                    className="mt-4 px-6 py-2 bg-emerald-500 text-white rounded-full font-bold text-sm hover:bg-emerald-400 transition-colors shadow-lg flex items-center gap-2 border-2 border-emerald-400/50"
                                >
                                    <Brain className="w-4 h-4" />
                                    Play Challenge
                                </button>
                            )}
                        </div>
                    </div>

                    {/* Tabs */}
                    <div className="flex gap-2 overflow-x-auto pb-2 premium-scrollbar">
                        {TABS.map((tab) => {
                            const Icon = tab.icon;
                            const isActive = activeTab === tab.key;
                            return (
                                <button
                                    key={tab.key}
                                    onClick={() => setActiveTab(tab.key)}
                                    className={cn(
                                        'flex items-center gap-2 px-6 py-3 rounded-xl font-semibold transition-all whitespace-nowrap',
                                        isActive
                                            ? 'bg-white text-primary-500'
                                            : 'bg-white/10 text-white/70 hover:bg-white/20'
                                    )}
                                >
                                    <Icon className="w-5 h-5" />
                                    {tab.label}
                                </button>
                            );
                        })}
                    </div>
                </div>
            </div>

            {/* Content */}
            <div className="max-w-5xl mx-auto px-4 md:px-8 py-8">
                {!leaderboard ? (
                    <div className="text-center text-light-muted dark:text-dark-muted py-12">
                        Loading leaderboard...
                    </div>
                ) : leaderboard.length === 0 ? (
                    <Card glass>
                        <CardContent className="p-8 text-center">
                            <Trophy className="w-16 h-16 text-primary-300 mx-auto mb-4" />
                            <p className="text-lg text-light-muted dark:text-dark-muted">No entries yet for this category.</p>
                            <p className="text-sm text-light-muted dark:text-dark-muted mt-2">
                                Be the first to earn Rubies in the {activeTabConfig.label} pool!
                            </p>
                        </CardContent>
                    </Card>
                ) : (
                    <div className="space-y-4">
                        {leaderboard.map((entry: any, _index: number) => (
                            <LeaderboardEntry
                                key={entry.userId}
                                entry={entry}
                                rank={entry.rank}
                                tabColor={activeTabConfig.color}
                                activeTab={activeTab}
                            />
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
}

function LeaderboardEntry({ entry, rank, tabColor, activeTab }: { entry: any; rank: number; tabColor: string, activeTab: string }) {
    let RankIcon = null;
    let rankColor = "bg-light-card dark:bg-dark-card border-light-border dark:border-dark-border";

    if (rank === 1) {
        RankIcon = <Trophy className="w-7 h-7 text-yellow-500" />;
        rankColor = "bg-gradient-to-br from-yellow-50 to-amber-50 dark:from-yellow-900/20 dark:to-amber-900/20 border-2 border-yellow-400 dark:border-yellow-600 shadow-lg shadow-yellow-500/20";
    } else if (rank === 2) {
        RankIcon = <Medal className="w-7 h-7 text-gray-400" />;
        rankColor = "bg-gradient-to-br from-gray-50 to-gray-100 dark:from-gray-900/20 dark:to-gray-800/20 border-2 border-gray-300 dark:border-gray-600 shadow-md shadow-gray-500/10";
    } else if (rank === 3) {
        RankIcon = <Medal className="w-7 h-7 text-orange-500" />;
        rankColor = "bg-gradient-to-br from-orange-50 to-amber-50 dark:from-orange-900/20 dark:to-amber-900/20 border-2 border-orange-400 dark:border-orange-600 shadow-md shadow-orange-500/10";
    }

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: rank * 0.05 }}
            className={cn(
                "flex items-center gap-3 sm:gap-4 p-3 sm:p-5 rounded-2xl border transition-all hover:scale-[1.02]",
                rankColor
            )}
        >
            {/* Rank - Hidden on mobile, shown on desktop */}
            <div className="hidden md:flex flex-shrink-0 w-14 text-center font-bold text-2xl items-center justify-center">
                {RankIcon || <span className="text-light-muted dark:text-dark-muted">#{rank}</span>}
            </div>

            {/* Avatar with Overlay Rank on Mobile */}
            <Link to={`/profile/${entry.userId}`} className="relative flex-shrink-0 hover:opacity-80 transition-opacity">
                <div className="w-12 h-12 md:w-14 md:h-14 rounded-full overflow-hidden bg-gradient-to-br from-primary-400 to-primary-600 ring-2 ring-white dark:ring-dark-border">
                    {entry.userAvatar ? (
                        <img src={entry.userAvatar} alt={entry.userName} className="w-full h-full object-cover" />
                    ) : (
                        <div className="w-full h-full flex items-center justify-center text-white font-bold text-xl">
                            {entry.userName[0]}
                        </div>
                    )}
                </div>

                {/* Mobile Rank Badge */}
                <div className="absolute -top-1 -left-1 md:hidden w-6 h-6 rounded-full bg-white dark:bg-dark-card border border-light-border dark:border-dark-border flex items-center justify-center shadow-sm z-10 text-xs font-bold">
                    {rank <= 3 ? (
                        rank === 1 ? <Trophy className="w-3.5 h-3.5 text-yellow-500" /> : <Medal className={cn("w-3.5 h-3.5", rank === 2 ? "text-gray-400" : "text-orange-500")} />
                    ) : (
                        `#${rank}`
                    )}
                </div>
            </Link>

            {/* User Info */}
            <div className="flex-1 min-w-0 pr-1">
                <Link
                    to={`/profile/${entry.userId}`}
                    className="font-bold text-base sm:text-xl text-light-text dark:text-dark-text truncate hover:text-primary-500 transition-colors block leading-tight"
                >
                    {entry.userName}
                </Link>
                <div className="flex flex-col sm:flex-row sm:items-center gap-0.5 sm:gap-4 mt-0.5 sm:mt-1 text-xs sm:text-sm text-light-muted dark:text-dark-muted">
                    {activeTab === 'diamond' ? (
                        <>
                            <span className="truncate font-medium text-emerald-500 dark:text-emerald-400">
                                {entry.totalWins} Correct
                            </span>
                            <span className="flex items-center gap-1 truncate text-light-muted dark:text-dark-muted">
                                Attempts: {entry.currentStreak}
                            </span>
                        </>
                    ) : entry.totalWins !== undefined ? (
                        <>
                            <span className="truncate font-medium text-blue-500 dark:text-blue-400">
                                {entry.totalWins} Wins
                            </span>
                            <span className="flex items-center gap-1 truncate text-light-muted dark:text-dark-muted">
                                Streak: {entry.currentStreak}
                            </span>
                        </>
                    ) : (
                        <>
                            <span className="truncate">
                                {entry.earnedRubies} Rubies
                            </span>
                            {entry.badgeCount > 0 && (
                                <span className="flex items-center gap-1">
                                    <Trophy className="w-3 h-3 sm:w-4 sm:h-4" />
                                    {entry.badgeCount} badge{entry.badgeCount !== 1 ? 's' : ''}
                                </span>
                            )}
                        </>
                    )}
                </div>
            </div>

            {/* Stats */}
            <div className="flex flex-col items-end gap-0.5 sm:gap-1 pl-1 shrink-0">
                <div className={cn("text-lg sm:text-3xl font-bold bg-gradient-to-r bg-clip-text text-transparent truncate max-w-[80px] sm:max-w-none text-right", tabColor)}>
                    {entry.currentBalance}
                </div>
                <div className="text-[9px] sm:text-xs text-light-muted dark:text-dark-muted uppercase tracking-wider font-medium">
                    {activeTab === 'diamond' ? 'Best Time' : 'Balance'}
                </div>
                <button
                    onClick={(e) => {
                        e.preventDefault();
                    }}
                    className="mt-0.5 sm:mt-1 p-1 sm:p-1.5 rounded-full hover:bg-gray-100 dark:hover:bg-gray-800 text-gray-400 hover:text-red-500 transition-colors"
                >
                    <Heart className="w-3.5 h-3.5 sm:w-4 sm:h-4" />
                </button>
            </div>
        </motion.div>
    );
}

export default Leaderboard;
