import { useParams, Link } from 'react-router-dom';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { Avatar } from '@/components/ui/Avatar';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { BackButton } from '@/components/ui/BackButton';
import { Shield, UserPlus, UserMinus } from 'lucide-react';
import { motion } from 'framer-motion';
import { Id } from '../../convex/_generated/dataModel';
import { formatDistanceToNow } from 'date-fns';
import { useState } from 'react';
import { Search } from 'lucide-react';
import { PostCard } from '@/components/posts/PostCard';
import { PageHeaderSkeleton, PostCardSkeleton } from '@/components/ui/Skeletons';

export function MemberProfile() {
    const { userId } = useParams();
    const { user, isLoading: authLoading } = useAuth();
    const [searchQuery, setSearchQuery] = useState('');

    const profile = useQuery(
        api.users.getUserProfile,
        userId ? { userId: userId as Id<'users'> } : 'skip'
    );

    // Fetch user posts
    const posts = useQuery(
        api.posts.getPosts,
        userId ? { authorId: userId as Id<'users'>, userId: user?._id } : 'skip'
    );

    const filteredPosts = posts?.filter((post: any) =>
        post.content.toLowerCase().includes(searchQuery.toLowerCase())
    ) || [];
    const isFollowing = useQuery(
        api.follows.isFollowing,
        user && userId ? { followerId: user._id, followingId: userId as Id<'users'> } : 'skip'
    );
    const toggleFollow = useMutation(api.follows.toggleFollow);
    const [isToggling, setIsToggling] = useState(false);

    const handleToggleFollow = async () => {
        if (!user || !userId || isToggling) return;
        setIsToggling(true);
        try {
            await toggleFollow({
                followerId: user._id,
                followingId: userId as Id<'users'>,
            });
        } catch (error) {
            console.error('Failed to toggle follow:', error);
        } finally {
            setIsToggling(false);
        }
    };

    if (authLoading) {
        return (
            <div className="min-h-screen py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                    <BackButton />
                    <PageHeaderSkeleton />
                    <div className="glass rounded-xl h-64 animate-pulse mb-6" />
                    <div className="space-y-4">
                        <PostCardSkeleton />
                        <PostCardSkeleton />
                    </div>
                </div>
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    if (profile === undefined) {
        return (
            <div className="min-h-screen py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                    <BackButton />
                    <PageHeaderSkeleton />
                    <div className="glass rounded-xl h-64 animate-pulse mb-6" />
                    <div className="space-y-4">
                        <PostCardSkeleton />
                        <PostCardSkeleton />
                    </div>
                </div>
            </div>
        );
    }

    if (profile === null) {
        return (
            <div className="min-h-screen py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                    <BackButton />
                    <div className="text-center py-12 glass rounded-xl mt-6">
                        <p className="text-light-muted dark:text-dark-muted">
                            Member not found
                        </p>
                    </div>
                </div>
            </div>
        );
    }

    const isAdmin = profile.role === 'admin' || profile.role === 'super_admin';
    const isSuperAdmin = profile.role === 'super_admin';

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <BackButton />

                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mt-6 space-y-6"
                >
                    {/* Profile Header - Facebook Style */}
                    <div className="glass rounded-xl overflow-hidden">
                        {/* Cover Photo Placeholder (Optional - can be added later) */}
                        {/* Cover Photo */}
                        <div className="h-32 md:h-48 bg-gray-100 dark:bg-gray-800 relative">
                            {profile.coverImageUrl ? (
                                <img src={profile.coverImageUrl} className="w-full h-full object-cover" alt="Cover" />
                            ) : (
                                <div className="w-full h-full bg-gradient-to-r from-primary-500/20 to-secondary-500/20" />
                            )}
                        </div>

                        <div className="px-4 pb-4 md:px-8 md:pb-8 relative">
                            {/* Avatar - Overlapping Cover */}
                            <div className="relative -mt-10 md:-mt-16 mb-3 md:mb-4 flex flex-col items-center md:items-start">
                                <Avatar
                                    src={profile.avatarUrl}
                                    alt={profile.name}
                                    size="xl"
                                    className={`
                                        ring-4 ring-light-bg dark:ring-dark-bg 
                                        ${isSuperAdmin ? 'ring-yellow-500' : isAdmin ? 'ring-primary-500' : ''}
                                        w-20 h-20 md:w-32 md:h-32 shadow-lg
                                    `}
                                />
                            </div>

                            {/* User Info & Actions */}
                            <div className="flex flex-col md:flex-row md:items-start md:justify-between gap-4 text-center md:text-left">
                                <div className="flex-1 min-w-0">
                                    <div className="flex flex-col md:flex-row items-center md:items-center gap-2 mb-1">
                                        <h1 className="text-xl md:text-3xl font-bold text-light-text dark:text-dark-text truncate max-w-full">
                                            {profile.name}
                                        </h1>
                                        {/* Role Badge */}
                                        {isSuperAdmin ? (
                                            <Badge variant="primary" className="bg-gradient-to-r from-yellow-500 to-amber-600 border-yellow-300 text-[10px] h-5">
                                                <Shield className="w-3 h-3 mr-1" /> Super Admin
                                            </Badge>
                                        ) : isAdmin ? (
                                            <Badge variant="primary" className="bg-gradient-to-r from-primary-500 to-primary-600 text-[10px] h-5">
                                                <Shield className="w-3 h-3 mr-1" /> Admin
                                            </Badge>
                                        ) : null}
                                    </div>

                                    <div className="flex items-center justify-center md:justify-start gap-4 text-sm text-light-muted dark:text-dark-muted mb-4">
                                        <span>Joined {formatDistanceToNow(profile.createdAt, { addSuffix: true })}</span>
                                    </div>

                                    {/* Stats Row */}
                                    {/* Stats Row */}
                                    <div className="flex items-center justify-center md:justify-start gap-6 mb-4 md:mb-0">
                                        <Link to={`/profile/${userId}/followers`} className="flex flex-col items-center md:items-start hover:opacity-80 transition-opacity">
                                            <span className="text-lg font-bold text-light-text dark:text-dark-text leading-none">{profile.stats.followerCount || 0}</span>
                                            <span className="text-xs text-light-muted dark:text-dark-muted">Followers</span>
                                        </Link>
                                        <Link to={`/profile/${userId}/following`} className="flex flex-col items-center md:items-start hover:opacity-80 transition-opacity">
                                            <span className="text-lg font-bold text-light-text dark:text-dark-text leading-none">{profile.stats.followingCount || 0}</span>
                                            <span className="text-xs text-light-muted dark:text-dark-muted">Following</span>
                                        </Link>
                                        <div className="flex flex-col items-center md:items-start">
                                            <span className="text-lg font-bold text-light-text dark:text-dark-text leading-none">{profile.stats.likesReceived || 0}</span>
                                            <span className="text-xs text-light-muted dark:text-dark-muted">Likes</span>
                                        </div>
                                    </div>
                                </div>

                                {/* Actions */}
                                {user._id !== userId && (
                                    <div className="shrink-0 flex justify-center md:justify-start">
                                        <Button
                                            variant={isFollowing ? "outline" : "primary"}
                                            onClick={handleToggleFollow}
                                            isLoading={isToggling}
                                            className="min-w-[120px] rounded-lg h-9 text-sm"
                                        >
                                            {isFollowing ? (
                                                <><UserMinus className="w-4 h-4 mr-2" /> Unfollow</>
                                            ) : (
                                                <><UserPlus className="w-4 h-4 mr-2" /> Follow</>
                                            )}
                                        </Button>
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>

                    {/* Member Details section could be added here if needed */}

                    {/* User Posts Section */}
                    <div className="space-y-6 pt-6 border-t border-light-border dark:border-dark-border">
                        <div className="flex flex-col md:flex-row items-center justify-between gap-4">
                            <h2 className="text-xl font-bold text-light-text dark:text-dark-text">
                                Posts
                            </h2>
                            <div className="relative w-full md:w-64">
                                <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-light-muted dark:text-dark-muted" />
                                <input
                                    type="text"
                                    placeholder="Search posts..."
                                    value={searchQuery}
                                    onChange={(e) => setSearchQuery(e.target.value)}
                                    className="w-full pl-9 pr-4 py-2 rounded-lg bg-light-card/50 dark:bg-dark-card/50 border border-light-border dark:border-dark-border focus:ring-2 focus:ring-primary-500 outline-none text-sm"
                                />
                            </div>
                        </div>

                        {posts === undefined ? (
                            <div className="grid gap-6">
                                {[1, 2].map((i) => (
                                    <div key={i} className="glass rounded-2xl p-6 h-40 animate-pulse" />
                                ))}
                            </div>
                        ) : posts.length === 0 ? (
                            <div className="text-center py-12 glass rounded-2xl">
                                <p className="text-light-muted dark:text-dark-muted">No posts yet</p>
                            </div>
                        ) : filteredPosts.length === 0 ? (
                            <div className="text-center py-12 glass rounded-2xl">
                                <p className="text-light-muted dark:text-dark-muted">No posts match your search</p>
                            </div>
                        ) : (
                            <div className="grid gap-6">
                                {filteredPosts.map((post: any) => (
                                    <PostCard
                                        key={post._id}
                                        post={post}
                                        onCommentClick={() => window.location.href = `/post/${post._id}`}
                                        hasUserLiked={post.hasLiked}
                                    />
                                ))}
                            </div>
                        )}
                    </div>
                </motion.div>
            </div>
        </div>
    );
}
