import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Download, X } from 'lucide-react';
import { Button } from '@/components/ui/Button';

export function InstallPrompt() {
    const [deferredPrompt, setDeferredPrompt] = useState<any>(null);
    const [showPrompt, setShowPrompt] = useState(false);

    useEffect(() => {
        const handler = (e: any) => {
            // Prevent the mini-infobar from appearing on mobile
            e.preventDefault();
            // Stash the event so it can be triggered later.
            setDeferredPrompt(e);
            // Check if user has already dismissed it
            const hasDismissed = localStorage.getItem('pwa_dismissed');
            if (!hasDismissed) {
                setShowPrompt(true);
            }
        };

        window.addEventListener('beforeinstallprompt', handler);

        return () => {
            window.removeEventListener('beforeinstallprompt', handler);
        };
    }, []);

    const handleInstallClick = async () => {
        if (!deferredPrompt) return;

        // hide our user interface that shows our A2HS button
        setShowPrompt(false);
        // Show the install prompt
        deferredPrompt.prompt();
        // Wait for the user to respond to the prompt
        const { outcome } = await deferredPrompt.userChoice;
        console.log(`User response to the install prompt: ${outcome}`);
        setDeferredPrompt(null);
    };

    const handleDismiss = () => {
        setShowPrompt(false);
        // Remember dismissal for a session or longer? Let's say session for now, 
        // or longer if desired. User just said "on first open".
        localStorage.setItem('pwa_dismissed', 'true');
    };

    if (!showPrompt) return null;

    return (
        <AnimatePresence>
            <motion.div
                initial={{ opacity: 0, y: 50 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: 50 }}
                className="fixed bottom-0 left-0 right-0 z-[100] p-4 md:p-6 flex justify-center pointer-events-none"
            >
                <div className="bg-white/90 dark:bg-gray-900/95 backdrop-blur-xl border border-light-border dark:border-dark-border shadow-2xl rounded-2xl p-4 w-[95%] max-w-sm pointer-events-auto relative overflow-hidden mx-auto mb-4 md:mb-6">
                    {/* Decorative gradient blob */}
                    <div className="absolute -top-10 -right-10 w-24 h-24 bg-primary-500/20 rounded-full blur-2xl pointer-events-none" />
                    <div className="absolute -bottom-10 -left-10 w-24 h-24 bg-secondary-500/20 rounded-full blur-2xl pointer-events-none" />

                    <button
                        onClick={handleDismiss}
                        className="absolute top-2 right-2 p-1 rounded-full hover:bg-black/5 dark:hover:bg-white/5 transition-colors z-10"
                    >
                        <X className="w-4 h-4 text-gray-500" />
                    </button>

                    <div className="flex flex-col items-center text-center">
                        <img
                            src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/premium-logo.png"
                            alt="Baptizo"
                            className="w-12 h-12 rounded-xl shadow-md mb-2"
                        />
                        <h3 className="text-base md:text-lg font-bold text-gray-900 dark:text-white mb-1">
                            Install Baptizo Application
                        </h3>
                        <p className="text-xs text-gray-500 dark:text-gray-400 mb-4 max-w-[90%]">
                            Get the best experience with offline access and real-time updates.
                        </p>

                        <div className="flex gap-2 w-full">
                            <Button
                                variant="outline"
                                className="flex-1 h-9 text-xs"
                                onClick={handleDismiss}
                            >
                                Not Now
                            </Button>
                            <Button
                                className="flex-1 bg-primary-600 hover:bg-primary-700 text-white shadow-lg shadow-primary-500/25 h-9 text-xs"
                                onClick={handleInstallClick}
                            >
                                <Download className="w-3 h-3 mr-1.5" />
                                Install App
                            </Button>
                        </div>
                    </div>
                </div>
            </motion.div>
        </AnimatePresence>
    );
}
