import { useState } from 'react';
import { Modal } from '../ui/Modal';
import { Button } from '../ui/Button';
import { Input } from '../ui/Input';
import { useMutation, useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { PremiumSelect } from '../ui/PremiumSelect';
import { useToast } from '@/context/ToastContext';

interface SubmitExcuseModalProps {
    isOpen: boolean;
    onClose: () => void;
    userId: string;
}

export function SubmitExcuseModal({ isOpen, onClose, userId }: SubmitExcuseModalProps) {
    const [meetingId, setMeetingId] = useState('');
    const [reason, setReason] = useState('');
    const [proofUrl, setProofUrl] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    const meetings: any = useQuery(api.governance.getMeetings);
    const submitExcuse = useMutation(api.governance.submitExcuse);
    const { showToast } = useToast();

    const meetingOptions = meetings?.map((meeting: any) => ({
        value: meeting._id,
        label: `${meeting.title} - ${new Date(meeting.date).toLocaleDateString()}`
    })) || [];

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsSubmitting(true);

        try {
            await submitExcuse({
                userId: userId as any,
                meetingId: meetingId as any,
                reason,
                proofUrl: proofUrl || undefined,
            });

            setMeetingId('');
            setReason('');
            setProofUrl('');
            onClose();
            showToast('success', 'Excuse submitted successfully! Awaiting admin review.');
        } catch (error) {
            console.error('Failed to submit excuse:', error);
            showToast('error', 'Failed to submit excuse. Please try again.');
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <Modal isOpen={isOpen} onClose={onClose} title="Submit Excuse">
            <form onSubmit={handleSubmit} className="space-y-6">
                <div>
                    <label className="block text-sm font-bold mb-2">Select Meeting</label>
                    <PremiumSelect
                        options={meetingOptions}
                        value={meetingId}
                        onChange={setMeetingId}
                        placeholder="Choose a meeting..."
                    />
                </div>

                <div>
                    <label className="block text-sm font-bold mb-2">Reason for Absence</label>
                    <textarea
                        value={reason}
                        onChange={(e) => setReason(e.target.value)}
                        placeholder="Please explain why you couldn't attend..."
                        className="w-full px-4 py-2 rounded-lg border border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg min-h-[100px]"
                        required
                    />
                </div>

                <div>
                    <label className="block text-sm font-bold mb-2">Proof URL (Optional)</label>
                    <Input
                        type="url"
                        value={proofUrl}
                        onChange={(e) => setProofUrl(e.target.value)}
                        placeholder="https://example.com/proof.jpg"
                    />
                    <p className="text-xs text-light-muted mt-2">
                        Upload proof to ImageKit or another service and paste the URL here
                    </p>
                </div>

                <div className="flex gap-4">
                    <Button
                        type="button"
                        variant="ghost"
                        onClick={onClose}
                        className="flex-1"
                        disabled={isSubmitting}
                    >
                        Cancel
                    </Button>
                    <Button
                        type="submit"
                        variant="primary"
                        className="flex-1"
                        disabled={isSubmitting}
                    >
                        {isSubmitting ? 'Submitting...' : 'Submit Excuse'}
                    </Button>
                </div>
            </form>
        </Modal>
    );
}
