import { useState, useEffect, useRef } from 'react';
import { Link, useLocation } from 'react-router-dom';
import {
    LayoutDashboard,
    Gift,
    Mail,
    ChevronLeft,
    X,
    Shield,
    Search,
    Sun,
    Moon,
    HelpCircle,
    Tag,
    ChevronDown,
    Database,
    Settings,
    Megaphone,
    Brain,
    UserCheck,
    DollarSign,
    CreditCard,
    Users,
    Trophy,
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { useAuth } from '@/hooks/useAuth';
import { useTheme } from '@/context/ThemeContext';
import { useNavigate } from 'react-router-dom';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';

const scrollbarStyles = `
  .premium-scrollbar::-webkit-scrollbar {
    width: 4px;
  }
  .premium-scrollbar::-webkit-scrollbar-track {
    background: transparent;
  }
  .premium-scrollbar::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
  }
  .premium-scrollbar::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.2);
  }
  .dark .premium-scrollbar::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.05);
  }
  .dark .premium-scrollbar::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.1);
  }
`;

interface AdminSidebarProps {
    isOpen: boolean;
    onClose: () => void;
}

export function AdminSidebar({ isOpen, onClose }: AdminSidebarProps) {
    const location = useLocation();
    const navigate = useNavigate();
    const { user } = useAuth();
    const { theme, toggleTheme } = useTheme();
    const [expandedGroups, setExpandedGroups] = useState<string[]>(['Board Hub']);

    // Refs for each group button so we can scroll them into view when expanded
    const groupRefs = useRef<Record<string, HTMLDivElement | null>>({});
    const navRef = useRef<HTMLElement>(null);

    // Keep active group expanded based on current route
    useEffect(() => {
        const path = location.pathname;
        if (path.startsWith('/board')) {
            setExpandedGroups(prev =>
                prev.includes('Board Hub') ? prev : [...prev, 'Board Hub']
            );
        }
    }, [location.pathname]);

    const toggleGroup = (label: string) => {
        const willExpand = !expandedGroups.includes(label);

        setExpandedGroups(prev =>
            prev.includes(label)
                ? prev.filter(l => l !== label)
                : [...prev, label]
        );

        // When expanding: wait for animation then scroll the group into view
        if (willExpand) {
            setTimeout(() => {
                const groupEl = groupRefs.current[label];
                const navEl = navRef.current;
                if (groupEl && navEl) {
                    const groupTop = groupEl.offsetTop;
                    const groupBottom = groupTop + groupEl.offsetHeight;
                    const navScrollTop = navEl.scrollTop;
                    const navVisibleBottom = navScrollTop + navEl.clientHeight;

                    // Only scroll if the bottom of the expanded group is out of view
                    if (groupBottom > navVisibleBottom - 16) {
                        navEl.scrollTo({
                            top: groupBottom - navEl.clientHeight + 24,
                            behavior: 'smooth',
                        });
                    }
                }
            }, 50); // give framer-motion a head start
        } else {
            // When collapsing: scroll back so the group button stays visible
            setTimeout(() => {
                const groupEl = groupRefs.current[label];
                const navEl = navRef.current;
                if (groupEl && navEl) {
                    const groupTop = groupEl.offsetTop;
                    if (navEl.scrollTop > groupTop - 8) {
                        navEl.scrollTo({ top: Math.max(0, groupTop - 8), behavior: 'smooth' });
                    }
                }
            }, 50);
        }
    };

    const adminBadges = useQuery(api.stats.getAdminBadgeCounts, {
        adminId: user?._id as any
    });

    const getBadgeCount = (label: string) => {
        if (!adminBadges) return 0;

        const isRealAdmin = user?.role === 'super_admin' || user?.role === 'admin';
        const isFinancialManager = (user as any)?.isFinancialManager === true;

        if (label === 'Questions') return isRealAdmin ? adminBadges.questions : 0;
        if (label === 'Messages') return isRealAdmin ? adminBadges.contacts : 0;
        if (label === 'Banking System') return isRealAdmin ? adminBadges.withdrawals : 0;
        if (label === 'Finance Hub') return (isRealAdmin || isFinancialManager) ? adminBadges.pendingPaymentSubmissions : 0;
        return 0;
    };

    const navItems = [
        // Admin Section
        { path: '/admin', label: 'Dashboard', icon: LayoutDashboard, roles: ['admin', 'super_admin'] },
        { path: '/admin/master-pool', label: 'Master Pool', icon: Database, roles: ['admin', 'super_admin'] },
        { path: '/admin/announcements', label: 'Announcements', icon: Megaphone, roles: ['admin', 'super_admin'] },
        {
            label: 'Banking System',
            icon: CreditCard,
            roles: ['admin', 'super_admin'],
            children: [
                { path: '/admin/banking?tab=overview', label: 'Overview', icon: LayoutDashboard },
                { path: '/admin/banking?tab=withdrawals', label: 'Withdrawals', icon: DollarSign },
                { path: '/admin/banking?tab=wallets', label: 'User Wallets', icon: Users },
                { path: '/admin/banking?tab=rewards', label: 'Reward Activity', icon: Trophy },
            ]
        },
        { path: '/admin/polls', label: 'Weekly Polls', icon: Gift, roles: ['admin', 'super_admin'] },
        { path: '/admin/diamond-challenge', label: 'Challenge Manager', icon: Brain, roles: ['admin', 'super_admin'] },
        { path: '/admin/questions', label: 'Questions', icon: HelpCircle, roles: ['admin', 'super_admin'] },
        { path: '/admin/user-tags', label: 'User Tags', icon: Tag, roles: ['admin', 'super_admin'] },
        { path: '/admin/contacts', label: 'Messages', icon: Mail, roles: ['admin', 'super_admin'] },
        { path: '/admin/roles', label: 'User Roles', icon: Shield, roles: ['admin', 'super_admin'] },
        { path: '/admin/settings', label: 'Settings', icon: Settings, roles: ['admin', 'super_admin'] },

        // Board Member Section
        {
            label: 'Board Hub',
            icon: Shield,
            roles: ['board_member', 'admin', 'super_admin'],
            children: [
                { path: '/board', label: 'Dashboard', icon: LayoutDashboard },
                { path: '/board/attendance', label: 'Attendance', icon: UserCheck },
                { path: '/board/finance', label: 'Finance Hub', icon: DollarSign },
                { path: '/board/contributions', label: 'My Contributions', icon: CreditCard },
            ]
        },
    ];

    const filteredNavItems = navItems.filter(item => {
        if (!item.roles) return true;
        return item.roles.includes(user?.role as string);
    });

    const sidebarContent = (
        <div className="flex flex-col h-full bg-slate-50 dark:bg-[#0d0d0f] border-r border-light-border dark:border-dark-border py-6 px-4">
            {/* Logo Section */}
            <div className="flex items-center gap-3 mb-10 px-2 flex-shrink-0">
                <img
                    src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/premium-logo.png"
                    alt="Logo"
                    className="w-10 h-10 object-contain"
                />
                <div>
                    <h2 className="text-xl font-black text-gradient font-display">
                        {user?.role === 'board_member' ? 'Board Hub' : 'Admin'}
                    </h2>
                    <p className="text-[10px] font-bold text-light-muted dark:text-dark-muted uppercase tracking-widest">Baptizo Community</p>
                </div>
                <button onClick={onClose} className="lg:hidden ml-auto p-2 text-light-muted">
                    <X className="w-6 h-6" />
                </button>
            </div>

            {/* Navigation Items  scrollable section */}
            <nav ref={navRef} className="flex-1 space-y-1 overflow-y-auto premium-scrollbar pr-1 min-h-0">
                {filteredNavItems.map((item) => {
                    const Icon = item.icon;
                    const hasChildren = item.children && item.children.length > 0;
                    const isExpanded = expandedGroups.includes(item.label);
                    const isAnyChildActive = hasChildren && item.children!.some(child => location.pathname === child.path);
                    const isActive = location.pathname === item.path || isAnyChildActive;

                    if (hasChildren) {
                        return (
                            <div
                                key={item.label}
                                className="space-y-1"
                                ref={el => { groupRefs.current[item.label] = el; }}
                            >
                                <button
                                    onClick={() => toggleGroup(item.label)}
                                    className={`
                                        w-full flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200 group
                                        ${isAnyChildActive
                                            ? 'bg-primary-500/10 text-primary-500 font-bold border border-primary-500/20'
                                            : 'text-light-text dark:text-dark-text hover:bg-light-card/80 dark:hover:bg-dark-card/80'
                                        }
                                    `}
                                >
                                    <Icon className={`w-5 h-5 transition-transform duration-300 ${isAnyChildActive ? 'scale-110' : 'group-hover:scale-110'}`} />
                                    <span className="text-sm font-medium">{item.label}</span>
                                    <ChevronDown className={`ml-auto w-4 h-4 transition-transform duration-300 ${isExpanded ? 'rotate-180' : ''}`} />
                                </button>

                                <AnimatePresence initial={false}>
                                    {isExpanded && (
                                        <motion.div
                                            initial={{ height: 0, opacity: 0 }}
                                            animate={{ height: 'auto', opacity: 1 }}
                                            exit={{ height: 0, opacity: 0 }}
                                            transition={{ duration: 0.25, ease: 'easeInOut' }}
                                            className="overflow-hidden pl-4 space-y-1"
                                        >
                                            {item.children!.map((child) => {
                                                const ChildIcon = child.icon;
                                                const isChildActive = location.pathname === child.path;

                                                return (
                                                    <Link
                                                        key={child.path}
                                                        to={child.path}
                                                        onClick={() => {
                                                            if (window.innerWidth < 1024) onClose();
                                                        }}
                                                        className={`
                                                            flex items-center gap-3 px-4 py-2.5 rounded-xl transition-all duration-200 group
                                                            ${isChildActive
                                                                ? 'text-primary-500 font-bold bg-primary-500/5'
                                                                : 'text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text hover:bg-light-card/50 dark:hover:bg-dark-card/50'
                                                            }
                                                        `}
                                                    >
                                                        <ChildIcon className={`w-4 h-4 transition-transform ${isChildActive ? 'scale-110' : 'group-hover:scale-110'}`} />
                                                        <span className="text-xs font-medium">{child.label}</span>

                                                        {getBadgeCount(child.label) > 0 && (
                                                            <span className={`
                                                                ml-auto px-1.5 py-0.5 rounded-full text-[8px] font-bold
                                                                ${isChildActive
                                                                    ? 'bg-primary-500 text-white'
                                                                    : 'bg-primary-500/20 text-primary-500'
                                                                }
                                                            `}>
                                                                {getBadgeCount(child.label)}
                                                            </span>
                                                        )}

                                                        {isChildActive && !getBadgeCount(child.label) && (
                                                            <motion.div
                                                                layoutId="activeIndicator"
                                                                className="ml-auto w-1 h-1 rounded-full bg-primary-500 shadow-[0_0_8px_rgba(59,130,246,0.5)]"
                                                            />
                                                        )}
                                                    </Link>
                                                );
                                            })}
                                        </motion.div>
                                    )}
                                </AnimatePresence>
                            </div>
                        );
                    }

                    return (
                        <Link
                            key={item.path}
                            to={item.path!}
                            onClick={() => {
                                if (window.innerWidth < 1024) onClose();
                            }}
                            className={`
                                flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200 group
                                ${isActive
                                    ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30 font-bold'
                                    : 'text-light-text dark:text-dark-text hover:bg-light-card/80 dark:hover:bg-dark-card/80'
                                }
                            `}
                        >
                            <Icon className={`w-5 h-5 transition-transform duration-300 ${isActive ? 'scale-110' : 'group-hover:scale-110'}`} />
                            <span className="text-sm font-medium">{item.label}</span>

                            {getBadgeCount(item.label) > 0 && (
                                <span className={`
                                    ml-auto px-2 py-0.5 rounded-full text-[10px] font-bold
                                    ${isActive
                                        ? 'bg-white text-primary-500'
                                        : 'bg-primary-500 text-white shadow-sm shadow-primary-500/20'
                                    }
                                `}>
                                    {getBadgeCount(item.label)}
                                </span>
                            )}

                            {isActive && !getBadgeCount(item.label) && (
                                <motion.div
                                    layoutId="activeIndicatorMain"
                                    className="ml-auto w-1.5 h-1.5 rounded-full bg-white shadow-[0_0_8px_white]"
                                />
                            )}
                        </Link>
                    );
                })}
            </nav>

            {/* Bottom Section  never scrolls away */}
            <div className="mt-4 flex-shrink-0 space-y-4 px-2">
                <div className="p-4 rounded-xl glass border border-light-border/50 dark:border-dark-border/50">
                    <div className="flex items-center gap-3 mb-3">
                        <div className="w-10 h-10 rounded-full bg-gradient-to-br from-primary-500 to-secondary-500 flex items-center justify-center text-white font-bold overflow-hidden">
                            {user?.avatarUrl ? (
                                <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover" />
                            ) : (
                                user?.name[0]
                            )}
                        </div>
                        <div className="flex-1 min-w-0">
                            <p className="text-sm font-bold truncate">{user?.name}</p>
                            <p className="text-[10px] text-primary-500 font-bold uppercase tracking-wider">
                                {user?.role.replace('_', ' ')}
                            </p>
                        </div>
                    </div>
                </div>

                {/* Mobile-only Controls */}
                <div className="grid grid-cols-2 gap-2 lg:hidden">
                    <button
                        onClick={() => {
                            navigate('/search');
                            onClose();
                        }}
                        className="flex items-center justify-center gap-2 py-3 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border text-sm font-bold text-light-text dark:text-dark-text"
                    >
                        <Search className="w-4 h-4" />
                        Search
                    </button>
                    <button
                        onClick={toggleTheme}
                        className="flex items-center justify-center gap-2 py-3 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border text-sm font-bold text-light-text dark:text-dark-text"
                    >
                        {theme === 'dark' ? (
                            <>
                                <Sun className="w-4 h-4 text-yellow-500" />
                                Light
                            </>
                        ) : (
                            <>
                                <Moon className="w-4 h-4 text-slate-700" />
                                Dark
                            </>
                        )}
                    </button>
                </div>

                <Link
                    to="/feed"
                    className="flex items-center justify-center gap-2 w-full py-3 rounded-xl border border-light-border dark:border-dark-border hover:bg-light-card dark:hover:bg-dark-card transition-all text-sm font-bold text-light-muted dark:text-dark-muted hover:text-primary-500"
                >
                    <ChevronLeft className="w-4 h-4" />
                    Back to Feed
                </Link>
            </div>
        </div>
    );

    return (
        <>
            {/* Desktop Sidebar */}
            <aside className="hidden lg:block fixed inset-y-0 left-0 w-64 z-50">
                <style>{scrollbarStyles}</style>
                {sidebarContent}
            </aside>

            {/* Mobile Sidebar Overlay */}
            <AnimatePresence>
                {isOpen && (
                    <>
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            onClick={onClose}
                            className="fixed inset-0 bg-black/60 backdrop-blur-sm z-[60] lg:hidden"
                        />
                        <motion.aside
                            initial={{ x: '-100%' }}
                            animate={{ x: 0 }}
                            exit={{ x: '-100%' }}
                            transition={{ type: 'spring', damping: 25, stiffness: 200 }}
                            className="fixed inset-y-0 left-0 w-64 z-[70] lg:hidden"
                        >
                            <style>{scrollbarStyles}</style>
                            {sidebarContent}
                        </motion.aside>
                    </>
                )}
            </AnimatePresence>
        </>
    );
}
