import { motion } from 'framer-motion';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useNavigate } from 'react-router-dom';
import {
    Users,
    Calendar,
    DollarSign,
    Shield,
    CheckCircle2,
    ArrowRight,
    CreditCard,
    History as HistoryIcon
} from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Badge } from '@/components/ui/Badge';
import { StatsCardSkeleton, PostCardSkeleton } from '@/components/ui/Skeletons';

export function BoardDashboard() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();

    // Data Queries
    const meetings = useQuery(api.governance.getMeetings);
    const transactions = useQuery(api.finance.getTransactions, { limit: 5 });
    const contributions = useQuery(api.finance.getMyContributions, user ? { userId: user._id, year: new Date().getFullYear() } : 'skip');
    const userAttendance = useQuery(api.governance.getUserAttendance, user ? { userId: user._id } : 'skip');
    const currentBalance = useQuery(api.finance.getCurrentBalance);

    if (isLoading || meetings === undefined || transactions === undefined || contributions === undefined || userAttendance === undefined) {
        return (
            <div className="space-y-8 p-6">
                <div className="flex items-center gap-2 mb-8">
                    <div className="w-8 h-1 bg-primary-500 rounded-full" />
                    <div className="h-4 w-48 bg-light-muted/10 animate-pulse rounded" />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                    {[1, 2, 3, 4].map(i => <StatsCardSkeleton key={i} />)}
                </div>
                <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                    <PostCardSkeleton />
                    <PostCardSkeleton />
                    <PostCardSkeleton />
                </div>
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    if (user.role !== 'board_member' && user.role !== 'admin' && user.role !== 'super_admin') {
        return <Navigate to="/feed" replace />;
    }

    // Calculations
    const attendanceRate = userAttendance?.length
        ? Math.round((userAttendance.filter((a: any) => a.status === 'present' || a.status === 'late' || a.status === 'excused').length / userAttendance.length) * 100)
        : 100;

    const totalPaid = contributions?.reduce((acc: number, curr: any) => acc + curr.totalPaid, 0) || 0;
    const latestDue = contributions && contributions.length > 0 ? contributions[0] : null;

    const formatCurrency = (amount: number) => {
        return new Intl.NumberFormat('en-NG', {
            style: 'currency',
            currency: 'NGN',
        }).format(amount);
    };

    const stats = [
        {
            label: 'Attendance Rate',
            value: `${attendanceRate}%`,
            icon: Calendar,
            color: 'text-primary-500',
            bg: 'bg-primary-500/10',
            path: '/board/attendance'
        },
        {
            label: 'Community Balance',
            value: formatCurrency(currentBalance || 0),
            icon: Shield,
            color: 'text-amber-500',
            bg: 'bg-amber-500/10',
            path: '/board/finance'
        },
        {
            label: 'Yearly Contributions',
            value: formatCurrency(totalPaid),
            icon: DollarSign,
            color: 'text-emerald-500',
            bg: 'bg-emerald-500/10',
            path: '/board/contributions'
        },
        {
            label: 'Current Status',
            value: latestDue ? latestDue.status.charAt(0).toUpperCase() + latestDue.status.slice(1) : 'Cleared',
            icon: CheckCircle2,
            color: latestDue?.status === 'owing' ? 'text-rose-500' : 'text-emerald-500',
            bg: latestDue?.status === 'owing' ? 'bg-rose-500/10' : 'bg-emerald-500/10',
            path: '/board/contributions'
        },
    ];

    return (
        <div className="space-y-8 pb-20">
            {/* Header */}
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-10"
            >
                <div className="flex items-center gap-2 mb-2">
                    <div className="w-8 h-1 bg-primary-500 rounded-full" />
                    <span className="text-[10px] font-black uppercase tracking-[0.3em] text-primary-500">
                        Board Hub Dashboard
                    </span>
                </div>
                <h1 className="text-4xl md:text-5xl font-black text-gradient font-display mb-2 tracking-tight">
                    Shalom, {user.name.split(' ')[0]}
                </h1>
                <p className="text-light-muted dark:text-dark-muted font-medium">
                    Centralized overview of governance, finance, and your contributions.
                </p>
            </motion.div>

            {/* Stats Grid */}
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
                {stats.map((stat, idx) => (
                    <motion.div
                        key={stat.label}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: idx * 0.1 }}
                    >
                        <Card
                            className="cursor-pointer hover:border-primary-500/30 transition-all group"
                            onClick={() => navigate(stat.path)}
                        >
                            <CardContent className="p-6">
                                <div className="flex items-center justify-between mb-4">
                                    <div className={`p-3 rounded-2xl ${stat.bg} ${stat.color}`}>
                                        <stat.icon className="w-6 h-6" />
                                    </div>
                                    <div className={`text-xl font-black ${stat.color}`}>
                                        {stat.value}
                                    </div>
                                </div>
                                <h3 className="text-xs font-black uppercase tracking-widest text-light-muted">
                                    {stat.label}
                                </h3>
                            </CardContent>
                        </Card>
                    </motion.div>
                ))}
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                {/* 1. Records of Attendee (Global History) */}
                <Card className="lg:col-span-1">
                    <CardHeader>
                        <CardTitle className="flex items-center justify-between">
                            <div className="flex items-center gap-2">
                                <Users className="w-5 h-5 text-primary-500" />
                                Meeting Records
                            </div>
                            <Button variant="ghost" size="sm" className="h-8 px-2" onClick={() => navigate('/board/attendance')}>
                                <ArrowRight className="w-4 h-4" />
                            </Button>
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        {meetings && meetings.slice(0, 4).map((meeting: any) => (
                            <div key={meeting._id} className="p-3 rounded-xl border border-light-border dark:border-dark-border bg-light-bg/30 dark:bg-dark-bg/30">
                                <p className="font-bold text-sm truncate">{meeting.title}</p>
                                <div className="flex items-center justify-between mt-1">
                                    <span className="text-[10px] text-light-muted font-medium">{new Date(meeting.date).toLocaleDateString()}</span>
                                    <Badge className={`${meeting.status === 'active' ? 'bg-emerald-500 animate-pulse' :
                                        meeting.status === 'ended' ? 'bg-light-muted/20 text-light-muted' : 'bg-primary-500/50'
                                        }`}>
                                        {meeting.status}
                                    </Badge>
                                </div>
                            </div>
                        ))}
                        {(!meetings || meetings.length === 0) && (
                            <div className="text-center py-6 text-xs text-light-muted italic">No meeting records yet</div>
                        )}
                    </CardContent>
                </Card>

                {/* 2. Financial History (Global Ledger) */}
                <Card className="lg:col-span-1 border-emerald-500/20">
                    <CardHeader>
                        <CardTitle className="flex items-center justify-between">
                            <div className="flex items-center gap-2">
                                <HistoryIcon className="w-5 h-5 text-emerald-500" />
                                Financial History
                            </div>
                            <Button variant="ghost" size="sm" className="h-8 px-2 text-emerald-500 hover:text-emerald-600" onClick={() => navigate('/board/finance')}>
                                <ArrowRight className="w-4 h-4" />
                            </Button>
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        {transactions && transactions.map((txn: any) => (
                            <div key={txn._id} className="p-3 rounded-xl border border-emerald-500/10 bg-emerald-500/5">
                                <div className="flex justify-between items-start mb-1">
                                    <p className="text-xs font-bold truncate max-w-[120px]">{txn.description}</p>
                                    <p className={`text-xs font-black ${txn.type === 'deposit' ? 'text-emerald-500' : 'text-rose-500'}`}>
                                        {txn.type === 'deposit' ? '+' : '-'}{formatCurrency(txn.amount)}
                                    </p>
                                </div>
                                <div className="flex items-center justify-between">
                                    <span className="text-[9px] text-light-muted">{new Date(txn.createdAt).toLocaleDateString()}</span>
                                    <span className="text-[9px] text-light-muted uppercase font-black">{txn.memberName || 'System'}</span>
                                </div>
                            </div>
                        ))}
                    </CardContent>
                </Card>

                {/* 3. My Contribution History (Personal) */}
                <Card className="lg:col-span-1 border-primary-500/20">
                    <CardHeader>
                        <CardTitle className="flex items-center justify-between">
                            <div className="flex items-center gap-2">
                                <CreditCard className="w-5 h-5 text-primary-500" />
                                My Stake
                            </div>
                            <Button variant="ghost" size="sm" className="h-8 px-2" onClick={() => navigate('/board/contributions')}>
                                <ArrowRight className="w-4 h-4" />
                            </Button>
                        </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <div className="grid grid-cols-2 gap-3 mb-2">
                            <div className="p-3 rounded-xl bg-primary-500/5 text-center border border-primary-500/10">
                                <p className="text-[9px] font-black text-light-muted uppercase">Paid this Year</p>
                                <p className="text-sm font-black text-primary-500">{formatCurrency(totalPaid)}</p>
                            </div>
                            <div className="p-3 rounded-xl bg-primary-500/5 text-center border border-primary-500/10">
                                <p className="text-[9px] font-black text-light-muted uppercase">Monthly Due</p>
                                <p className="text-sm font-black text-primary-500">1,000</p>
                            </div>
                        </div>
                        <div className="space-y-2">
                            {contributions && contributions.slice(0, 3).map((due: any, idx: number) => (
                                <div key={idx} className="flex items-center justify-between p-2.5 rounded-lg bg-light-bg/50 dark:bg-dark-bg/50 text-xs">
                                    <span className="font-bold">{new Date(2024, due.month - 1).toLocaleDateString('en-US', { month: 'short' })}</span>
                                    <span className="font-medium">{formatCurrency(due.totalPaid)}</span>
                                    <Badge className={due.status === 'cleared' ? 'bg-emerald-500' : 'bg-rose-500'}>{due.status}</Badge>
                                </div>
                            ))}
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
}
