import { useState } from 'react';
import { motion } from 'framer-motion';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import {
    Calendar,
    Clock,
    Users,
    CheckCircle,
    XCircle,
    AlertCircle,
    Plus,
    Play,
    Eye,
    FileText,
    Check,
    X,
    StopCircle,
    Copy
} from 'lucide-react';
import { Badge } from '../ui/Badge';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/Card';
import { Button } from '../ui/Button';
import { formatDistanceToNow } from 'date-fns';
import { CreateMeetingModal } from './CreateMeetingModal';
import { ClockInModal } from './ClockInModal';
import { SubmitExcuseModal } from './SubmitExcuseModal';
import { MeetingAttendanceModal } from './MeetingAttendanceModal';
import { EndMeetingModal } from './EndMeetingModal';
import { StatsCardSkeleton, PostCardSkeleton } from '../ui/Skeletons';

interface AttendanceTabProps {
    user: any;
}

export function AttendanceTab({ user }: AttendanceTabProps) {
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [showClockInModal, setShowClockInModal] = useState(false);
    const [showExcuseModal, setShowExcuseModal] = useState(false);
    const [showAttendanceModal, setShowAttendanceModal] = useState(false);
    const [showEndMeetingModal, setShowEndMeetingModal] = useState(false);
    const [selectedMeeting, setSelectedMeeting] = useState<any>(null);

    // Queries
    const meetings = useQuery(api.governance.getMeetings);
    const activeMeeting = useQuery(api.governance.getActiveMeeting);
    const pendingExcuses = useQuery(api.governance.getPendingExcuses);
    const userAttendance = useQuery(
        api.governance.getUserAttendance,
        user ? { userId: user._id } : 'skip'
    );

    // Mutations
    const reviewExcuse = useMutation(api.governance.reviewExcuse);

    const isLoading = meetings === undefined || userAttendance === undefined;

    if (isLoading) {
        return (
            <div className="space-y-8">
                <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
                    {[1, 2, 3, 4].map(i => <StatsCardSkeleton key={i} />)}
                </div>
                <div className="space-y-4">
                    {[1, 2, 3].map(i => <PostCardSkeleton key={i} />)}
                </div>
            </div>
        );
    }

    const isAdmin = user?.role === 'super_admin' || user?.role === 'admin';


    const getStatusBadge = (status: string) => {
        switch (status) {
            case 'present':
                return <Badge className="bg-emerald-500 text-white">Present</Badge>;
            case 'late':
                return <Badge className="bg-amber-500 text-white">Late</Badge>;
            case 'absent':
                return <Badge className="bg-rose-500 text-white">Absent</Badge>;
            case 'excused':
                return <Badge className="bg-blue-500 text-white">Excused</Badge>;
            default:
                return <Badge variant="outline">Unknown</Badge>;
        }
    };

    return (
        <div className="space-y-8">
            {/* Stats Overview */}
            <div className="grid grid-cols-2 sm:grid-cols-2 lg:grid-cols-4 gap-4 md:gap-6">
                <Card className="border-primary-500/20">
                    <CardContent className="p-4 md:p-6">
                        <div className="flex items-center justify-between mb-3 md:mb-4">
                            <div className="p-2 md:p-3 rounded-2xl bg-primary-500/10 text-primary-500">
                                <Calendar className="w-5 h-5 md:w-6 md:h-6" />
                            </div>
                            <div className="text-right text-2xl md:text-3xl font-black text-primary-500">
                                {meetings?.length || 0}
                            </div>
                        </div>
                        <h3 className="text-[10px] md:text-xs font-black uppercase tracking-widest text-light-muted mb-1">
                            Total Meetings
                        </h3>
                    </CardContent>
                </Card>

                <Card className="border-emerald-500/20">
                    <CardContent className="p-4 md:p-6">
                        <div className="flex items-center justify-between mb-3 md:mb-4">
                            <div className="p-2 md:p-3 rounded-2xl bg-emerald-500/10 text-emerald-500">
                                <CheckCircle className="w-5 h-5 md:w-6 md:h-6" />
                            </div>
                            <div className="text-right text-2xl md:text-3xl font-black text-emerald-500">
                                {userAttendance?.filter((a: any) => a.status === 'present').length || 0}
                            </div>
                        </div>
                        <h3 className="text-[10px] md:text-xs font-black uppercase tracking-widest text-light-muted mb-1">
                            Present
                        </h3>
                    </CardContent>
                </Card>

                <Card className="border-amber-500/20">
                    <CardContent className="p-4 md:p-6">
                        <div className="flex items-center justify-between mb-3 md:mb-4">
                            <div className="p-2 md:p-3 rounded-2xl bg-amber-500/10 text-amber-500">
                                <Clock className="w-5 h-5 md:w-6 md:h-6" />
                            </div>
                            <div className="text-right text-2xl md:text-3xl font-black text-amber-500">
                                {userAttendance?.filter((a: any) => a.status === 'late').length || 0}
                            </div>
                        </div>
                        <h3 className="text-[10px] md:text-xs font-black uppercase tracking-widest text-light-muted mb-1">
                            Late
                        </h3>
                    </CardContent>
                </Card>

                <Card className="border-rose-500/20">
                    <CardContent className="p-4 md:p-6">
                        <div className="flex items-center justify-between mb-3 md:mb-4">
                            <div className="p-2 md:p-3 rounded-2xl bg-rose-500/10 text-rose-500">
                                <XCircle className="w-5 h-5 md:w-6 md:h-6" />
                            </div>
                            <div className="text-right text-2xl md:text-3xl font-black text-rose-500">
                                {userAttendance?.filter((a: any) => a.status === 'absent').length || 0}
                            </div>
                        </div>
                        <h3 className="text-[10px] md:text-xs font-black uppercase tracking-widest text-light-muted mb-1">
                            Absent
                        </h3>
                    </CardContent>
                </Card>
            </div>

            {/* Active Meeting or Pending Excuses */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                {/* Active Meeting Card */}
                {activeMeeting && (
                    <motion.div
                        initial={{ opacity: 0, scale: 0.95 }}
                        animate={{ opacity: 1, scale: 1 }}
                    >
                        <Card className="border-primary-500 bg-primary-500/5 relative overflow-hidden group">
                            <div className="absolute top-0 right-0 p-8 opacity-5 group-hover:scale-110 transition-transform">
                                <Play className="w-32 h-32 text-primary-500" />
                            </div>
                            <CardHeader>
                                <div className="flex items-center justify-between mb-2">
                                    <Badge className="bg-primary-500 text-white animate-pulse">Meeting In Progress</Badge>
                                    <div className="flex items-center gap-2 text-xs font-bold text-primary-500">
                                        <Clock className="w-4 h-4" />
                                        <span>Started {formatDistanceToNow(activeMeeting.createdAt)} ago</span>
                                    </div>
                                </div>
                                <CardTitle className="text-2xl font-black">{activeMeeting.title}</CardTitle>
                            </CardHeader>
                            <CardContent className="space-y-4 md:space-y-6">
                                <div className="p-3 md:p-4 rounded-2xl bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border dark:border-dark-border">
                                    <div className="flex items-center justify-between mb-2">
                                        <span className="text-[10px] md:text-xs font-bold text-light-muted uppercase tracking-wider">Attendance Code</span>
                                        {isAdmin && (
                                            <Button
                                                variant="ghost"
                                                size="sm"
                                                className="h-6 gap-1 text-[10px] px-2"
                                                onClick={() => {
                                                    navigator.clipboard.writeText(activeMeeting.code);
                                                    // Add toast notification here
                                                }}
                                            >
                                                <Copy className="w-3 h-3" />
                                                Copy
                                            </Button>
                                        )}
                                    </div>
                                    <div className="text-2xl md:text-4xl font-black tracking-[0.3em] md:tracking-[0.5em] text-center text-primary-500 font-mono">
                                        {isAdmin ? activeMeeting.code : ''}
                                    </div>
                                </div>

                                <div className="flex flex-col sm:flex-row gap-2 md:gap-3">
                                    {!isAdmin && (
                                        <>
                                            {!userAttendance?.some((a: any) => a.meetingId === activeMeeting._id) ? (
                                                <Button
                                                    className="flex-1 bg-primary-500 hover:bg-primary-600 text-white rounded-xl h-11 md:h-12 text-sm md:text-base font-bold shadow-lg shadow-primary-500/20"
                                                    onClick={() => setShowClockInModal(true)}
                                                >
                                                    <Play className="w-4 h-4 md:w-5 md:h-5 mr-2" />
                                                    Clock In Now
                                                </Button>
                                            ) : (
                                                <div className="flex-1 flex items-center justify-center gap-2 p-2.5 md:p-3 rounded-xl bg-emerald-500/10 border border-emerald-500/20 text-emerald-500 font-bold text-xs md:text-sm">
                                                    <CheckCircle className="w-4 h-4 md:w-5 md:h-5" />
                                                    You are clocked in
                                                </div>
                                            )}
                                        </>
                                    )}
                                    {isAdmin && (
                                        <Button
                                            variant="danger"
                                            className="flex-1 rounded-xl h-11 md:h-12 text-sm md:text-base font-bold shadow-lg shadow-rose-500/20"
                                            onClick={() => setShowEndMeetingModal(true)}
                                        >
                                            <StopCircle className="w-4 h-4 md:w-5 md:h-5 mr-2" />
                                            End Meeting
                                        </Button>
                                    )}
                                    <Button
                                        variant="outline"
                                        className="flex-1 rounded-xl h-11 md:h-12 text-sm md:text-base font-bold border-2"
                                        onClick={() => {
                                            setSelectedMeeting(activeMeeting);
                                            setShowAttendanceModal(true);
                                        }}
                                    >
                                        <Users className="w-4 h-4 md:w-5 md:h-5 mr-2" />
                                        View Attendees
                                    </Button>
                                </div>
                            </CardContent>
                        </Card>
                    </motion.div>
                )}

                {/* Create Meeting (Admin) or General Banner */}
                {!activeMeeting && isAdmin && (
                    <Card className="border-dashed border-2 flex flex-col items-center justify-center p-8 text-center bg-light-bg/30 dark:bg-dark-bg/30">
                        <div className="p-4 rounded-full bg-primary-500/10 text-primary-500 mb-4">
                            <Plus className="w-10 h-10" />
                        </div>
                        <h3 className="text-xl font-bold mb-2">No Active Meeting</h3>
                        <p className="text-light-muted mb-6 max-w-xs">Ready to start the next session? Setup a meeting and generate an attendance code.</p>
                        <Button
                            className="bg-primary-500 hover:bg-primary-600 text-white rounded-xl h-12 px-8 font-bold shadow-lg shadow-primary-500/20"
                            onClick={() => setShowCreateModal(true)}
                        >
                            Create Meeting
                        </Button>
                    </Card>
                )}

                {!activeMeeting && !isAdmin && (
                    <Card className="p-8 bg-gradient-to-br from-primary-500/5 to-transparent border-primary-500/20">
                        <div className="flex items-center gap-4">
                            <div className="p-3 rounded-2xl bg-primary-500/10 text-primary-500">
                                <Calendar className="w-8 h-8" />
                            </div>
                            <div>
                                <h3 className="font-bold text-lg">Next Meeting</h3>
                                <p className="text-light-muted">No meetings currently in progress. Stay tuned for announcements.</p>
                            </div>
                        </div>
                    </Card>
                )}

                {/* Pending Excuses (Admin only) */}
                {isAdmin && pendingExcuses && pendingExcuses.length > 0 && (
                    <Card className="border-amber-500/50 bg-amber-500/5">
                        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                            <CardTitle className="text-base font-bold flex items-center gap-2">
                                <AlertCircle className="w-5 h-5 text-amber-500" />
                                Pending Excuses
                            </CardTitle>
                            <Badge variant="outline" className="text-amber-500 border-amber-500">{pendingExcuses.length}</Badge>
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-4">
                                {pendingExcuses.map((excuse: any) => (
                                    <div key={excuse._id} className="flex items-center justify-between p-3 rounded-xl bg-white/50 dark:bg-black/20 border border-amber-500/20">
                                        <div>
                                            <p className="font-bold text-sm">{excuse.userName}</p>
                                            <p className="text-xs text-light-muted">{excuse.meetingTitle}</p>
                                        </div>
                                        <div className="flex gap-2">
                                            <Button
                                                size="sm"
                                                variant="ghost"
                                                className="h-8 w-8 p-0 text-rose-500 hover:bg-rose-500/10"
                                                onClick={() => reviewExcuse({ adminId: user._id, excuseId: excuse._id, approved: false })}
                                            >
                                                <X className="w-4 h-4" />
                                            </Button>
                                            <Button
                                                size="sm"
                                                variant="ghost"
                                                className="h-8 w-8 p-0 text-emerald-500 hover:bg-emerald-500/10"
                                                onClick={() => reviewExcuse({ adminId: user._id, excuseId: excuse._id, approved: true })}
                                            >
                                                <Check className="w-4 h-4" />
                                            </Button>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </CardContent>
                    </Card>
                )}
            </div>

            {/* Attendance History */}
            <div className="space-y-4">
                <div className="flex items-center justify-between">
                    <h2 className="text-xl md:text-2xl font-black flex items-center gap-2">
                        <FileText className="w-5 h-5 md:w-6 md:h-6 text-primary-500" />
                        Meeting History
                    </h2>
                </div>

                <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 md:gap-6">
                    {meetings?.map((meeting: any) => {
                        const userRecord = userAttendance?.find((a: any) => a.meetingId === meeting._id);
                        return (
                            <Card key={meeting._id} className="hover:border-primary-500/30 transition-all group">
                                <CardHeader className="pb-2">
                                    <div className="flex items-center justify-between mb-2">
                                        <Badge variant="outline" className="text-[10px] font-bold">
                                            {new Date(meeting.date).toLocaleDateString()}
                                        </Badge>
                                        {userRecord ? getStatusBadge(userRecord.status) : (
                                            meeting.status === 'ended' ? <Badge className="bg-rose-500 text-white">Absent</Badge> : <Badge variant="secondary">Scheduled</Badge>
                                        )}
                                    </div>
                                    <CardTitle className="text-lg font-bold group-hover:text-primary-500 transition-colors">
                                        {meeting.title}
                                    </CardTitle>
                                </CardHeader>
                                <CardContent>
                                    <div className="flex items-center gap-4 text-xs text-light-muted mb-6">
                                        <div className="flex items-center gap-1">
                                            <Clock className="w-3 h-3" />
                                            {meeting.startTime}
                                        </div>
                                        <div className="flex items-center gap-1">
                                            <Users className="w-3 h-3" />
                                            {meeting.durationMinutes} mins
                                        </div>
                                    </div>

                                    <div className="flex gap-2">
                                        <Button
                                            variant="outline"
                                            className="flex-1 text-[10px] h-9 rounded-lg font-bold"
                                            onClick={() => {
                                                setSelectedMeeting(meeting);
                                                setShowAttendanceModal(true);
                                            }}
                                        >
                                            <Eye className="w-3.5 h-3.5 mr-1.5" />
                                            Attendance
                                        </Button>
                                        {!userRecord && meeting.status === 'ended' && (
                                            <Button
                                                variant="secondary"
                                                className="flex-1 text-[10px] h-9 rounded-lg font-bold text-amber-500"
                                                onClick={() => {
                                                    setSelectedMeeting(meeting);
                                                    setShowExcuseModal(true);
                                                }}
                                            >
                                                Submit Excuse
                                            </Button>
                                        )}
                                    </div>
                                    {userRecord?.joinTime && (
                                        <p className="text-[10px] md:text-xs text-light-muted mt-2">
                                            Clocked in at {new Date(userRecord.joinTime).toLocaleTimeString()}
                                        </p>
                                    )}
                                </CardContent>
                            </Card>
                        );
                    })}
                </div>
            </div>

            {/* Modals */}
            <CreateMeetingModal
                isOpen={showCreateModal}
                onClose={() => setShowCreateModal(false)}
                userId={user?._id}
            />
            {activeMeeting && (
                <ClockInModal
                    isOpen={showClockInModal}
                    onClose={() => setShowClockInModal(false)}
                    userId={user?._id}
                />
            )}
            <EndMeetingModal
                isOpen={showEndMeetingModal}
                onClose={() => setShowEndMeetingModal(false)}
                meeting={activeMeeting}
                userId={user?._id}
            />
            <SubmitExcuseModal
                isOpen={showExcuseModal}
                onClose={() => setShowExcuseModal(false)}
                userId={user?._id}
            />
            <MeetingAttendanceModal
                isOpen={showAttendanceModal}
                onClose={() => setShowAttendanceModal(false)}
                meeting={selectedMeeting}
            />
        </div>
    );
}
