import { useNavigate } from 'react-router-dom';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Id } from '../../../convex/_generated/dataModel';
import { Trophy, X, Brain, Gamepad2, ArrowRight, CheckCircle2 } from 'lucide-react';
import { WeeklyPollCard } from '@/components/polls/WeeklyPollCard';
import { Button } from '@/components/ui/Button';
import { useState } from 'react';
import { AnimatePresence, motion } from 'framer-motion';
import { cn } from '@/lib/cn';

interface StickyHeaderProps {
    announcement?: {
        _id: Id<"announcements">;
        imageUrl: string;
        title: string;
    } | null;
    showGame?: boolean;
}

export function StickyHeader({ announcement, showGame: scrollShowGame }: StickyHeaderProps) {
    const navigate = useNavigate();
    const { user } = useAuth();
    const [isGameOpen, setIsGameOpen] = useState(false);
    const [view, setView] = useState<'menu' | 'poll' | 'word'>('menu');

    // Queries
    const poll = useQuery(api.polls.getActivePoll, user ? { userId: user._id } : "skip");
    const wunder = useQuery(api.wordUnderPressure.getActivePassageStatus, user ? { userId: user._id } : "skip");

    const hasActivePoll = poll !== null && poll !== undefined;
    const hasActiveWunder = wunder?.hasActivePassage === true;

    const needsPoll = hasActivePoll && !poll.hasVoted;
    const needsWunder = hasActiveWunder && !wunder.rewardClaimed;

    const totalUnfinished = (needsPoll ? 1 : 0) + (needsWunder ? 1 : 0);
    const hasAnyGame = hasActivePoll || hasActiveWunder;

    // Visibility: scrollShowGame means we are sticky. 
    // If we are sticky but no games are active, return null.
    if (!announcement && (!scrollShowGame || !hasAnyGame)) return null;

    return (
        <>
            {/* Sticky Icons Container */}
            <div className="fixed top-20 left-4 z-40 flex flex-col gap-3 animate-in fade-in zoom-in duration-300">

                {/* 1. Announcement Story */}
                {announcement && (
                    <div
                        onClick={() => navigate(`/announcement/${announcement._id}`)}
                        className="cursor-pointer"
                    >
                        <div className="relative group w-12 h-12">
                            {/* Story Gradient Ring */}
                            <div className="absolute -inset-[3px] bg-gradient-to-tr from-yellow-500 via-orange-500 to-red-500 rounded-full opacity-90 group-hover:opacity-100 transition-opacity animate-pulse" />
                            {/* Border */}
                            <div className="absolute -inset-[1px] bg-light-bg dark:bg-dark-bg rounded-full z-0" />
                            {/* Image */}
                            <div className="relative w-full h-full rounded-full overflow-hidden z-10 border border-black/10 dark:border-white/10">
                                <img
                                    src={announcement.imageUrl}
                                    alt={announcement.title}
                                    className="w-full h-full object-cover"
                                />
                            </div>
                        </div>
                    </div>
                )}

                {/* 2. Game Story (Smaller) */}
                {scrollShowGame && hasAnyGame && (
                    <div
                        onClick={() => {
                            setView('menu');
                            setIsGameOpen(true);
                        }}
                        className="cursor-pointer"
                    >
                        <div className="relative group w-10 h-10 ml-1">
                            {/* Gradient Ring - different colors if unfinished */}
                            <div className={cn(
                                "absolute -inset-[2px] rounded-full opacity-90 group-hover:opacity-100 transition-all duration-500",
                                totalUnfinished > 0
                                    ? "bg-gradient-to-tr from-emerald-500 via-indigo-500 to-purple-500 animate-pulse"
                                    : "bg-light-border dark:bg-dark-border"
                            )} />
                            <div className="absolute -inset-[1px] bg-light-bg dark:bg-dark-bg rounded-full z-0" />
                            <div className="relative w-full h-full rounded-full overflow-hidden z-10 border border-black/10 dark:border-white/10 bg-white dark:bg-gray-900 flex items-center justify-center">
                                <Trophy className={cn("w-5 h-5", totalUnfinished > 0 ? "text-indigo-500" : "text-light-muted opacity-50")} />
                            </div>

                            {/* Notification Badge */}
                            {totalUnfinished > 0 && (
                                <div className="absolute -top-1 -right-1 z-20 bg-red-500 text-white text-[8px] font-bold px-1.5 py-0.5 rounded-full border-2 border-light-bg dark:border-dark-bg animate-bounce shadow-lg">
                                    {totalUnfinished}
                                </div>
                            )}
                        </div>
                    </div>
                )}
            </div>

            {/* Game Modal */}
            <AnimatePresence>
                {isGameOpen && (
                    <div className="fixed inset-0 z-[60] flex items-center justify-center p-4">
                        {/* Backdrop */}
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            onClick={() => setIsGameOpen(false)}
                            className="absolute inset-0 bg-black/80 backdrop-blur-sm"
                        />

                        {/* Modal Content */}
                        <motion.div
                            initial={{ scale: 0.95, opacity: 0, y: 20 }}
                            animate={{ scale: 1, opacity: 1, y: 0 }}
                            exit={{ scale: 0.95, opacity: 0, y: 20 }}
                            className="relative w-full max-w-lg z-10 max-h-[90vh] overflow-y-auto custom-scrollbar rounded-[2.5rem]"
                        >
                            <AnimatePresence mode="wait">
                                {view === 'menu' ? (
                                    <motion.div
                                        key="menu"
                                        initial={{ opacity: 0, x: -20 }}
                                        animate={{ opacity: 1, x: 0 }}
                                        exit={{ opacity: 0, x: 20 }}
                                        className="bg-light-card dark:bg-dark-card rounded-[2.5rem] p-8 border border-light-border dark:border-dark-border shadow-2xl space-y-6"
                                    >
                                        <div className="text-center space-y-2">
                                            <h2 className="text-2xl font-display font-bold text-gradient">Active Challenges</h2>
                                            <p className="text-sm text-light-muted dark:text-dark-muted">Choose a quest to earn Rubies</p>
                                        </div>

                                        <div className="grid gap-4">
                                            {/* Weekly Poll Option */}
                                            {hasActivePoll && (
                                                <button
                                                    onClick={() => setView('poll')}
                                                    className={cn(
                                                        "group flex items-center gap-4 p-5 rounded-3xl border transition-all duration-300 text-left",
                                                        needsPoll
                                                            ? "bg-primary-500/5 border-primary-500/20 hover:bg-primary-500/10 hover:border-primary-500/40"
                                                            : "bg-light-bg dark:bg-dark-bg border-light-border dark:border-dark-border opacity-60"
                                                    )}
                                                >
                                                    <div className={cn(
                                                        "w-12 h-12 rounded-2xl flex items-center justify-center transition-colors",
                                                        needsPoll ? "bg-primary-500 text-white" : "bg-light-muted/10 text-light-muted"
                                                    )}>
                                                        <Gamepad2 className="w-6 h-6" />
                                                    </div>
                                                    <div className="flex-1">
                                                        <h3 className="font-bold">Weekly Wisdom</h3>
                                                        <p className="text-xs text-light-muted">{needsPoll ? "Earn up to 1 Ruby" : "Poll completed"}</p>
                                                    </div>
                                                    {needsPoll ? (
                                                        <ArrowRight className="w-5 h-5 text-primary-500 transform group-hover:translate-x-1 transition-transform" />
                                                    ) : (
                                                        <CheckCircle2 className="w-5 h-5 text-green-500" />
                                                    )}
                                                </button>
                                            )}

                                            {/* Word Challenge Option */}
                                            {hasActiveWunder && (
                                                <button
                                                    onClick={() => navigate('/diamond-challenge')}
                                                    className={cn(
                                                        "group flex items-center gap-4 p-5 rounded-3xl border transition-all duration-300 text-left",
                                                        needsWunder
                                                            ? "bg-emerald-500/5 border-emerald-500/20 hover:bg-emerald-500/10 hover:border-emerald-500/40"
                                                            : "bg-light-bg dark:bg-dark-bg border-light-border dark:border-dark-border opacity-60"
                                                    )}
                                                >
                                                    <div className={cn(
                                                        "w-12 h-12 rounded-2xl flex items-center justify-center transition-colors",
                                                        needsWunder ? "bg-emerald-500 text-white" : "bg-light-muted/10 text-light-muted"
                                                    )}>
                                                        <Brain className="w-6 h-6" />
                                                    </div>
                                                    <div className="flex-1">
                                                        <h3 className="font-bold">Diamond Challenge</h3>
                                                        <p className="text-xs text-light-muted">{needsWunder ? "Earn up to 11.5 Rubies" : "Challenge participated"}</p>
                                                    </div>
                                                    {needsWunder ? (
                                                        <ArrowRight className="w-5 h-5 text-emerald-500 transform group-hover:translate-x-1 transition-transform" />
                                                    ) : (
                                                        <CheckCircle2 className="w-5 h-5 text-green-500" />
                                                    )}
                                                </button>
                                            )}
                                        </div>
                                    </motion.div>
                                ) : view === 'poll' ? (
                                    <motion.div
                                        key="poll"
                                        initial={{ opacity: 0, x: 20 }}
                                        animate={{ opacity: 1, x: 0 }}
                                        exit={{ opacity: 0, x: -20 }}
                                    >
                                        <WeeklyPollCard />
                                        <Button
                                            variant="ghost"
                                            className="mt-4 text-xs mx-auto block font-bold text-light-muted"
                                            onClick={() => setView('menu')}
                                        >
                                            Back to Menu
                                        </Button>
                                    </motion.div>
                                ) : null}
                            </AnimatePresence>

                            {/* Close Button */}
                            <button
                                onClick={() => setIsGameOpen(false)}
                                className="absolute top-3 right-3 md:-top-12 md:-right-12 w-8 h-8 md:w-10 md:h-10 rounded-full bg-black/20 md:bg-white/10 hover:bg-black/30 md:hover:bg-white/20 flex items-center justify-center text-white backdrop-blur-md transition-colors border border-white/10 z-50"
                            >
                                <X className="w-5 h-5 md:w-6 md:h-6" />
                            </button>
                        </motion.div>
                    </div>
                )}
            </AnimatePresence>
        </>
    );
}
