import { useState, useRef, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronDown, Check, Search } from 'lucide-react';

interface User {
    _id: string;
    name: string;
    role: string;
    avatarUrl?: string;
}

interface PremiumUserPickerProps {
    users: User[];
    selectedUserId: string;
    onSelect: (userId: string) => void;
    label?: string;
}

export function PremiumUserPicker({ users, selectedUserId, onSelect, label = "Assign To" }: PremiumUserPickerProps) {
    const [isOpen, setIsOpen] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    const containerRef = useRef<HTMLDivElement>(null);

    const selectedUser = users.find(u => u._id === selectedUserId);

    const filteredUsers = users.filter(u =>
        u.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        u.role.toLowerCase().includes(searchQuery.toLowerCase())
    );

    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (containerRef.current && !containerRef.current.contains(event.target as Node)) {
                setIsOpen(false);
            }
        };

        document.addEventListener('mousedown', handleClickOutside);
        return () => document.removeEventListener('mousedown', handleClickOutside);
    }, []);

    return (
        <div className="w-full relative" ref={containerRef}>
            {label && <label className="block text-sm font-medium mb-1.5 text-light-text dark:text-dark-text">{label}</label>}

            <button
                type="button"
                onClick={() => setIsOpen(!isOpen)}
                className={`w-full flex items-center justify-between p-3 rounded-xl border transition-all ${isOpen
                        ? 'border-primary-500 ring-2 ring-primary-500/20 bg-light-card/80 dark:bg-dark-card/80'
                        : 'border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg hover:border-primary-500/50'
                    }`}
            >
                {selectedUser ? (
                    <div className="flex items-center gap-3">
                        <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-primary-500 to-secondary-500 p-0.5">
                            <div className="w-full h-full rounded-lg bg-light-bg dark:bg-dark-bg flex items-center justify-center overflow-hidden">
                                {selectedUser.avatarUrl ? (
                                    <img src={selectedUser.avatarUrl} alt={selectedUser.name} className="w-full h-full object-cover" />
                                ) : (
                                    <span className="text-xs font-bold">{selectedUser.name[0]}</span>
                                )}
                            </div>
                        </div>
                        <div className="flex flex-col items-start leading-none">
                            <span className="font-semibold text-sm">{selectedUser.name}</span>
                            <span className="text-[10px] text-light-muted dark:text-dark-muted font-bold uppercase tracking-wider mt-0.5">
                                {selectedUser.role.replace('_', ' ')}
                            </span>
                        </div>
                    </div>
                ) : (
                    <span className="text-light-muted dark:text-dark-muted">Select a member...</span>
                )}
                <ChevronDown className={`w-5 h-5 text-light-muted dark:text-dark-muted transition-transform ${isOpen ? 'rotate-180' : ''}`} />
            </button>

            <AnimatePresence>
                {isOpen && (
                    <motion.div
                        initial={{ opacity: 0, y: 5, scale: 0.95 }}
                        animate={{ opacity: 1, y: 0, scale: 1 }}
                        exit={{ opacity: 0, scale: 0.95 }}
                        className="absolute top-full left-0 right-0 mt-2 z-50 bg-light-card/95 dark:bg-dark-card/95 backdrop-blur-xl border border-light-border/20 dark:border-dark-border/20 rounded-xl shadow-2xl overflow-hidden"
                    >
                        <div className="p-2 border-b border-light-border/10 dark:border-dark-border/10">
                            <div className="relative">
                                <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-light-muted dark:text-dark-muted" />
                                <input
                                    type="text"
                                    placeholder="Search members..."
                                    value={searchQuery}
                                    onChange={(e) => setSearchQuery(e.target.value)}
                                    className="w-full pl-9 pr-3 py-2 bg-light-bg/50 dark:bg-dark-bg/50 rounded-lg text-sm border-none focus:ring-1 focus:ring-primary-500 outline-none"
                                    autoFocus
                                />
                            </div>
                        </div>

                        <div className="max-h-[250px] overflow-y-auto thin-scrollbar p-1">
                            {filteredUsers.length > 0 ? (
                                filteredUsers.map((user) => (
                                    <button
                                        key={user._id}
                                        onClick={() => {
                                            onSelect(user._id);
                                            setIsOpen(false);
                                        }}
                                        className={`w-full flex items-center justify-between p-2 rounded-lg transition-colors ${selectedUserId === user._id
                                                ? 'bg-primary-500/10 text-primary-500'
                                                : 'hover:bg-light-bg/80 dark:hover:bg-dark-bg/80'
                                            }`}
                                    >
                                        <div className="flex items-center gap-3">
                                            <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-primary-500 to-secondary-500 p-0.5">
                                                <div className="w-full h-full rounded-lg bg-light-bg dark:bg-dark-bg flex items-center justify-center overflow-hidden">
                                                    {user.avatarUrl ? (
                                                        <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover" />
                                                    ) : (
                                                        <span className="text-xs font-bold">{user.name[0]}</span>
                                                    )}
                                                </div>
                                            </div>
                                            <div className="flex flex-col items-start leading-none">
                                                <span className="font-semibold text-sm">{user.name}</span>
                                                <span className="text-[10px] opacity-70 font-bold uppercase tracking-wider mt-0.5">
                                                    {user.role.replace('_', ' ')}
                                                </span>
                                            </div>
                                        </div>
                                        {selectedUserId === user._id && <Check className="w-4 h-4" />}
                                    </button>
                                ))
                            ) : (
                                <div className="p-4 text-center text-sm text-light-muted dark:text-dark-muted">
                                    No members found
                                </div>
                            )}
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    );
}
