import { Eye, EyeOff } from 'lucide-react';
import { motion } from 'framer-motion';

interface PostVisibilityToggleProps {
    isVisible: boolean;
    onToggle: () => void;
    category: 'Ancient Culture' | 'Wisdom of The Now';
}

export function PostVisibilityToggle({ isVisible, onToggle, category }: PostVisibilityToggleProps) {
    return (
        <motion.button
            onClick={onToggle}
            whileTap={{ scale: 0.95 }}
            className={`flex items-center gap-2 px-4 py-2 rounded-lg font-medium transition-all ${isVisible
                    ? 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border hover:bg-light-bg dark:hover:bg-dark-bg'
                    : 'bg-primary-500 text-white shadow-lg shadow-primary-500/20 hover:bg-primary-600'
                }`}
        >
            {isVisible ? (
                <>
                    <EyeOff className="w-4 h-4" />
                    <span className="text-sm">Hide {category} Posts</span>
                </>
            ) : (
                <>
                    <Eye className="w-4 h-4" />
                    <span className="text-sm">Show {category} Posts</span>
                </>
            )}
        </motion.button>
    );
}
