import React, { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Avatar } from '@/components/ui/Avatar';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/Card';
import { formatDistanceToNow } from 'date-fns';
import { motion } from 'framer-motion';
import { BackButton } from '@/components/ui/BackButton';
import { useNavigate } from 'react-router-dom';
import { Bell, Heart, MessageCircle, FileText, Check, Trash2, Award, ClipboardList, User, AlertCircle, Calendar } from 'lucide-react';
import { ListSkeleton } from '@/components/ui/Skeletons';
import { ConfirmationModal } from '@/components/ui/ConfirmationModal';

export function Notifications() {
    const { user } = useAuth();
    const navigate = useNavigate();
    const notifications = useQuery(api.notifications.getNotifications, user ? { userId: user._id } : 'skip');
    const markAllAsRead = useMutation(api.notifications.markAllAsRead);
    const markAsRead = useMutation(api.notifications.markAsRead);
    const deleteNotification = useMutation(api.notifications.deleteNotification);
    const [deleteId, setDeleteId] = useState<string | null>(null);

    const handleMarkAllRead = async () => {
        if (!user) return;
        try {
            await markAllAsRead({ userId: user._id });
        } catch (error) {
            console.error('Failed to mark all as read:', error);
        }
    };

    const handleNotificationClick = async (notification: any) => {
        if (!user) return;
        if (!notification.read) {
            await markAsRead({ notificationId: notification._id, userId: user._id });
        }

        // Navigation logic based on notification type
        if (notification.postId) {
            if (notification.type === 'reward') {
                navigate(`/post/${notification.postId}/responses`);
            } else {
                navigate(`/post/${notification.postId}`);
            }
        } else if (notification.type === 'assignment') {
            navigate('/tasks');
        } else if (notification.type === 'follower') {
            navigate(`/profile/${notification.senderId}`);
        } else if (notification.type === 'chat') {
            navigate('/chat');
        }
    };

    const handleDelete = (e: React.MouseEvent, notificationId: any) => {
        e.stopPropagation();
        setDeleteId(notificationId);
    };

    const confirmDelete = async () => {
        if (!deleteId || !user) return;
        try {
            await deleteNotification({ notificationId: deleteId as any, userId: user._id });
        } catch (error) {
            console.error('Failed to delete notification:', error);
        } finally {
            setDeleteId(null);
        }
    };

    const getIcon = (type: string) => {
        switch (type) {
            case 'like': return <Heart className="w-5 h-5 text-red-500 fill-red-500" />;
            case 'comment': return <MessageCircle className="w-5 h-5 text-blue-500 fill-blue-500" />;
            case 'reply': return <MessageCircle className="w-5 h-5 text-indigo-500 fill-indigo-500" />;
            case 'new_post': return <FileText className="w-5 h-5 text-green-500 fill-green-500" />;
            case 'reward': return <Award className="w-5 h-5 text-yellow-500 fill-yellow-500" />;
            case 'assignment': return <ClipboardList className="w-5 h-5 text-purple-500 fill-purple-500" />;
            case 'follower': return <User className="w-5 h-5 text-pink-500 fill-pink-500" />;
            case 'system_alert': return <AlertCircle className="w-5 h-5 text-amber-500" />;
            case 'meeting': return <Calendar className="w-5 h-5 text-indigo-500" />;
            case 'question': return <MessageCircle className="w-5 h-5 text-cyan-500" />;
            case 'chat': return <MessageCircle className="w-5 h-5 text-blue-500" />;
            default: return <Bell className="w-5 h-5 text-primary-500" />;
        }
    };

    const getMessage = (notification: any) => {
        if (notification.message) {
            return <span>{notification.message}</span>;
        }

        const senderName = notification.sender?.name || 'Someone';
        switch (notification.type) {
            case 'like': return <span><span className="font-semibold text-primary-500">{senderName}</span> liked your post</span>;
            case 'comment': return <span><span className="font-semibold text-primary-500">{senderName}</span> commented on your post</span>;
            case 'new_post': return <span><span className="font-semibold text-primary-500">{senderName}</span> shared a new post</span>;
            case 'reply': return <span><span className="font-semibold text-primary-500">{senderName}</span> replied to your comment</span>;
            case 'reward': return <span>Congratulations! You were <span className="font-semibold text-yellow-500">awarded</span> for your response</span>;
            case 'assignment': return <span>You have a new <span className="font-semibold text-purple-500">assignment</span> from an admin</span>;
            case 'follower': return <span><span className="font-semibold text-primary-500">{senderName}</span> started following you</span>;
            case 'meeting': return <span>New update regarding a <span className="font-semibold text-indigo-500">meeting</span></span>;
            case 'question': return <span><span className="font-semibold text-primary-500">{senderName}</span> asked a question on your post</span>;
            default: return <span>New notification from <span className="font-semibold text-primary-500">{senderName}</span></span>;
        }
    };

    if (!user) return null;

    return (
        <div className="min-h-screen py-4 md:py-8 pb-20 md:pb-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-2xl">
                <BackButton />
                <Card glass className="mt-4">
                    <CardHeader className="flex flex-col sm:flex-row sm:items-center justify-between gap-4 border-b border-light-border dark:border-dark-border py-4 px-4 sm:px-6">
                        <CardTitle className="flex items-center gap-2 text-lg sm:text-xl">
                            <Bell className="w-5 h-5 sm:w-6 sm:h-6 text-primary-500" />
                            Center of Alerts
                        </CardTitle>
                        {notifications && notifications.some((n: any) => !n.read) && (
                            <button
                                onClick={handleMarkAllRead}
                                className="text-xs sm:text-sm text-primary-500 hover:text-primary-600 font-bold flex items-center justify-center gap-1.5 px-4 py-2 sm:px-3 sm:py-1 rounded-full bg-primary-500/10 dark:hover:bg-primary-950/20 transition-all w-full sm:w-auto border border-primary-500/20"
                            >
                                <Check className="w-4 h-4" />
                                Mark all as read
                            </button>
                        )}
                    </CardHeader>
                    <CardContent className="p-0">
                        {!notifications ? (
                            <div className="p-6">
                                <ListSkeleton count={4} />
                            </div>
                        ) : notifications.length === 0 ? (
                            <div className="text-center py-20 text-light-muted dark:text-dark-muted">
                                <div className="w-20 h-20 bg-light-card dark:bg-dark-card rounded-full flex items-center justify-center mx-auto mb-4 border border-light-border dark:border-dark-border">
                                    <Bell className="w-10 h-10 opacity-20" />
                                </div>
                                <h3 className="text-lg font-medium text-light-text dark:text-dark-text">All Caught Up!</h3>
                                <p className="text-sm">No new notifications at the moment.</p>
                            </div>
                        ) : (
                            <div className="divide-y divide-light-border dark:divide-dark-border">
                                {notifications.map((notification: any) => (
                                    <motion.div
                                        key={notification._id}
                                        initial={{ opacity: 0 }}
                                        animate={{ opacity: 1 }}
                                        onClick={() => handleNotificationClick(notification)}
                                        className={`
                                            flex items-start gap-4 p-4 cursor-pointer transition-all group relative
                                            ${notification.read
                                                ? 'bg-transparent'
                                                : 'bg-primary-50/30 dark:bg-primary-900/5'
                                            }
                                            hover:bg-light-card dark:hover:bg-dark-card
                                        `}
                                    >
                                        <div className="relative flex-shrink-0">
                                            <button
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    navigate(`/profile/${notification.senderId}`);
                                                }}
                                            >
                                                <Avatar
                                                    src={notification.sender?.avatarUrl}
                                                    alt={notification.sender?.name || 'User'}
                                                    size="md"
                                                    className="ring-2 ring-transparent group-hover:ring-primary-500/20"
                                                />
                                            </button>
                                            <div className="absolute -bottom-1 -right-1 bg-white dark:bg-dark-bg rounded-full p-1 shadow-md border border-light-border dark:border-dark-border">
                                                {getIcon(notification.type)}
                                            </div>
                                        </div>
                                        <div className="flex-1 min-w-0">
                                            <p className="text-light-text dark:text-dark-text text-sm leading-relaxed">
                                                {getMessage(notification)}
                                            </p>
                                            <div className="flex items-center gap-2 mt-1">
                                                <span className="text-[10px] text-light-muted dark:text-dark-muted uppercase font-bold tracking-wider">
                                                    {formatDistanceToNow(notification.createdAt, { addSuffix: true })}
                                                </span>
                                                {!notification.read && (
                                                    <span className="w-1.5 h-1.5 rounded-full bg-primary-500 shadow-[0_0_8px_rgba(59,130,246,0.5)]" />
                                                )}
                                            </div>
                                        </div>
                                        <div className="flex flex-col gap-2">
                                            <button
                                                onClick={(e) => handleDelete(e, notification._id)}
                                                className="p-2 text-light-muted hover:text-red-500 hover:bg-red-50 dark:hover:bg-red-950/20 rounded-lg transition-all opacity-0 group-hover:opacity-100"
                                                title="Delete notification"
                                            >
                                                <Trash2 className="w-4 h-4" />
                                            </button>
                                        </div>
                                    </motion.div>
                                ))}
                            </div>
                        )}
                    </CardContent>
                </Card>
            </div>

            <ConfirmationModal
                isOpen={!!deleteId}
                onClose={() => setDeleteId(null)}
                onConfirm={confirmDelete}
                title="Delete Notification"
                description="Are you sure you want to delete this notification? This action cannot be undone."
                confirmText="Delete"
                variant="danger"
            />
        </div>
    );
}
