import { useState } from 'react';
import { Modal } from '../ui/Modal';
import { Button } from '../ui/Button';
import { Input } from '../ui/Input';
import { useMutation, useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { PremiumSelect } from '../ui/PremiumSelect';
import { ArrowRight, ArrowLeft, TrendingUp, TrendingDown, FileText, User, Image, X } from 'lucide-react';
import { useToast } from '@/context/ToastContext';

interface AddTransactionModalProps {
    isOpen: boolean;
    onClose: () => void;
    userId: string;
}

export function AddTransactionModal({ isOpen, onClose, userId }: AddTransactionModalProps) {
    const [step, setStep] = useState(1);
    const [type, setType] = useState<'deposit' | 'withdrawal'>('deposit');
    const [amount, setAmount] = useState('');
    const [description, setDescription] = useState('');
    const [memberId, setMemberId] = useState('');
    const [memberName, setMemberName] = useState(''); // For "Other" option
    const [proofFile, setProofFile] = useState<File | null>(null);
    const [proofUrl, setProofUrl] = useState('');
    const [isUploading, setIsUploading] = useState(false);
    const [isSubmitting, setIsSubmitting] = useState(false);

    const generateUploadUrl = useMutation(api.files.generateUploadUrl);

    const allUsers = useQuery(api.users.getAllUsers, { adminId: userId as any });
    const addTransaction = useMutation(api.finance.addTransaction);
    const { showToast } = useToast();

    // Separate board members from others
    const boardMembers = allUsers?.filter((user: any) => user.role === 'board_member') || [];

    const memberOptions = [
        { value: '', label: 'Not linked to a member' },
        // Board Members Section
        ...(boardMembers.length > 0 ? [
            { value: '__board_header__', label: '--- Board Members (Monthly Dues) ---', disabled: true },
            ...boardMembers.map((user: any) => ({
                value: user._id,
                label: user.name
            }))
        ] : []),
        // Other Members Section
        { value: '__other_header__', label: '--- Other Members ---', disabled: true },
        { value: '__other__', label: '+ Add Other Person (Non-Board)' },
    ];

    const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file) return;

        // Validate file type
        if (!file.type.startsWith('image/')) {
            showToast('error', 'Please upload an image file');
            return;
        }

        // Validate file size (max 5MB)
        if (file.size > 5 * 1024 * 1024) {
            showToast('error', 'File size must be less than 5MB');
            return;
        }

        setProofFile(file);
    };

    const handleSubmit = async () => {
        setIsSubmitting(true);

        try {
            let uploadedUrl = proofUrl;

            // Upload file if selected
            if (proofFile) {
                setIsUploading(true);
                try {
                    const uploadUrl = await generateUploadUrl();
                    const result = await fetch(uploadUrl, {
                        method: 'POST',
                        headers: { 'Content-Type': proofFile.type },
                        body: proofFile,
                    });
                    const { storageId } = await result.json();

                    // Get the URL from storage
                    const response = await fetch(`${window.location.origin}/api/storage/${storageId}`);
                    uploadedUrl = response.url;
                } catch (uploadError) {
                    console.error('Upload failed:', uploadError);
                    showToast('error', 'Failed to upload proof image');
                    setIsUploading(false);
                    setIsSubmitting(false);
                    return;
                } finally {
                    setIsUploading(false);
                }
            }

            await addTransaction({
                userId: userId as any,
                type,
                amount: parseFloat(amount),
                description,
                memberId: memberId && memberId !== '__other__' ? (memberId as any) : undefined,
                memberName: memberId === '__other__' ? memberName : undefined,
                proofUrl: uploadedUrl || undefined,
            });

            // Reset form
            setStep(1);
            setType('deposit');
            setAmount('');
            setDescription('');
            setMemberId('');
            setMemberName('');
            setProofFile(null);
            setProofUrl('');
            onClose();
            showToast('success', `${type === 'deposit' ? 'Deposit' : 'Withdrawal'} of ${parseFloat(amount).toLocaleString()} added successfully!`);
        } catch (error) {
            console.error('Failed to add transaction:', error);
            showToast('error', 'Failed to add transaction. Please try again.');
        } finally {
            setIsSubmitting(false);
        }
    };

    const handleClose = () => {
        setStep(1);
        setType('deposit');
        setAmount('');
        setDescription('');
        setMemberId('');
        setProofUrl('');
        onClose();
    };

    const canProceedStep1 = type !== null;
    const canProceedStep2 = amount && parseFloat(amount) > 0;
    const canProceedStep3 = description.trim().length > 0;

    return (
        <Modal isOpen={isOpen} onClose={handleClose} title="Add Financial Transaction">
            <div className="space-y-6">
                {/* Progress Indicator */}
                <div className="flex items-center justify-between mb-6">
                    {[1, 2, 3, 4].map((s) => (
                        <div key={s} className="flex items-center flex-1">
                            <div className={`w-8 h-8 rounded-full flex items-center justify-center font-bold text-sm transition-all ${s === step ? 'bg-primary-500 text-white scale-110' :
                                s < step ? 'bg-emerald-500 text-white' :
                                    'bg-light-muted/20 text-light-muted'
                                }`}>
                                {s < step ? '' : s}
                            </div>
                            {s < 4 && (
                                <div className={`flex-1 h-1 mx-2 rounded-full transition-all ${s < step ? 'bg-emerald-500' : 'bg-light-muted/20'
                                    }`} />
                            )}
                        </div>
                    ))}
                </div>

                {/* Step 1: Transaction Type */}
                {step === 1 && (
                    <div className="space-y-4">
                        <h3 className="text-lg font-bold mb-4">Select Transaction Type</h3>
                        <div className="grid grid-cols-2 gap-4">
                            <button
                                type="button"
                                onClick={() => setType('deposit')}
                                className={`p-6 rounded-2xl border-2 transition-all ${type === 'deposit'
                                    ? 'border-emerald-500 bg-emerald-500/10 scale-105'
                                    : 'border-light-border dark:border-dark-border hover:border-emerald-500/50'
                                    }`}
                            >
                                <TrendingUp className="w-12 h-12 mx-auto mb-3 text-emerald-500" />
                                <div className="font-bold text-lg">Deposit</div>
                                <div className="text-xs text-light-muted mt-1">Money In</div>
                            </button>
                            <button
                                type="button"
                                onClick={() => setType('withdrawal')}
                                className={`p-6 rounded-2xl border-2 transition-all ${type === 'withdrawal'
                                    ? 'border-rose-500 bg-rose-500/10 scale-105'
                                    : 'border-light-border dark:border-dark-border hover:border-rose-500/50'
                                    }`}
                            >
                                <TrendingDown className="w-12 h-12 mx-auto mb-3 text-rose-500" />
                                <div className="font-bold text-lg">Withdrawal</div>
                                <div className="text-xs text-light-muted mt-1">Money Out</div>
                            </button>
                        </div>
                    </div>
                )}

                {/* Step 2: Amount */}
                {step === 2 && (
                    <div className="space-y-4">
                        <h3 className="text-lg font-bold mb-4">Enter Amount</h3>
                        <div className="relative">
                            <div className="absolute left-4 top-1/2 -translate-y-1/2 text-2xl font-bold text-light-muted"></div>
                            <Input
                                type="number"
                                value={amount}
                                onChange={(e) => setAmount(e.target.value)}
                                placeholder="0.00"
                                step="0.01"
                                min="0"
                                className="text-3xl font-bold pl-12 py-6 text-center"
                                autoFocus
                            />
                        </div>
                        <p className="text-sm text-light-muted text-center">
                            {amount && parseFloat(amount) > 0 ? (
                                <span className="font-bold text-primary-500">
                                    {new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN' }).format(parseFloat(amount))}
                                </span>
                            ) : (
                                'Enter the transaction amount'
                            )}
                        </p>
                    </div>
                )}

                {/* Step 3: Description */}
                {step === 3 && (
                    <div className="space-y-4">
                        <h3 className="text-lg font-bold mb-4 flex items-center gap-2">
                            <FileText className="w-5 h-5" />
                            Transaction Details
                        </h3>
                        <div>
                            <label className="block text-sm font-bold mb-2">Description</label>
                            <textarea
                                value={description}
                                onChange={(e) => setDescription(e.target.value)}
                                placeholder="e.g., Monthly dues collection, Office supplies purchase..."
                                className="w-full px-4 py-3 rounded-xl border border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg min-h-[120px]"
                                autoFocus
                            />
                        </div>
                    </div>
                )}

                {/* Step 4: Optional Details */}
                {step === 4 && (
                    <div className="space-y-4">
                        <h3 className="text-lg font-bold mb-4">Additional Information (Optional)</h3>

                        <div>
                            <label className="block text-sm font-bold mb-2 flex items-center gap-2">
                                <User className="w-4 h-4" />
                                Link to Member
                            </label>
                            <PremiumSelect
                                options={memberOptions.filter(opt => !(opt as any).disabled)}
                                value={memberId}
                                onChange={setMemberId}
                                placeholder="Select a member..."
                            />
                            <p className="text-xs text-light-muted mt-2">
                                Board members pay monthly dues. Select "Other" for non-board members.
                            </p>
                        </div>

                        {/* Show custom name input when "Other" is selected */}
                        {memberId === '__other__' && (
                            <div>
                                <label className="block text-sm font-bold mb-2">Person's Name</label>
                                <Input
                                    value={memberName}
                                    onChange={(e) => setMemberName(e.target.value)}
                                    placeholder="Enter name of person..."
                                    required
                                />
                            </div>
                        )}

                        <div>
                            <label className="block text-sm font-bold mb-2 flex items-center gap-2">
                                <Image className="w-4 h-4" />
                                Proof/Receipt Image
                            </label>
                            <div className="space-y-3">
                                <input
                                    type="file"
                                    accept="image/*"
                                    onChange={handleFileChange}
                                    className="hidden"
                                    id="proof-upload"
                                />
                                <label
                                    htmlFor="proof-upload"
                                    className="flex items-center justify-center gap-2 px-4 py-3 rounded-xl border-2 border-dashed border-light-border dark:border-dark-border hover:border-primary-500 cursor-pointer transition-colors bg-light-bg dark:bg-dark-bg"
                                >
                                    <Image className="w-5 h-5 text-light-muted" />
                                    <span className="text-sm font-medium">
                                        {proofFile ? proofFile.name : 'Choose Image'}
                                    </span>
                                </label>
                                {proofFile && (
                                    <div className="flex items-center justify-between p-3 rounded-xl bg-emerald-500/10 border border-emerald-500/30">
                                        <div className="flex items-center gap-2">
                                            <div className="w-8 h-8 rounded bg-emerald-500/20 flex items-center justify-center">
                                                <Image className="w-4 h-4 text-emerald-500" />
                                            </div>
                                            <div>
                                                <p className="text-sm font-bold text-emerald-600 dark:text-emerald-400">{proofFile.name}</p>
                                                <p className="text-xs text-light-muted">{(proofFile.size / 1024).toFixed(1)} KB</p>
                                            </div>
                                        </div>
                                        <button
                                            type="button"
                                            onClick={() => setProofFile(null)}
                                            className="text-rose-500 hover:text-rose-600"
                                        >
                                            <X className="w-5 h-5" />
                                        </button>
                                    </div>
                                )}
                                <p className="text-xs text-light-muted">
                                    Upload a screenshot or photo of the receipt/proof (Max 5MB)
                                </p>
                            </div>
                        </div>
                    </div>
                )}

                {/* Navigation Buttons */}
                <div className="flex gap-4 pt-4">
                    {step > 1 && (
                        <Button
                            type="button"
                            variant="ghost"
                            onClick={() => setStep(step - 1)}
                            className="flex-1"
                            disabled={isSubmitting}
                        >
                            <ArrowLeft className="w-4 h-4 mr-2" />
                            Back
                        </Button>
                    )}
                    {step < 4 ? (
                        <Button
                            type="button"
                            variant="primary"
                            onClick={() => setStep(step + 1)}
                            className="flex-1"
                            disabled={
                                (step === 1 && !canProceedStep1) ||
                                (step === 2 && !canProceedStep2) ||
                                (step === 3 && !canProceedStep3)
                            }
                        >
                            Next
                            <ArrowRight className="w-4 h-4 ml-2" />
                        </Button>
                    ) : (
                        <Button
                            type="button"
                            variant="primary"
                            onClick={handleSubmit}
                            className="flex-1"
                            disabled={isSubmitting || isUploading}
                        >
                            {isUploading ? 'Uploading...' : isSubmitting ? 'Adding...' : 'Complete Transaction'}
                        </Button>
                    )}
                </div>
            </div>
        </Modal>
    );
}
