import { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useNavigate } from 'react-router-dom';
import { Avatar } from '@/components/ui/Avatar';
import { Badge } from '@/components/ui/Badge';
import { Search, User } from 'lucide-react';
import { motion } from 'framer-motion';
import { PageHeaderSkeleton, ListSkeleton } from '@/components/ui/Skeletons';

export function SearchPage() {
    const { user, isLoading: authLoading } = useAuth();
    const navigate = useNavigate();
    const [searchQuery, setSearchQuery] = useState('');
    const toggleFollow = useMutation(api.follows.toggleFollow);
    const [togglingId, setTogglingId] = useState<string | null>(null);

    const searchResults = useQuery(
        api.users.searchMembers,
        searchQuery.trim().length > 0 ? { query: searchQuery } : 'skip'
    );

    if (authLoading) {
        return (
            <div className="min-h-screen py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                    <PageHeaderSkeleton />
                    <ListSkeleton count={3} />
                </div>
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mb-6 md:mb-8"
                >
                    <h1 className="text-2xl md:text-4xl font-bold text-primary-600 dark:text-primary-400 mb-2">
                        Search Members
                    </h1>
                    <p className="text-light-text dark:text-dark-text text-sm md:text-base">
                        Find and connect with community members
                    </p>
                </motion.div>

                {/* Search Input */}
                <div className="mb-6 md:mb-8">
                    <div className="relative flex items-center">

                        {/* Back button (mobile only, inside flow) */}
                        <button
                            onClick={() => navigate(-1)}
                            className="mr-2 p-2 text-primary-500 md:hidden"
                            aria-label="Go back"
                        >
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="22"
                                height="22"
                                viewBox="0 0 24 24"
                                fill="none"
                                stroke="currentColor"
                                strokeWidth="2"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                            >
                                <path d="m12 19-7-7 7-7" />
                                <path d="M19 12H5" />
                            </svg>
                        </button>

                        {/* Search wrapper */}
                        <div className="relative flex-1">
                            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-light-muted dark:text-dark-muted" />

                            <input
                                type="text"
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                                placeholder="Search by name..."
                                className="
                                w-full
                                pl-10 pr-10 py-2.5
                                rounded-lg
                                border border-light-border dark:border-dark-border
                                bg-light-card dark:bg-dark-card
                                text-sm text-light-text dark:text-dark-text
                                placeholder-light-muted dark:placeholder-dark-muted
                                focus:ring-2 focus:ring-primary-500 focus:border-transparent
                                outline-none transition-all
                                "
                            />

                            {searchQuery && (
                                <button
                                    onClick={() => setSearchQuery('')}
                                    className="absolute right-3 top-1/2 -translate-y-1/2 p-1 text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text"
                                    aria-label="Clear search"
                                >
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="14"
                                        height="14"
                                        viewBox="0 0 24 24"
                                        fill="none"
                                        stroke="currentColor"
                                        strokeWidth="2"
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                    >
                                        <line x1="18" y1="6" x2="6" y2="18" />
                                        <line x1="6" y1="6" x2="18" y2="18" />
                                    </svg>
                                </button>
                            )}
                        </div>
                    </div>
                </div>

                {/* Search Results */}
                {searchQuery.trim().length === 0 ? (
                    <div className="text-center py-12 glass rounded-xl">
                        <User className="w-12 h-12 mx-auto mb-4 text-light-muted dark:text-dark-muted" />
                        <p className="text-light-muted dark:text-dark-muted">
                            Start typing to search for members
                        </p>
                    </div>
                ) : searchResults === undefined ? (
                    <ListSkeleton count={3} />
                ) : searchResults.length === 0 ? (
                    <div className="text-center py-12 glass rounded-xl">
                        <User className="w-12 h-12 mx-auto mb-4 text-light-muted dark:text-dark-muted" />
                        <p className="text-light-muted dark:text-dark-muted">
                            No members found matching "{searchQuery}"
                        </p>
                    </div>
                ) : (
                    <div className="space-y-3">
                        {searchResults.map((member: any) => (
                            <motion.div
                                key={member._id}
                                initial={{ opacity: 0, y: 10 }}
                                animate={{ opacity: 1, y: 0 }}
                                onClick={() => navigate(`/profile/${member._id}`)}
                                className="glass rounded-lg p-4 hover:shadow-lg transition-all cursor-pointer group"
                            >
                                <div className="flex items-center gap-3">
                                    <Avatar
                                        src={member.avatarUrl}
                                        alt={member.name}
                                        size="md"
                                    />
                                    <div className="flex-1">
                                        <div className="flex items-center justify-between">
                                            <div>
                                                <h3 className="font-semibold text-light-text dark:text-dark-text group-hover:text-primary-500 transition-colors">
                                                    {member.name}
                                                </h3>
                                                <div className="flex items-center gap-2 mt-1">
                                                    <Badge
                                                        variant={member.role === 'admin' || member.role === 'super_admin' ? 'primary' : 'default'}
                                                        className="text-[10px]"
                                                    >
                                                        {member.role === 'super_admin' ? 'Super Admin' : member.role === 'admin' ? 'Admin' : 'Member'}
                                                    </Badge>
                                                    <span className="text-xs text-light-muted dark:text-dark-muted">
                                                        {member.followerCount || 0} followers
                                                    </span>
                                                </div>
                                            </div>

                                            {user._id !== member._id && (
                                                <button
                                                    onClick={(e) => {
                                                        e.stopPropagation();
                                                        if (togglingId) return;
                                                        setTogglingId(member._id);
                                                        toggleFollow({ followerId: user._id, followingId: member._id })
                                                            .finally(() => setTogglingId(null));
                                                    }}
                                                    className="p-2 rounded-full hover:bg-primary-50 dark:hover:bg-primary-900/20 text-primary-500 transition-colors"
                                                >
                                                    <User className="w-5 h-5" />
                                                </button>
                                            )}
                                            {member.latestPost && (
                                                <div className="w-full mt-2 p-2 bg-light-card/50 dark:bg-dark-card/50 rounded-lg border border-light-border/50 dark:border-dark-border/50 text-xs text-light-muted dark:text-dark-muted">
                                                    <span className="font-semibold text-primary-500">Latest: </span>
                                                    <span className="line-clamp-1 break-all">{member.latestPost.content}</span>
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </motion.div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
}
