import { useState } from 'react';
import { Modal } from '../ui/Modal';
import { Button } from '../ui/Button';
import { Input } from '../ui/Input';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useToast } from '@/context/ToastContext';
import { Calendar, Clock, FileText } from 'lucide-react';

interface CreateMeetingModalProps {
    isOpen: boolean;
    onClose: () => void;
    userId: string;
}

export function CreateMeetingModal({ isOpen, onClose, userId }: CreateMeetingModalProps) {
    const [title, setTitle] = useState('');
    const [date, setDate] = useState('');
    const [startTime, setStartTime] = useState('');
    const [durationMinutes, setDurationMinutes] = useState('60');
    const [isSubmitting, setIsSubmitting] = useState(false);

    const createMeeting = useMutation(api.governance.createMeeting);
    const { showToast } = useToast();

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsSubmitting(true);

        try {
            await createMeeting({
                adminId: userId as any,
                title,
                date: new Date(date).getTime(),
                startTime,
                durationMinutes: parseInt(durationMinutes),
            });

            // Reset form
            setTitle('');
            setDate('');
            setStartTime('');
            setDurationMinutes('60');
            onClose();
            showToast('success', `Meeting "${title}" created successfully!`);
        } catch (error) {
            console.error('Failed to create meeting:', error);
            showToast('error', 'Failed to create meeting. Please try again.');
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <Modal isOpen={isOpen} onClose={onClose} title="Create Board Meeting">
            <form onSubmit={handleSubmit} className="space-y-6">
                <div>
                    <label className="block text-sm font-bold mb-2 flex items-center gap-2">
                        <FileText className="w-4 h-4" />
                        Meeting Title
                    </label>
                    <Input
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                        placeholder="e.g., Monthly Board Meeting"
                        required
                    />
                </div>

                <div className="grid grid-cols-2 gap-4">
                    <div>
                        <label className="block text-sm font-bold mb-2 flex items-center gap-2">
                            <Calendar className="w-4 h-4" />
                            Date
                        </label>
                        <Input
                            type="date"
                            value={date}
                            onChange={(e) => setDate(e.target.value)}
                            required
                            className="w-full"
                        />
                    </div>

                    <div>
                        <label className="block text-sm font-bold mb-2 flex items-center gap-2">
                            <Clock className="w-4 h-4" />
                            Start Time
                        </label>
                        <Input
                            type="time"
                            value={startTime}
                            onChange={(e) => setStartTime(e.target.value)}
                            required
                            className="w-full"
                        />
                    </div>
                </div>

                <div>
                    <label className="block text-sm font-bold mb-2">Duration (minutes)</label>
                    <div className="grid grid-cols-4 gap-2">
                        {[30, 60, 90, 120].map((mins) => (
                            <button
                                key={mins}
                                type="button"
                                onClick={() => setDurationMinutes(mins.toString())}
                                className={`px-4 py-2 rounded-lg border-2 transition-all ${durationMinutes === mins.toString()
                                        ? 'border-primary-500 bg-primary-500/10 text-primary-500'
                                        : 'border-light-border dark:border-dark-border hover:border-primary-500/50'
                                    }`}
                            >
                                {mins}m
                            </button>
                        ))}
                    </div>
                    <Input
                        type="number"
                        value={durationMinutes}
                        onChange={(e) => setDurationMinutes(e.target.value)}
                        placeholder="Custom duration"
                        min="15"
                        max="480"
                        className="mt-2"
                    />
                </div>

                <div className="flex gap-4 pt-4">
                    <Button
                        type="button"
                        variant="ghost"
                        onClick={onClose}
                        className="flex-1"
                        disabled={isSubmitting}
                    >
                        Cancel
                    </Button>
                    <Button
                        type="submit"
                        variant="primary"
                        className="flex-1"
                        disabled={isSubmitting}
                    >
                        {isSubmitting ? 'Creating...' : 'Create Meeting'}
                    </Button>
                </div>
            </form>
        </Modal>
    );
}
