import { useState } from 'react';
import { useQuery, useMutation, useAction } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import {
    DollarSign,
    TrendingUp,
    TrendingDown,
    FileText,
    Plus,
    Eye,
    RotateCcw,
    User,
    Users,
    Clock,
    Sparkles
} from 'lucide-react';
import { Badge } from '../ui/Badge';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/Card';
import { Button } from '../ui/Button';
import { formatDistanceToNow } from 'date-fns';
import { AddTransactionModal } from './AddTransactionModal';
import { PendingSubmissions } from './PendingSubmissionsTab';
import { StatsCardSkeleton, PostCardSkeleton } from '../ui/Skeletons';
import { AIReportModal } from './AIReportModal';
import { useToast } from '@/context/ToastContext';

interface FinanceTabProps {
    user: any;
}

export function FinanceTab({ user }: FinanceTabProps) {
    const [activeTab, setActiveTab] = useState<'overview' | 'pending'>('overview');
    const [showAddTransactionModal, setShowAddTransactionModal] = useState(false);
    const [selectedMonth, setSelectedMonth] = useState(new Date().getMonth() + 1);
    const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());

    // Queries
    const currentBalance = useQuery(api.finance.getCurrentBalance);
    const transactions = useQuery(api.finance.getTransactions, { limit: 50 });
    const monthlySummary = useQuery(api.finance.getMonthlySummary, {
        month: selectedMonth,
        year: selectedYear,
    });

    // Mutations & Actions
    const reverseTransaction = useMutation(api.finance.reverseTransaction);
    const generateAIReport = useAction(api.finance.generateAIReport);

    // UI State
    const [reportModalOpen, setReportModalOpen] = useState(false);
    const [aiReportText, setAiReportText] = useState<string | null>(null);
    const [isGenerating, setIsGenerating] = useState(false);
    const { showToast } = useToast();

    const isSuperAdmin = user?.role === 'super_admin';
    const isFinancialManager = (user as any)?.isFinancialManager === true;
    const canManageFinance = isSuperAdmin || isFinancialManager;

    const formatCurrency = (amount: number) => {
        return new Intl.NumberFormat('en-NG', {
            style: 'currency',
            currency: 'NGN',
        }).format(amount);
    };

    const isLoading = currentBalance === undefined || transactions === undefined;

    if (isLoading) {
        return (
            <div className="space-y-8">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    {[1, 2, 3].map(i => <StatsCardSkeleton key={i} />)}
                </div>
                <div className="space-y-4">
                    {[1, 2, 3].map(i => <PostCardSkeleton key={i} />)}
                </div>
            </div>
        );
    }

    return (
        <div className="space-y-8">
            {/* Balance Overview */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 md:gap-6">
                <Card className="md:col-span-1 border-primary-500/20 bg-gradient-to-br from-primary-500/10 to-transparent">
                    <CardContent className="p-4 md:p-6">
                        <div className="flex items-center justify-between mb-3 md:mb-4">
                            <div className="p-2 md:p-3 rounded-2xl bg-primary-500/20 text-primary-500">
                                <DollarSign className="w-6 h-6 md:w-8 md:h-8" />
                            </div>
                        </div>
                        <h3 className="text-[10px] md:text-xs font-black uppercase tracking-widest text-light-muted mb-1 md:mb-2 text-balance">
                            Current Balance
                        </h3>
                        <p className="text-2xl md:text-4xl font-black text-primary-500 truncate">
                            {formatCurrency(currentBalance || 0)}
                        </p>
                    </CardContent>
                </Card>

                <Card className="border-emerald-500/20">
                    <CardContent className="p-4 md:p-6">
                        <div className="flex items-center justify-between mb-3 md:mb-4">
                            <div className="p-2 md:p-3 rounded-2xl bg-emerald-500/10 text-emerald-500">
                                <TrendingUp className="w-5 h-5 md:w-6 md:h-6" />
                            </div>
                            <div className="text-right text-xl md:text-2xl font-black text-emerald-500 truncate ml-2">
                                {formatCurrency(monthlySummary?.totalDeposits || 0)}
                            </div>
                        </div>
                        <h3 className="text-[10px] md:text-xs font-black uppercase tracking-widest text-light-muted mb-1">
                            Monthly Deposits
                        </h3>
                        <p className="text-[10px] text-light-muted">
                            {selectedMonth}/{selectedYear}
                        </p>
                    </CardContent>
                </Card>

                <Card className="border-rose-500/20">
                    <CardContent className="p-4 md:p-6">
                        <div className="flex items-center justify-between mb-3 md:mb-4">
                            <div className="p-2 md:p-3 rounded-2xl bg-rose-500/10 text-rose-500">
                                <TrendingDown className="w-5 h-5 md:w-6 md:h-6" />
                            </div>
                            <div className="text-right text-xl md:text-2xl font-black text-rose-500 truncate ml-2">
                                {formatCurrency(monthlySummary?.totalWithdrawals || 0)}
                            </div>
                        </div>
                        <h3 className="text-[10px] md:text-xs font-black uppercase tracking-widest text-light-muted mb-1">
                            Monthly Withdrawals
                        </h3>
                        <p className="text-[10px] text-light-muted">
                            {selectedMonth}/{selectedYear}
                        </p>
                    </CardContent>
                </Card>
            </div>

            {/* Sub-Tabs Navigation */}
            <div className="flex items-center gap-1 p-1 rounded-xl md:rounded-2xl bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border dark:border-dark-border w-fit max-w-full overflow-x-auto hide-scrollbar">
                <button
                    onClick={() => setActiveTab('overview')}
                    className={`px-4 md:px-6 py-2 md:py-2.5 rounded-lg md:rounded-xl text-xs md:text-sm font-bold transition-all whitespace-nowrap ${activeTab === 'overview'
                        ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/20'
                        : 'text-light-muted hover:text-light-text dark:hover:text-dark-text'
                        }`}
                >
                    Overview
                </button>

                {canManageFinance && (
                    <button
                        onClick={() => setActiveTab('pending')}
                        className={`px-4 md:px-6 py-2 md:py-2.5 rounded-lg md:rounded-xl text-xs md:text-sm font-bold transition-all whitespace-nowrap ${activeTab === 'pending'
                            ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/20'
                            : 'text-light-muted hover:text-light-text dark:hover:text-dark-text'
                            }`}
                    >
                        Pending Submissions
                    </button>
                )}
            </div>

            {activeTab === 'overview' && (
                <>
                    {/* Action Buttons */}
                    {canManageFinance && (
                        <div className="flex flex-col sm:flex-row gap-3 md:gap-4">
                            <Button
                                onClick={() => setShowAddTransactionModal(true)}
                                className="bg-primary-500 hover:bg-primary-600 w-full sm:w-auto"
                            >
                                <Plus className="w-4 h-4 mr-2" />
                                Add Transaction
                            </Button>
                            <Button
                                variant="outline"
                                className="w-full sm:w-auto border-light-border dark:border-dark-border mb-4 sm:mb-0 hover:border-primary-500 hover:text-primary-500 transition-colors"
                                disabled={isGenerating}
                                onClick={async () => {
                                    const summary = monthlySummary as any;
                                    if (summary?.aiReportText) {
                                        setAiReportText(summary.aiReportText);
                                        setReportModalOpen(true);
                                        return;
                                    }

                                    setIsGenerating(true);
                                    setReportModalOpen(true);
                                    setAiReportText(null);

                                    try {
                                        const result = await generateAIReport({
                                            userId: user._id,
                                            month: selectedMonth,
                                            year: selectedYear
                                        });
                                        if (result.success) {
                                            setAiReportText(result.aiReportText);
                                            showToast('success', 'Financial analysis complete!');
                                        }
                                    } catch (error) {
                                        console.error("Report generation failed:", error);
                                        showToast('error', 'Failed to generate financial analysis');
                                        setReportModalOpen(false);
                                    } finally {
                                        setIsGenerating(false);
                                    }
                                }}
                            >
                                <Sparkles className={`w-4 h-4 mr-2 ${isGenerating ? 'animate-spin' : 'text-primary-500'}`} />
                                {isGenerating ? 'Analyzing...' : 'Generate Report'}
                            </Button>
                        </div>
                    )}

                    {/* Transaction History */}
                    <Card>
                        <CardHeader className="py-4 px-4 md:px-6">
                            <CardTitle className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
                                <div className="flex items-center gap-2">
                                    <FileText className="w-5 h-5 text-primary-500" />
                                    <span className="text-lg md:text-xl">Transaction History</span>
                                </div>
                                <div className="flex items-center gap-2 w-full sm:w-auto">
                                    <select
                                        value={selectedMonth}
                                        onChange={(e) => setSelectedMonth(Number(e.target.value))}
                                        className="flex-1 sm:flex-none px-2 py-1 rounded-lg border border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg text-xs md:text-sm outline-none focus:ring-1 focus:ring-primary-500"
                                    >
                                        {Array.from({ length: 12 }, (_, i) => (
                                            <option key={i + 1} value={i + 1}>
                                                {new Date(2024, i).toLocaleDateString('en-US', { month: 'short' })}
                                            </option>
                                        ))}
                                    </select>
                                    <select
                                        value={selectedYear}
                                        onChange={(e) => setSelectedYear(Number(e.target.value))}
                                        className="flex-1 sm:flex-none px-2 py-1 rounded-lg border border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg text-xs md:text-sm outline-none focus:ring-1 focus:ring-primary-500"
                                    >
                                        {Array.from({ length: 5 }, (_, i) => (
                                            <option key={2024 + i} value={2024 + i}>
                                                {2024 + i}
                                            </option>
                                        ))}
                                    </select>
                                </div>
                            </CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-3">
                                {transactions && transactions.length > 0 ? (
                                    transactions.map((txn: any) => {
                                        const isWithin24Hours = Date.now() - txn.createdAt < 24 * 60 * 60 * 1000;
                                        const canReverse = canManageFinance && isWithin24Hours && !txn.isReversed;

                                        return (
                                            <div
                                                key={txn._id}
                                                className={`p-4 rounded-lg border transition-colors ${txn.isReversed
                                                    ? 'border-light-border/50 dark:border-dark-border/50 opacity-50'
                                                    : 'border-light-border dark:border-dark-border hover:border-primary-500/30'
                                                    }`}
                                            >
                                                <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                                                    <div className="flex items-start md:items-center gap-4 flex-1">
                                                        <div
                                                            className={`p-3 rounded-xl shrink-0 ${txn.type === 'deposit'
                                                                ? 'bg-emerald-500/10 text-emerald-500'
                                                                : 'bg-rose-500/10 text-rose-500'
                                                                }`}
                                                        >
                                                            {txn.type === 'deposit' ? (
                                                                <TrendingUp className="w-5 h-5 md:w-6 md:h-6" />
                                                            ) : (
                                                                <TrendingDown className="w-5 h-5 md:w-6 md:h-6" />
                                                            )}
                                                        </div>
                                                        <div className="flex-1 min-w-0">
                                                            <div className="flex flex-wrap items-center gap-2 mb-1">
                                                                <h4 className="font-bold text-sm md:text-base truncate">
                                                                    {txn.description}
                                                                </h4>
                                                                {txn.isReversed && (
                                                                    <Badge variant="outline" className="text-[10px] h-4">
                                                                        Reversed
                                                                    </Badge>
                                                                )}
                                                                {txn.reversedFrom && (
                                                                    <Badge className="bg-amber-500 text-[10px] h-4">
                                                                        Reversal
                                                                    </Badge>
                                                                )}
                                                            </div>
                                                            <div className="flex flex-wrap items-center gap-x-4 gap-y-1 text-[10px] md:text-xs text-light-muted">
                                                                <span className="flex items-center gap-1">
                                                                    <User className="w-3 h-3" />
                                                                    {txn.recordedByName}
                                                                </span>
                                                                {txn.memberName && (
                                                                    <span className="flex items-center gap-1">
                                                                        <Users className="w-3 h-3" />
                                                                        {txn.memberName}
                                                                    </span>
                                                                )}
                                                                <span className="flex items-center gap-1">
                                                                    <Clock className="w-3 h-3" />
                                                                    {formatDistanceToNow(txn.createdAt, { addSuffix: true })}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div className="flex items-center justify-between md:justify-end gap-4 pl-14 md:pl-0">
                                                        <div className="text-left md:text-right">
                                                            <p
                                                                className={`text-lg md:text-xl font-black ${txn.type === 'deposit'
                                                                    ? 'text-emerald-500'
                                                                    : 'text-rose-500'
                                                                    }`}
                                                            >
                                                                {txn.type === 'deposit' ? '+' : '-'}
                                                                {formatCurrency(txn.amount)}
                                                            </p>
                                                        </div>
                                                        <div className="flex gap-2">
                                                            {txn.proofStorageId && (
                                                                <Button size="sm" variant="ghost" className="h-8 w-8 p-0">
                                                                    <Eye className="w-4 h-4" />
                                                                </Button>
                                                            )}
                                                            {canReverse && (
                                                                <Button
                                                                    size="sm"
                                                                    variant="outline"
                                                                    onClick={async () => {
                                                                        if (confirm('Are you sure you want to reverse this transaction?')) {
                                                                            await reverseTransaction({
                                                                                userId: user._id,
                                                                                transactionId: txn._id,
                                                                            });
                                                                        }
                                                                    }}
                                                                    className="h-8 px-2 text-amber-500 border-amber-500/30 hover:bg-amber-500/10 text-xs gap-1"
                                                                >
                                                                    <RotateCcw className="w-3 h-3" />
                                                                    <span className="hidden sm:inline">Reverse</span>
                                                                </Button>
                                                            )}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        );
                                    })
                                ) : (
                                    <div className="text-center py-12 text-light-muted">
                                        No transactions yet
                                    </div>
                                )}
                            </div>
                        </CardContent>
                    </Card>
                </>
            )}



            {activeTab === 'pending' && (
                <PendingSubmissions user={user} />
            )}

            {/* Modals */}
            <AddTransactionModal
                isOpen={showAddTransactionModal}
                onClose={() => setShowAddTransactionModal(false)}
                userId={user._id}
            />

            <AIReportModal
                isOpen={reportModalOpen}
                onClose={() => setReportModalOpen(false)}
                report={aiReportText}
                isLoading={isGenerating}
                month={selectedMonth}
                year={selectedYear}
            />
        </div>
    );
}
