import { motion } from 'framer-motion';
import { useAuth } from '@/hooks/useAuth';
import { FinanceTab } from '@/components/governance/FinanceTab';


export function BoardFinance() {
    const { user } = useAuth();

    return (
        <div className="space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
            >
                <div className="flex items-center gap-2 mb-2">
                    <div className="w-8 h-1 bg-emerald-500 rounded-full" />
                    <span className="text-[10px] font-black uppercase tracking-[0.3em] text-emerald-500">
                        Financial Management
                    </span>
                </div>
                <h1 className="text-3xl md:text-5xl font-black text-gradient font-display mb-2 tracking-tight">
                    Community Finance
                </h1>
                <p className="text-light-muted dark:text-dark-muted font-medium">
                    Overview of community funds and financial transactions
                </p>
            </motion.div>

            <FinanceTab user={user} />
        </div>
    );
}
