import { useState, useRef } from 'react';
import { Image as ImageIcon, Video, X, Loader2 } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { uploadToImageKit, validateImageFile, validateVideoFile } from '@/lib/imagekit';

interface MediaUploadProps {
    onUploadComplete: (url: string, type: 'image' | 'video') => void;
    allowVideo?: boolean;
    currentMedia?: { url: string; type: 'image' | 'video' };
}

export function MediaUpload({ onUploadComplete, allowVideo = false, currentMedia }: MediaUploadProps) {
    const [isUploading, setIsUploading] = useState(false);
    const [progress, setProgress] = useState(0);
    const [preview, setPreview] = useState(currentMedia);
    const imageInputRef = useRef<HTMLInputElement>(null);
    const videoInputRef = useRef<HTMLInputElement>(null);

    const handleFileSelect = async (file: File, type: 'image' | 'video') => {
        if (type === 'image' && !validateImageFile(file)) {
            alert('Please upload a valid image file (JPG, PNG, GIF, or WebP)');
            return;
        }

        if (type === 'video' && !validateVideoFile(file)) {
            alert('Please upload a valid video file (MP4, WebM, or OGG)');
            return;
        }

        // Show preview
        const reader = new FileReader();
        reader.onloadend = () => {
            setPreview({ url: reader.result as string, type });
        };
        reader.readAsDataURL(file);

        // Upload to ImageKit
        setIsUploading(true);
        setProgress(0);

        try {
            const folder = type === 'image' ? 'posts/images' : 'posts/videos';
            const url = await uploadToImageKit(file, folder, setProgress);
            onUploadComplete(url, type);
            setPreview({ url, type });
        } catch (error) {
            console.error('Upload failed:', error);
            alert(`Failed to upload ${type}. Please try again.`);
            setPreview(currentMedia);
        } finally {
            setIsUploading(false);
            setProgress(0);
        }
    };

    const handleRemove = () => {
        setPreview(undefined);
        onUploadComplete('', 'image');
        if (imageInputRef.current) imageInputRef.current.value = '';
        if (videoInputRef.current) videoInputRef.current.value = '';
    };

    return (
        <div className="space-y-3">
            {/* Preview */}
            <AnimatePresence>
                {preview && (
                    <motion.div
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: 'auto' }}
                        exit={{ opacity: 0, height: 0 }}
                        className="relative rounded-lg overflow-hidden bg-light-card dark:bg-dark-card"
                    >
                        {preview.type === 'image' ? (
                            <img
                                src={preview.url}
                                alt="Preview"
                                className="w-full max-h-96 object-contain"
                            />
                        ) : (
                            <video
                                src={preview.url}
                                controls
                                className="w-full max-h-96 object-contain"
                            />
                        )}

                        {isUploading && (
                            <div className="absolute inset-0 bg-black/50 flex items-center justify-center">
                                <div className="text-center">
                                    <Loader2 className="w-8 h-8 text-white animate-spin mx-auto mb-2" />
                                    <span className="text-sm text-white font-medium">{progress}%</span>
                                </div>
                            </div>
                        )}

                        {!isUploading && (
                            <button
                                onClick={handleRemove}
                                className="absolute top-2 right-2 w-8 h-8 rounded-full bg-red-500 hover:bg-red-600 flex items-center justify-center transition-colors"
                            >
                                <X className="w-5 h-5 text-white" />
                            </button>
                        )}
                    </motion.div>
                )}
            </AnimatePresence>

            {/* Upload Buttons */}
            {!preview && (
                <div className="flex gap-2">
                    <input
                        ref={imageInputRef}
                        type="file"
                        accept="image/*"
                        onChange={(e) => {
                            const file = e.target.files?.[0];
                            if (file) handleFileSelect(file, 'image');
                        }}
                        className="hidden"
                        disabled={isUploading}
                    />

                    <button
                        type="button"
                        onClick={() => imageInputRef.current?.click()}
                        disabled={isUploading}
                        className="flex-1 px-4 py-2 rounded-lg border-2 border-dashed border-light-border dark:border-dark-border hover:border-primary-500 dark:hover:border-primary-500 transition-colors flex items-center justify-center gap-2 text-light-muted dark:text-dark-muted hover:text-primary-500"
                    >
                        <ImageIcon className="w-5 h-5" />
                        <span className="text-sm font-medium">Add Image</span>
                    </button>

                    {allowVideo && (
                        <>
                            <input
                                ref={videoInputRef}
                                type="file"
                                accept="video/*"
                                onChange={(e) => {
                                    const file = e.target.files?.[0];
                                    if (file) handleFileSelect(file, 'video');
                                }}
                                className="hidden"
                                disabled={isUploading}
                            />

                            <button
                                type="button"
                                onClick={() => videoInputRef.current?.click()}
                                disabled={isUploading}
                                className="flex-1 px-4 py-2 rounded-lg border-2 border-dashed border-light-border dark:border-dark-border hover:border-secondary-500 dark:hover:border-secondary-500 transition-colors flex items-center justify-center gap-2 text-light-muted dark:text-dark-muted hover:text-secondary-500"
                            >
                                <Video className="w-5 h-5" />
                                <span className="text-sm font-medium">Add Video</span>
                            </button>
                        </>
                    )}
                </div>
            )}
        </div>
    );
}
