import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { BackButton } from '@/components/ui/BackButton';
import { PostCard } from '@/components/posts/PostCard';
import { PostCardSkeleton } from '@/components/ui/Skeletons';
import { motion } from 'framer-motion';
import { FeedSidebarLeft } from '@/components/layout/FeedSidebarLeft';
import { FeedSidebarRight } from '@/components/layout/FeedSidebarRight';

export function SavedPosts() {
    const { user } = useAuth();
    const posts = useQuery(api.posts.getSavedPosts, user ? { userId: user._id } : "skip");

    if (!user) return null;

    return (
        <div className="min-h-screen py-4 md:py-8 bg-light-bg/50 dark:bg-dark-bg/50">
            <div className="max-w-[1440px] mx-auto px-4 sm:px-6 lg:px-8">
                <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">

                    {/* Left Sidebar */}
                    <div className="hidden lg:block lg:col-span-4 xl:col-span-3">
                        <FeedSidebarLeft />
                    </div>

                    {/* Middle Content */}
                    <div className="lg:col-span-8 xl:col-span-6">
                        <div className="max-w-2xl mx-auto xl:max-w-none">
                            <div className="lg:hidden">
                                <BackButton />
                            </div>

                            <div className="flex items-center gap-3 mb-6 pt-16 md:pt-0">
                                <h1 className="text-2xl font-bold text-light-text dark:text-dark-text">Saved Posts</h1>
                            </div>

                            <div className="space-y-6">
                                {posts === undefined ? (
                                    [1, 2, 3].map(i => <PostCardSkeleton key={i} />)
                                ) : posts.length === 0 ? (
                                    <div className="text-center py-12 text-light-muted dark:text-dark-muted glass rounded-2xl border border-light-border dark:border-dark-border">
                                        <p className="text-lg font-semibold">No saved posts yet</p>
                                        <p className="text-sm mt-2">Posts you save will appear here for easy access.</p>
                                    </div>
                                ) : (
                                    posts.map((post: any) => (
                                        <motion.div
                                            key={post._id}
                                            initial={{ opacity: 0, y: 20 }}
                                            animate={{ opacity: 1, y: 0 }}
                                        >
                                            <PostCard
                                                post={post}
                                                hasUserLiked={post.hasLiked}
                                            />
                                        </motion.div>
                                    ))
                                )}
                            </div>
                        </div>
                    </div>

                    {/* Right Sidebar */}
                    <div className="hidden xl:block xl:col-span-3">
                        <FeedSidebarRight />
                    </div>

                </div>
            </div>
        </div>
    );
}
