import { useState, useMemo } from "react";
import { Button } from "@/components/ui/Button";
import { X, ChevronRight, ChevronLeft, CheckCircle2, Search, Building2 } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";

interface BankDetailsFormProps {
    poolType: "diamond" | "gold" | "silver" | "bronze" | "big_pull";
    amount: number; // Rubies
    talents: number;
    nairaAmount: number;
    exchangeRate: number;
    onSubmit: (bankDetails: {
        bankName: string;
        accountNumber: string;
        accountName: string;
    }) => void;
    onCancel: () => void;
}

const NIGERIAN_BANKS = [
    "Access Bank",
    "Citibank",
    "Ecobank Nigeria",
    "Fairmoney",
    "Fidelity Bank",
    "First Bank of Nigeria",
    "First City Monument Bank (FCMB)",
    "Globus Bank",
    "Guaranty Trust Bank (GTBank)",
    "Heritage Bank",
    "Keystone Bank",
    "Kuda Bank",
    "Moniepoint",
    "OPay",
    "PalmPay",
    "Polaris Bank",
    "Providus Bank",
    "Stanbic IBTC Bank",
    "Standard Chartered",
    "Sterling Bank",
    "SunTrust Bank",
    "Union Bank of Nigeria",
    "United Bank for Africa (UBA)",
    "Unity Bank",
    "Wema Bank",
    "Zenith Bank",
].sort();

export function BankDetailsForm({
    poolType,
    amount,
    talents,
    nairaAmount,
    exchangeRate,
    onSubmit,
    onCancel,
}: BankDetailsFormProps) {
    const [step, setStep] = useState(1);
    const [bankName, setBankName] = useState("");
    const [accountNumber, setAccountNumber] = useState("");
    const [accountName, setAccountName] = useState("");
    const [error, setError] = useState("");

    // Bank Selection Modal State
    const [isBankModalOpen, setIsBankModalOpen] = useState(false);
    const [bankSearchTerm, setBankSearchTerm] = useState("");

    const filteredBanks = useMemo(() => {
        return NIGERIAN_BANKS.filter(bank =>
            bank.toLowerCase().includes(bankSearchTerm.toLowerCase())
        );
    }, [bankSearchTerm]);

    const handleNext = () => {
        if (step === 2) {
            // Validate Step 2
            if (!bankName) {
                setError("Please select a bank");
                return;
            }
            if (!accountNumber || accountNumber.length !== 10) {
                setError("Account number must be exactly 10 digits");
                return;
            }
            if (!accountName || accountName.trim().length < 3) {
                setError("Please enter a valid account name");
                return;
            }
        }
        setError("");
        setStep(step + 1);
    };

    const handleBack = () => {
        setStep(step - 1);
        setError("");
    };

    const handleSubmit = () => {
        onSubmit({
            bankName,
            accountNumber,
            accountName: accountName.trim(),
        });
    };

    const poolEmoji = {
        diamond: "",
        gold: "",
        silver: "",
        bronze: "",
        big_pull: "",
    };

    const poolName = {
        diamond: "Diamond",
        gold: "Gold",
        silver: "Silver",
        bronze: "Bronze",
        big_pull: "Big Pull",
    };

    return (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center p-4 z-50 overflow-y-auto">
            <motion.div
                initial={{ opacity: 0, scale: 0.95 }}
                animate={{ opacity: 1, scale: 1 }}
                className="bg-light-bg dark:bg-dark-bg rounded-2xl shadow-2xl max-w-md w-full relative overflow-hidden flex flex-col max-h-[90vh]"
            >
                {/* Header */}
                <div className="p-4 md:p-6 pb-2 md:pb-4 border-b border-light-border dark:border-dark-border flex justify-between items-center bg-light-bg dark:bg-dark-bg sticky top-0 z-10">
                    <div className="min-w-0">
                        <h2 className="text-lg md:text-xl font-bold text-light-text dark:text-dark-text flex items-center gap-2 truncate">
                            {poolEmoji[poolType]} Withdraw {poolName[poolType]}
                        </h2>
                        <div className="flex gap-1 mt-2">
                            {[1, 2, 3].map((i) => (
                                <div
                                    key={i}
                                    className={`h-1 w-6 md:w-8 rounded-full transition-colors duration-300 ${step >= i ? "bg-primary-500" : "bg-light-border dark:bg-dark-border"
                                        }`}
                                />
                            ))}
                        </div>
                    </div>
                    <button
                        onClick={onCancel}
                        className="p-1.5 md:p-2 rounded-lg hover:bg-light-card dark:hover:bg-dark-card transition-colors flex-shrink-0"
                    >
                        <X className="w-4 h-4 md:w-5 md:h-5 text-light-muted dark:text-dark-muted" />
                    </button>
                </div>

                <div className="flex-1 overflow-y-auto p-6 relative">
                    <AnimatePresence mode="wait">
                        {step === 1 && (
                            <motion.div
                                key="step1"
                                initial={{ opacity: 0, x: 20 }}
                                animate={{ opacity: 1, x: 0 }}
                                exit={{ opacity: 0, x: -20 }}
                                transition={{ duration: 0.2 }}
                                className="space-y-6"
                            >
                                <div className="text-center space-y-2">
                                    <h3 className="text-lg font-semibold text-light-text dark:text-dark-text">Review Earnings</h3>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">
                                        You are about to withdraw your earnings from the {poolName[poolType]} pool.
                                    </p>
                                </div>

                                <div className="p-4 md:p-6 rounded-2xl bg-gradient-to-br from-primary-500/10 to-secondary-500/10 border border-primary-500/20">
                                    <div className="flex flex-col items-center gap-4 md:gap-6">
                                        <div className="text-center">
                                            <p className="text-2xl md:text-4xl font-display font-bold text-light-text dark:text-dark-text mb-1">
                                                {amount.toFixed(2)}
                                            </p>
                                            <p className="text-[10px] md:text-sm font-medium text-light-muted dark:text-dark-muted uppercase tracking-wider">Rubies</p>
                                        </div>

                                        <div className="w-full h-px bg-primary-500/20" />

                                        <div className="flex justify-between w-full gap-2 md:gap-4">
                                            <div className="text-center flex-1">
                                                <p className="text-lg md:text-xl font-bold text-amber-500 mb-0.5 md:mb-1">
                                                    {talents.toFixed(2)}
                                                </p>
                                                <p className="text-[10px] md:text-xs font-medium text-light-muted dark:text-dark-muted uppercase tracking-wider">Talents</p>
                                            </div>
                                            <div className="w-px h-8 md:h-10 bg-primary-500/20" />
                                            <div className="text-center flex-1">
                                                <p className="text-lg md:text-xl font-bold text-green-500 mb-0.5 md:mb-1">
                                                    {nairaAmount.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                                                </p>
                                                <p className="text-[10px] md:text-xs font-medium text-light-muted dark:text-dark-muted uppercase tracking-wider">Naira</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div className="p-3 bg-blue-500/10 border border-blue-500/20 rounded-xl text-xs text-center text-blue-600 dark:text-blue-400 font-medium">
                                    Current Exchange Rate: 1 Ruby = {exchangeRate}
                                </div>
                            </motion.div>
                        )}

                        {step === 2 && (
                            <motion.div
                                key="step2"
                                initial={{ opacity: 0, x: 20 }}
                                animate={{ opacity: 1, x: 0 }}
                                exit={{ opacity: 0, x: -20 }}
                                transition={{ duration: 0.2 }}
                                className="space-y-6"
                            >
                                <div className="text-center space-y-2">
                                    <h3 className="text-lg font-semibold text-light-text dark:text-dark-text">Bank Details</h3>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">
                                        Enter your bank information to receive payments.
                                    </p>
                                </div>

                                <div className="space-y-4">
                                    {/* Bank Name Selector */}
                                    <div className="space-y-1.5">
                                        <label className="text-[10px] md:text-xs font-semibold uppercase tracking-wider text-light-muted dark:text-dark-muted ml-1">
                                            Bank Name
                                        </label>
                                        <button
                                            onClick={() => setIsBankModalOpen(true)}
                                            className="w-full px-3 md:px-4 py-2.5 md:py-3.5 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border text-left flex items-center justify-between hover:border-primary-500 transition-colors group"
                                        >
                                            <span className={`text-sm md:text-base ${bankName ? "text-light-text dark:text-dark-text" : "text-light-muted/70 dark:text-dark-muted/70"}`}>
                                                {bankName || "Select Bank"}
                                            </span>
                                            <ChevronRight className="w-4 h-4 text-light-muted group-hover:text-primary-500 transition-colors" />
                                        </button>
                                    </div>

                                    {/* Account Number */}
                                    <div className="space-y-1.5">
                                        <label className="text-[10px] md:text-xs font-semibold uppercase tracking-wider text-light-muted dark:text-dark-muted ml-1">
                                            Account Number
                                        </label>
                                        <input
                                            type="text"
                                            value={accountNumber}
                                            onChange={(e) => setAccountNumber(e.target.value.replace(/\D/g, "").slice(0, 10))}
                                            className="w-full px-3 md:px-4 py-2.5 md:py-3.5 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border text-sm md:text-base text-light-text dark:text-dark-text focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none font-mono tracking-wider transition-shadow placeholder:text-light-muted/50 dark:placeholder:text-dark-muted/50"
                                            placeholder="0123456789"
                                        />
                                        <p className="text-[10px] text-right text-light-muted dark:text-dark-muted pr-1">
                                            {accountNumber.length}/10 digits
                                        </p>
                                    </div>

                                    {/* Account Name */}
                                    <div className="space-y-1.5">
                                        <label className="text-[10px] md:text-xs font-semibold uppercase tracking-wider text-light-muted dark:text-dark-muted ml-1">
                                            Account Name
                                        </label>
                                        <input
                                            type="text"
                                            value={accountName}
                                            onChange={(e) => setAccountName(e.target.value)}
                                            className="w-full px-3 md:px-4 py-2.5 md:py-3.5 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border text-sm md:text-base text-light-text dark:text-dark-text focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none transition-shadow placeholder:text-light-muted/50 dark:placeholder:text-dark-muted/50"
                                            placeholder="John Doe"
                                        />
                                    </div>

                                    <AnimatePresence>
                                        {error && (
                                            <motion.div
                                                initial={{ opacity: 0, y: -10 }}
                                                animate={{ opacity: 1, y: 0 }}
                                                exit={{ opacity: 0, y: -10 }}
                                                className="p-3 bg-red-500/10 border border-red-500/20 rounded-xl text-sm text-red-600 dark:text-red-400 flex items-center gap-2"
                                            >
                                                <div className="w-1.5 h-1.5 rounded-full bg-red-500" />
                                                {error}
                                            </motion.div>
                                        )}
                                    </AnimatePresence>
                                </div>
                            </motion.div>
                        )}

                        {step === 3 && (
                            <motion.div
                                key="step3"
                                initial={{ opacity: 0, x: 20 }}
                                animate={{ opacity: 1, x: 0 }}
                                exit={{ opacity: 0, x: -20 }}
                                transition={{ duration: 0.2 }}
                                className="space-y-6"
                            >
                                <div className="text-center space-y-2">
                                    <h3 className="text-lg font-semibold text-light-text dark:text-dark-text">Confirm Pay Out</h3>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">
                                        Please review your details before confirming.
                                    </p>
                                </div>

                                <div className="bg-light-card dark:bg-dark-card rounded-2xl p-5 border border-light-border dark:border-dark-border space-y-4 shadow-sm">
                                    <div className="flex justify-between items-center pb-4 border-b border-light-border/50 dark:border-dark-border/50">
                                        <span className="text-sm text-light-muted dark:text-dark-muted">Total Amount</span>
                                        <span className="font-bold text-xl text-green-500">{nairaAmount.toLocaleString()}</span>
                                    </div>

                                    <div className="space-y-3">
                                        <div className="flex justify-between items-center text-sm">
                                            <span className="text-light-muted dark:text-dark-muted">Bank Name</span>
                                            <span className="font-medium text-light-text dark:text-dark-text text-right">{bankName}</span>
                                        </div>
                                        <div className="flex justify-between items-center text-sm">
                                            <span className="text-light-muted dark:text-dark-muted">Account Number</span>
                                            <span className="font-mono text-light-text dark:text-dark-text">{accountNumber}</span>
                                        </div>
                                        <div className="flex justify-between items-center text-sm">
                                            <span className="text-light-muted dark:text-dark-muted">Account Name</span>
                                            <span className="font-medium text-light-text dark:text-dark-text text-right">{accountName}</span>
                                        </div>
                                    </div>
                                </div>

                                <div className="flex gap-4 p-4 bg-amber-500/10 border border-amber-500/20 rounded-xl">
                                    <CheckCircle2 className="w-5 h-5 text-amber-500 flex-shrink-0 mt-0.5" />
                                    <p className="text-xs text-amber-700 dark:text-amber-400/90 leading-relaxed">
                                        Your withdrawal request will be reviewed by an admin. Once approved, the Naira amount will be transferred to your account within 1-3 business days.
                                    </p>
                                </div>
                            </motion.div>
                        )}
                    </AnimatePresence>

                    {/* Bank Selection Modal */}
                    <AnimatePresence>
                        {isBankModalOpen && (
                            <motion.div
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                exit={{ opacity: 0 }}
                                className="absolute inset-0 bg-light-bg dark:bg-dark-bg z-20 flex flex-col"
                            >
                                <div className="p-4 border-b border-light-border dark:border-dark-border flex gap-3 items-center bg-light-card dark:bg-dark-card sticky top-0">
                                    <button
                                        onClick={() => setIsBankModalOpen(false)}
                                        className="p-2 -ml-2 rounded-lg hover:bg-light-border dark:hover:bg-dark-border transition-colors"
                                    >
                                        <ChevronLeft className="w-5 h-5 text-light-text dark:text-dark-text" />
                                    </button>
                                    <div className="flex-1 relative">
                                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-light-muted" />
                                        <input
                                            type="text"
                                            placeholder="Search banks..."
                                            autoFocus
                                            value={bankSearchTerm}
                                            onChange={(e) => setBankSearchTerm(e.target.value)}
                                            className="w-full pl-9 pr-4 py-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-sm outline-none focus:ring-2 focus:ring-primary-500"
                                        />
                                    </div>
                                </div>
                                <div className="flex-1 overflow-y-auto p-2">
                                    <div className="space-y-1">
                                        {filteredBanks.length > 0 ? (
                                            filteredBanks.map((bank) => (
                                                <button
                                                    key={bank}
                                                    onClick={() => {
                                                        setBankName(bank);
                                                        setIsBankModalOpen(false);
                                                        setBankSearchTerm("");
                                                    }}
                                                    className={`w-full text-left px-4 py-3 rounded-xl flex items-center justify-between transition-colors ${bankName === bank
                                                        ? "bg-primary-500/10 text-primary-500"
                                                        : "hover:bg-light-card dark:hover:bg-dark-card text-light-text dark:text-dark-text"
                                                        }`}
                                                >
                                                    <span className="flex items-center gap-3">
                                                        <Building2 className={`w-4 h-4 ${bankName === bank ? "text-primary-500" : "text-light-muted"}`} />
                                                        {bank}
                                                    </span>
                                                    {bankName === bank && <CheckCircle2 className="w-4 h-4 text-primary-500" />}
                                                </button>
                                            ))
                                        ) : (
                                            <div className="text-center py-8 text-light-muted dark:text-dark-muted">
                                                <p>No banks found matching "{bankSearchTerm}"</p>
                                            </div>
                                        )}
                                    </div>
                                </div>
                            </motion.div>
                        )}
                    </AnimatePresence>
                </div>

                {/* Footer */}
                <div className="p-4 md:p-6 pt-2 bg-light-bg dark:bg-dark-bg sticky bottom-0 z-10 border-t border-light-border/50 dark:border-dark-border/50">
                    <div className="flex gap-3 md:gap-4">
                        {step > 1 && (
                            <Button
                                variant="outline"
                                onClick={handleBack}
                                className="flex-1 py-4 md:py-6 rounded-xl hover:bg-light-card dark:hover:bg-dark-card border-light-border dark:border-dark-border text-sm"
                            >
                                <ChevronLeft className="w-4 h-4 mr-1" /> Back
                            </Button>
                        )}
                        <Button
                            variant="primary"
                            onClick={step === 3 ? handleSubmit : handleNext}
                            className={`flex-[2] py-4 md:py-6 rounded-xl bg-gradient-to-r from-primary-500 to-secondary-500 shadow-lg shadow-primary-500/20 hover:shadow-primary-500/30 transition-all text-sm ${step === 1 ? 'w-full' : ''}`}
                        >
                            {step === 3 ? (
                                "Confirm & Withdraw"
                            ) : (
                                <span className="flex items-center">Next Step <ChevronRight className="w-4 h-4 ml-1" /></span>
                            )}
                        </Button>
                    </div>
                </div>
            </motion.div>
        </div>
    );
}
