import { Modal } from "./Modal";
import { Button } from "./Button";

interface ConfirmationModalProps {
    isOpen: boolean;
    onClose: () => void;
    onConfirm: () => void;
    title: string;
    description: string;
    confirmText?: string;
    cancelText?: string;
    variant?: "danger" | "primary";
}

export function ConfirmationModal({
    isOpen,
    onClose,
    onConfirm,
    title,
    description,
    confirmText = "Confirm",
    cancelText = "Cancel",
    variant = "primary"
}: ConfirmationModalProps) {
    return (
        <Modal isOpen={isOpen} onClose={onClose} title={title}>
            <div className="space-y-6">
                <p className="text-light-muted dark:text-dark-muted leading-relaxed">
                    {description}
                </p>

                <div className="flex justify-end gap-3 pt-2">
                    <Button
                        variant="outline"
                        onClick={onClose}
                        className="border-light-border dark:border-dark-border hover:bg-light-bg dark:hover:bg-dark-bg"
                    >
                        {cancelText}
                    </Button>
                    <Button
                        variant={variant === "danger" ? "danger" : "primary"}
                        onClick={() => {
                            onConfirm();
                            onClose();
                        }}
                        className={variant === "primary" ? "bg-primary-500 hover:bg-primary-600 text-white shadow-lg shadow-primary-500/20" : ""}
                    >
                        {confirmText}
                    </Button>
                </div>
            </div>
        </Modal>
    );
}
