import { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Card, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Badge } from '@/components/ui/Badge';
import {
    Search,
    User,
    Gift,
    X,
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { Id } from '../../../convex/_generated/dataModel';

import { AdminPageSkeleton } from '@/components/ui/Skeletons';

export default function AdminTagsPage() {
    const { user } = useAuth();
    const [searchTerm, setSearchTerm] = useState('');

    // Queries
    const allUsers = useQuery(api.users.getAllUsers, user?._id ? { adminId: user._id } : 'skip');
    const teachings = useQuery(api.posts.getPosts, { category: 'Ancient Culture', limit: 50 });
    const activeTags = useQuery(api.tags.getAllActiveTags, user?._id ? { adminId: user._id } : 'skip');

    // Mutations
    const tagUserQuestion = useMutation(api.tags.tagUserForQuestions);
    const tagUserPrayer = useMutation(api.tags.tagUserForPrayer);
    const revokeTag = useMutation(api.tags.revokeTag);

    // Modal State
    const [selectedUser, setSelectedUser] = useState<any>(null);
    const [tagType, setTagType] = useState<'question' | 'prayer'>('question');
    const [selectedPostId, setSelectedPostId] = useState<string>('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    if (allUsers === undefined || activeTags === undefined) {
        return <AdminPageSkeleton />;
    }

    // Filtering
    const filteredUsers = allUsers?.filter((u: any) =>
        u.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        u.email.toLowerCase().includes(searchTerm.toLowerCase())
    );

    const handleGrantTag = async () => {
        if (!user || !selectedUser) return;
        setIsSubmitting(true);
        try {
            if (tagType === 'question') {
                if (!selectedPostId) {
                    alert("Please select a teaching post");
                    setIsSubmitting(false);
                    return;
                }
                await tagUserQuestion({
                    adminId: user._id,
                    userId: selectedUser._id,
                    postId: selectedPostId as Id<"posts">
                });
            } else {
                await tagUserPrayer({
                    adminId: user._id,
                    userId: selectedUser._id,
                });
            }
            alert("Tag granted successfully!");
            setSelectedUser(null);
            setSelectedPostId('');
        } catch (error: any) {
            alert(error.message || "Failed to grant tag");
        } finally {
            setIsSubmitting(false);
        }
    };

    const handleRevoke = async (tagId: Id<"userTags">) => {
        if (!user || !confirm("Revoke this tag?")) return;
        try {
            await revokeTag({ adminId: user._id, tagId });
        } catch (error) {
            alert("Failed to revoke tag");
        }
    };

    return (
        <div className="space-y-6">
            <div>
                <h1 className="text-3xl font-display font-bold text-light-text dark:text-dark-text mb-2">
                    User Tag Management
                </h1>
                <p className="text-light-muted dark:text-dark-muted">
                    Grant or revoke specialized posting permissions (Questions, Prayers)
                </p>
            </div>

            {/* Active Tags Overview */}
            <Card glass className="border border-light-border dark:border-dark-border">
                <CardContent className="p-6">
                    <h3 className="font-bold text-lg mb-4">Active Tags ({activeTags?.length || 0})</h3>
                    <div className="flex flex-wrap gap-2">
                        {activeTags?.length === 0 ? (
                            <p className="text-sm text-light-muted">No active tags.</p>
                        ) : (
                            activeTags?.map((tag: any) => (
                                <Badge key={tag._id} variant="outline" className="flex items-center gap-2 pr-1">
                                    <span className={tag.tagType === 'question' ? 'text-purple-500' : 'text-orange-500'}>
                                        {tag.tagType === 'question' ? 'Question' : 'Prayer'}
                                    </span>
                                    <span className="text-light-text dark:text-dark-text border-l border-gray-200 dark:border-gray-700 pl-2">
                                        {tag.userName}
                                    </span>
                                    <button
                                        onClick={() => handleRevoke(tag._id)}
                                        className="p-1 hover:bg-red-100 rounded-full text-red-500 transition-colors ml-1"
                                    >
                                        <X className="w-3 h-3" />
                                    </button>
                                </Badge>
                            ))
                        )}
                    </div>
                </CardContent>
            </Card>

            {/* User Search & List */}
            <div className="relative">
                <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-light-muted" />
                <input
                    type="text"
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    placeholder="Search users to tag..."
                    className="w-full pl-10 pr-4 py-3 rounded-xl bg-white dark:bg-[#1A1A1A] border border-light-border dark:border-dark-border focus:ring-2 focus:ring-primary-500 focus:outline-none transition-all"
                />
            </div>

            <div className="grid gap-4">
                {filteredUsers?.slice(0, 10).map((u: any) => ( // Limit display for perf
                    <Card key={u._id} glass className="border border-light-border dark:border-dark-border">
                        <CardContent className="p-4 flex items-center justify-between">
                            <div className="flex items-center gap-3">
                                <div className="w-10 h-10 rounded-full bg-gray-200 dark:bg-gray-700 flex items-center justify-center overflow-hidden">
                                    {u.avatarUrl ? <img src={u.avatarUrl} alt={u.name} className="w-full h-full object-cover" /> : <User className="w-5 h-5 text-gray-400" />}
                                </div>
                                <div>
                                    <h4 className="font-bold text-light-text dark:text-dark-text">{u.name}</h4>
                                    <p className="text-xs text-light-muted">{u.role}</p>
                                </div>
                            </div>
                            <Button size="sm" onClick={() => setSelectedUser(u)}>
                                <Gift className="w-4 h-4 mr-2" /> Grant Tag
                            </Button>
                        </CardContent>
                    </Card>
                ))}
            </div>

            {/* Grant Tag Modal */}
            <AnimatePresence>
                {selectedUser && (
                    <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center p-4 z-50">
                        <motion.div
                            initial={{ opacity: 0, scale: 0.95 }}
                            animate={{ opacity: 1, scale: 1 }}
                            exit={{ opacity: 0, scale: 0.95 }}
                            className="bg-white dark:bg-[#1A1A1A] rounded-2xl shadow-xl max-w-md w-full p-6 relative border border-gray-100 dark:border-gray-800"
                        >
                            <button
                                onClick={() => setSelectedUser(null)}
                                className="absolute top-4 right-4 p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-white/5"
                            >
                                <X className="w-5 h-5" />
                            </button>

                            <h2 className="text-xl font-bold mb-4">Grant Permission</h2>
                            <p className="text-sm text-light-muted mb-6">
                                Allow <span className="font-bold text-primary-500">{selectedUser.name}</span> to post special content.
                            </p>

                            <div className="space-y-4">
                                <div>
                                    <label className="block text-sm font-medium mb-2">Tag Type</label>
                                    <div className="grid grid-cols-2 gap-2">
                                        <button
                                            onClick={() => setTagType('question')}
                                            className={`p-3 rounded-lg border text-sm font-medium transition-all ${tagType === 'question' ? 'border-primary-500 bg-primary-50 text-primary-700 dark:bg-primary-900/20 dark:text-primary-400' : 'border-gray-200 dark:border-gray-700'}`}
                                        >
                                            Question Tag
                                        </button>
                                        <button
                                            onClick={() => setTagType('prayer')}
                                            className={`p-3 rounded-lg border text-sm font-medium transition-all ${tagType === 'prayer' ? 'border-orange-500 bg-orange-50 text-orange-700 dark:bg-orange-900/20 dark:text-orange-400' : 'border-gray-200 dark:border-gray-700'}`}
                                        >
                                            Prayer Tag
                                        </button>
                                    </div>
                                </div>

                                {tagType === 'question' && (
                                    <div>
                                        <label className="block text-sm font-medium mb-2">Select Teaching (Context)</label>
                                        <select
                                            value={selectedPostId}
                                            onChange={(e) => setSelectedPostId(e.target.value)}
                                            className="w-full p-3 rounded-lg bg-gray-50 dark:bg-[#222] border border-gray-200 dark:border-gray-700 focus:border-primary-500 focus:outline-none text-sm"
                                        >
                                            <option value="">Select a teaching...</option>
                                            {teachings?.map((post: any) => (
                                                <option key={post._id} value={post._id}>
                                                    {post.content.substring(0, 40)}...
                                                </option>
                                            ))}
                                        </select>
                                        <p className="text-xs text-light-muted mt-1">user can only ask questions on this specific post.</p>
                                    </div>
                                )}

                                <Button
                                    className="w-full mt-4"
                                    onClick={handleGrantTag}
                                    disabled={isSubmitting}
                                >
                                    {isSubmitting ? 'Granting...' : 'Grant Permission'}
                                </Button>
                            </div>
                        </motion.div>
                    </div>
                )}
            </AnimatePresence>
        </div>
    );
}
