import { motion } from 'framer-motion';
import { useAuth } from '@/hooks/useAuth';
import { MyContributionsTab } from '@/components/governance/MyContributionsTab';

export function BoardContributions() {
    const { user } = useAuth();

    return (
        <div className="space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
            >
                <div className="flex items-center gap-2 mb-2">
                    <div className="w-8 h-1 bg-primary-500 rounded-full" />
                    <span className="text-[10px] font-black uppercase tracking-[0.3em] text-primary-500">
                        Personal Contributions
                    </span>
                </div>
                <h1 className="text-3xl md:text-5xl font-black text-gradient font-display mb-2 tracking-tight">
                    My Contributions
                </h1>
                <p className="text-xs md:text-base text-light-muted dark:text-dark-muted font-medium max-w-xl">
                    Manage your monthly dues and track surplus payments
                </p>
            </motion.div>

            <MyContributionsTab user={user} />
        </div>
    );
}
