import React from 'react';
import { motion } from 'framer-motion';
import { cn } from '@/lib/cn';

interface CardProps {
    children: React.ReactNode;
    className?: string;
    glass?: boolean;
    hover?: boolean;
    onClick?: () => void;
}

export function Card({ children, className, glass = false, hover = false, onClick }: CardProps) {
    const Component = onClick ? motion.div : motion.div;

    return (
        <Component
            onClick={onClick}
            whileHover={hover ? { y: -4, transition: { duration: 0.2 } } : undefined}
            className={cn(
                'p-6 transition-all duration-200',
                glass
                    ? 'glass'
                    : 'bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border',
                hover && 'cursor-pointer',
                className
            )}
        >
            {children}
        </Component>
    );
}

interface CardHeaderProps {
    children: React.ReactNode;
    className?: string;
}

export function CardHeader({ children, className }: CardHeaderProps) {
    return (
        <div className={cn('mb-4', className)}>
            {children}
        </div>
    );
}

interface CardTitleProps {
    children: React.ReactNode;
    className?: string;
}

export function CardTitle({ children, className }: CardTitleProps) {
    return (
        <h3 className={cn('text-xl font-semibold text-light-text dark:text-dark-text', className)}>
            {children}
        </h3>
    );
}

interface CardContentProps {
    children: React.ReactNode;
    className?: string;
}

export function CardContent({ children, className }: CardContentProps) {
    return (
        <div className={cn('text-light-text dark:text-dark-text', className)}>
            {children}
        </div>
    );
}
