import { useState } from 'react';
import { Modal } from '../ui/Modal';
import { Button } from '../ui/Button';
import { Input } from '../ui/Input';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useToast } from '@/context/ToastContext';

interface ClockInModalProps {
    isOpen: boolean;
    onClose: () => void;
    userId: string;
}

export function ClockInModal({ isOpen, onClose, userId }: ClockInModalProps) {
    const [code, setCode] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    const clockIn = useMutation(api.governance.clockIn);
    const { showToast } = useToast();

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsSubmitting(true);

        try {
            await clockIn({
                userId: userId as any,
                code,
            });

            setCode('');
            onClose();
            showToast('success', ' Successfully clocked in to the meeting!');
        } catch (error: any) {
            console.error('Failed to clock in:', error);
            showToast('error', error.message || 'Failed to clock in. Please check the code and try again.');
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <Modal isOpen={isOpen} onClose={onClose} title="Clock In to Meeting">
            <form onSubmit={handleSubmit} className="space-y-6">
                <div>
                    <label className="block text-sm font-bold mb-2">Meeting Code</label>
                    <Input
                        value={code}
                        onChange={(e) => setCode(e.target.value.toUpperCase())}
                        placeholder="Enter 6-digit code"
                        maxLength={6}
                        className="text-center text-2xl font-mono tracking-widest"
                        required
                    />
                    <p className="text-xs text-light-muted mt-2">
                        Enter the 6-digit code provided by the admin
                    </p>
                </div>

                <div className="flex gap-4">
                    <Button
                        type="button"
                        variant="ghost"
                        onClick={onClose}
                        className="flex-1"
                        disabled={isSubmitting}
                    >
                        Cancel
                    </Button>
                    <Button
                        type="submit"
                        variant="primary"
                        className="flex-1"
                        disabled={isSubmitting || code.length !== 6}
                    >
                        {isSubmitting ? 'Clocking In...' : 'Clock In'}
                    </Button>
                </div>
            </form>
        </Modal>
    );
}
