import { useState } from 'react';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import {
    Users,
    Calendar,
    DollarSign,
    Search,
    ChevronRight,
    ArrowLeft
} from 'lucide-react';
import { motion } from 'framer-motion';

interface MemberLedgerTabProps {
    admin: any;
}

export function MemberLedgerTab({ admin }: MemberLedgerTabProps) {
    const [selectedMemberId, setSelectedMemberId] = useState<string | null>(null);
    const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());
    const [searchQuery, setSearchQuery] = useState('');

    const members = useQuery(api.users.getBoardMembers);
    const selectedMember = members?.find((m: any) => m._id === selectedMemberId);

    const ledger = useQuery(api.finance.getMemberContributions,
        selectedMemberId ? {
            adminId: admin._id,
            targetUserId: selectedMemberId as any,
            year: selectedYear
        } : 'skip'
    );

    const filteredMembers = members?.filter((m: any) =>
        m.name.toLowerCase().includes(searchQuery.toLowerCase())
    );

    const formatCurrency = (amount: number) => {
        return new Intl.NumberFormat('en-NG', {
            style: 'currency',
            currency: 'NGN',
        }).format(amount);
    };

    const getMonthName = (month: number) => {
        return new Date(2024, month - 1).toLocaleDateString('en-US', { month: 'long' });
    };

    if (!selectedMemberId) {
        return (
            <div className="space-y-6">
                <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                    <div className="flex-1 min-w-0">
                        <h2 className="text-xl md:text-2xl font-black text-gradient truncate">Member Ledger</h2>
                        <p className="text-light-muted text-[10px] md:text-sm font-medium">Select a member to view records.</p>
                    </div>

                    <div className="relative w-full md:w-72">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-light-muted" />
                        <input
                            type="text"
                            placeholder="Search board members..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="w-full pl-10 pr-4 py-2.5 rounded-xl bg-light-bg/50 border border-light-border focus:border-primary-500/50 outline-none text-xs md:text-sm transition-all"
                        />
                    </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                    {filteredMembers?.map((member: any) => (
                        <motion.div
                            key={member._id}
                            initial={{ opacity: 0, y: 10 }}
                            animate={{ opacity: 1, y: 0 }}
                            whileHover={{ y: -4 }}
                        >
                            <Card
                                className="cursor-pointer hover:border-primary-500/30 transition-all border-light-border/50 bg-light-card/40"
                                onClick={() => setSelectedMemberId(member._id)}
                            >
                                <CardContent className="p-4 flex items-center justify-between">
                                    <div className="flex items-center gap-3 min-w-0">
                                        <div className="w-10 h-10 rounded-full bg-primary-500/10 flex items-center justify-center text-primary-500 font-bold shrink-0">
                                            {member.name.charAt(0)}
                                        </div>
                                        <div className="min-w-0">
                                            <p className="font-bold text-sm truncate">{member.name}</p>
                                            <p className="text-[10px] text-light-muted uppercase font-black tracking-widest">{member.role.replace('_', ' ')}</p>
                                        </div>
                                    </div>
                                    <ChevronRight className="w-4 h-4 text-light-muted" />
                                </CardContent>
                            </Card>
                        </motion.div>
                    ))}
                    {filteredMembers?.length === 0 && (
                        <div className="col-span-full py-12 text-center text-light-muted bg-light-bg/20 rounded-2xl border-2 border-dashed border-light-border">
                            <Users className="w-12 h-12 mx-auto mb-2 opacity-20" />
                            <p className="text-sm">No members found matching your search</p>
                        </div>
                    )}
                </div>
            </div>
        );
    }

    const totalPaid = ledger?.reduce((acc: number, curr: any) => acc + curr.totalPaid, 0) || 0;
    const totalOutstanding = ledger?.reduce((acc: number, curr: any) => acc + curr.outstanding, 0) || 0;
    const totalSurplus = ledger?.reduce((acc: number, curr: any) => acc + curr.surplus, 0) || 0;

    return (
        <div className="space-y-6">
            <div className="flex items-center gap-3 md:gap-4 min-w-0">
                <Button variant="ghost" size="sm" onClick={() => setSelectedMemberId(null)} className="rounded-xl h-10 w-10 p-0 flex-shrink-0">
                    <ArrowLeft className="w-5 h-5" />
                </Button>
                <div className="min-w-0">
                    <h2 className="text-lg md:text-2xl font-black text-gradient truncate">Ledger: {selectedMember?.name}</h2>
                    <div className="flex items-center gap-2 mt-0.5">
                        <select
                            value={selectedYear}
                            onChange={(e) => setSelectedYear(Number(e.target.value))}
                            className="bg-transparent text-[10px] md:text-xs font-black text-primary-500 border-none outline-none cursor-pointer"
                        >
                            {[2024, 2025, 2026].map(y => <option key={y} value={y}>{y} Fiscal Year</option>)}
                        </select>
                    </div>
                </div>
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-3 gap-3 md:gap-4">
                <Card className="bg-emerald-500/5 border-emerald-500/20">
                    <CardContent className="p-4">
                        <div className="flex items-center justify-between mb-2">
                            <DollarSign className="w-4 h-4 md:w-5 md:h-5 text-emerald-500" />
                            <Badge className="bg-emerald-500 text-[10px]">Total Paid</Badge>
                        </div>
                        <p className="text-xl md:text-2xl font-black text-emerald-500">{formatCurrency(totalPaid)}</p>
                    </CardContent>
                </Card>
                <Card className="bg-rose-500/5 border-rose-500/20">
                    <CardContent className="p-4">
                        <div className="flex items-center justify-between mb-2">
                            <Calendar className="w-4 h-4 md:w-5 md:h-5 text-rose-500" />
                            <Badge className="bg-rose-500 text-[10px]">Outstanding</Badge>
                        </div>
                        <p className="text-xl md:text-2xl font-black text-rose-500">{formatCurrency(totalOutstanding)}</p>
                    </CardContent>
                </Card>
                <Card className="bg-primary-500/5 border-primary-500/20">
                    <CardContent className="p-4">
                        <div className="flex items-center justify-between mb-2">
                            <TrendingUp className="w-4 h-4 md:w-5 md:h-5 text-primary-500" />
                            <Badge className="bg-primary-500 text-[10px]">Surplus</Badge>
                        </div>
                        <p className="text-xl md:text-2xl font-black text-primary-500">{formatCurrency(totalSurplus)}</p>
                    </CardContent>
                </Card>
            </div>

            <Card className="border-light-border/50">
                <CardHeader className="pb-2">
                    <CardTitle className="text-sm font-black uppercase tracking-widest text-light-muted">Monthly Breakdown</CardTitle>
                </CardHeader>
                <CardContent className="px-1 md:px-6">
                    <div className="overflow-x-auto hide-scrollbar">
                        <table className="w-full text-left min-w-[600px] md:min-w-0">
                            <thead>
                                <tr className="border-b border-light-border text-[9px] md:text-[10px] font-black uppercase tracking-wider text-light-muted">
                                    <th className="py-3 px-3 md:px-4">Month</th>
                                    <th className="py-3 px-3 md:px-4">Required</th>
                                    <th className="py-3 px-3 md:px-4">Paid</th>
                                    <th className="py-3 px-3 md:px-4">Surplus</th>
                                    <th className="py-3 px-3 md:px-4">Outstanding</th>
                                    <th className="py-3 px-3 md:px-4">Status</th>
                                </tr>
                            </thead>
                            <tbody className="divide-y divide-light-border/50">
                                {ledger?.map((row: any) => (
                                    <tr key={row.month} className="text-[10px] md:text-xs hover:bg-light-bg/30 transition-colors">
                                        <td className="py-3 px-3 md:px-4 font-bold">{getMonthName(row.month)}</td>
                                        <td className="py-3 px-3 md:px-4 font-medium">{formatCurrency(row.requiredAmount)}</td>
                                        <td className="py-3 px-3 md:px-4 font-black text-emerald-500">{formatCurrency(row.totalPaid)}</td>
                                        <td className="py-3 px-3 md:px-4 font-black text-primary-500">{formatCurrency(row.surplus)}</td>
                                        <td className="py-3 px-3 md:px-4 font-black text-rose-500">{formatCurrency(row.outstanding)}</td>
                                        <td className="py-3 px-3 md:px-4 text-[9px] md:text-[10px] font-black uppercase italic text-light-muted">
                                            <Badge className={`text-[9px] h-5 min-w-[50px] flex items-center justify-center ${row.status === 'cleared' ? 'bg-emerald-500/20 text-emerald-500 hover:bg-emerald-500/30' :
                                                row.status === 'partial' ? 'bg-amber-500/20 text-amber-500 hover:bg-amber-500/30' :
                                                    'bg-rose-500/20 text-rose-500 hover:bg-rose-500/30'
                                                }`}>
                                                {row.status}
                                            </Badge>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}

// Helper icon
function TrendingUp(props: any) {
    return (
        <svg
            {...props}
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
        >
            <polyline points="23 6 13.5 15.5 8.5 10.5 1 18" />
            <polyline points="17 6 23 6 23 12" />
        </svg>
    )
}
