import { useState, useEffect } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useNavigate } from 'react-router-dom';
import { PostForm } from '@/components/posts/PostForm';
import { PostFeed } from '@/components/posts/PostFeed';
import { PostVisibilityToggle } from '@/components/posts/PostVisibilityToggle';
import { InfoIcon } from '@/components/ui/InfoIcon';
import { motion } from 'framer-motion';
import { Sparkles, Share2 } from 'lucide-react';
import { PageHeaderSkeleton, PostCardSkeleton } from '@/components/ui/Skeletons';

export function WisdomNow() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();
    const [refreshTrigger, setRefreshTrigger] = useState(0);
    const [isPostsVisible, setIsPostsVisible] = useState(() => {
        const saved = localStorage.getItem('wisdomNow_postsVisible');
        return saved !== null ? JSON.parse(saved) : true;
    });

    useEffect(() => {
        localStorage.setItem('wisdomNow_postsVisible', JSON.stringify(isPostsVisible));
    }, [isPostsVisible]);

    if (isLoading) {
        return (
            <div className="min-h-screen py-4 md:py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                    <PageHeaderSkeleton />
                    <div className="space-y-4">
                        <PostCardSkeleton />
                        <PostCardSkeleton />
                    </div>
                </div>
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    return (
        <div className="min-h-screen py-4 md:py-8 pb-20 md:pb-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mb-6 md:mb-8 p-6 md:p-8 rounded-2xl bg-gradient-to-br from-secondary-500/10 via-secondary-500/5 to-secondary-600/10 border border-secondary-500/20"
                >
                    <div className="flex items-start gap-4 justify-between mb-4">
                        <div className="flex items-start gap-3 flex-1">
                            <div className="w-12 h-12 md:w-16 md:h-16 rounded-xl bg-secondary-500 flex items-center justify-center shadow-lg shadow-secondary-500/30 flex-shrink-0">
                                <Sparkles className="w-6 h-6 md:w-8 md:h-8 text-white" />
                            </div>
                            <div className="flex-1 min-w-0">
                                <div className="flex items-center gap-2 mb-2">
                                    <h1 className="text-2xl md:text-4xl font-bold text-secondary-600 dark:text-secondary-400 font-display">
                                        Wisdom of The Now
                                    </h1>
                                    <InfoIcon
                                        title="Wisdom of The Now"
                                        content="This section features teachings based on spiritual identity and insights from the New Testament. Engage with admin posts to deepen your understanding."
                                    />
                                </div>
                                <p className="text-light-text dark:text-dark-text text-sm md:text-base leading-relaxed">
                                    Spiritual identity and insights from the New Testament
                                </p>
                            </div>
                        </div>
                        <motion.button
                            whileTap={{ scale: 0.95 }}
                            onClick={async () => {
                                const url = `${window.location.origin}/wisdom-now`;
                                if (navigator.share) {
                                    await navigator.share({ title: 'Wisdom of The Now - Baptizo', url });
                                } else {
                                    await navigator.clipboard.writeText(url);
                                    alert('Link copied to clipboard!');
                                }
                            }}
                            className="p-2 rounded-lg hover:bg-secondary-100 dark:hover:bg-secondary-900/20 transition-all flex-shrink-0"
                            title="Share this page"
                        >
                            <Share2 className="w-5 h-5 text-secondary-500" />
                        </motion.button>
                    </div>
                </motion.div>

                <div className="space-y-8">
                    <PostForm onSuccess={() => setRefreshTrigger(prev => prev + 1)} defaultCategory="Wisdom of The Now" />

                    {/* Visibility Toggle */}
                    <div className="flex justify-end">
                        <PostVisibilityToggle
                            isVisible={isPostsVisible}
                            onToggle={() => setIsPostsVisible(!isPostsVisible)}
                            category="Wisdom of The Now"
                        />
                    </div>

                    {isPostsVisible && (
                        <PostFeed
                            category="Wisdom of The Now"
                            key={refreshTrigger}
                            onlyOfficial={true}
                            onCommentClick={(post: any) => navigate(`/post/${post._id}`)}
                        />
                    )}

                    {!isPostsVisible && (
                        <div className="text-center py-12 glass rounded-xl">
                            <p className="text-light-muted dark:text-dark-muted">
                                Posts are hidden. Click "Show Wisdom of The Now Posts" to view them.
                            </p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
