import { motion } from 'framer-motion';
import { useAuth } from '@/hooks/useAuth';
import { AttendanceTab } from '@/components/governance/AttendanceTab';


export function BoardAttendance() {
    const { user } = useAuth();

    return (
        <div className="space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
            >
                <div className="flex items-center gap-2 mb-2">
                    <div className="w-8 h-1 bg-primary-500 rounded-full" />
                    <span className="text-[10px] font-black uppercase tracking-[0.3em] text-primary-500">
                        Attendance System
                    </span>
                </div>
                <h1 className="text-4xl md:text-5xl font-black text-gradient font-display mb-2 tracking-tight">
                    Meeting Attendance
                </h1>
                <p className="text-light-muted dark:text-dark-muted font-medium">
                    Track your meeting participation and submit excuses
                </p>
            </motion.div>

            <AttendanceTab user={user} />
        </div>
    );
}
