import { useState } from 'react';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Button } from '@/components/ui/Button';
import { MediaUpload } from '@/components/ui/MediaUpload';
import { motion } from 'framer-motion';
import { Sparkles } from 'lucide-react';

interface PostFormProps {
    onSuccess?: () => void;
    defaultCategory?: 'Ancient Culture' | 'Wisdom of The Now';
    variant?: 'default' | 'bottom-sheet';
    postType?: 'general' | 'question' | 'prayer';
}

export function PostForm({ onSuccess, defaultCategory, variant = 'default', postType }: PostFormProps) {
    const { user } = useAuth();
    const createPost = useMutation(api.posts.createPost);

    const [content, setContent] = useState('');
    const [category, setCategory] = useState<'Ancient Culture' | 'Wisdom of The Now'>(defaultCategory || 'Ancient Culture');
    const [questionContext, setQuestionContext] = useState<'personal_experience' | 'about_someone_else'>('personal_experience');
    const [scripture, setScripture] = useState('');
    const [mediaUrl, setMediaUrl] = useState('');
    const [mediaType, setMediaType] = useState<'image' | 'video' | null>(null);
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState('');

    const handleMediaUpload = (url: string, type: 'image' | 'video') => {
        setMediaUrl(url);
        setMediaType(type);
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        if (!user) {
            setError('You must be logged in to post');
            return;
        }

        if (content.trim().length < 10) {
            setError('Post must be at least 10 characters');
            return;
        }

        setError('');
        setIsLoading(true);

        try {
            await createPost({
                authorId: user._id,
                content: content.trim(),
                category,
                imageUrl: mediaType === 'image' ? mediaUrl : undefined,
                videoUrl: mediaType === 'video' ? mediaUrl : undefined,
                postType: postType || 'general',
                questionContext: postType === 'question' ? questionContext : undefined,
                scripture: postType === 'prayer' ? scripture : undefined,
            });

            setContent('');
            setMediaUrl('');
            setMediaType(null);
            onSuccess?.();
        } catch (err) {
            setError(err instanceof Error ? err.message : 'Failed to create post');
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <motion.form
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            onSubmit={handleSubmit}
            className={variant === 'bottom-sheet' ? '' : 'glass rounded-xl p-4 md:p-6 shadow-lg'}
        >
            {variant === 'default' && (
                <div className="flex items-center gap-2 mb-4">
                    <Sparkles className="w-5 h-5 text-primary-500" />
                    <h3 className="text-xl font-semibold text-light-text dark:text-dark-text">
                        {postType === 'question' ? 'Ask a Question' :
                            postType === 'prayer' ? 'Share a Prayer Point' :
                                'Share Your Learning'}
                    </h3>
                </div>
            )}

            {error && (
                <div className="mb-4 p-3 rounded-lg bg-red-50 dark:bg-red-950 border border-red-200 dark:border-red-800">
                    <p className="text-sm text-red-700 dark:text-red-300">{error}</p>
                </div>
            )}

            <div className="space-y-4">
                {/* Category Selection - Different for questions vs teachings */}
                {!defaultCategory && (
                    <>
                        {/* Question Specific Context Selection */}
                        {postType === 'question' && (
                            <div className="mb-1">
                                <label className="block text-sm font-medium mb-2 text-light-text dark:text-dark-text">
                                    Question Context
                                </label>
                                <div className="grid grid-cols-2 gap-3">
                                    <label className={`flex items-center gap-3 p-3 rounded-xl border cursor-pointer transition-all ${questionContext === 'personal_experience'
                                        ? 'bg-blue-500/10 border-blue-500/50'
                                        : 'bg-light-card dark:bg-dark-card border-light-border dark:border-dark-border hover:border-blue-500/30'
                                        }`}>
                                        <div className={`w-4 h-4 rounded-full border flex items-center justify-center ${questionContext === 'personal_experience' ? 'border-blue-500' : 'border-gray-400'}`}>
                                            {questionContext === 'personal_experience' && <div className="w-2 h-2 rounded-full bg-blue-500" />}
                                        </div>
                                        <input
                                            type="radio"
                                            name="questionContext"
                                            value="personal_experience"
                                            checked={questionContext === 'personal_experience'}
                                            onChange={() => setQuestionContext('personal_experience')}
                                            className="hidden"
                                        />
                                        <span className={`text-sm font-medium ${questionContext === 'personal_experience' ? 'text-blue-600 dark:text-blue-400' : 'text-light-text dark:text-dark-text'}`}>
                                            Personal
                                        </span>
                                    </label>

                                    <label className={`flex items-center gap-3 p-3 rounded-xl border cursor-pointer transition-all ${questionContext === 'about_someone_else'
                                        ? 'bg-purple-500/10 border-purple-500/50'
                                        : 'bg-light-card dark:bg-dark-card border-light-border dark:border-dark-border hover:border-purple-500/30'
                                        }`}>
                                        <div className={`w-4 h-4 rounded-full border flex items-center justify-center ${questionContext === 'about_someone_else' ? 'border-purple-500' : 'border-gray-400'}`}>
                                            {questionContext === 'about_someone_else' && <div className="w-2 h-2 rounded-full bg-purple-500" />}
                                        </div>
                                        <input
                                            type="radio"
                                            name="questionContext"
                                            value="about_someone_else"
                                            checked={questionContext === 'about_someone_else'}
                                            onChange={() => setQuestionContext('about_someone_else')}
                                            className="hidden"
                                        />
                                        <span className={`text-sm font-medium ${questionContext === 'about_someone_else' ? 'text-purple-600 dark:text-purple-400' : 'text-light-text dark:text-dark-text'}`}>
                                            Someone Else
                                        </span>
                                    </label>
                                </div>
                            </div>
                        )}

                        {/* General Post Category Selection (Hidden for Question/Prayer) */}
                        {postType !== 'question' && postType !== 'prayer' && (
                            <div>
                                <label className="block text-sm font-medium mb-2 text-light-text dark:text-dark-text">
                                    Teaching Category
                                </label>
                                <div className="flex gap-2">
                                    <button
                                        type="button"
                                        onClick={() => setCategory('Ancient Culture')}
                                        className={`flex-1 px-4 py-2 rounded-lg font-medium transition-all ${category === 'Ancient Culture'
                                            ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                                            : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border'
                                            }`}
                                    >
                                        Ancient
                                    </button>
                                    <button
                                        type="button"
                                        onClick={() => setCategory('Wisdom of The Now')}
                                        className={`flex-1 px-4 py-2 rounded-lg font-medium transition-all ${category === 'Wisdom of The Now'
                                            ? 'bg-secondary-500 text-white shadow-lg shadow-secondary-500/30'
                                            : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border'
                                            }`}
                                    >
                                        Wisdom
                                    </button>
                                </div>
                            </div>
                        )}
                    </>
                )}

                {/* Scripture Input (Prayer Only) */}
                {postType === 'prayer' && (
                    <div className="mb-1">
                        <label className="block text-sm font-medium mb-2 text-light-text dark:text-dark-text">
                            Scripture Reference <span className="text-xs text-light-muted dark:text-dark-muted font-normal ml-1">(Optional)</span>
                        </label>
                        <input
                            type="text"
                            value={scripture}
                            onChange={(e) => setScripture(e.target.value)}
                            placeholder="e.g. Philippians 4:6-7"
                            className="w-full px-4 py-3 rounded-xl border border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg text-light-text dark:text-dark-text placeholder:text-light-muted dark:placeholder:text-dark-muted focus:outline-none focus:ring-2 focus:ring-purple-500/50 focus:border-purple-500 transition-all"
                        />
                    </div>
                )}

                {/* Content Input */}
                <div>
                    <label className="block text-sm font-medium mb-2 text-light-text dark:text-dark-text">
                        {postType === 'question' ? 'Your Question' :
                            postType === 'prayer' ? 'Your Prayer Point' :
                                'What did you learn?'}
                    </label>
                    <textarea
                        value={content}
                        onChange={(e) => setContent(e.target.value)}
                        placeholder={postType === 'question' ? "Type your question here..." :
                            postType === 'prayer' ? "Share your prayer request..." :
                                "Share your insights, revelations, and key takeaways from today's teaching session..."}
                        rows={6}
                        className="w-full px-4 py-3 rounded-lg border border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg text-light-text dark:text-dark-text placeholder:text-light-muted dark:placeholder:text-dark-muted focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent resize-none"
                        required
                    />
                    <p className="mt-1 text-xs text-light-muted dark:text-dark-muted">
                        {content.length} characters
                    </p>
                </div>

                {/* Media Upload */}
                <div>
                    <label className="block text-sm font-medium mb-2 text-light-text dark:text-dark-text">
                        Add Media (Optional)
                    </label>
                    <MediaUpload
                        onUploadComplete={handleMediaUpload}
                        allowVideo={user?.role === 'admin'}
                        currentMedia={mediaUrl ? { url: mediaUrl, type: mediaType! } : undefined}
                    />
                    {user?.role === 'admin' && (
                        <p className="mt-1 text-xs text-accent-500">
                             As an admin, you can upload both images and videos
                        </p>
                    )}
                </div>

                <div className="flex items-center justify-between">
                    <p className="text-xs text-light-muted dark:text-dark-muted">
                         Be among the first 5 to post and earn the <strong className="text-primary-500">SENT</strong> badge!
                    </p>
                    <Button
                        type="submit"
                        variant="primary"
                        isLoading={isLoading}
                        disabled={content.trim().length < 10}
                    >
                        Post
                    </Button>
                </div>
            </div>
        </motion.form>
    );
}
