import { motion } from 'framer-motion';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { Mail, Trash2, Eye, EyeOff } from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';
import { Id } from '../../convex/_generated/dataModel';
import { useState } from 'react';
import { PageHeaderSkeleton, ListSkeleton } from '../components/ui/Skeletons';

export function AdminContacts({ isEmbedded = false }: { isEmbedded?: boolean }) {
    const { user, isLoading: authLoading } = useAuth();
    const contacts = useQuery(api.contacts.getContacts, user ? { adminId: user._id } : 'skip');
    const markAsRead = useMutation(api.contacts.markContactAsRead);
    const deleteContact = useMutation(api.contacts.deleteContact);
    const [deletingId, setDeletingId] = useState<Id<'contacts'> | null>(null);

    if (authLoading) {
        return (
            <div className="min-h-screen py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-6xl">
                    <PageHeaderSkeleton />
                    <ListSkeleton count={3} />
                </div>
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    if (user.role !== 'admin' && user.role !== 'super_admin') {
        return <Navigate to="/feed" replace />;
    }

    const handleMarkAsRead = async (contactId: Id<'contacts'>) => {
        try {
            await markAsRead({ adminId: user._id, contactId });
        } catch (error) {
            console.error('Failed to mark as read:', error);
        }
    };

    const handleDelete = async (contactId: Id<'contacts'>) => {
        if (!confirm('Are you sure you want to delete this message?')) return;

        setDeletingId(contactId);
        try {
            await deleteContact({ adminId: user._id, contactId });
        } catch (error) {
            console.error('Failed to delete contact:', error);
        } finally {
            setDeletingId(null);
        }
    };

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="space-y-6"
        >
            {/* Header */}
            {!isEmbedded && (
                <div className="flex items-center justify-between">
                    <div>
                        <h1 className="text-3xl md:text-4xl font-bold text-primary-600 dark:text-primary-400 mb-2">
                            Contact Messages
                        </h1>
                        <p className="text-light-muted dark:text-dark-muted">
                            {contacts?.length || 0} total messages
                        </p>
                    </div>
                    <Mail className="w-12 h-12 text-primary-500" />
                </div>
            )}

            {/* Messages List */}
            {!contacts ? (
                <ListSkeleton count={3} />
            ) : (
                <div className="space-y-4">
                    {isEmbedded && (
                        <p className="text-light-muted dark:text-dark-muted mb-4 font-bold text-sm">
                            {contacts.length || 0} total messages
                        </p>
                    )}
                    {contacts.length === 0 ? (
                        <div className="glass rounded-xl p-12 text-center">
                            <Mail className="w-16 h-16 mx-auto mb-4 text-light-muted dark:text-dark-muted opacity-50" />
                            <p className="text-light-muted dark:text-dark-muted text-lg">
                                No contact messages yet
                            </p>
                        </div>
                    ) : (
                        <div className="space-y-4">
                            {contacts.map((contact: any) => (
                                <motion.div
                                    key={contact._id}
                                    layout
                                    initial={{ opacity: 0, scale: 0.95 }}
                                    animate={{ opacity: 1, scale: 1 }}
                                    exit={{ opacity: 0, scale: 0.95 }}
                                    className={`glass rounded-xl p-6 border ${contact.read
                                        ? 'border-light-border dark:border-dark-border'
                                        : 'border-primary-500/50 bg-primary-50/30 dark:bg-primary-900/10'
                                        }`}
                                >
                                    <div className="flex items-start justify-between gap-4 mb-4">
                                        <div className="flex-1">
                                            <div className="flex items-center gap-3 mb-2">
                                                <h3 className="text-lg font-bold text-light-text dark:text-dark-text">
                                                    {contact.subject}
                                                </h3>
                                                {!contact.read && (
                                                    <span className="px-2 py-0.5 rounded-full bg-primary-500 text-white text-xs font-bold">
                                                        NEW
                                                    </span>
                                                )}
                                            </div>
                                            <div className="flex items-center gap-4 text-sm text-light-muted dark:text-dark-muted">
                                                <span className="font-semibold">{contact.name}</span>
                                                <span></span>
                                                <a href={`mailto:${contact.email}`} className="hover:text-primary-500 transition-colors">
                                                    {contact.email}
                                                </a>
                                                <span></span>
                                                <span>{formatDistanceToNow(contact.createdAt, { addSuffix: true })}</span>
                                            </div>
                                        </div>

                                        <div className="flex items-center gap-2">
                                            <button
                                                onClick={() => handleMarkAsRead(contact._id)}
                                                disabled={contact.read}
                                                className={`p-2 rounded-lg transition-all ${contact.read
                                                    ? 'text-light-muted dark:text-dark-muted cursor-not-allowed'
                                                    : 'text-primary-500 hover:bg-primary-100 dark:hover:bg-primary-900/20'
                                                    }`}
                                                title={contact.read ? 'Already read' : 'Mark as read'}
                                            >
                                                {contact.read ? (
                                                    <EyeOff className="w-5 h-5" />
                                                ) : (
                                                    <Eye className="w-5 h-5" />
                                                )}
                                            </button>

                                            <button
                                                onClick={() => handleDelete(contact._id)}
                                                disabled={deletingId === contact._id}
                                                className="p-2 rounded-lg text-red-500 hover:bg-red-100 dark:hover:bg-red-900/20 transition-all disabled:opacity-50"
                                                title="Delete message"
                                            >
                                                <Trash2 className="w-5 h-5" />
                                            </button>
                                        </div>
                                    </div>

                                    <p className="text-light-text dark:text-dark-text whitespace-pre-wrap">
                                        {contact.message}
                                    </p>
                                </motion.div>
                            ))}
                        </div>
                    )}
                </div>
            )}
        </motion.div>
    );
}
