import { useParams, useNavigate } from 'react-router-dom';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Button } from '@/components/ui/Button';
import { ChevronLeft, Calendar, User, Megaphone } from 'lucide-react';
import { Badge } from '@/components/ui/Badge';
import { Card, CardContent } from '@/components/ui/Card';
import { PageHeaderSkeleton } from '@/components/ui/Skeletons';

export function AnnouncementDetails() {
    const { id } = useParams();
    const navigate = useNavigate();
    const announcement = useQuery(api.announcements.getAnnouncementById, id ? { id: id as any } : 'skip');

    if (announcement === undefined) {
        return (
            <div className="container mx-auto px-4 py-8 max-w-4xl">
                <PageHeaderSkeleton />
                <div className="h-64 bg-light-card dark:dark-card rounded-3xl animate-pulse mt-8" />
            </div>
        );
    }

    if (!announcement) {
        return (
            <div className="container mx-auto px-4 py-32 text-center">
                <h1 className="text-2xl font-bold mb-4">Announcement not found</h1>
                <Button onClick={() => navigate('/feed')}>Back to Feed</Button>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-light-bg dark:bg-dark-bg py-8">
            <div className="container mx-auto px-4 max-w-4xl">
                <button
                    onClick={() => navigate(-1)}
                    className="flex items-center gap-2 text-light-muted dark:text-dark-muted hover:text-primary-500 transition-colors mb-8 group"
                >
                    <div className="p-2 rounded-xl bg-light-card dark:bg-dark-card border border-light-border dark:border-dark-border group-hover:border-primary-500/50">
                        <ChevronLeft className="w-5 h-5" />
                    </div>
                    <span className="font-bold text-sm uppercase tracking-widest">Back</span>
                </button>

                <div className="grid grid-cols-1 gap-8">
                    {/* Hero Section */}
                    {announcement.imageUrl && (
                        <div className="w-full h-80 md:h-[400px] rounded-[40px] overflow-hidden border border-light-border/20 dark:border-dark-border/20 shadow-2xl relative">
                            <img
                                src={announcement.imageUrl}
                                alt={announcement.title}
                                className="w-full h-full object-cover"
                            />
                            <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent" />
                            <div className="absolute bottom-8 left-8 right-8">
                                <Badge variant="primary" className="mb-4">Official Announcement</Badge>
                                <h1 className="text-3xl md:text-5xl font-black text-white font-display leading-tight">
                                    {announcement.title}
                                </h1>
                            </div>
                        </div>
                    )}

                    {!announcement.imageUrl && (
                        <div className="mb-4">
                            <Badge variant="primary" className="mb-4">Official Announcement</Badge>
                            <h1 className="text-3xl md:text-5xl font-black text-light-text dark:text-dark-text font-display leading-tight">
                                {announcement.title}
                            </h1>
                        </div>
                    )}

                    {/* Metadata Card */}
                    <Card glass className="border-primary-500/10">
                        <CardContent className="p-6">
                            <div className="flex flex-wrap items-center gap-6">
                                <div className="flex items-center gap-3">
                                    <div className="w-10 h-10 rounded-xl bg-primary-500/10 flex items-center justify-center text-primary-500">
                                        <User className="w-5 h-5" />
                                    </div>
                                    <div>
                                        <p className="text-[10px] font-bold text-light-muted uppercase tracking-wider leading-none mb-1">Posted By</p>
                                        <p className="font-bold text-sm capitalize">{announcement.creatorRole?.replace('_', ' ') || 'Community Admin'}</p>
                                    </div>
                                </div>

                                <div className="flex items-center gap-3">
                                    <div className="w-10 h-10 rounded-xl bg-emerald-500/10 flex items-center justify-center text-emerald-500">
                                        <Calendar className="w-5 h-5" />
                                    </div>
                                    <div>
                                        <p className="text-[10px] font-bold text-light-muted uppercase tracking-wider leading-none mb-1">Date Published</p>
                                        <p className="font-bold text-sm">
                                            {new Date(announcement._creationTime).toLocaleDateString('en-US', {
                                                month: 'long',
                                                day: 'numeric',
                                                year: 'numeric'
                                            })}
                                        </p>
                                    </div>
                                </div>

                                <div className="flex items-center gap-3">
                                    <div className="w-10 h-10 rounded-xl bg-orange-500/10 flex items-center justify-center text-orange-500">
                                        <Megaphone className="w-5 h-5" />
                                    </div>
                                    <div>
                                        <p className="text-[10px] font-bold text-light-muted uppercase tracking-wider leading-none mb-1">Category</p>
                                        <p className="font-bold text-sm">General Update</p>
                                    </div>
                                </div>
                            </div>
                        </CardContent>
                    </Card>

                    {/* Content Section */}
                    <div className="space-y-6">
                        <div className="prose prose-lg dark:prose-invert max-w-none">
                            <p className="text-light-text dark:text-dark-text text-xl md:text-2xl leading-relaxed font-medium">
                                {announcement.description}
                            </p>

                        </div>


                        <div className="pt-12 flex items-center justify-center">
                            <Button
                                variant="primary"
                                size="lg"
                                onClick={() => navigate('/feed')}
                                className="px-12 rounded-2xl shadow-xl shadow-primary-500/20"
                            >
                                Return to Feed
                            </Button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
