import { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Check as CheckIcon, AlertTriangle, CheckCircle2 } from 'lucide-react';
import { ConfirmationModal } from '@/components/ui/ConfirmationModal';
import { AnimatePresence, motion } from 'framer-motion';

export function AdminPolls() {
    const { user } = useAuth();
    // Assuming we have a query for poll history or all polls
    const polls = useQuery(api.polls.getAdminPolls, { limit: 20 });
    const createPoll = useMutation(api.polls.createPoll);
    const closePoll = useMutation(api.polls.closePoll);

    // Form State
    const [question, setQuestion] = useState('');
    const [options, setOptions] = useState<string[]>(['', '', '', '']); // Default 4 options
    const [correctIndex, setCorrectIndex] = useState<number>(0);
    const [duration, setDuration] = useState<number>(48); // Hours
    const [isCreating, setIsCreating] = useState(false);
    const [showSuccess, setShowSuccess] = useState(false);
    const [pollToClose, setPollToClose] = useState<any>(null);
    const [errorMsg, setErrorMsg] = useState<string | null>(null);

    const handleOptionChange = (index: number, value: string) => {
        const newOptions = [...options];
        newOptions[index] = value;
        setOptions(newOptions);
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setErrorMsg(null);
        if (!user || isCreating) return;

        // Validation
        if (!question.trim()) {
            setErrorMsg("Question is required");
            return;
        }
        if (options.some(o => !o.trim())) {
            setErrorMsg("All options are required");
            return;
        }

        setIsCreating(true);
        try {
            await createPoll({
                question,
                options,
                correctOptionIndex: correctIndex,
                durationHours: duration,
                adminId: user._id
            });

            setShowSuccess(true);
            setTimeout(() => setShowSuccess(false), 5000);

            // Reset form
            setQuestion('');
            setOptions(['', '', '', '']);
            setCorrectIndex(0);
        } catch (err: any) {
            console.error(err);
            setErrorMsg(err.message || "Failed to create poll");
        } finally {
            setIsCreating(false);
        }
    };

    const handleClose = async () => {
        if (!pollToClose || !user) return;
        try {
            await closePoll({ pollId: pollToClose, adminId: user._id });
            setPollToClose(null);
        } catch (e: any) {
            setErrorMsg(e.message || "Failed to close poll");
        }
    };

    if (polls === undefined) return <div className="p-8">Loading polls...</div>;

    return (
        <div className="space-y-8">
            <div>
                <h1 className="text-3xl font-display font-bold text-light-text dark:text-dark-text mb-2">
                    Weekly Poll Management
                </h1>
                <p className="text-light-muted dark:text-dark-muted">
                    Create and manage wisdom polls for the Silver Pool game.
                </p>
            </div>

            <div className="grid lg:grid-cols-2 gap-8">
                {/* Create Poll Form */}
                <Card glass>
                    <CardHeader>
                        <CardTitle>Create New Poll</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <form onSubmit={handleSubmit} className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium mb-1">Question</label>
                                <Input
                                    value={question}
                                    onChange={e => setQuestion(e.target.value)}
                                    placeholder="Enter wisdom question..."
                                    required
                                />
                            </div>

                            <div className="space-y-2">
                                <label className="block text-sm font-medium">Options</label>
                                {options.map((opt, idx) => (
                                    <div key={idx} className="flex gap-2 items-center">
                                        <div
                                            className={`w-6 h-6 rounded-full border flex items-center justify-center cursor-pointer transition-colors ${correctIndex === idx ? 'bg-green-500 border-green-500 text-white' : 'border-gray-300 hover:border-primary-500'}`}
                                            onClick={() => setCorrectIndex(idx)}
                                            title="Mark as correct answer"
                                        >
                                            {correctIndex === idx && <CheckIcon className="w-4 h-4" />}
                                        </div>
                                        <Input
                                            value={opt}
                                            onChange={e => handleOptionChange(idx, e.target.value)}
                                            placeholder={`Option ${idx + 1}`}
                                            required
                                        />
                                    </div>
                                ))}
                                <p className="text-xs text-light-muted">Click the circle to mark the correct answer.</p>
                            </div>

                            <div>
                                <label className="block text-sm font-medium mb-1">Duration (Hours)</label>
                                <Input
                                    type="number"
                                    value={duration}
                                    onChange={e => setDuration(Number(e.target.value))}
                                    min={1}
                                    max={168}
                                />
                            </div>

                            <AnimatePresence>
                                {showSuccess && (
                                    <motion.div
                                        initial={{ opacity: 0, y: -10 }}
                                        animate={{ opacity: 1, y: 0 }}
                                        exit={{ opacity: 0, y: -10 }}
                                        className="p-4 bg-green-500/10 border border-green-500/20 rounded-2xl flex items-center gap-3 text-green-600 dark:text-green-400 mb-4"
                                    >
                                        <CheckCircle2 className="w-5 h-5 flex-shrink-0" />
                                        <span className="text-sm font-medium">Poll created and broadcasted successfully!</span>
                                    </motion.div>
                                )}

                                {errorMsg && (
                                    <motion.div
                                        initial={{ opacity: 0, y: -10 }}
                                        animate={{ opacity: 1, y: 0 }}
                                        exit={{ opacity: 0, y: -10 }}
                                        className="p-4 bg-red-500/10 border border-red-500/20 rounded-2xl flex items-center gap-3 text-red-600 dark:text-red-400 mb-4"
                                    >
                                        <AlertTriangle className="w-5 h-5 flex-shrink-0" />
                                        <span className="text-sm font-medium">{errorMsg}</span>
                                    </motion.div>
                                )}
                            </AnimatePresence>

                            <Button type="submit" isLoading={isCreating} className="w-full h-12 rounded-2xl font-bold tracking-wide">
                                Create Poll
                            </Button>
                        </form>
                    </CardContent>
                </Card>

                {/* Poll History */}
                <div className="space-y-4">
                    <h3 className="text-xl font-bold">Recent Polls</h3>
                    {polls.length === 0 ? (
                        <p className="text-light-muted">No polls found.</p>
                    ) : (
                        polls.map((poll: any) => {
                            const isActive = poll.status === 'active' && Date.now() < poll.expiresAt;
                            return (
                                <Card key={poll._id} className="border border-light-border dark:border-dark-border group overflow-hidden">
                                    <div className="p-4">
                                        <div className="flex justify-between items-start mb-2">
                                            <span className={`px-2 py-0.5 rounded text-[10px] font-bold uppercase transition-colors ${isActive ? 'bg-emerald-500/10 text-emerald-500 border border-emerald-500/20' : 'bg-gray-500/10 text-gray-500 border border-gray-500/20'}`}>
                                                {isActive ? 'Active' : 'Closed'}
                                            </span>
                                            <span className="text-[10px] text-light-muted font-mono uppercase tracking-widest">
                                                {new Date(poll.createdAt).toLocaleDateString()}
                                            </span>
                                        </div>
                                        <h4 className="font-bold mb-2 line-clamp-2 text-sm leading-relaxed">{poll.question}</h4>
                                        <div className="flex justify-between items-center text-sm text-light-muted mb-2">
                                            <span className="text-xs font-medium">{poll.totalVotes} Members Voted</span>
                                            {isActive && (
                                                <Button size="sm" variant="danger" className="h-8 rounded-xl text-[10px] font-bold tracking-widest" onClick={() => setPollToClose(poll._id)}>
                                                    CLOSE POLL
                                                </Button>
                                            )}
                                        </div>
                                        {poll.totalVotes > 0 && (
                                            <div className="flex gap-4 text-[10px] pt-3 mt-1 border-t border-light-border dark:border-dark-border">
                                                <div className="flex items-center gap-1.5 text-emerald-600 dark:text-emerald-400 font-bold uppercase tracking-wider">
                                                    <CheckIcon className="w-3 h-3" />
                                                    {poll.options.find((o: any) => o.id === poll.correctOptionId)?.voteCount || 0} Correct
                                                </div>
                                                <div className="flex items-center gap-1.5 text-red-500 font-bold uppercase tracking-wider">
                                                    <AlertTriangle className="w-3 h-3" />
                                                    {poll.totalVotes - (poll.options.find((o: any) => o.id === poll.correctOptionId)?.voteCount || 0)} Incorrect
                                                </div>
                                            </div>
                                        )}
                                    </div>
                                </Card>
                            )
                        })
                    )}
                </div>
            </div>

            <ConfirmationModal
                isOpen={!!pollToClose}
                onClose={() => setPollToClose(null)}
                onConfirm={handleClose}
                title="Close Poll"
                description="Are you sure you want to end this poll early? No more votes will be accepted."
                confirmText="Close Poll"
                variant="danger"
            />
        </div>
    );
}
