import React, { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Modal } from '@/components/ui/Modal';
import { Button } from '@/components/ui/Button';
import { Id } from '../../../convex/_generated/dataModel';
import { useAuth } from '@/hooks/useAuth';
import { Search, User } from 'lucide-react';

interface TaskAssignModalProps {
    isOpen: boolean;
    onClose: () => void;
    postId: Id<"posts">;
}

export function TaskAssignModal({ isOpen, onClose, postId }: TaskAssignModalProps) {
    const { user } = useAuth();
    const [searchQuery, setSearchQuery] = useState('');
    const [selectedUser, setSelectedUser] = useState<Id<"users"> | null>(null);
    const [instructions, setInstructions] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    // Fetch all users for selection (filtered by search client-side for now)
    const allUsers = useQuery(
        api.users.getAllUsers,
        user && (user.role === 'admin' || user.role === 'super_admin')
            ? { adminId: user._id }
            : 'skip'
    );

    const createAssignment = useMutation(api.assignments.createAssignment);

    const filteredUsers = allUsers?.filter((u: any) =>
        u.name?.toLowerCase().includes(searchQuery.toLowerCase()) ||
        u.email?.toLowerCase().includes(searchQuery.toLowerCase())
    ).slice(0, 5); // Limit to top 5 matches

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!user || !selectedUser || !instructions.trim()) return;

        setIsSubmitting(true);
        try {
            await createAssignment({
                adminId: user._id,
                userId: selectedUser,
                postId,
                instructions: instructions.trim(),
            });
            onClose();
            // Reset form
            setSelectedUser(null);
            setInstructions('');
            setSearchQuery('');
        } catch (error) {
            console.error("Failed to assign task:", error);
            alert("Failed to assign task. Please try again.");
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <Modal isOpen={isOpen} onClose={onClose} title="Assign Task">
            <form onSubmit={handleSubmit} className="space-y-4">
                <div>
                    <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-1">
                        Select Member
                    </label>
                    <div className="relative">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-light-muted dark:text-dark-muted" />
                        <input
                            type="text"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            placeholder="Search by name or email..."
                            className="w-full pl-9 pr-4 py-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border focus:ring-2 focus:ring-primary-500"
                        />
                    </div>

                    {/* User Selection List */}
                    {searchQuery && filteredUsers && (
                        <div className="mt-2 space-y-2 max-h-40 overflow-y-auto">
                            {filteredUsers.map((u: any) => (
                                <div
                                    key={u._id}
                                    onClick={() => setSelectedUser(u._id)}
                                    className={`p-2 rounded-lg cursor-pointer flex items-center gap-2 transition-colors ${selectedUser === u._id
                                        ? 'bg-primary-500 text-white'
                                        : 'bg-light-card dark:bg-dark-card hover:bg-light-bg dark:hover:bg-dark-bg text-light-text dark:text-dark-text'
                                        }`}
                                >
                                    <User className="w-4 h-4" />
                                    <div className="flex-1">
                                        <p className="text-sm font-medium">{u.name}</p>
                                        <p className="text-xs opacity-80">{u.email}</p>
                                    </div>
                                    {selectedUser === u._id && <div className="w-2 h-2 bg-white rounded-full" />}
                                </div>
                            ))}
                        </div>
                    )}
                    {selectedUser && !searchQuery && (
                        <div className="mt-2 p-2 rounded-lg bg-primary-100 dark:bg-primary-900 border border-primary-500 text-primary-700 dark:text-primary-300 text-sm">
                            Selected: {allUsers?.find((u: any) => u._id === selectedUser)?.name}
                        </div>
                    )}
                </div>

                <div>
                    <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-1">
                        Instructions
                    </label>
                    <textarea
                        value={instructions}
                        onChange={(e) => setInstructions(e.target.value)}
                        placeholder="Describe the task for this member..."
                        className="w-full min-h-[100px] p-3 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border focus:ring-2 focus:ring-primary-500 text-light-text dark:text-dark-text"
                    />
                </div>

                <div className="flex justify-end gap-2 pt-4">
                    <Button type="button" variant="ghost" onClick={onClose}>Cancel</Button>
                    <Button
                        type="submit"
                        variant="primary"
                        disabled={!selectedUser || !instructions.trim() || isSubmitting}
                    >
                        {isSubmitting ? 'Assigning...' : 'Assign Task'}
                    </Button>
                </div>
            </form>
        </Modal>
    );
}
