import { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/Card';
import { Button } from '../ui/Button';
import { Badge } from '../ui/Badge';
import { useToast } from '@/context/ToastContext';
import {
    Check,
    X as XIcon,
    Eye,
    User,
    Clock,
    AlertCircle,
    CheckCircle
} from 'lucide-react';
import { ImageModal } from '../ui/ImageModal';

interface PendingSubmissionsProps {
    user: any;
}

export function PendingSubmissions({ user }: PendingSubmissionsProps) {
    const [isActioning, setIsActioning] = useState<string | null>(null);
    const submissions = useQuery(api.finance.getPendingSubmissions);
    const approvePayment = useMutation(api.finance.approvePaymentSubmission);
    const rejectPayment = useMutation(api.finance.rejectPaymentSubmission);
    const [selectedProof, setSelectedProof] = useState<{ url: string; title: string } | null>(null);
    const { showToast } = useToast();

    if (submissions === undefined) {
        return (
            <Card className="animate-pulse">
                <CardHeader>
                    <div className="h-6 w-48 bg-light-muted/10 rounded" />
                </CardHeader>
                <CardContent>
                    <div className="space-y-4">
                        {[1, 2].map(i => (
                            <div key={i} className="h-40 bg-light-muted/10 rounded-2xl" />
                        ))}
                    </div>
                </CardContent>
            </Card>
        );
    }

    const formatCurrency = (amount: number) => {
        return new Intl.NumberFormat('en-NG', {
            style: 'currency',
            currency: 'NGN',
        }).format(amount);
    };

    const getMonthName = (month: number) => {
        return new Date(2024, month - 1).toLocaleDateString('en-US', { month: 'long' });
    };

    const handleApprove = async (submissionId: any) => {
        if (!confirm('Approve this payment? It will increase the total balance and update member dues.')) return;

        setIsActioning(submissionId);
        try {
            await approvePayment({
                adminId: user._id,
                submissionId,
            });
            showToast('success', 'Payment approved successfully!');
        } catch (error: any) {
            console.error('Failed to approve:', error);
            showToast('error', error.message || 'Failed to approve');
        } finally {
            setIsActioning(null);
        }
    };

    const handleReject = async (submissionId: any) => {
        const reason = prompt('Please enter rejection reason:');
        if (reason === null) return;

        setIsActioning(submissionId);
        try {
            await rejectPayment({
                adminId: user._id,
                submissionId,
                reason: reason || 'Information provided is incomplete/incorrect',
            });
            showToast('success', 'Payment rejected.');
        } catch (error: any) {
            console.error('Failed to reject:', error);
            showToast('error', error.message || 'Failed to reject');
        } finally {
            setIsActioning(null);
        }
    };

    return (
        <Card>
            <CardHeader className="py-4 px-4 md:px-6">
                <CardTitle className="flex items-center gap-2 text-lg md:text-xl">
                    <Clock className="w-5 h-5 text-primary-500" />
                    Pending Payment Submissions
                    {submissions && submissions.length > 0 && (
                        <Badge className="ml-2 bg-primary-500 text-[10px] h-5 min-w-5 flex items-center justify-center">{submissions.length}</Badge>
                    )}
                </CardTitle>
            </CardHeader>
            <CardContent>
                <div className="space-y-4">
                    {submissions && submissions.length > 0 ? (
                        submissions.map((sub: any) => (
                            <div
                                key={sub._id}
                                className="p-4 rounded-2xl border border-light-border dark:border-dark-border bg-light-bg/50 dark:bg-dark-bg/50 flex flex-col md:flex-row gap-6"
                            >
                                {/* Member & Amount Info */}
                                <div className="flex-1 space-y-3 md:space-y-4">
                                    <div className="flex items-center gap-3">
                                        <div className="w-9 h-9 md:w-10 md:h-10 rounded-full bg-primary-500/10 flex items-center justify-center flex-shrink-0">
                                            <User className="w-4 h-4 md:w-5 md:h-5 text-primary-500" />
                                        </div>
                                        <div className="min-w-0">
                                            <p className="text-sm font-black truncate">{sub.userName}</p>
                                            <p className="text-[10px] md:text-xs text-light-muted">Submitted {new Date(sub.createdAt).toLocaleDateString()}</p>
                                        </div>
                                    </div>

                                    <div className="grid grid-cols-2 gap-3 md:gap-4">
                                        <div className="p-2 md:p-3 rounded-xl bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border">
                                            <p className="text-[9px] md:text-[10px] font-bold text-light-muted uppercase mb-0.5 md:mb-1">Amount</p>
                                            <p className="text-base md:text-lg font-black text-emerald-500 truncate">{formatCurrency(sub.amount)}</p>
                                        </div>
                                        <div className="p-2 md:p-3 rounded-xl bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border">
                                            <p className="text-[9px] md:text-[10px] font-bold text-light-muted uppercase mb-0.5 md:mb-1">Period</p>
                                            <p className="font-bold text-xs md:text-sm truncate">{getMonthName(sub.month)} {sub.year}</p>
                                        </div>
                                    </div>
                                </div>

                                {/* Proof Preview */}
                                <div className="md:w-48 shrink-0">
                                    <div className="relative group rounded-xl overflow-hidden border border-light-border dark:border-dark-border aspect-video md:aspect-square bg-black/5">
                                        {sub.proofUrl ? (
                                            <img
                                                src={sub.proofUrl}
                                                alt="Proof"
                                                className="w-full h-full object-cover"
                                            />
                                        ) : (
                                            <div className="w-full h-full flex items-center justify-center">
                                                <AlertCircle className="w-6 h-6 text-light-muted" />
                                            </div>
                                        )}
                                        <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                                            <button
                                                onClick={() => setSelectedProof({ url: sub.proofUrl, title: `Proof - ${sub.userName}` })}
                                                className="p-2 rounded-full bg-white/20 backdrop-blur-md text-white border border-white/20 hover:scale-110 transition-transform"
                                            >
                                                <Eye className="w-5 h-5" />
                                            </button>
                                        </div>
                                    </div>
                                </div>

                                {/* Actions */}
                                <div className="md:w-40 shrink-0 flex flex-row md:flex-col gap-2">
                                    <Button
                                        onClick={() => handleApprove(sub._id)}
                                        className="flex-1 bg-emerald-500 hover:bg-emerald-600 text-white font-bold h-11 md:h-12 text-xs md:text-sm"
                                        disabled={isActioning !== null}
                                    >
                                        <Check className="w-3.5 h-3.5 md:w-4 md:h-4 mr-1 md:mr-2" />
                                        Approve
                                    </Button>
                                    <Button
                                        onClick={() => handleReject(sub._id)}
                                        variant="outline"
                                        className="flex-1 border-rose-500/30 text-rose-500 hover:bg-rose-500/10 font-bold h-11 md:h-12 text-xs md:text-sm"
                                        disabled={isActioning !== null}
                                    >
                                        <XIcon className="w-3.5 h-3.5 md:w-4 md:h-4 mr-1 md:mr-2" />
                                        Reject
                                    </Button>
                                </div>
                            </div>
                        ))
                    ) : (
                        <div className="text-center py-12 text-light-muted flex flex-col items-center gap-2">
                            <CheckCircle className="w-8 h-8 text-emerald-500/50" />
                            <p>No pending submissions to review</p>
                        </div>
                    )}
                </div>
            </CardContent>
            {/* Modals */}
            <ImageModal
                isOpen={!!selectedProof}
                onClose={() => setSelectedProof(null)}
                imageUrl={selectedProof?.url || ''}
                title={selectedProof?.title}
            />
        </Card>
    );
}
