import React, { useState, useRef } from 'react';
import { Camera, Loader2 } from 'lucide-react';
import { uploadToImageKit, validateImageFile } from '@/lib/imagekit';

interface CoverUploadProps {
    currentCoverUrl?: string;
    onUploadComplete: (url: string) => void;
}

export function CoverUpload({ currentCoverUrl, onUploadComplete }: CoverUploadProps) {
    const [isUploading, setIsUploading] = useState(false);
    const [previewUrl, setPreviewUrl] = useState(currentCoverUrl);
    const fileInputRef = useRef<HTMLInputElement>(null);

    const handleFileSelect = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file) return;
        
        if (!validateImageFile(file)) {
            alert('Please upload a valid image file');
            return;
        }

        const reader = new FileReader();
        reader.onloadend = () => setPreviewUrl(reader.result as string);
        reader.readAsDataURL(file);

        setIsUploading(true);
        try {
            // Determine folder or use default
            const url = await uploadToImageKit(file, 'covers');
            onUploadComplete(url);
            setPreviewUrl(url);
        } catch (error) {
            console.error('Upload failed:', error);
            setPreviewUrl(currentCoverUrl);
            alert('Failed to upload cover image');
        } finally {
            setIsUploading(false);
        }
    };

    return (
        <div className="relative w-full h-full group">
            {previewUrl ? (
                <img 
                    src={previewUrl} 
                    alt="Cover" 
                    className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-105" 
                />
            ) : (
                <div className="w-full h-full bg-gradient-to-r from-primary-500/20 to-secondary-500/20" />
            )}
            
            {/* Overlay Gradient for Text Contrast */}
            <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-60" />

            {isUploading && (
                <div className="absolute inset-0 flex items-center justify-center bg-black/40 backdrop-blur-sm z-20">
                   <Loader2 className="w-8 h-8 text-white animate-spin" />
                </div>
            )}

            <button 
                onClick={() => fileInputRef.current?.click()}
                className="absolute bottom-4 right-4 p-2.5 rounded-full bg-black/40 hover:bg-black/60 text-white backdrop-blur-md opacity-100 md:opacity-0 group-hover:opacity-100 transition-all transform hover:scale-110 z-20 border border-white/20"
                title="Change Cover Photo"
            >
                <Camera className="w-5 h-5" />
            </button>
            <input 
                type="file" 
                ref={fileInputRef} 
                className="hidden" 
                accept="image/*" 
                onChange={handleFileSelect} 
            />
        </div>
    );
}
