import { useParams } from 'react-router-dom';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Id } from '../../convex/_generated/dataModel';
import { BackButton } from '@/components/ui/BackButton';
import { Avatar } from '@/components/ui/Avatar';
import { Badge } from '@/components/ui/Badge';
import { Trophy, Medal, MessageSquare, Calendar } from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';
import { motion } from 'framer-motion';

export function PostResponses() {
    const { postId } = useParams<{ postId: string }>();
    const post = useQuery(api.posts.getPost, { postId: postId as Id<"posts"> });
    const responses = useQuery(api.responses.getPostResponses, { postId: postId as Id<"posts"> });

    if (!post || !responses) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    const tier1 = responses.slice(0, 5);
    const tier2 = responses.slice(5, 10);

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-3xl">
                <BackButton />

                {/* Post Context Header */}
                <div className="mb-8 p-6 glass rounded-2xl border border-primary-500/20 bg-gradient-to-br from-primary-500/5 to-transparent">
                    <div className="flex items-center gap-3 mb-4">
                        <Avatar src={post.author?.avatarUrl} alt={post.author?.name || 'Admin'} size="sm" />
                        <div>
                            <h2 className="font-bold text-light-text dark:text-dark-text">{post.author?.name}</h2>
                            <p className="text-xs text-light-muted dark:text-dark-muted flex items-center gap-1">
                                <Calendar className="w-3 h-3" />
                                {formatDistanceToNow(post.createdAt, { addSuffix: true })}
                            </p>
                        </div>
                    </div>
                    <p className="text-light-text dark:text-dark-text text-sm md:text-base whitespace-pre-wrap italic opacity-80">
                        "{post.content}"
                    </p>
                </div>

                <div className="flex items-center justify-between mb-6">
                    <h1 className="text-2xl font-bold text-gradient flex items-center gap-2">
                        <MessageSquare className="w-6 h-6 text-primary-500" />
                        Official Responses
                    </h1>
                    <Badge variant="primary" className="px-3 py-1">
                        {responses.length} Total
                    </Badge>
                </div>

                <div className="space-y-8">
                    {/* Tier 1: Gold */}
                    {tier1.length > 0 && (
                        <div className="space-y-4">
                            <div className="flex items-center gap-2 text-primary-600 dark:text-primary-400 font-bold uppercase tracking-wider text-xs">
                                <Trophy className="w-4 h-4" />
                                Top Tier Responders (1-5)
                            </div>
                            <div className="grid gap-4">
                                {tier1.map((response: any, index: number) => (
                                    <ResponseDetailCard
                                        key={response._id}
                                        response={response}
                                        rank={index + 1}
                                        tier="gold"
                                    />
                                ))}
                            </div>
                        </div>
                    )}

                    {/* Tier 2: Silver */}
                    {tier2.length > 0 && (
                        <div className="space-y-4">
                            <div className="flex items-center gap-2 text-secondary-600 dark:text-secondary-400 font-bold uppercase tracking-wider text-xs">
                                <Medal className="w-4 h-4" />
                                Second Tier Responders (6-10)
                            </div>
                            <div className="grid gap-4">
                                {tier2.map((response: any, index: number) => (
                                    <ResponseDetailCard
                                        key={response._id}
                                        response={response}
                                        rank={index + 6}
                                        tier="silver"
                                    />
                                ))}
                            </div>
                        </div>
                    )}

                    {responses.length === 0 && (
                        <div className="text-center py-12 glass rounded-xl border-dashed border-2 border-light-border dark:border-dark-border">
                            <MessageSquare className="w-12 h-12 text-light-muted mx-auto mb-4 opacity-30" />
                            <p className="text-light-muted">No responses yet for this post.</p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}

function ResponseDetailCard({ response, rank, tier }: { response: any, rank: number, tier: 'gold' | 'silver' }) {
    const isGold = tier === 'gold';

    return (
        <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: rank * 0.05 }}
            className={`flex flex-col md:flex-row gap-4 p-5 rounded-2xl border transition-all ${isGold
                ? 'bg-primary-500/5 border-primary-500/30'
                : 'bg-light-bg dark:bg-dark-bg border-light-border dark:border-dark-border'
                }`}
        >
            <div className="flex items-start gap-4 flex-1">
                <div className={`relative flex-shrink-0`}>
                    <Avatar src={response.userAvatar} alt={response.userName} size="md" />
                    <div className={`absolute -top-2 -right-2 w-6 h-6 rounded-full flex items-center justify-center text-[10px] font-bold text-white shadow-lg ${isGold ? 'bg-primary-500' : 'bg-secondary-500'
                        }`}>
                        #{rank}
                    </div>
                </div>

                <div className="flex-1 space-y-2">
                    <div className="flex items-center justify-between">
                        <h3 className="font-bold text-light-text dark:text-dark-text">{response.userName}</h3>
                        <span className="text-[10px] text-light-muted dark:text-dark-muted">
                            {formatDistanceToNow(response.createdAt, { addSuffix: true })}
                        </span>
                    </div>
                    <p className="text-sm text-light-text dark:text-dark-text leading-relaxed">
                        {response.content}
                    </p>

                    {response.grade !== undefined && (
                        <div className="pt-3 flex flex-wrap gap-2 items-center">
                            <Badge variant={response.grade >= 70 ? 'success' : 'primary'} className="text-[10px] font-bold uppercase transition-all hover:scale-105">
                                Score: {response.grade}/100
                            </Badge>
                            {response.rewardStatus === 'paid' && (
                                <Badge variant="warning" className="text-[10px] flex items-center gap-1 animate-pulse">
                                    <Trophy className="w-3 h-3" />
                                    Awarded
                                </Badge>
                            )}
                            {response.feedback && (
                                <div className="w-full mt-2 p-3 rounded-lg bg-light-bg dark:bg-dark-bg/50 border border-light-border dark:border-dark-border/50 italic text-xs text-light-muted">
                                    Admin feedback: "{response.feedback}"
                                </div>
                            )}
                        </div>
                    )}
                </div>
            </div>
        </motion.div>
    );
}
